import { useEffect, memo, useState } from "react";
import { ethers } from "ethers";
import styled from "styled-components";
import { useManageFeeContract } from "hooks/useContract";
import { useDaoContext } from "views/dao/provider";
import { VENTURE_MANAGEFEE_MANAGER } from "utils/constant";
import api from "api";
import CopyStr from "components/copy";
import LinkImg from "img/shareGray.png";
import { useWeb3React } from "@web3-react/core";
import useFundLogo from "hooks/useFundLogo";
import publicJs from "utils/publicJs";
import { useMemo } from "react";
import Avatar from "components/common/avatar";

const ManagementFeeVote = memo((props) => {
  const { wid, orgMap } = props;
  const { provider } = useWeb3React();
  const {
    state: { componentAddressMap },
  } = useDaoContext();
  const fundLogo = useFundLogo();

  const manageFeeContract = useManageFeeContract(
    componentAddressMap.get(VENTURE_MANAGEFEE_MANAGER)
  );

  const [toAddress, setToAddress] = useState();
  const [amount, setAmount] = useState(0);
  const [symbol, setSymbol] = useState("");

  const addressName = useMemo(() => {
    return orgMap && toAddress && orgMap[toAddress];
  }, [toAddress, orgMap]);

  useEffect(async () => {
    try {
      const wdata = await manageFeeContract.withdrawApply(wid);
      setToAddress(wdata.proposer);
      const erc20 = await api.erc20.getContract(provider, wdata.token);
      setSymbol(await erc20.symbol());
      setAmount(
        ethers.utils.formatUnits(
          wdata.amount.toString(),
          await erc20.decimals()
        )
      );
    } catch (error) {
      console.error("get wdata failed", error);
    }
  }, [wid, provider]);
  return (
    <>
      <dl>
        <dt>Address</dt>
        <dd className="bgBox">
          <Avatar address={toAddress} size="30px" />
          <Account>
            {addressName && <div className="name">{addressName}</div>}
            <div className="address">
              {publicJs.AddresstoShow(toAddress)}{" "}
              <CopyStr address={toAddress} gray={true} />
            </div>
          </Account>
        </dd>
      </dl>
      <dl>
        <dt>Amount</dt>
        <dd className="flexLine">
          <span className="num font-bold">{amount}</span>
          <img src={fundLogo} alt="" className="symbolBox" />
          <span className="symbol">{symbol}</span>
        </dd>
      </dl>
    </>
  );
});

export default ManagementFeeVote;

const Account = styled.div`
    margin-left: 10px;
`