import React, { memo, useState, useEffect } from "react";
import { useWeb3React } from "@web3-react/core";
import styled from "styled-components";
import { ethers } from "ethers";
import { useTranslation } from "react-i18next";
import { Select, Input } from "antd";
import { useCSVReader } from "react-papaparse";
import {
  InputLabel,
  InputItem,
  InputNumberItem,
} from "../../components/common/inputItem";
import DateBox from "components/common/closeDate";

import { useNetworkData } from "../../hooks/useNetwork";
import TokenSelect from "./tokenSelect";
import { useAppContext } from "components/provider/appProvider";
import { LOCAL_KEY as BASIC_LOCAL_KEY } from "./basicInfo";
import { useMemo } from "react";
import {Radio} from "antd";
const KEY = "fundraising";
export const LOCAL_KEY = `CreateDao_${KEY}`;

const TemplateConfig = memo(() => {
  const { t } = useTranslation();

  const { chainId } = useWeb3React();
  const {
    state: { fundList },
  } = useAppContext();

  const networkData = useNetworkData(chainId);

  const [selectToken, setSelectToken] = useState({});
  const [raiseTime, setRaiseTime] = useState();

  const [equityToken, setEquityToken] = useState({});
  const [softCap, setSoftCap] = useState();
  const [hardCap, setHardCap] = useState();

  const [addressInput, setAddressInput] = useState("");
  const { CSVReader } = useCSVReader();
  const [value, setValue] = useState(0);
  const [whitelistVisible, setWhitelistVisible] = useState(false)
  const [init, setInit] = useState(true);

  // useEffect(() => {
  //   if (!networkData) return;
  //   const funds = (networkData.constant && networkData.constant.fundList) || [];
  //   setFundList(funds);
  // }, [networkData]);

  const handleEquitToken = (e) => {
    const { name, value } = e.target;
    setEquityToken({
      ...equityToken,
      [name]: value,
    });
  };

  const handleCSVfile = (data) => {
    const addressList = [];
    data.forEach((address) => {
      if (ethers.utils.isAddress(address[0])) addressList.push(address[0]);
    });

    const arr = Array.from(new Set(addressList));
    setAddressInput(arr.join("\n"));
  };

  const savePage = () => {
    const data = {
      equityToken,
      softCap,
      hardCap,
      selectToken,
      raiseTime,
      addressInput,
    };
    sessionStorage.setItem(LOCAL_KEY, JSON.stringify(data));
  };

  useEffect(() => {
    if (init) {
      return;
    }
    savePage();
  }, [selectToken, raiseTime]);

  useEffect(() => {
    try {
      const data = JSON.parse(sessionStorage.getItem(BASIC_LOCAL_KEY) || "");
      if (data.name) {
        setEquityToken({ name: data.name });
      }
    } catch (error) {}
    try {
      const data = JSON.parse(sessionStorage.getItem(LOCAL_KEY) || "");
      if (data.selectToken) {
        setSelectToken(data.selectToken);
      }
      if (data.equityToken && (data.selectToken.name || data.selectToken.symbol)) {
        setEquityToken(data.equityToken);
      }
      if (data.softCap) {
        setSoftCap(data.softCap);
      }
      if (data.hardCap) {
        setHardCap(data.hardCap);
      }
      if (data.raiseTime) {
        setRaiseTime(data.raiseTime);
      }
      if (data.addressInput) {
        setAddressInput(data.addressInput);
      }
    } catch (error) {
      console.warn("get local failed", error);
    } finally {
      setInit(false);
    }
  }, []);

  const hardcapError = useMemo(() => {
    console.log(softCap, hardCap, softCap > hardCap)
    return softCap > hardCap
  }, [softCap, hardCap])


  const onChange = e => {
    setValue(e.target.value);
    if (e.target.value === 1) {
      setWhitelistVisible(true)
    }else{
      setWhitelistVisible(false)
    }
  };

  return (
    <>
      <Intro>{t("Create.FundraisingIntro")}</Intro>
      <FlexRow>
        <Block>
          <InputItem
            title={t("Create.TokenName")}
            placeholder={t("Create.FillTokenName")}
            name="name"
            onChange={handleEquitToken}
            onBlur={savePage}
            autoComplete="off"
            value={equityToken.name}
          />
        </Block>
        <Block>
          <InputItem
            title={t("Create.TokenSymbol")}
            placeholder={t("Create.FillTokenSymbol")}
            name="symbol"
            onChange={handleEquitToken}
            onBlur={savePage}
            autoComplete="off"
            value={equityToken.symbol}
          />
        </Block>
        <Block />
      </FlexRow>
      <Block />
      <FlexRow>
        <Block>
          <InputLabel title={t("Create.FundType")} />
          <TokenSelect
            list={fundList}
            selected={selectToken}
            onChange={setSelectToken}
          />
        </Block>
        <Block>
          <InputNumberItem
            title={t("Create.softcap")}
            placeholder={t("Create.Enter")}
            value={softCap}
            controls={false}
            onChange={(v) => setSoftCap(v)}
            onBlur={savePage}
            suffix={selectToken.symbol}
            toolTip={t("Hover.Create.Target", {
              token: equityToken.symbol,
              fund: selectToken.symbol,
            })}
          />
        </Block>
        <Block>
          <InputNumberItem
            title={t("Create.hardcap")}
            value={hardCap}
            controls={false}
            onChange={(v) => setHardCap(v)}
            onBlur={savePage}
            isError={hardcapError}
            suffix={selectToken.symbol}
          />
        </Block>
      </FlexRow>
      <FlexRow>
        <Block>
          <InputLabel title={t("Create.RaiseTime")} />
          <DateBox targetTime={raiseTime} updateTargetTime={setRaiseTime} />
        </Block>
        <Block />
        <Block />
      </FlexRow>
      <FlexRow>
        <Radio.Group onChange={(e)=>onChange(e)} value={value}>

          <div><Radio value={0}>No participation restrictions</Radio></div>
          <Top>
            <div><Radio value={1}>{t("Create.AddWhite")}</Radio></div>
            {
              whitelistVisible && <BtmBgBox>
                {/*<InputLabel title={t("Create.AddWhite")} />*/}
                <WhiteInput
                    rows={6}
                    name="Address"
                    value={addressInput}
                    placeholder={t("Create.Enter")}
                    onChange={(e) => setAddressInput(e.target.value)}
                    onBlur={savePage}
                />

                <CSVReader
                    onUploadAccepted={(results) => {
                      handleCSVfile(results.data);
                    }}
                >
                  {({ getRootProps }) => (
                      <>
                        <ChooseBox {...getRootProps()}>
                          <ChooseBtn> {t("Create.ChooseFile")}</ChooseBtn>
                          <FileTip>{t("Create.whilelistFileTip")}</FileTip>
                        </ChooseBox>
                      </>
                  )}
                </CSVReader>
              </BtmBgBox>
            }

          </Top>

        </Radio.Group>
      </FlexRow>

    </>
  );
});

export default TemplateConfig;

const Intro = styled.div`
  width: 100%;
  color: #606793;
  line-height: 19px;
  margin-bottom: 40px;
`;

const FlexRow = styled.div`
  display: flex;
  margin-bottom: 30px;

  &.flex {
    display: flex;
  }
`;

const Block = styled.div`
  flex: 1;
  margin-left: 50px;
  &:first-child {
    margin-left: 0;
  }
`;

const SelectStyled = styled(Select)`
  width: 100%;
  height: 44px;
  .ant-select-selector {
    height: 100% !important;
    background: #ffffff !important;
    border: none !important;
    box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  }
  .ant-select-selection-placeholder {
    line-height: 44px !important;
  }
`;

const ChooseBox = styled.div`
  display: flex;
  align-items: center;
  margin-top: 12px;
`;

const ChooseBtn = styled.div`
  width: 129px;
  height: 35px;
  line-height: 35px;
  background: #ffe6f2;
  border-radius: 14px;
  text-align: center;
  cursor: pointer;
  font-size: 14px;
  padding: 0 26px;
  white-space: nowrap;
  color: #10164B;
`;

const FileTip = styled.span`
  margin-left: 10px;
  line-height: 35px;
`;

const WhiteInput = styled(Input.TextArea)`
  width: 406px;
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1),
    0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  border: none;
  padding: 11px 14px;
  color: #10164b;
`;

const BtmBgBox = styled('div')`
  padding: 20px 0 0 20px;
  span,button{
    color: #10164B;
    width: 100%;
    font-size: 14px;
  }
`
const Top = styled('div')`
  margin-top: 20px;
`