import styled from "styled-components";
import CloseImg from "../../img/closeImg.png";

const BgBox = styled.div`
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9999999;
  background: rgba(247, 247, 247, 0.95);
  display: flex;
  justify-content: center;
  align-items: center;
`;
const BoxCenter = styled.div`
  width: ${(props) => props.width + "px"};
  background: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(16, 22, 75, 0.1);
  border-radius: 32px;
  display: flex;
  flex-direction: column;
  padding: 15px;
  height: ${(props) => (props.height ? props.height + "px" : "80vh")};
  position: relative;
`;

const TitleBox = styled.div`
  position: absolute;
  right: 15px;
  top: 15px;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
  img {
    cursor: pointer;
  }
`;
const ContentBox = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  height: 630px;
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none; /* Chrome Safari */
  }

  @media (max-width: 1366px) {
    height: 430px;
  }
  @media (min-width: 1367px) and (max-width: 1440px) {
  }
`;

export default function General(props) {
  const { closeModal, children, width, height } = props;

  const handleClose = () => {
    closeModal();
  };
  return (
    <BgBox>
      <BoxCenter width={width} height={height}>
        <TitleBox>
          <img src={CloseImg} alt="" onClick={() => handleClose()} />
        </TitleBox>
        <ContentBox>{children}</ContentBox>
      </BoxCenter>
    </BgBox>
  );
}
