import React, { useState } from "react";
import { useCallback } from "react";
import { useEffect } from "react";
import { useDaoContext } from "views/dao/provider";

const useVenturePeriod = (ventureContract, updateSignal) => {
  const [period, setPeriod] = useState();
  const { dispatch } = useDaoContext();

  const handlePeriod = useCallback(async () => {
    const period = await ventureContract.getVenturePeriod();
    console.log("period: ", period);
    setPeriod(period);
  }, [ventureContract]);

  useEffect(async () => {
    ventureContract && handlePeriod();
  }, [ventureContract]);

  useEffect(() => {
    if (updateSignal === "period") {
      handlePeriod();
      dispatch({ type: "UPDATE_FUND_OR_PERIOD", payload: null });
    }
  }, [updateSignal]);

  return period;
};

export default useVenturePeriod;
