import { ethers } from "ethers";
import React, { useCallback, useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

import { useDaoContext } from "../../../views/dao/provider";
import { useOrgContract, useDaoOrgContract } from "../../../hooks/useContract";
import useSigner from "hooks/useSigner";
import { ORG_MANAGER, DAO_ORG, VOTE_MANAGER } from "../../../utils/constant";
import api from "../../../api";
import publicJs from "../../../utils/publicJs";

import CheckInput from "../../common/checkInput";
import Button from "../../common/button";
import VoteLoadingModal, { VoteTxStatus } from "./loading";
import DateBox from "components/common/date";
import { useAppContext } from "components/provider/appProvider";
import VentureVoteConfig from "./voteConfig";
import { checkBonus } from "utils/check";
import { getVoteIdByEvent } from "utils/utils";

export default function AddManager(props) {
  const { handleClose } = props;

  const { t } = useTranslation();
  const { state: daoState } = useDaoContext();
  const { componentAddressMap } = daoState;
  const { dispatch } = useAppContext();

  const orgContract = useOrgContract(componentAddressMap.get(ORG_MANAGER));
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const [newGpAddress, setNewGpAddress] = useState();
  const [addressInvalidMsg, setAddressInvalidMsg] = useState();

  const [newGpBonus, setNewGpBonus] = useState();
  const [gplist, setGplist] = useState([]);
  const [endTime, setEndTime] = useState();
  const [validTime, setValidTime] = useState();

  const [status, setStatus] = useState(VoteTxStatus.Default);
  const [txHash, setTxHash] = useState();
  const [minSupportNumber, setMinSupportNumber] = useState();
  const [newVoteId, setVoteId] = useState()

  const getGpList = useCallback(async () => {
    const { lst } = await api.org.getDaoMembersList(orgContract, 0, 1000);
    console.log(lst);
    setGplist(lst);
  }, [orgContract]);

  useEffect(() => {
    if (orgContract) {
      getGpList();
    }
  }, [orgContract]);

  const onChangeNewGpAddress = useCallback((e) => {
    const v = e.target.value;
    setNewGpAddress(v);
    if (!ethers.utils.isAddress(v)) {
      setAddressInvalidMsg(t("Msg.InvalidAddress"));
    } else {
      setAddressInvalidMsg(null);
    }
  }, []);

  const handleChangeBonus = useCallback(
    (i, v) => {
      const newArray = [...gplist];
      newArray[i].bonus = Number(v);
      setGplist(newArray);
    },
    [gplist]
  );

  const checkValid = () => {
    if (!newGpAddress || !ethers.utils.isAddress(newGpAddress)) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid new manager address", closable: true },
      });
      return;
    }
    if (!newGpBonus) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid new manager's bonus", closable: true },
      });
      return;
    }
    if (
      !minSupportNumber ||
      minSupportNumber <= 0 ||
      minSupportNumber > gplist.length + 1
    ) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid min support number", closable: true },
      });
      return;
    }
    if (
      !checkBonus([
        ...gplist,
        {
          address: newGpAddress,
          bonus: newGpBonus,
        },
      ])
    ) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: t("Msg.BonusMatch"), closable: true },
      });
      return;
    }

    return true;
  };

  const createVote = async () => {


    let duration = Math.round((endTime - Date.now()) / 1000);
    if (duration < 86400) {
      duration = 86400;
    }
    setValidTime(duration);

    if (!checkValid()) {
      return;
    }

    let txHash;
    try {
      setStatus(VoteTxStatus.Loading);
      const res = await api.vote.newAddGPVote(daoOrg, signer, {
        newGP: newGpAddress,
        vote_time: duration,
        minSurportRequire: minSupportNumber,
        gpList: gplist.map((g) => g.address).concat([newGpAddress]),
        gpBonus: gplist.map((g) => g.bonus * 100).concat([newGpBonus * 100]),
      });

      txHash = res.hash;
      const r = await res.wait();
      const event = r.events.find(e => e.address === componentAddressMap.get(VOTE_MANAGER))
      const vid = getVoteIdByEvent(event);
      setVoteId(vid);
      setStatus(VoteTxStatus.Success);
    } catch (error) {
      setStatus(VoteTxStatus.Failed);
      setTxHash(txHash);
      console.error("newAddGPVote failed", error);
    }
  };

  const closeLoading = useCallback(() => {
    setStatus(VoteTxStatus.Default);
    txHash && setTxHash();
  }, []);

  const closeAll = useCallback(() => {
    closeLoading();
    props.handleClose();
  }, [props.handleClose]);

  return (
    <AddBox>
      <VoteLoadingModal
        status={status}
        txHash={txHash}
        voteId={newVoteId}
        endTime={validTime}
        handleRetry={createVote}
        // handleGoto={ }
        handleClose={closeLoading}
        handleCloseAll={closeAll}
      />
      <dl>
        <dt>{t("Vote.NewAddress")}</dt>
        <dd>
          <CheckInput
            autoFocus
            value={newGpAddress}
            onChange={onChangeNewGpAddress}
            errorMsg={addressInvalidMsg}
          />
        </dd>
      </dl>
      <dl>
        <dt>{t("Vote.ResetInterest")}</dt>
        <dd>
          <UlBox>
            {gplist.map((gp, index) => (
              <li key={index}>
                <div className="lft">
                  <span>Address</span>
                  <div className="address">
                    {publicJs.AddresstoShow(gp.address)}
                  </div>
                </div>
                <div className="rht">
                  <span>Bonus</span>
                  <BonusInput>
                    <input
                      type="number"
                      value={gp.bonus}
                      onChange={(e) => handleChangeBonus(index, e.target.value)}
                    />
                    <span>%</span>
                  </BonusInput>
                </div>
              </li>
            ))}
            <li>
              <div className="lft">
                <span>Address</span>
                <div className="address">
                  {newGpAddress && publicJs.AddresstoShow(newGpAddress)}
                </div>
              </div>
              <div className="rht">
                <span>Bonus</span>
                <BonusInput>
                  <input
                    type="number"
                    value={newGpBonus}
                    onChange={(e) => setNewGpBonus(Number(e.target.value))}
                  />
                  <span>%</span>
                </BonusInput>
              </div>
            </li>
          </UlBox>
        </dd>
      </dl>
      <VentureVoteConfig
        minSupportNumber={minSupportNumber}
        updateMinSupportNumber={setMinSupportNumber}
        gpLength={gplist.length + 1}
      />
      <DateBox
        label={t("Vote.Expiry")}
        targetTime={endTime}
        updateTargetTime={setEndTime}
      />
      <BtnGroup>
        <Button onClick={handleClose}>{t("Cancel")}</Button>
        <Button primary onClick={createVote}>
          {t("Submit")}
        </Button>
      </BtnGroup>
    </AddBox>
  );
}

const BonusInput = styled.div`
  flex: 1;
  margin-left: 30px;
  display: flex;
  align-items: center;
  input {
    text-align: right;
    flex: 1;
    margin-left: 0 !important;
    margin-right: 20px !important;
  }
`;

const AddBox = styled.div`
  dt {
    font-size: 14px;
    font-weight: 400;
    color: #a6a6b9;
    line-height: 19px;
    margin-bottom: 10px;
  }
  dd {
    position: relative;
  }
  dl {
    margin-bottom: 20px;
  }
`;

const UlBox = styled.ul`
  li {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  input {
    flex-grow: 1;
    margin: 0 30px;
    width: 50px;
    border: 0;
    background: transparent;
    font-size: 14px;
  }
  .lft,
  .rht {
    background: #f9f9f9;
    border-radius: 8px;
    padding: 12px;
    display: flex;

    align-items: center;
  }
  .lft {
    width: 55%;
    justify-content: flex-start;
  }
  .rht {
    width: 43%;
    justify-content: space-between;
  }
  .address {
    padding-left: 30px;
  }
  span {
    font-size: 12px;
    font-weight: 400;
    color: #10164b;
    opacity: 0.6;
    line-height: 14px;
  }
`;

const BtnGroup = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 36px 0 20px 0;
  button {
    &:first-child {
      margin-right: 30px;
    }
  }
`;
