import React, { useEffect, useState } from "react";

import * as echarts from "echarts/lib/echarts";
import "echarts/lib/chart/pie";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";
import { useCallback } from "react";

const option = {
  title: {
    show: true,
    text: "Ongoing",
    x: "center",
    y: "45%",
    backgroundColor: "#FFEBF5",
    borderRadius: 8,
    padding: 10,
    textStyle: {
      fontFamily: "AvenirNext-Regular",
      fontSize: 14,
      fontStyle: "normal",
      fontWeight: "normal",

      color: "#D51172",
    },
  },
  tooltip: {
    trigger: "item",
    confine: false,
    formatter: "{c} <br/>{b}:  ({d}%)",
  },
  color: ["#79DFC4", "#FFA4D1", "#7DDC8B", "#96E9F2", "#FFC475", "#D51172"],
  legend: {
    show: true,
    orient: "horizontal",
    x: "center",
    y: "80%",
    align: "left",
    icon: "circle",
    textStyle: {
      fontWeight: 300,
      color: "#10164B",
      fontSize: "16px",
    },
  },
  series: [
    {
      name: "",
      type: "pie",
      radius: ["43%", "60%"],
      center: ["50%", "47%"],
      avoidLabelOverlap: false,
      label: {
        show: false,
      },
      emphasis: {
        label: {
          show: false,
        },
      },
      labelLine: {
        normal: {
          show: true,
        },
      },
      data: [],
    },
  ],
};

export default function VoteEcharts({ status, optionlist }) {
  const getStatusText = useCallback((status) => {
    switch (status) {
      case 0:
        return "Ongoing";
      case 1:
        return "Agreed";
      case 2:
        return "Rejected";
      default:
        return "";
    }
  }, []);

  useEffect(() => {
    if (optionlist.length) {
      const c = echarts.init(document.getElementById("main"));
      c.setOption({
        title: {
          show: true,
          text: getStatusText(status),
          x: "center",
          y: "43%",
          backgroundColor: "#FFEBF5",
          borderRadius: 8,
          padding: 10,
          textStyle: {
            fontFamily: "AvenirNext-Regular",
            fontSize: 14,
            fontStyle: "normal",
            fontWeight: "normal",

            color: "#D51172",
          },
        },
        tooltip: {
          trigger: "item",
          confine: false,
          formatter: "{c} <br/>{b}:  ({d}%)",
        },
        color: [
          "#79DFC4",
          "#FFA4D1",
          "#7DDC8B",
          "#96E9F2",
          "#FFC475",
          "#D51172",
        ],
        legend: {
          show: true,
          orient: "horizontal",
          x: "center",
          y: "80%",
          align: "left",
          icon: "circle",
          textStyle: {
            fontWeight: 300,
            color: "#10164B",
            fontSize: "16px",
          },
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["43%", "60%"],
            center: ["50%", "47%"],
            avoidLabelOverlap: false,
            label: {
              show: false,
            },
            emphasis: {
              label: {
                show: false,
              },
            },
            labelLine: {
              normal: {
                show: true,
              },
            },
            data: optionlist,
          },
        ],
      });
    }
  }, [optionlist]);

  return <div id="main" style={{ width: "100%", height: "100%" }} />;
}
