import { GraphQLClient, gql } from "graphql-request";

const client = new GraphQLClient(
  "https://api.thegraph.com/subgraphs/name/aave/aave-v2-polygon-mumbai"
);

export function getPrice() {
  const query = gql`
    {
      priceOracles {
        usdPriceEth
      }
    }
  `;
  return client.request(query);
}

export function getReserves() {
  const query = gql`
    {
      priceOracles {
        usdPriceEth
      }
      reserves {
        id
        underlyingAsset
        symbol
        name
        decimals
        borrowingEnabled
        pool {
          id
          active
          paused
        }
        price {
          id
          priceInEth
          priceSource
        }
        liquidityRate
        averageStableRate
        variableBorrowRate

        availableLiquidity
        stableBorrowRate
        totalLiquidity

        totalATokenSupply
        totalCurrentVariableDebt
        totalScaledVariableDebt

        aEmissionPerSecond
        vEmissionPerSecond
        sEmissionPerSecond

        aToken {
          id
          underlyingAssetAddress
          underlyingAssetDecimals
        }
      }
    }
  `;

  return client.request(query);
}

export function getUserReserves(vaultAddress) {
  const query = gql`
    query AAVE($user: int!) {
      priceOracles {
        usdPriceEth
      }
      userReserves(where: { user: $user }) {
        id
        currentTotalDebt
        currentStableDebt
        scaledATokenBalance
        principalStableDebt
        currentATokenBalance
        lastUpdateTimestamp
        reserve {
          id
          symbol
          name
          decimals
          totalLiquidity
          reserveInterestRateStrategy
          stableBorrowRate
          variableBorrowRate
          variableBorrowIndex
          totalScaledVariableDebt
          liquidityRate
          underlyingAsset
          lastUpdateTimestamp
          stableDebtLastUpdateTimestamp
          liquidityIndex
          price {
            oracle {
              usdPriceEth
            }
            priceInEth
          }
        }
        user {
          id
          borrowedReservesCount
          unclaimedRewards
          lifetimeRewards
          reserves {
            id
          }
        }
      }
    }
  `;

  return client.request(query, {
    user: vaultAddress,
  });
}
