import {
  memo,
  useMemo,
  useEffect,
  useState,
  useCallback,
  forwardRef,
  useImperativeHandle,
} from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { useDaoContext } from "views/dao/provider";
import useVaultTokens from "hooks/useVaultTokens";
import { VAULT_MANAGER } from "utils/constant";
import TokenSelect from "../selectToken";
import { ethers } from "ethers";
import { useAppContext } from "components/provider/appProvider";

const TransferToken = memo(
  forwardRef((props, ref) => {
    const { t } = useTranslation();

    const { dispatch } = useAppContext();
    const { state: dapState } = useDaoContext();
    const { componentAddressMap } = dapState;

    const tokens = useVaultTokens(componentAddressMap.get(VAULT_MANAGER));
    const [tokenAmount, setTokenAmount] = useState();

    const [currentToken, setCurrentToken] = useState();

    const list = useMemo(() => {
      return tokens ? tokens.map((t) => ({ ...t })) : [];
    }, [tokens]);

    const chooseToken = useCallback((t) => {
      setCurrentToken(t);
    }, []);

    const setMaxAmount = useCallback(() => {
      if (currentToken) {
        setTokenAmount(Number(currentToken.balance));
      }
      console.log(currentToken);
    }, [currentToken]);

    const updateTokenAmount = useCallback((e) => {
      setTokenAmount(e.target.value);
    }, []);

    useImperativeHandle(ref, () => ({
      data: currentToken && {
        tokenAddress: currentToken.address,
        tokenAmount: ethers.utils.parseUnits(
          String(tokenAmount || 0),
          currentToken.deci
        ),
      },
      checkValid: () => {
        if (
          !tokenAmount ||
          !currentToken ||
          Number(currentToken.balance < tokenAmount)
        ) {
          dispatch({
            type: "MSGTYPE",
            payload: { msg: "invalid amount", closable: true },
          });
          return;
        }
        return true;
      },
    }));

    return (
      <>
        <TokenSelect
          list={list}
          selected={currentToken}
          onChange={chooseToken}
        />
        <dl>
          <dt>{t("Vote.Amount")}</dt>
          <dd>
            <AmountBox>
              <input
                type="number"
                value={tokenAmount}
                onChange={updateTokenAmount}
              />
              <Max onClick={setMaxAmount}>{t("Max")}</Max>
              <span>{currentToken && currentToken.symbol}</span>
            </AmountBox>
          </dd>
        </dl>
      </>
    );
  })
);

const AmountBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  padding: 0 20px;
  min-height: 44px;
  input {
    flex-grow: 1;
    border: 0;
  }
  span {
    font-size: 14px;
    font-weight: 400;
    color: #0e164e;
    line-height: 19px;
  }
`;

const Max = styled.div`
  text-align: center;
  width: 48px;
  background: #fae9f1;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 400;
  color: #d52473;
  line-height: 16px;
  margin: 0 20px;
  cursor: pointer;
`;

export default TransferToken;
