import { memo, useCallback, useState } from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";

import TableList from "./tokenTable";
import Button from "components/common/button";
import AddTokenModal from "components/modal/addTokenModal";
import EmptyBox from "components/common/empty";

import { useLazyQuery } from "@apollo/client";
import { QueryMyTokens } from "api/graphql/explore";
import { useEffect } from "react";
import { useWeb3React } from "@web3-react/core";
import api from "api";
import { ethers } from "ethers";
import { getShortDisplay } from "utils/publicJs";

const MyTokens = memo(() => {
  const { t } = useTranslation();
  const { chainId, provider, account } = useWeb3React();

  const [showModal, setShowModal] = useState(false);
  const [list, setList] = useState([]);

  const [queryMyTokens] = useLazyQuery(QueryMyTokens, {
    onCompleted(data) {
      console.log("===data", data);
      setList(
        (data.myTokenList.data || []).map((d) => ({
          ...d,
          balance: 0.0,
        }))
      );
    },
    onError(error) {},
  });

  const doQueryTokens = useCallback(() => {
    queryMyTokens({
      variables: {
        chainId,
        page: 1,
        size: 10000,
      },
    });
  }, [chainId]);

  useEffect(() => {
    if (!chainId) {
      return;
    }
    doQueryTokens();
  }, [chainId]);

  const getBalance = useCallback(
    async (address) => {
      const contract = await api.erc20.getContract(provider, address);
      // TODO remove the hardcode deci
      const deci = 6
      // const deci = await contract.decimals();
      const balance = await contract.balanceOf(account);
      const balanceDisplay = ethers.utils.formatUnits(balance, deci);
      return {
        // balance: balance.toNumber(),
        balanceDisplay: getShortDisplay(balanceDisplay),
      };
    },
    [provider, account]
  );

  useEffect(() => {
    if (!list.length) {
      return;
    }
    const newList = [];
    const reqs = list.map((d) => getBalance(d.address));
    Promise.all(reqs).then((datas) => {
      datas.forEach((item, i) => {
        newList.push({
          ...list[i],
          // balance: item.balance,
          balanceDisplay: item.balanceDisplay,
        });
      });
      setList(newList);
    });
  }, [list]);

  const handleModal = useCallback(() => {
    setShowModal(true);
  }, []);

  const closeModal = useCallback(() => {
    doQueryTokens();
    setShowModal(false);
  }, [doQueryTokens]);

  const updateListBalance = async (token) => {
    const data = await getBalance(token.address);

    const newList = list.map((t) => {
      if (t.address === token.address) {
        return { ...t };
      } else {
        return {
          ...t,
          // balance: data.balance,
          balanceDisplay: data.balanceDisplay,
        };
      }
    });
    setList(newList);
  };

  return (
    <>
      {showModal && <AddTokenModal closeModal={closeModal} />}
      {list.length ? (
        <TableList list={list} updatelist={updateListBalance} />
      ) : (
        <EmptyBox />
      )}

      <ButtonBoxBg>
        <Button primary width={105} height={34} onClick={handleModal}>
          {t("Explore.AddToken")}
        </Button>
      </ButtonBoxBg>
    </>
  );
});

export default MyTokens;

const ButtonBoxBg = styled.div`
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: flex-end;
  background: #fff;
`;
