import { useWeb3React, useMemo } from "@web3-react/core";
import { memo, useState, useEffect } from "react";

import { useDaoContext } from "views/dao/provider";
import useWalletConnect from "hooks/useWalletConnect";
import useSigner from "hooks/useSigner";
import { useDaoOrgContract } from "hooks/useContract";
import { DAO_ORG, VAULT_MANAGER } from "utils/constant";
import api from "api";
import TxSubmitModal from "./modals/TxSubmitModal";

import { useWCTContext } from "components/provider/WCTProvider";
import { CONNECTION_STATUS } from "utils/constant";
import { useAppContext } from "components/provider/appProvider";
import { useTranslation } from "react-i18next";

const WalletConnectIndex = memo(() => {
  const { account, chainId } = useWeb3React();
  const { dispatch } = useAppContext();
  const { t } = useTranslation();

  const {
    state: { status, connectURI, behavior, source },
    dispatch: dispatchEvent,
  } = useWCTContext();

  const {
    state: {
      daoId,
      fullHandle,
      componentAddressMap,
      whoAmI: { isOwner, isGp },
    },
  } = useDaoContext();
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));

  const signer = useSigner();

  const { wcClientData, wcConnect, wcDisconnect, rejectWithMessage } =
    useWalletConnect(componentAddressMap.get(VAULT_MANAGER), chainId);

  const [txdata, setTxdata] = useState({});
  const [showTxModal, setShowTxModal] = useState(false);

  // callback
  const onReceivedTx = (data) => {
    // eth_sendTransaction
    console.log("[onReceivedTx]", data);
    const tx = (data.txs || [])[0];
    if (tx) {
      tx.id = data.id;
      setTxdata(tx);
      setShowTxModal(true);
    }
  };
  const onReceivedMultiTx = (data) => {
    // gs_multi_send
    console.log("[onReceivedMultiTx]", data);
    setTxdata(data);
  };
  const onReceivedSign = (data) => {
    // eth_sign
    console.log("[onReceivedSign]", data);
  };
  const onReceivedPersionalSign = (data) => {
    // personal_sign
  };

  const onSubmitTx = async () => {
    setShowTxModal(false);
    // TODO refresh the list
    // call contract
    console.log("txdata: ", txdata);
    try {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
      await api.vault.submitTransaction(daoOrg, signer, {
        account: account,
        vaultContractAddress: txdata.to,
        value: txdata.value,
        source: source,
        bytes: txdata.data,
      });
    } catch (error) {
      console.error(error);
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  const onCancelTx = () => {
    setShowTxModal(false);
    rejectWithMessage(txdata.id);
  };

  useEffect(() => {
    console.log("wcClientData:", wcClientData);
    if (wcClientData) {
      dispatchEvent({
        type: "SET_STATUS",
        payload: CONNECTION_STATUS.CONNECTED,
      });
      dispatchEvent({
        type: "SET_SOURCE",
        payload: wcClientData.name,
      });
    } else {
      dispatchEvent({
        type: "SET_STATUS",
        payload: CONNECTION_STATUS.DISCONNECTED,
      });
    }
    dispatchEvent({ type: "SET_WCDATA", payload: wcClientData });
  }, [wcClientData]);

  useEffect(() => {
    if (status === CONNECTION_STATUS.CONNECTING) {
      try {
        wcConnect({
          uri: connectURI,
          callbacks: [
            onReceivedTx,
            onReceivedMultiTx,
            onReceivedSign,
            onReceivedPersionalSign,
          ],
        });
      } catch (error) {
        console.error("connected failed", error);
      }
    }
  }, [connectURI]);

  useEffect(() => {
    if (behavior === "disconnect") {
      wcDisconnect();
    }
  }, [behavior, wcDisconnect]);

  useEffect(() => {
    const vaultAddress = componentAddressMap.get(VAULT_MANAGER);
    if (!wcClientData || !vaultAddress) {
      const session = localStorage.getItem(`SubDAO_SESSION_${vaultAddress}`);
      if (session) {
        wcConnect({
          session: JSON.parse(session),
          callbacks: [
            onReceivedTx,
            onReceivedMultiTx,
            onReceivedSign,
            onReceivedPersionalSign,
          ],
        });
      }
    }
  }, [wcClientData, componentAddressMap]);

  return (
    <TxSubmitModal
      visible={showTxModal}
      data={txdata}
      onSubmit={onSubmitTx}
      onCancel={onCancelTx}
    />
  );
});

export default WalletConnectIndex;
