import {
  memo,
  useMemo,
  useEffect,
  useState,
  useCallback,
  useImperativeHandle,
  forwardRef,
} from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import DropSvg from "components/svg/common/select";
import { ReceiveBox, DropRC } from "./style";
import { ethers } from "ethers";
import useVaultNFTs from "hooks/useVaultNFTs";
import NoneNFT from "img/common/none.png";
import { useAppContext } from "components/provider/appProvider";

const TransferNFT = memo(
  forwardRef((props, ref) => {
    const { t } = useTranslation();

    const nftList = useVaultNFTs();
    const { dispatch } = useAppContext();

    const [currentIdx, setCurrentIdx] = useState();
    const [showID, setShowID] = useState(false);
    const [tokenAmount, setTokenAmount] = useState();

    useEffect(() => {
      document.addEventListener("click", () => {
        setShowID(false);
      });
    }, []);

    const currentToken = useMemo(() => {
      return nftList[currentIdx];
    }, [currentIdx]);

    const updateTokenAmount = useCallback((e) => {
      setTokenAmount(Number(e.target.value));
    }, []);

    const handleToken = (e) => {
      e.nativeEvent.stopImmediatePropagation();
      setShowID(true);
    };
    const chooseToken = (num, e) => {
      e.stopPropagation();
      setShowID(false);
      setCurrentIdx(num);
    };

    useImperativeHandle(ref, () => ({
      data: currentToken && {
        nftAddress: currentToken.address,
        tokenId: currentToken.id,
        tokenAmount: ethers.BigNumber.from(currentToken.is_erc721 ? 0 : tokenAmount || 1),
      },
      checkValid: () => {
        if (!currentToken) {
          return false;
        }
        if (currentToken.amount < tokenAmount || tokenAmount < 1) {
          dispatch({
            type: "MSGTYPE",
            payload: { msg: "invalid amount", closable: true },
          });
          return false;
        }
        return true;
      },
    }));

    return (
      <>
        <dl>
          <dt></dt>
          <ReceiveBox>
            <ReceiveBoxHead className="box" onClick={(e) => handleToken(e)}>
              {currentIdx == null && <div className="tips">Select a token</div>}
              {currentToken && (
                <>
                  <img
                    src={currentToken.image || NoneNFT}
                    alt=""
                    className="lft"
                  />
                  <div>
                    <div className="title font-medium">
                      {currentToken.name}
                    </div>
                    <div className="Total">{currentToken.amount}</div>
                  </div>
                </>
              )}
              <DropSvg class="drop" />
            </ReceiveBoxHead>
            {showID && (
              <DropRC>
                {nftList.map((item, index) => (
                  <li key={index} onClick={(e) => chooseToken(index, e)}>
                    <img src={item.image || NoneNFT} alt="" className="lft" />
                    <div>
                      <div className="title font-medium">{item.name}</div>
                      <div className="Total">{item.amount}</div>
                    </div>
                  </li>
                ))}
              </DropRC>
            )}
          </ReceiveBox>
        </dl>
        <dl>
          <dt>{t("Vote.TokenID")}</dt>
          <TokenDD>{currentToken && currentToken.id}</TokenDD>
        </dl>
        {currentToken && currentToken.amount > 1 && (
          <dl>
            <dt>{t("Vote.Amount")}</dt>
            <dd>
              <AmountBox>
                <input
                  type="number"
                  value={tokenAmount}
                  onChange={updateTokenAmount}
                />
                <span>{currentToken && currentToken.symbol}</span>
              </AmountBox>
            </dd>
          </dl>
        )}
      </>
    );
  })
);

export default TransferNFT;

const AmountBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  padding: 0 20px;
  min-height: 44px;
  input {
    flex-grow: 1;
    border: 0;
  }
  span {
    font-size: 14px;
    font-weight: 400;
    color: #0e164e;
    line-height: 19px;
  }
`;

const TokenDD = styled.dd`
  height: 44px;
  line-height: 44px;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  padding: 0 20px;
  border-radius: 8px;
  background: #ffffff;
`;

const ReceiveBoxHead = styled.div`
  width: 100%;
  position: relative;
  .drop {
    position: absolute;
    right: 0px;
  }
`;
