import { useEffect, useState } from "react";
import { ethers } from "ethers";
import { useWeb3React } from "@web3-react/core";
import { useVaultContract } from "./useContract";
import api from "../api";
import { getShortDisplay } from "../utils/publicJs";
import { useWeb3 } from "../api/contracts";
import { useCallback } from "react";

const getToken = async (address, web3Instance) => {
  try {
    const mockERC20 = await api.erc20.getContract(web3Instance, address);
    const symbol = await mockERC20.symbol();
    const deci = await mockERC20.decimals();
    return { symbol, deci, address };
  } catch (error) {
    console.error(`get token failed - ${address}`, error);
  }
};

const useManageFee = (manageFeeAddress, options = {}) => {
  const { useBalance } = options;
  const { provider: web3Instance } = useWeb3React();

  const manageFeeContract = useVaultContract(manageFeeAddress);
  const [token, setToken] = useState();

  useEffect(() => {
    web3Instance && manageFeeContract && getTokens();
  }, [manageFeeContract, web3Instance]);

  const getTokens = useCallback(async () => {
    let data = await api.vault.getTokenList(manageFeeContract);
    if (!data || data.length === 0) {
      return;
    }

    const tokenAddress = data[0];
    const token = await getToken(tokenAddress, web3Instance);
    if (useBalance) {
      const _balance = await manageFeeContract.getBalanceOf(token.address);
      const _format_balance = ethers.utils.formatUnits(_balance, token.deci);
      token.balance = _format_balance;
      token.balanceDisplay = getShortDisplay(_format_balance);
    }
    setToken(token);
  }, [manageFeeContract, web3Instance, useBalance]);

  return token;
};

export default useManageFee;
