import React, { memo, useState, useEffect, useMemo } from "react";
import styled from "styled-components";

import DecorationImg from "img/swap/decoration.png";
import LogoImg from "img/swap/logo.png";
import DownArrow from "img/swap/downArrow.png";
import TopArrowImg from "img/swap/topArrow.png";

const RouteRow = memo(({ path }) => {
  // const p = [
  //   [{ symbol: "ABC" }],
  //   [{ symbol: "ABC" }, { symbol: "ABC" }],
  //   [{ symbol: "ABC" }, { symbol: "ABC" }],
  //   [{ symbol: "ABC" }],
  // ];
  return (
    <AutoMid>
      {path.map((t, i) => {
        if (t.length === 1) {
          return (
            <React.Fragment>
              <div className="first" key={i}>
                <img src={LogoImg} alt="" />
              </div>
              {i === t.length - 1 && <div className="line" />}
            </React.Fragment>
          );
        } else {
          return (
            <React.Fragment>
              <div className="second">
                <div>
                  <img src={t[0].logo || LogoImg} alt="" />
                  <img src={t[1].logo || LogoImg} alt="" />
                </div>
              </div>
              <div className="line" />
            </React.Fragment>
          );
        }
      })}
    </AutoMid>
  );
});

const SwapRoute = memo(({ tokenPath, tokens }) => {
  const [showAuto, setShowAuto] = useState(false);

  const handleAuto = () => {
    setShowAuto(!showAuto);
  };

  const paths = useMemo(() => {
    // let pairs;
    // if (tokenPath.length === 2) {
    //   pairs = [tokenPath];
    // }
    // tokenPath.forEach((p, i) => {
    //   const pair = [p];
    //   if (i > 0) {
    //   }
    // });
    return tokenPath.map(p => [p])
  }, [tokenPath]);

  return (
    <li className="b12">
      <div className="auto" onClick={() => handleAuto()}>
        <div className="lftDecor">
          <img src={DecorationImg} alt="" />
          <span>Auto Router</span>
        </div>
        <div>
          {!showAuto && <img src={DownArrow} alt="" />}
          {showAuto && <img src={TopArrowImg} alt="" />}
        </div>
      </div>
      {showAuto && (
        <>
          <RouteRow path={paths} />
          <div className="tipsBtm">
            This route optimizes your total output by considering split routes,
            multiple hops, and the gas cost of each step.
          </div>
        </>
      )}
    </li>
  );
});

export default SwapRoute;

const AutoMid = styled.div`
  display: flex;
  margin-top: 20px;
  align-items: center;
  justify-content: space-between;
  .line {
    width: 90px;
    height: 1px;
    border-bottom: 2px dotted #d8d8d8;
  }
  .first {
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: 400;
    color: #0b1362;
    line-height: 14px;
    img {
      margin-right: 6px;
    }
  }
  .second {
    display: flex;
    align-items: center;
    img:last-child {
      margin-left: -8px;
    }
    span {
      margin-left: 6px;
      font-size: 12px;
      font-weight: 400;
      color: #0b1362;
      line-height: 14px;
    }
  }
`;
