import React, { memo } from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

const ImgBox = styled.div`
    margin:auto 30px;
    img{
      width: 32px;
    }
  @media (max-width: 1440px) {
    margin:auto 10px auto 24px;
    img{
      width: 24px;
    }
  }
`

const SideNavLayout = memo((props) => {
  const { t } = useTranslation();
  const { navs, onChangeNav, containerStyle, step } = props;
  const [curNav, setCurNav] = useState();

  useEffect(()=>{
      if(navs.length <= 0) return;
      setCurNav(step);
  },[step,navs]);

  return (
    <SideNavLayoutStyled className="container">
      <SideNav>
        <NavUl>
          {navs.map((n, i) => (
            <NavLi
              key={i}
              onClick={() => {
                if (n.key === curNav) {
                  return;
                }
                setCurNav(n.key);
                onChangeNav && onChangeNav(n.key);
              }}
              className={n.key === curNav ? "selected" : ""}
            >
                {
                    !n.icon && <span className="dot" />
                }
                {
                    !!n.icon && <ImgBox>
                        {
                            n.key === curNav && <img src={n.iconHover} alt=""/>
                        }
                        {
                            n.key !== curNav && <img src={n.icon} alt=""/>
                        }
                    </ImgBox>
                }
                {
                    n.newDot && <div className="newDot"/>
                }

              {t(n.name)}
            </NavLi>
          ))}
        </NavUl>
      </SideNav>
      <LayoutContainer style={containerStyle}>
          {props.children}
    </LayoutContainer>
    </SideNavLayoutStyled>
  );
});

export default SideNavLayout;

const SideNavLayoutStyled = styled.div`
  display: flex;
  align-items: stretch;
  flex-grow: 1;
  height: 100%;
  @media (max-width:1440px)  {
    align-items: flex-start;
  }
`;

const SideNav = styled.div`
  width: 286px;
  background: #ffffff;
  box-shadow: 0px 0px 16px 0px rgba(16, 22, 75, 0.06);
  border-radius: 24px;
  margin-right: 16px;
  padding: 26px 25px;

  @media (max-width:1366px) {
    width: 182px;
    padding: 30px 10px;
  }
  @media (min-width:1367px)  and (max-width: 1440px) {
    width: 212px;
    padding: 30px 10px;
  }
  @media (max-width:1440px)  {
  height: 500px;
  }
`;

const LayoutContainer = styled.div`
  flex: 1;
  background: #ffffff;
  box-shadow: 0px 0px 16px 0px rgba(16, 22, 75, 0.06);
  border-radius: 24px;
  border: 1px solid #f0f0f2;
  //padding: 40px 20px;
`;

const NavUl = styled.ul``;

const NavLi = styled.li`
  display: flex;
  align-items: center;
  border-radius: 14px;
  line-height: 70px;
  font-size: 18px;
  cursor: pointer;
  position: relative;
  .newDot{
    width: 9px;
    height: 9px;
    background: #FF0000;
    border-radius: 9px;
    position: absolute;
    right: 5px;
    top: 15px;
  }

  .dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #ccc;
    display: block;
    margin: 0 30px;
  }
  &.selected {
    background: #d51172;
    color: #ffffff;
    .dot {
      background: transparent;
      border: 2px solid #ffffff;
    }
  }
  @media (max-width:1440px)  {
    font-size: 14px;
    line-height: 50px;
  }
`;
