import React, { useEffect, useState } from 'react'
import { useMemo } from 'react';
import { useWeb3 } from "../api/contracts";
import getNetworkData from "../network"

const useNetwork = () => {
    const { state } = useWeb3();
    const { ChainID } = state;

    const [networkData, setNetworkData] = useState()

    useEffect(
        async () => {
            if (!ChainID) return

            const data = getNetworkData(ChainID)
            setNetworkData(data)

        },
        [ChainID]
    )
    return networkData
}

export default useNetwork


export const useNetworkData = (chainID) => {
    return useMemo(() => {
        if (!chainID) {
            return
        }
        return getNetworkData(chainID)
    }, [chainID])
}