import React, { memo, useEffect, useState, useCallback, useMemo } from "react";
import { Modal, Input } from "antd";
import { ethers } from "ethers";

import { useTranslation } from "react-i18next";
import styled from "styled-components";

import Button from "components/common/button";
import CheckInput from "components/common/checkInput";
import { TokenAvatar } from "components/common/avatar";
import { Token } from "graphql";
import { useWeb3React } from "@web3-react/core";
import { useAppContext } from "components/provider/appProvider";
import useSigner from "hooks/useSigner";
import { useERC20Contract } from "hooks/useContract";

const SendModal = memo((props) => {
  const { show, token, closeModal } = props;
  const { t } = useTranslation();
  // const mockToken = {
  //   name: "USDT",
  //   symbol: "USDT",
  //   balance: 1000.9998014,
  //   balanceDisplay: "1000.99980",
  //   address: "0x174535B59f340B4C501D5543Bf8d9249342615Dd",
  // };

  const { account } = useWeb3React();
  const { dispatch } = useAppContext();

  const signer = useSigner();
  const ERC20contract = useERC20Contract(token && token.address);

  const [toAddress, setToAddress] = useState();
  const [addressInvalidMsg, setAddressInvalidMsg] = useState();
  const [amount, setAmount] = useState('');

  const onChangeToAddress = useCallback((e) => {
    const v = e.target.value;
    setToAddress(v);
    if (!ethers.utils.isAddress(v)) {
      setAddressInvalidMsg(t("Msg.InvalidAddress"));
    } else {
      setAddressInvalidMsg(null);
    }
  }, []);

  const setMaxAmount = useCallback(() => {
    setAmount(Number(token.balance));
  }, [token]);

  const confirm = async () => {
    if (!ethers.utils.isAddress(toAddress)) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid address", closable: true },
      });
      return;
    }
    if (!amount || amount <= 0 || amount > token.balance) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid amount", closable: true },
      });
      return true;
    }
    dispatch({
      type: "LOADINGTYPE",
      payload: t("Msg.Sending", { symbol: token.symbol }),
    });

    const deci = await ERC20contract.decimals();
    const formatAmount = ethers.utils.parseUnits(String(amount), deci);
    try {
      const allowanceAmount = await ERC20contract.connect(signer).allowance(
        account,
        toAddress
      );

      const allowance = Number(ethers.utils.formatUnits(allowanceAmount, deci));
      if (!allowance || allowance < amount) {
        const res = await ERC20contract.connect(signer).approve(
          toAddress,
          formatAmount,
          { gasLimit: 50000 }
        );

        await res.wait();
      }
    } catch (error) {
      console.error("allowance failed", error);
      dispatch({ type: "LOADINGTYPE", payload: null });
      return;
    }
    try {
      const res = await ERC20contract.transfer(toAddress, formatAmount);
      await res.wait();
    } catch (error) {
      console.error("transfer failed", error);
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };
  return (
    <ModalBox
      visible={show}
      onCancel={closeModal}
      footer={null}
      destroyOnClose={true}
      maskClosable={false}
      width={423}
    >
      <div className="title font-medium">
        <span>{t("Send")}</span>
      </div>
      <SendContent>
        <div className="line">
          <Label>{t("Explore.SendingFrom")}</Label>
          <SendingFrom>{account}</SendingFrom>
        </div>
        <div className="line">
          <Label>{t("Vote.Recipient")}</Label>
          <CheckInput
            autoFocus
            value={toAddress}
            onChange={onChangeToAddress}
            errorMsg={addressInvalidMsg}
          />
        </div>
        <TokenBox>
          <AmountLeft>
            <Label>{t("Vote.Amount")}</Label>
            <InputStyled
              value={amount}
              autoComplete="off"
              type="number"
              onChange={({ target: { value } }) => {
                setAmount(value);
              }}
            />
          </AmountLeft>
          {token && (
            <InfoRight>
              <div>
                <TokenAvatar size="24px" symbol={token.symbol} />
                <span className="token">{token.symbol}</span>
              </div>
              <BalanceBox>
                <span>
                  {t("Balance")}: {token.balanceDisplay}
                </span>
                <span className="max" onClick={setMaxAmount}>
                  {t("Max")}
                </span>
              </BalanceBox>
            </InfoRight>
          )}
        </TokenBox>
      </SendContent>

      <Footer>
        <Button primary onClick={confirm} style={{ width: "100%" }}>
          {t("Submit")}
        </Button>
      </Footer>
    </ModalBox>
  );
});
export default SendModal;

const ModalBox = styled(Modal)``;

const SendContent = styled.div`
  margin-top: 10px;
  .line {
    margin-top: 20px;
  }
`;

const SendingFrom = styled.div`
  height: 44px;
  line-height: 44px;
  background: #f9f9f9;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  color: #a6a6b7;
  padding-left: 20px;
`;

const Label = styled.div`
  color: #a6a6b9;
  line-height: 19px;
  margin-bottom: 10px;
`;

const TokenBox = styled.div`
  height: 76px;
  background: #f9f9f9;
  border-radius: 8px;
  border-radius: 8px;
  position: relative;
  color: #a6a6b7;
  margin-top: 20px;
`;

const AmountLeft = styled.div`
  .lebel {
    color: #a6a6b9;
    line-height: 19px;
  }
  padding-left: 20px;
  padding-top: 10px;
`;

const InputStyled = styled(Input)`
  &.ant-input {
    background: transparent !important;
    height: 27px !important;
    padding: 0;
    font-size: 18px;
    border-radius: 0 !important;
    border: none;
    &:focus {
      box-shadow: none;
    }
  }
`;

