import React, { useReducer, createContext, useContext } from "react";
import { LOCALSTORAGE } from "utils/constant";
import { checkTokenValid } from "utils/auth";

const AppContext = createContext();

const reducer = (state, action) => {
  switch (action.type) {
    case "CHANGE_WALLET":
      return { ...state, selectedWallet: action.payload };
    case "CHANGE_ACCOUNT":
      return { ...state, selectedAccount: action.payload };
    case "LOADINGTYPE":
      return { ...state, loadingType: action.payload };
    case "MSGTYPE":
      return { ...state, msgType: action.payload };
    case "SET_FUNDLIST":
      return { ...state, fundList: action.payload };
    case "SET_ALL_TOKENS":
      return { ...state, tokens: action.payload };
    case "SET_TRENDINGS":
      return { ...state, trendingDaos: action.payload };
    case "SET_RECOMMEND":
      return { ...state, recommendDaos: action.payload };
    case "SET_EXPLORE_SCAN":
      return { ...state, exploreScan: action.payload };
    default:
      throw new Error(`Unknown type: ${action.type}`);
  }
};

const initState = {
  tokens: [],
  selectedWallet: null,
  msgType: null,
  loadingType: null,
  fundList: [],
  trendingDaos: [],
  recommendDaos: [],
  exploreScan: "",
};

const AppProvider = (props) => {
  const st = { ...initState };
  const selectedWallet = localStorage.getItem(LOCALSTORAGE.WALLET);
  st.selectedWallet = !checkTokenValid() ? null : selectedWallet;

  const [state, dispatch] = useReducer(reducer, st);
  // TODO localstorage

  return (
    <AppContext.Provider value={{ state, dispatch }}>
      {props.children}
    </AppContext.Provider>
  );
};

export const useAppContext = () => ({ ...useContext(AppContext) });

export default AppProvider;
