import { useCallback, useState } from "react";
import { Tooltip } from "antd";
import * as history from "history";
import { useParams, useNavigate } from "react-router-dom";
import styled from "styled-components";

import ConfirmModal from "../modals/confirm";
import RejectModal from "../modals/reject";
import { UserTxSTATUS } from "../TxDetail";
import EmptyBox from "components/common/empty";

import ExpiryImg from "img/voteNV/explains.png";
import TransferImg from "img/voteNV/transfer.png";

import { useLazyQuery } from "@apollo/client";
import { SubgraphApiQuery, getWCTHistory } from "api/graphql/subQuery";
import { useEffect } from "react";
import { useWeb3React } from "@web3-react/core";

import PaginationBox from "components/common/pagination";

export default function TxList(props) {
  const navigate = useNavigate();
  const { fullhandle } = useParams();

  const { chainId, daoId } = useWeb3React();

  const [confirmVisible, setConfirmVisible] = useState(false);
  const [rejectVisible, setRejectVisible] = useState(false);
  const [list, setList] = useState([]);

  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [total, setTotal] = useState(0);

  const toGo = (id) => {
    navigate(`/dao/${fullhandle}/app/nft?tx=${id}`);
  };

  const showConfirm = useCallback((e) => {
    e.stopPropagation();
    setConfirmVisible(true);
  }, []);

  const closeConfirm = useCallback(() => {
    setConfirmVisible(false);
  }, []);

  const handleConfirm = useCallback(() => {
    //   TODO
  }, []);

  const showReject = useCallback((e) => {
    e.stopPropagation();
    setRejectVisible(true);
  }, []);

  const closeReject = useCallback(() => {
    setRejectVisible(false);
  }, []);

  const handleReject = useCallback(() => {
    //   TODO
  }, []);

  const statusDisplay = useCallback((status) => {
    switch (status) {
      case UserTxSTATUS.Confirmed:
        return "Confirmed";
      case UserTxSTATUS.Rejected:
        return "Rejected";
      default:
        return status;
    }
  }, []);

  const [getHistory] = useLazyQuery(SubgraphApiQuery, {
    onCompleted(resp) {
      console.log("====", resp);
      try {
        const data = JSON.parse(resp.subgraphApi);
        setList(data.transactionRecords || []);
      } catch (error) {}
    },
    onError(error) {},
  });

  useEffect(() => {
    if (!chainId || !daoId) {
      return;
    }
    getHistory({
      variables: {
        query: getWCTHistory(daoId, page, pageSize, true),
        chainId,
      },
      fetchPolicy: 'network-only',
    });
  }, [daoId, chainId, page, pageSize]);

  const onChangePage = useCallback((v) => {
    setPage(v);
  }, []);

  const onShowSizeChange = useCallback((c, size) => {
    setPageSize(size);
  }, []);

  if (!list.length) {
    return <EmptyBox />;
  }

  return (
    <TableBox>
      <ConfirmModal
        show={confirmVisible}
        handleClose={closeConfirm}
        handleConfirm={handleConfirm}
      />
      <RejectModal
        show={rejectVisible}
        handleClose={closeReject}
        handleConfirm={handleReject}
      />
      <table cellPadding="0" cellSpacing="0">
        <thead>
          <tr>
            <th>No.</th>
            <th>Source</th>
            <th>Contract interaction</th>
            <th>Created time</th>
            <th className="Left">
              Confirm/Reject
              <Tooltip title="The ratio of approval to disapproval.">
                <img src={ExpiryImg} alt="" />
              </Tooltip>
            </th>
          </tr>
        </thead>
        <tbody>
          {list.map((tx) => (
            <tr key={tx.id} onClick={() => toGo(tx.txID)}>
              <td className="font-medium">{tx.txID}</td>
              <td className="font-medium Left">
                <img src={TransferImg} alt="" />
                <span>{tx.source}</span>
              </td>
              <td className="font-medium">{tx.interaction}</td>
              <td className="font-medium">{tx.createdAt}</td>
              <td className="font-medium Left">
                <span className="percent">
                  {tx.confirmedNum} : {tx.rejectedNum}
                </span>{" "}
                <StatusButton>{statusDisplay(tx.state)}</StatusButton>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <div style={{ textAlign: "right" }}>
        <PaginationBox
          showQuickJumper
          pageSize={pageSize}
          defaultCurrent={page}
          total={total}
          onChange={onChangePage}
          onShowSizeChange={onShowSizeChange}
        />
      </div>
    </TableBox>
  );
}

