import ConnectContract from './connectContract';
import Accounts from "./Account";
import publicJs from "../utils/publicJs";

import {ethers} from 'ethers'
import abi from "../abi/web3Abi/MockERC20.json"

let erc20contract;
const InitErc20 = async (state, dispatch, address) =>  {

    const {apiState, api,} = state;
    if (apiState !== 'READY' ) return;

    try {
        erc20contract = await ConnectContract(api, 'erc20',address);
        dispatch({ type: 'SET_ERC20', payload: {erc20contract,address} });
    } catch (e) {
        console.error(e);
    }
    return erc20contract;
}

const value = 0;
const gasLimit = -1;

const approveOp = async (erc20contract, spender, total,cb) => {
    const AccountId = await Accounts.accountAddress();
    const injector = await Accounts.accountInjector();

    if (erc20contract === null || !erc20contract || !erc20contract.tx || !AccountId) return;

    await erc20contract.tx.approve({value, gasLimit}, spender, total)
        .signAndSend(AccountId, { signer: injector.signer }, (result) => {
          if (result.status.isFinalized || result.status.isInBlock) {
             cb(true);
         }
       });
}

const queryInfo = async (erc20contract) => {

    const AccountId = await Accounts.accountAddress();
    if (erc20contract === null || !erc20contract || !erc20contract.query || !AccountId) return;

    let data = await erc20contract.query.queryInfo(AccountId, {value, gasLimit});
    data = publicJs.formatResult(data);

    return data;
};

const BalanceOf = async (erc20contract) => {

    const AccountId = await Accounts.accountAddress();
    if (erc20contract === null || !erc20contract || !erc20contract.query || !AccountId) return;
    let data = await erc20contract.query.balanceOf(AccountId, {value, gasLimit},AccountId);

    data = publicJs.formatResult(data);

    return data;
};

const transfer = async (erc20contract,obj,cb) => {
    const AccountId = await Accounts.accountAddress();
    const injector = await Accounts.accountInjector();

    const { address, amount} = obj;

    if (erc20contract === null || !erc20contract || !erc20contract.query || !AccountId) return;
    let data;
    await erc20contract.tx.transfer({value, gasLimit}, address, amount)
        .signAndSend(AccountId, { signer: injector.signer }, (result) => {
            console.error(result)
            if (result.status.isInBlock ||result.status.isFinalized) {
                // console.log("====result.status.isInBlock",result.status.isInBlock || result.status.isFinalized);
                // data = (result.status.isInBlock || result.status.isFinalized);
                // return
                cb(true)
            }
        });
    return data;
}

async function getContract(web3Instance, erc20Address) {
    return await new ethers.Contract(erc20Address, abi, web3Instance);
}

export default {
    getContract,

    InitErc20,
    approveOp,
    BalanceOf,
    transfer,
    queryInfo
}
