import React, { useCallback, useMemo, useState } from "react";
import { Input, Button, Tooltip } from "antd";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

import WalletConnectLogo from "./modals/logo";
import LoadingNew from "components/loadingNEW";
import Tip from "components/svg/tip";
import Qrcode from "img/walletConnect/qrcode.png";

import { CONNECTION_STATUS } from "utils/constant";
import { useWCTContext } from "components/provider/WCTProvider";

const WalletConnect = (props) => {
  const { t } = useTranslation();

  const {
    state: { status: connectionStatus, wcClientData },
    dispatch: dispatchEvent,
  } = useWCTContext();

  const handlePaste = (event) => {
    const items = event.clipboardData.items;

    for (const index in items) {
      const item = items[index];

      if (item.kind === "string" && item.type === "text/plain") {
        dispatchEvent({
          type: "SET_STATUS",
          payload: CONNECTION_STATUS.CONNECTING,
        });
        dispatchEvent({
          type: "SET_WCURI",
          payload: event.clipboardData.getData("Text"),
        });
      }
    }
  };

  const onClickDisconnect = useCallback(() => {
    dispatchEvent({ type: "SET_BEHAVIOR", payload: "disconnect" });
  }, []);

  const statusDisplay = useMemo(() => {
    switch (connectionStatus) {
      case CONNECTION_STATUS.CONNECTING:
        return <LoadingNew />;
      default:
        // return connectionStatus;
        return <></>
    }
  }, [connectionStatus]);

  const ConnectedDisplay = () => {
    return (
      <ConnectedStyled>
        <DappStyled>
          <img src={wcClientData.icons && wcClientData.icons[0]} alt="" />
          <div>
            <div className="title">{wcClientData.name}</div>
            <div className="desc">{wcClientData.description}</div>
          </div>
        </DappStyled>
        <CancelConnectStyled>
          <ConnectTip>
            <span className="text">Connected</span>
            <Tooltip
              overlayClassName="tipTool"
              placement="top"
              title={t("walletConnect.ConnectedTip")}
              color="#FFFFFF"
              arrowPointAtCenter
            >
              <Tip />
            </Tooltip>
          </ConnectTip>
          <DisconnectBtn type="primary" onClick={onClickDisconnect}>
            {t("walletConnect.DisconnectBtn")}
          </DisconnectBtn>
        </CancelConnectStyled>
      </ConnectedStyled>
    );
  };

  const DisconnectDisplay = () => {
    return (
      <WalletConnectStyled>
        <div className="connectWrapper">
          <WalletConnectLogo small={true} />
          <span className="intro">{t("walletConnect.beforeConnectIntro")}</span>
        </div>
        <div className="connectWrapper">
          <WalletConnectInput
            onPaste={handlePaste}
            placeholder={t("walletConnect.qrcodePlaceholder")}
            prefix={<QrcodeStyled src={Qrcode} alt="" />}
          />

          <span className="status">{statusDisplay}</span>
        </div>
      </WalletConnectStyled>
    );
  };

  if (connectionStatus === CONNECTION_STATUS.CONNECTED && wcClientData) {
    return <ConnectedDisplay />;
  } else {
    return <DisconnectDisplay />;
  }
};

export default WalletConnect;

const WalletConnectStyled = styled.div`
  margin-bottom: 26px;
  .connectWrapper {
    display: flex;
    align-items: center;
    &:first-child {
      margin-bottom: 15px;
    }
    .intro {
      color: #a6a6b7;
      font-size: 14px;
      margin-left: 10px;
    }
    .status {
      margin-left: 10px;
    }
  }
`;

const WalletConnectInput = styled(Input)`
  width: 360px;
  height: 44px;
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  margin-left: 1px;
  border-radius: 8px;
  border: none;
`;

const QrcodeStyled = styled.img`
  width: 24px;
  height: 24px; ;
`;

const ConnectedStyled = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 26px;
`;

const DappStyled = styled.div`
  display: flex;
  align-items: center;

  img {
    height: 66px;
    margin-right: 17px;
  }
  .title {
    color: #101352;
    line-height: 21px;
    font-size: 18px;
  }
  .desc {
    font-size: 14px;
    line-height: 16px;
    margin-top: 5px;
  }
`;

const CancelConnectStyled = styled.div`
  display: flex;
`;

const DisconnectBtn = styled(Button)`
  width: 100px;
  height: 34px !important;
  margin-left: 30px;
`;

const ConnectTip = styled.div`
  display: flex;
  align-items: center;
  .text {
    color: #969bb2;
    margin-right: 5px;
  }
`;
