import React, { useEffect, useState, useCallback } from "react";

import { useLazyQuery } from "@apollo/client";
import { GetGateway } from "api/graphql/dao";

const useGateway = () => {
  const [gateway, setGateway] = useState();

  const [getGateway] = useLazyQuery(GetGateway, {
    onCompleted(data) {
      console.log("===", data);
      setGateway(data.gateway);
    },
    onError(error) {
      console.error("get gateway failed", error);
    },
  });

  const reqGateway = useCallback(async () => {
    try {
      return await getGateway();
    } catch (error) {
      return "";
    }
  }, []);

  useEffect(() => {
    reqGateway();
  }, []);

  return gateway;
};

export default useGateway;
