import React from "react";
import styled from "styled-components";

import TxPanel from "./TxPanel";
import BackNav from "components/common/backNav";

import { useWeb3React } from "@web3-react/core";
import { useAaveActionContract } from "hooks/useContract";
import { useNetworkData } from "hooks/useNetwork";
import { useDaoContext } from "views/dao/provider";

import { useEffect, useMemo, memo } from "react";
import { VAULT_MANAGER } from "utils/constant";
import { getPrice } from "api/graphql/aave";
import { useState } from "react";
import BigNumber from "bignumber.js";
import { RAY } from "@aave/math-utils";

const DEFI = memo(() => {
  const { chainId } = useWeb3React();

  const networkData = useNetworkData(chainId);
  const contract = useAaveActionContract(
    networkData && networkData.mainAddress.actionVaultAave
  );

  const {
    state: { componentAddressMap },
  } = useDaoContext();

  const vaultAddress = useMemo(() => {
    return componentAddressMap.get(VAULT_MANAGER);
  }, [componentAddressMap]);

  const [health, setHealth] = useState();

  const computeData = async (totalCollateralETH) => {
    const resp = await getPrice()
    console.log("resp: ", resp)
    const usdPriceEth = BigNumber(resp.priceOracles[0].usdPriceEth);
    // marketReferencePriceInUsd = 1 / (queryResult.priceOracles[0].usdPriceEth / (10 ** 18))
    const x = BigNumber(totalCollateralETH)
    const r = BigNumber(1).div(usdPriceEth.div(BigNumber(10 ** 18)))
    console.log(x, r)
    console.log(x.toString(), r.toString())
  }

  useEffect(async () => {
    console.log(contract, networkData, vaultAddress)
    if (!contract || !networkData || !vaultAddress) {
      return;
    }
    const pool = networkData.aaveLendingPool;
    if (!pool) {
      return;
    }
    const data = await contract.getUserAccountData(
      pool,
      vaultAddress
    );
    console.log("====> result: ", data);
    setHealth(BigNumber(data.healthFactor.toString()).div(BigNumber(1e18)).toNumber().toFixed(2))
    console.log(data.totalCollateralETH.toString())
    // computeData(data.totalCollateralETH.toString())
  }, [networkData, contract, vaultAddress]);

  return (
    <Box>
      <BackNav to="app_settings" />
      <ContentBox id="contentBox">
        <div id="inner">
          <div>
            <Collection>
              You are currently in the Aave protocol interface, which is
              integrated by SubDAO. All trading parameter settings are the same
              as Aave.
            </Collection>
            <div>
              <UlBox>
                <dl>
                  <dt>Net worth</dt>
                  <dd className="font-demiBold">$891.34</dd>
                </dl>
                <dl>
                  <dt>Net APY</dt>
                  <dd className="font-demiBold">-24.71%</dd>
                </dl>
                <dl>
                  <dt>
                    Health factor{" "}
                    <div className="tag font-medium">Risk Details</div>
                  </dt>
                  <dd className="font-demiBold">{health}</dd>
                </dl>
              </UlBox>
            </div>
            <TxPanel />
          </div>
        </div>
      </ContentBox>
    </Box>
  );
});

export default DEFI;

const Box = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 40px 20px;
`;

const NavBox = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 34px;
  cursor: pointer;
`;

const ContentBox = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  #inner {
    height: 100%;
    overflow-y: auto;
  }
`;
const Collection = styled.div`
  display: flex;
  font-size: 16px;
  font-weight: 400;
  color: #0e164e;
  line-height: 22px;
  padding: 0 20px 30px;
`;

const UlBox = styled.div`
  display: flex;
  align-items: center;
  padding: 0 20px;
  dl {
    margin-right: 83px;
  }
  dt {
    font-size: 14px;
    font-weight: 400;
    color: #959bb4;
    line-height: 19px;
    .tag {
      display: inline-block;
      background: #ffebf5;
      border-radius: 7px;
      font-size: 12px;
      font-weight: 500;
      color: #d51172;
      line-height: 16px;
      padding: 6px;
      margin-left: 5px;
    }
  }
  dd {
    font-size: 18px;
    font-weight: 600;
    color: #10164b;
    line-height: 25px;
    margin-top: 10px;
  }
`;
