import { memo, useState, useCallback, useEffect } from "react";
import styled from "styled-components";

import DropSvg from "components/svg/common/select";

const TokenSelect = memo((props) => {
  const { list, onChange, selected } = props;
  const [show, setShow] = useState(false);

  useEffect(() => {
    document.addEventListener("click", () => {
      setShow(false);
    });
  }, []);

  const showList = useCallback(
    (e) => {
      e.nativeEvent.stopImmediatePropagation();
      setShow(!show);
    },
    [show]
  );

  const choose = useCallback(
    (item, e) => {
      e.stopPropagation();
      setShow(false);
      console.log(item, selected);
      if (!selected || item.address !== selected.address) {
        onChange(item);
      }
     
    },
    [selected]
  );

  return (
    <ReceiveBox onClick={showList}>
      <div className="box">
        {selected && (
          <>
            <img src={selected?.img} alt="" className="lft" />
            <div>
              <div className="title font-medium">{selected?.symbol}</div>
              <div className="Total">
                {selected?.balanceDisplay} {selected?.symbol}
              </div>
            </div>
          </>
        )}
      </div>
      <DropSvg />
      {show && (
        <DropRC>
          {list.map((item, index) => (
            <li onClick={(e) => choose(item, e)} key={`rc_${index}`}>
              <img src={item.img} alt="" className="lft" />
              <div>
                <div className="title font-medium">{item.symbol}</div>
                <div className="Total">
                  {item.balanceDisplay} {item.symbol}
                </div>
              </div>
            </li>
          ))}
        </DropRC>
      )}
    </ReceiveBox>
  );
});

export default TokenSelect;

const ReceiveBox = styled.div`
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px 10px 20px;
  position: relative;
  .box {
    display: flex;
    align-items: center;
  }
  .lft {
    width: 40px;
    height: 40px;
    border-radius: 40px;
    margin-right: 10px;
  }
  .title {
    font-size: 18px;
    font-weight: 500;
    color: #10164b;
    line-height: 24px;
  }
  .Total {
    font-size: 14px;
    font-weight: 500;
    color: #969cb4;
    line-height: 24px;
  }
`;
const DropBox = styled.ul`
  position: absolute;
  left: 0;
  top: 55px;
  z-index: 9999;
  padding: 10px 0;
  background: #ffffff;
  border-radius: 8px 8px 14px 14px;
  border: 1px solid #edeef3;
  width: 100%;
  li {
    padding: 6px 23px;
    cursor: pointer;
    &:hover {
      background: #fbfbfa;
    }
  }
`;

const DropRC = styled(DropBox)`
  top: 80px;
  li {
    display: flex;
    margin-bottom: 10px;
  }
`;
