import { useEffect, memo, useState } from "react";
import { useVoteExecutionManagerContract } from "hooks/useContract";
import { useDaoContext } from "views/dao/provider";

import publicJs from "utils/publicJs";
import { AddressBox } from "./styles";
import CopyStr from "components/copy";
import LinkImg from "img/shareGray.png";
import { PercentBox } from "./styles";
import Avatar from "components/common/avatar";

const RemoveManagerVote = memo((props) => {
  const { wid, orgMembersMap } = props;
  const {
    state: { componentAddressMap },
  } = useDaoContext();
  const voteExecutionManagerContract = useVoteExecutionManagerContract(
    componentAddressMap.get("VoteExecutionManager")
  );

  const [toAddress, setToAddress] = useState();
  const [gplist, setGplist] = useState();
  const [minSuportRequire, setMinSuportRequire] = useState();

  useEffect(async () => {
    if (!voteExecutionManagerContract || !Object.keys(orgMembersMap).length) {
      return;
    }
    const wdata = await voteExecutionManagerContract.getImpeachGp(wid);
    setToAddress(wdata[0]);

    const lst = [];
    for (const i in wdata[3]) {
      lst.push({
        address: wdata[3][i],
        bonus: wdata[4][i].toNumber() / 100,
        name: orgMembersMap[wdata[4][i]],
      });
    }
    setMinSuportRequire(wdata[2].toNumber());
    setGplist(lst);
  }, [voteExecutionManagerContract, wid, orgMembersMap]);

  return (
    <>
      <dl>
        <dt>Address</dt>
        <AddressBox>
          {toAddress} <CopyStr address={toAddress} gray={true} />
          <img src={LinkImg} alt="" />
        </AddressBox>
      </dl>
      {gplist.length > 0 && (
        <dd>
          <PercentBox>
            {gplist.map((item, index) => (
              <li key={index}>
                <div className="bgBox">
                  <Avatar
                    size="30px"
                    address={item.address}
                    style={{ marginRight: "10px" }}
                  />
                  <div>
                    <div className="name">Alex</div>
                    <div className="address">
                      <span>{publicJs.AddresstoShow(item.address)}</span>{" "}
                      <CopyStr address={item.address} gray={true} />
                      <img src={LinkImg} alt="" />
                    </div>
                  </div>
                </div>
                <div className="numBox">{item.bonus}</div>
                <div>%</div>
              </li>
            ))}
          </PercentBox>
        </dd>
      )}
      <dl>
        <dt>Reset carried interest for each manager</dt>
      </dl>
      <dl>
        <dt>Any votes require the confirmation of</dt>
        <dd>
          <span className="confirmBox">{minSuportRequire}</span> out of{" "}
          {gplist.length} managers
        </dd>
      </dl>
    </>
  );
});

export default RemoveManagerVote;
