import { memo } from "react";
import styled from "styled-components";

export const Button = memo((props) => {
  const { children, primary, width, height, className, ...rest } = props;
  return (
    <ButtonStyled
      width={width}
      height={height}
      className={primary ? `primary font-medium ${className}` : `font-medium ${className}`}
      {...rest}
    >
      {children}
    </ButtonStyled>
  );
});

export default Button;

export const ButtonGroup = memo((props) => {
  const { children, gap, ...rest } = props;
  return (
    <ButtonGroupStyled gap={gap} {...rest}>
      {children}
    </ButtonGroupStyled>
  );
});

const ButtonGroupStyled = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin: 36px 0 20px 0; */
  button {
    &:first-child {
      margin-right: ${(props) => props.gap || "30px"};
    }
  }
`;

const ButtonStyled = styled.button`
  width: ${(props) => (props.width && props.width + "px") || "140px"};
  height: ${(props) => props.height || props.height + "px" || "44px"};
  line-height: ${(props) =>
    (props.height && props.height - 2 + "px") || "44px"};
  box-sizing: border-box;
  border-radius: 14px;
  border: 1px solid #d52473;
  background: #ffffff;
  color: #d52473;
  cursor: pointer;
  &.primary {
    background: #d52473;
    color: #ffffff;
  }
`;
