import Accounts from "./Account";
import publicJs from "../utils/publicJs";

const value = 0;
const gasLimit = -1;

const bind = async (bindcontract, eth,cb) => {
    const AccountId = await Accounts.accountAddress();
    const injector = await Accounts.accountInjector();
    if (bindcontract === null || !bindcontract || !bindcontract.query || !AccountId) return;
    await bindcontract.tx.bind({value, gasLimit:280000n * 1000000n}, eth)
        .signAndSend(AccountId, { signer: injector.signer }, (result) => {
            if (result.status.isFinalized || result.status.isInBlock) {
                console.log('bindcontract finalized', result);
                cb(true)
            }
        });
};

const myEthAddress = async (bindcontract) => {

    const AccountId = await Accounts.accountAddress();
    if (bindcontract === null || !bindcontract || !bindcontract.query || !AccountId) return;
    let data = await bindcontract.query.myEthAddress(AccountId, {value, gasLimit});
    data = publicJs.formatResult(data);
    return data;
};

const isBind = async (bindcontract) => {

    const AccountId = await Accounts.accountAddress();
    if (bindcontract === null || !bindcontract || !bindcontract.query || !AccountId) return;
    let data = await bindcontract.query.isBind(AccountId, {value, gasLimit},AccountId);
    data = publicJs.formatResult(data);

    return data;
};


export default {
    bind,
    isBind,
    myEthAddress
}
