import { useEffect, useState, useCallback } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

import TimeImg from "img/management/time.png";
import RefreshImg from "img/management/refresh.png";
import RiseImg from "img/management/rise.png";

import BackNav from "components/common/backNav";
import NewVote from "components/modal/newVote";

export default function Period() {
  const { t } = useTranslation();

  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    const vheight = document.getElementById("contentBox").offsetHeight;
    const oHeight =
      document.body.clientHeight || document.documentElement.clientHeight;
    console.log("====oHeight===", oHeight, vheight);
    if  (oHeight > 900 && vheight >350) {
      document.getElementById("inner").style.height = `${vheight}px`;
    } else {
      document.getElementById("inner").style.height = `550px`;
    }
  }, []);

  const CloseModal = useCallback(() => {
    setShowModal(false);
  }, []);

  const handleModal = useCallback(() => {
    setShowModal(true);
  }, []);

  return (
    <Box>
      {showModal && (
        <NewVote
          selectedAction={true}
          selectedType={5}
          CloseModal={CloseModal}
        />
      )}
      <BackNav to="app_settings" />
      <ContentBox id="contentBox">
        <div id="inner">
          <div>
            <TitleBox className="font-medium">
              {t("Period.StartRedemption")}
            </TitleBox>
            <TipsBox>{t("Period.RedemptionText")}</TipsBox>
            <UlBox>
              <li>
                <div>
                  <img src={TimeImg} alt="" />
                  <div className="title font-medium">
                    {t("Period.Redemption")}
                  </div>
                  <div className="desc">
                    The redemption period is{" "}
                    <span className="font-bold">perpetual</span> until the last
                    equity token holder redeems their proceeds.{" "}
                  </div>
                </div>
              </li>
              <li>
                <div>
                  <img src={RefreshImg} alt="" />
                  <div className="title font-medium">
                    {t("Period.IntroTitle2")}
                  </div>
                  <div className="desc">
                    The redeemed tokens will be{" "}
                    <span className="font-bold">burned</span> , and the total
                    supply of equity tokens will be reduced accordingly.{" "}
                  </div>
                </div>
              </li>
              <li>
                <div>
                  <img src={RiseImg} alt="" />
                  <div className="title font-medium">
                    {t("Period.IntroTitle3")}
                  </div>
                  <div className="desc">
                    Suppose some investments have not been paid back, then the
                    current TAV will be <span className="font-bold">lower</span>{" "}
                    than the actual value, making the redemption value of those
                    who redeem later <span className="font-bold">higher</span>
                    than the former.
                  </div>
                </div>
              </li>
            </UlBox>
          </div>
          <BtnBox>
            <Button onClick={handleModal}>{t("Period.StartNow")}</Button>
          </BtnBox>
        </div>
      </ContentBox>
    </Box>
  );
}

const Box = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0 20px;
  padding: 40px 20px;
`;

const ContentBox = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  #inner {
    position: relative;
  }
`;

const BtnBox = styled.div`
  height: 80px;
  background: #fff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
`;
const Button = styled.div`
  height: 34px;
  padding: 0 15px;
  background: #d52473;
  border-radius: 8px;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  line-height: 34px;
  cursor: pointer;
`;

const UlBox = styled.div`
  display: flex;
  justify-content: space-between;
  max-width: 948px;
  li {
    width: 247px;
    background: #ffffff;
    box-shadow: 0px 0px 10px 0px rgba(16, 22, 75, 0.05);
    border-radius: 16px;
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 24px 20px;
    &:hover {
      background: rgba(233, 0, 115, 0.05);
      border: 1px solid #ffedf6;
    }
    img {
      margin-bottom: 24px;
    }
    .title {
      font-size: 16px;
      font-weight: 500;
      color: #10164b;
      line-height: 22px;
      margin-bottom: 10px;
    }
    .desc {
      font-size: 12px;
      font-weight: 400;
      color: #606793;
      line-height: 16px;
      span {
        color: #e56faa;
      }
    }
  }
`;

const TitleBox = styled.div`
  font-size: 18px;
  font-weight: 500;
  color: #10164b;
  line-height: 25px;
  margin-bottom: 10px;
`;

const TipsBox = styled.div`
  font-size: 14px;
  font-weight: 400;
  color: #10164b;
  line-height: 22px;
  margin-bottom: 50px;
  max-width: 948px;
`;
