import { LOCALSTORAGE } from "./constant";

export const clearStorage = () => {
  localStorage.removeItem(LOCALSTORAGE.USER_TOKEN);
  localStorage.removeItem(LOCALSTORAGE.TOKEN_EXPIRE);
};

export const checkTokenValid = () => {
  const token = localStorage.getItem(LOCALSTORAGE.USER_TOKEN);
  const tokenExpire = localStorage.getItem(LOCALSTORAGE.TOKEN_EXPIRE);

  if (!token || !tokenExpire) {
    return;
  }

  try {
    const now = Date.now();
    if (now >= Number(tokenExpire)) {
      clearStorage();
      return;
    }
    return true;
  } catch (error) {
    // invalid storage
    clearStorage();
  }
};
