import styled from "styled-components";
import React, { memo, useState, useMemo, useCallback } from "react";
import { useTranslation } from "react-i18next";
import { useDaoContext } from "views/dao/provider";
import { useDaoOrgContract, useOrgContract } from "hooks/useContract";
import useVaultTokens from "hooks/useVaultTokens";
import useSigner from "hooks/useSigner";
import api from "api";
import { ethers } from "ethers";

import TipsImg from "../../../img/newHome/tips.png";
import DropSvg from "../../svg/common/select";
import UniswapLogo from "img/voteNV/uniswap.png";
import AaveLogo from "img/voteNV/aave.png";
import StarIcon from "components/svg/create/star";
import { Checkbox } from "antd";
import { useAppContext } from "components/provider/appProvider";
import Button, { ButtonGroup } from "components/common/button";
import VoteLoadingModal, { VoteTxStatus } from "./loading";
import {
  DAO_ORG,
  VAULT_MANAGER,
  ORG_MANAGER,
  VOTE_MANAGER,
} from "utils/constant";
import TokenSelect from "./selectToken";
import DateBox from "components/common/date";
import { useEffect } from "react";
import { ReceiveBox, DropBox } from "./transfer/style";
import Avatar from "components/common/avatar";
import { getVoteIdByEvent } from "utils/utils";

const PRODUCT = {
  SWAP: "swap",
  AAVE: "aave",
};

export default function SuperManager(props) {
  const { t } = useTranslation();
  const { state: daoState } = useDaoContext();
  const { componentAddressMap } = daoState;
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const orgContract = useOrgContract(componentAddressMap.get(ORG_MANAGER));

  const { dispatch } = useAppContext();

  const tokens = useVaultTokens(componentAddressMap.get(VAULT_MANAGER));
  const signer = useSigner();

  const [selectToken, setSelectToken] = useState();
  const [time, setTime] = useState();
  const [endTime, setEndTime] = useState();

  const [selectedProduct, setSelectedProduct] = useState(PRODUCT.SWAP);
  const [gplist, setGplist] = useState([]);
  const [selectGP, setSelectGP] = useState();
  const [limitAmount, setLimitAmount] = useState('');

  const [status, setStatus] = useState(VoteTxStatus.Default);
  const [txHash, setTxHash] = useState();
  const [newVoteId, setVoteId] = useState();
  const [duration,setDuration] = useState();

  const list = useMemo(() => {
    setSelectToken(tokens ? tokens[0] : undefined);
    return tokens ? tokens.map((t) => ({ ...t })) : [];
  }, [tokens]);

  const getGpList = useCallback(async () => {
    const { lst } = await api.org.getDaoMembersList(orgContract, 0, 1000);
    setSelectGP(lst[0]);
    setGplist(lst);
  }, [orgContract]);

  useEffect(() => {
    if (orgContract) {
      getGpList();
    }
  }, [orgContract]);

  const checkVaild = () => {
    if (!limitAmount || limitAmount > Number(selectToken.balance)) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid spending limit", closable: true },
      });
      return;
    }
    // TODO check time
    return true;
  };

  const createVote = async () => {
    let duration = Math.round((endTime - Date.now()) / 1000);
    if (duration < 86400) {
      duration = 86400;
    }
    setDuration(duration)

    if (!checkVaild()) {
      return;
    }
    let txHash;
    try {
      setStatus(VoteTxStatus.Loading);

      const res = await api.vote.newApproveGPVote(daoOrg, signer, {
        gpAddress: selectGP.address,
        approvTime: Math.round((time - Date.now()) / 1000),
        vote_time: duration,
        callee: componentAddressMap.get(VAULT_MANAGER),
        limit: {
          address: selectToken.address,
          amount: ethers.utils.parseUnits(
            String(limitAmount),
            selectToken.deci
          ),
        },
        method: selectedProduct === PRODUCT.SWAP ? "GP_Buy" : "GP_Loan",
      });
      txHash = res.hash;
      const r = await res.wait();
      const event = r.events.find(
        (e) => e.address === componentAddressMap.get(VOTE_MANAGER)
      );
      const vid = getVoteIdByEvent(event);
      setVoteId(vid);
      setStatus(VoteTxStatus.Success);
    } catch (error) {
      setStatus(VoteTxStatus.Failed);
      setTxHash(txHash);
      console.error("newApproveGPVote failed", error);
    }
  };
  const closeLoading = useCallback(() => {
    setStatus(VoteTxStatus.Default);
    txHash && setTxHash();
  }, []);

  const closeAll = useCallback(() => {
    closeLoading();
    props.handleClose();
  }, [props.handleClose]);

  return (
    <SuperBox>
      <VoteLoadingModal
        status={status}
        txHash={txHash}
        handleRetry={createVote}
        voteId={newVoteId}
        endTime={duration}
        handleClose={closeLoading}
        handleCloseAll={closeAll}
      />
      <Tips>
        <img src={TipsImg} alt="" />
        <div className="tips">{t("Vote.SuperTip")}</div>
      </Tips>

      <dl>
        <dt>{t("Vote.Recipient")}</dt>
        <dd>
          <GPSelect list={gplist} onChange={setSelectGP} selected={selectGP} />
        </dd>
      </dl>
      <dl>
        <dt>{t("Vote.AvailableDApp")}</dt>
        <dd>
          <TypeChecked
            selected={selectedProduct}
            onChange={setSelectedProduct}
          />
        </dd>
      </dl>
      <dl>
        <dt>{t("Vote.SpendLimit")}</dt>
        <dd>
          <AmountBox>
            <input
              type="number"
              value={limitAmount}
              onChange={(e) => setLimitAmount(e.target.value)}
            />
            <span>{selectToken && selectToken.symbol}</span>
          </AmountBox>
        </dd>
      </dl>
      <TokenSelect
        list={list}
        selected={selectToken}
        onChange={setSelectToken}
      />
      <DateBox
        label={t("Vote.approveTime")}
        targetTime={time}
        updateTargetTime={setTime}
      />
      <DateBox
        label={t("Vote.ExpiryVoting")}
        targetTime={endTime}
        updateTargetTime={setEndTime}
      />
      <ButtonGroup>
        <Button onClick={props.handleClose}>{t("Cancel")}</Button>
        <Button primary onClick={createVote}>
          {t("Submit")}
        </Button>
      </ButtonGroup>
    </SuperBox>
  );
}

const TypeChecked = memo((props) => {
  const { selected, onChange } = props;

  const changeSelectSwap = useCallback((e) => {
    onChange(e.target.checked ? PRODUCT.SWAP : PRODUCT.AAVE);
  }, []);

  const changeSelectAave = useCallback((e) => {
    onChange(e.target.checked ? PRODUCT.AAVE : PRODUCT.SWAP);
  }, []);

  return (
    <>
      <UlBox>
        <li>
          <Checkbox
            checked={selected === PRODUCT.SWAP}
            onChange={changeSelectSwap}
          >
            <img
              className="uni"
              style={{ width: "109px" }}
              src={UniswapLogo}
              alt=""
            />
          </Checkbox>
        </li>
        {/* <li>
          <Checkbox
            checked={selected === PRODUCT.AAVE}
            onChange={changeSelectAave}
          >
            <img style={{ width: "58px" }} src={AaveLogo} alt="" />
          </Checkbox>
        </li> */}
      </UlBox>
      <UniswapTip>
        <StarIcon />
        Approve all swap pairs
      </UniswapTip>
    </>
  );
});

const GPSelect = memo((props) => {
  const { list, onChange, selected } = props;
  const [show, setShow] = useState(false);

  useEffect(() => {
    document.addEventListener("click", () => {
      setShow(false);
    });
  }, []);

  const showList = useCallback((e) => {
    e.nativeEvent.stopImmediatePropagation();
    setShow(true);
  }, []);

  const choose = useCallback(
    (item, e) => {
      e.stopPropagation();
      setShow(false);
      if (item.address !== selected?.address) {
        onChange(item);
      }
    },
    [selected]
  );

  const GpInfo = memo(({ data }) => {
    return (
      <GpBox>
        <Avatar address={data.address} />
        <div>
          <div>{data.name}</div>
          <div>{data.address}</div>
        </div>
      </GpBox>
    );
  });

  return (
    <ReceiveBox onClick={(e) => showList(e)}>
      {selected && <GpInfo data={selected} />}
      <DropSvg />
      {show && (
        <DropGP>
          {list.map((item, index) => (
            <li key={index} onClick={(e) => choose(item, e)}>
              <GpInfo data={item} />
            </li>
          ))}
        </DropGP>
      )}
    </ReceiveBox>
  );
});

