import { memo, useEffect, useState, useMemo, useCallback } from "react";
import { DatePicker } from "antd";
import styled from "styled-components";
import moment from "moment";
import DateSVG from "components/svg/shape/date"

const DateBox = memo((props) => {
  const { label, targetTime, updateTargetTime } = props;

  const [selectTag, setSelectTag] = useState(1);
  const [open, setOpen] = useState(false);

  const times = useMemo(() => {
    return [
      { label: "1day", value: 86400000 },
      { label: "3day", value: 259200000 },
      { label: "1week", value: 604800000 },
      { label: "1month", value: 2592000000 },
    ];
  }, []);

  const chooseTag = useCallback(
    (idx) => {
      if (selectTag === idx) {
        return;
      }
      setSelectTag(idx);
      updateTargetTime(Date.now() + times[idx - 1].value);
    },
    [selectTag]
  );

  const handleChange = useCallback((value) => {
    setSelectTag(undefined);
    updateTargetTime(Date.parse(value._d));
  }, []);

  const disabledDate = useCallback((current) => {
    return current && current < moment().endOf("day");
  }, []);

  const range = useCallback((start, end) => {
    const result = [];
    for (let i = start; i < end; i++) {
      result.push(i);
    }
    return result;
  });

  const disabledDateTime = useCallback((dates) => {
    let hours = moment().hours(); //0~23
    let minutes = moment().minutes(); //0~59
    let seconds = moment().seconds(); //0~59
    if (dates && moment(dates[1]).date() === moment().date()) {
      return {
        disabledHours: () => range(0, hours),
        disabledMinutes: () => range(0, minutes),
        disabledSeconds: () => range(0, seconds + 1),
      };
    }
  }, []);

  const formatDate = useCallback((m) => {
    return m.format("YYYY-MM-DD HH:mm:ss") + " GMT" + m.format("ZZ");
  }, []);

  const timeShow = useMemo(() => {
    let m;
    if (targetTime) {
      m = moment(targetTime);
    } 
    if (m) {
      return formatDate(m);
    }
  }, [targetTime]);

  const openDate = useCallback(() => {
    setOpen(true);
  }, []);

  useEffect(() => {
    if (!targetTime && selectTag) {
      updateTargetTime(Date.now() + times[selectTag - 1].value);
    }
  }, [])

  return (
    <dl>
      <dt>{label}</dt>
      <dd>
        <TagBoxStyled>
          {times.map((time, idx) => (
            <span
              key={idx}
              className={selectTag === idx + 1 ? "active" : ""}
              onClick={() => chooseTag(idx + 1)}
            >
              {time.label}
            </span>
          ))}
        </TagBoxStyled>
        <DisplayDateBox>
          <DisplayDate onClick={openDate}>{timeShow}</DisplayDate>
          <DateSVG className="icon" />
          <BoxBrdr
            open={open}
            onOpenChange={setOpen}
            placeholder=""
            style={{ width: "100%" }}
            format={formatDate}
            // disabledDate={disabledDate}
            // disabledTime={disabledDateTime}
            allowClear={false}
            onChange={handleChange}
            showTime={true}
            showNow={false}
          />
        </DisplayDateBox>
      </dd>
    </dl>
  );
});

export default DateBox;

const TagBoxStyled = styled.div`
  display: flex;
  margin-bottom: 12px;
  cursor: pointer;
  span {
    text-align: center;
    width: 67px;
    height: 30px;
    background: #f5f5f5;
    border-radius: 9px;
    line-height: 30px;
    margin-right: 10px;
    &:hover,
    &.active {
      background: #d52573;
      color: #ffffff;
    }
  }
`;

const BoxBrdr = styled(DatePicker)`
  visibility: hidden;
  padding: 0 20px;
  border: 0;
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  height: 44px;
`;

const DisplayDateBox = styled.div`
  position: relative;
  .icon {
    position: absolute;
    top: 11px;
    right: 20px;
  }
`;

const DisplayDate = styled.div`
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 20px;
  height: 44px;
  line-height: 44px;
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
`;
