import { useEffect, useState, useCallback, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate, useSearchParams } from "react-router-dom";
import styled from "styled-components";
import useGateway from "hooks/useGateway";

import SearchImg from "img/search.png";

import Button from "components/common/button";
import MyToken from "./myToken";
import SearchTab from "./searchTab";

import AllDao from "./tables/all";
import LikeDao from "./tables/likedao";
import MyDao from "./tables/mydao";

export default function Explore() {
  const { t } = useTranslation();
  const navigator = useNavigate();
  const [searchParams] = useSearchParams();

  const gateway = useGateway();

  const tabsList = useMemo(() => {
    return [
      t("Explore.All"),
      t("Explore.Trending"),
      t("Explore.MyDAO"),
      t("Explore.Liked"),
      t("Explore.MyTokens"),
    ];
  }, []);

  const [currentTab, setCurrentTab] = useState(0);
  const [searchVal, setSearchVal] = useState();

  useEffect(() => {
    const queryVal = searchParams.get("search");
    if (queryVal) {
      setSearchVal(queryVal);
      return;
    }
    const tabVal = searchParams.get("tab");
    if (tabVal === "tokens") {
      setCurrentTab(4);
    }
  }, [searchParams]);

  const changeTab = useCallback((num) => {
    setCurrentTab(num);
  }, []);

  const go2create = useCallback(() => {
    navigator("/create");
  }, []);

  const showTab = useMemo(() => {
    switch (currentTab) {
      case 0:
        return <AllDao gateway={gateway} />;
      case 1:
        return <></>;
      case 2:
        return <MyDao gateway={gateway} />;
      case 3:
        return <LikeDao gateway={gateway} />;
      case 4:
        return <MyToken gateway={gateway} />;
      default:
        return <></>
    }
  }, [currentTab, gateway]);

  const onChangeSearch = useCallback(({ target: { value } }) => {
    setSearchVal(value);
  }, []);

  return (
    <div className="container">
      <Box>
        <FirstLine>
          <SearchBox>
            <img src={SearchImg} alt="" />
            <input type="text" value={searchVal} onChange={onChangeSearch} />
          </SearchBox>
          <Button
            primary
            height="50px"
            style={{ fontSize: "16px" }}
            onClick={go2create}
          >
            <span className="font-medium">{t("Explore.Create")}</span>
          </Button>
        </FirstLine>
        <Decor />
        <ContentBox id="contentBox">
          {searchVal ? (
            <div id="inner">
              <SearchTab keyword={searchVal} gateway={gateway}/>
            </div>
          ) : (
            <>
              <TabBox>
                {tabsList.map((item, index) => (
                  <li
                    key={index}
                    className={currentTab === index ? "active font-medium" : ""}
                    onClick={() => changeTab(index)}
                  >
                    {item}
                  </li>
                ))}
              </TabBox>
              <div id="inner">{showTab}</div>
            </>
          )}
        </ContentBox>
      </Box>
    </div>
  );
}

const Box = styled.div`
  background: #ffffff;
  box-shadow: 0px 0px 16px 0px rgba(16, 22, 75, 0.06);
  border-radius: 24px;
  border: 1px solid #ffffff;

  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
  width: 100%;
  #inner {
    height: 550px;
    /* overflow-y: auto; */
    position: relative;
    /* scrollbar-width: none; */
    /* &::-webkit-scrollbar {
      display: none;
    } */
  }
`;

const FirstLine = styled.div`
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #edeef3;
  padding: 30px 50px 36px;
  width: 100%;
`;
const SearchBox = styled.div`
  width: 300px;
  height: 50px;
  background: #f5f5f7;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 13px 14px;
  input {
    flex-grow: 1;
    margin-left: 20px;
    border: 0;
    background: transparent;
  }
`;

const Decor = styled.div`
  width: 100%;
  height: 10px;
  background: rgba(246, 246, 247, 0.35);
`;
const TabBox = styled.ul`
  width: 100%;
  display: flex;
  li {
    font-size: 16px;
    font-weight: 400;
    color: #10164b;
    line-height: 22px;
    margin-right: 60px;
    min-width: 40px;
    padding-bottom: 7px;
    border-bottom: 3px solid #fff;
    text-align: center;
    cursor: pointer;
    &.active {
      border-bottom: 3px solid #d51172;
      font-size: 18px;
      font-weight: 500;
      color: #d51172;
    }
  }
`;

const ContentBox = styled.div`
  padding: 30px 60px;
  width: 100%;
  flex-grow: 1;
  overflow-y: auto;
`;
