import { memo, useState, useEffect, useCallback } from "react";
import styled from "styled-components";
import DropSvg from "components/svg/common/select";
import { useDaoContext } from "views/dao/provider";
import { useMemo } from "react";
import { useAppContext } from "components/provider/appProvider";

const Select = memo((props) => {
  const { token, onChange, selected, isEdit, ...rest } = props;

  const {
    state: { fundList },
  } = useAppContext();

  const choose = useCallback(
    (index, e) => {
      if (index !== selected) {
        onChange && onChange(fundList[index].address);
      }
    },
    [selected]
  );
  const [current, setCurrent] = useState();

  useEffect(() => {
    if (token && fundList) {
      const fund = fundList.find((f) => f.address === token);
      setCurrent(fund);
    } else if (fundList && fundList.length) {
      setCurrent(fundList[0]);
      onChange && onChange(fundList[0].address);
    }
  }, [fundList, token]);

  //   const currentSelect = useMemo(() => {
  //     return fundList[selected];
  //   }, [fundList, selected]);
  if (!isEdit) {
    return (
      <DisplayBox>
        {current && (
          <>
            <img src={current.logo} alt="" />
            <span>{current.symbol}</span>
            <DropSvg />
          </>
        )}
      </DisplayBox>
    );
  }

  return (
    <DropBoxBg {...rest}>
      {current && (
        <>
          <img src={current.logo} alt="" />
          <span>{current.symbol}</span>
        </>
      )}

      <DropSvg />
      {isEdit && (
        <DropBox className="options">
          {fundList.map((item, index) => (
            <li key={index} onClick={(e) => choose(index, e)}>
              <img src={item.logo} alt="" />
              {item.symbol}
            </li>
          ))}
        </DropBox>
      )}
    </DropBoxBg>
  );
});

export default Select;

const DisplayBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  line-height: 32px;
  height: 32px;
  margin-top: 16px;
  img {
    width: 22px;
    height: 22px;
    margin-right: 2px;
  }
`;

const DropBoxBg = styled(DisplayBox)`
  background: #ffffff;
  border-radius: 8px;
  border: 1px solid #edeef3;
  padding: 0 10px;
  position: relative;

  &:hover {
    .options {
      display: block;
    }
  }
`;

const DropBox = styled.ul`
  display: none;
  position: absolute;
  left: 0;
  top: 31px;
  z-index: 999;
  padding: 4px 0;
  width: 159px;
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;

  li {
    height: 44px;
    display: flex;
    align-items: center;
    padding: 0 10px;
    cursor: pointer;
    img {
      margin-right: 18px;
    }
    &:hover {
      background: #fbfbfa;
    }
  }
`;
