import styled from "styled-components";
import { useEffect, useState, useMemo, useCallback } from "react";
import * as echarts from "echarts";

import Select from "components/common/select";
import { useTranslation } from "react-i18next";

import { useLazyQuery } from "@apollo/client";
import { TavGraph } from "api/graphql/dao";
import { useWeb3React } from "@web3-react/core";
import { useDaoContext } from "views/dao/provider";

export default function Tav() {
  const { t } = useTranslation();

  const { chainId } = useWeb3React();
  const {
    state: { daoId },
  } = useDaoContext();

  const [myChart, setMyChart] = useState();
  const [selected, setSelected] = useState(0);
  const filters = useMemo(() => {
    return [
      { label: t("Info.Filter1m"), value: "1M" },
      { label: t("Info.Filter3m"), value: "3M" },
      { label: t("Info.Filter6m"), value: "6M" },
      { label: t("Info.Filter1y"), value: "1Y" },
    ];
  }, []);
  const option = useMemo(() => {
    return {
      color: ["#ff7ebb"],
      title: {
        show: false,
      },
      tooltip: {
        trigger: "axis",
        axisPointer: {
          type: "cross",
          label: {
            backgroundColor: "#6a7985",
          },
        },
      },
      grid: {
        x: 0,
        y: 30,
        x2: 10,
        y2: 10,
        containLabel: true,
      },
      xAxis: [
        {
          type: "category",
          boundaryGap: false,
          axisLine: {
            show: false,
            lineStyle: {
              color: "(65,97,128,0.45)",
              width: 1,
              type: "solid",
            },
          },
          axisLabel: {
            textStyle: {
              color: "rgba(12, 22, 81, .45)",
              margin: 15,
            },
          },
          data: ["0"],
        },
      ],
      yAxis: [
        {
          type: "value",
          axisLabel: {
            textStyle: {
              color: "rgba(44, 53, 66, 0.4500)",
              margin: 15,
            },
          },
        },
      ],
      series: [
        {
          name: "TAV",
          type: "line",
          stack: "Total",
          smooth: true,
          lineStyle: {
            width: 2,
          },
          showSymbol: false,
          areaStyle: {
            opacity: 0.8,
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "#F7cfe2",
              },
              {
                offset: 1,
                color: "#fff",
              },
            ]),
          },
          emphasis: {
            focus: "series",
          },
          data: [0],
        },
      ],
    };
  }, []);

  useEffect(() => {
    const chart = echarts.init(document.getElementById("main"));
    window.onresize = function () {
      chart.resize();
    };
    chart.setOption(option);
    setMyChart(chart);
  }, []);

  const [getGraph] = useLazyQuery(TavGraph, {
    onCompleted(data) {
      if(!data?.daoTav?.length)return;
      const graphData = data.daoTav || [];
      const newOption = JSON.parse(JSON.stringify(option));
      newOption.xAxis[0].data = graphData.map((d) => d.date);
      newOption.series[0].data = graphData.map((d) => d.value);
      myChart.setOption(newOption);
    },
    onError(error) {},
  });

  useEffect(() => {
    if (!myChart || !chainId || !daoId) {
      return;
    }
    getGraph({
      variables: {
        chainId,
        daoId,
        scope: filters[selected].value,
      },
    });
  }, [chainId, myChart, selected, daoId]);

  const changeFilter = useCallback((index) => {
    setSelected(index);
  }, []);

  return (
    <ChartBox>
      <TitleBox>
        <div className="lft"> Total Assets Value </div>
        <Select
          list={filters}
          onChange={changeFilter}
          selected={selected}
          style={{ marginTop: 0 }}
        />
      </TitleBox>
      <div id="main" style={{ width: "100%", height: "300px" }} />
    </ChartBox>
  );
}

const ChartBox = styled.div`
  background: #ffffff;
  box-shadow: 0 0 10px 0 rgba(16, 22, 75, 0.05);
  border: 1px solid #f0f0f0;
  border-radius: 24px;
  margin-top: 20px;
  padding: 24px 27px;
`;

const TitleBox = styled.div`
  display: flex;
  justify-content: space-between;
  .lft {
    font-size: 16px;
    display: flex;
    align-items: center;
    color: #0c1651;
    line-height: 16px;
    img {
      margin-left: 8px;
    }
    span {
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      white-space: nowrap;
    }
  }
`;
const DropBox = styled.ul`
  position: absolute;
  left: 0;
  top: 55px;
  z-index: 9999;
  width: 190px;
  padding: 10px 0;
  background: #ffffff;
  border-radius: 8px 8px 14px 14px;
  border: 1px solid #edeef3;
  li {
    padding: 6px 23px;
    cursor: pointer;
    &:hover {
      background: #fbfbfa;
    }
  }
`;
