import { useState, useEffect, useRef } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

import General from "./general";

import { InputItem, InputLabel } from "components/common/inputItem";
import Button, { ButtonGroup } from "components/common/button";

import TwitterSvg from "components/svg/create/socials/twitter";
import TelegramSvg from "components/svg/create/socials/telegram";
import DiscordSvg from "components/svg/create/socials/discord";

import { useMutation } from "@apollo/client";
import { UpdateDao } from "api/graphql/dao";
import { useWeb3React } from "@web3-react/core";
import { useAppContext } from "components/provider/appProvider";
import useGateway from "hooks/useGateway";
import LogoUpload from "components/upload/logo";
import { useDaoContext } from "views/dao/provider";

export default function EditDAO(props) {
  const { t } = useTranslation();
  const { data: baseData, closeModal } = props;
  const { chainId } = useWeb3React();
  const { dispatch } = useAppContext();
  const { state: { daoId }, dispatch: dispatchEvent } = useDaoContext();

  const [name, setName] = useState("");
  const [daohandle, setDaohandle] = useState("");
  const [description, setdescription] = useState("");

  const [imgUrl, setimgUrl] = useState("");

  const [twitter, setTwitter] = useState("");
  const [discord, setDiscord] = useState("");
  const [telegram, setTelegram] = useState("");

  const logoRef = useRef();

  useEffect(() => {
    if (!baseData) {
      return;
    }
    setName(baseData.name);
    setDaohandle(baseData.handle);
    setdescription(baseData.desc);
    setimgUrl(baseData.logo);

    setTwitter(baseData.twitter_url);
    setDiscord(baseData.discord_url);
    setTelegram(baseData.telegram_url);
  }, [baseData]);

  const [updateDao, { loading }] = useMutation(UpdateDao, {
    onCompleted: (data) => {
      console.log("====", data);
      if (data && data.updateDao) {
        closeModal();
        dispatchEvent({
          type: "SET_BASE_DATA", payload: {
          ...baseData,
          logo: imgUrl,
          desc: description,
          handle: daohandle,
          name,
          twitter_url: twitter,
          discord_url: discord,
          telegram_url: telegram,
        }})
      }
      dispatch({ type: "LOADINGTYPE", payload: null });
    },
    onError: (error) => {
      console.error("BundDao failed", error);
      dispatch({
        type: "MSGTYPE",
        payload: { closable: true, msg: "edit failed" },
      });
      dispatch({ type: "LOADINGTYPE", payload: null });
    },
  });

  useEffect(() => {
    if (loading) {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    }
  }, [loading]);

  const submitModify = () => {
    if (!name || !daohandle || !description) {
      dispatch({
        type: "MSGTYPE",
        payload: {
          closable: true,
          msg: "require name, handle and description",
        },
      });
      return;
    }
    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });

    updateDao({
      variables: {
        chainId: chainId,
        daoId: daoId,
        logo: imgUrl,
        desc: description,
        handle: daohandle,
        name,
        twitter_url: twitter,
        discord_url: discord,
        telegram_url: telegram,
      },
    });
  };

  return (
    <General closeModal={closeModal} width="954">
      <FirstLine>
        <Lft>
          <div className="label">Logo</div>
          <LogoUpload
            ref={logoRef}
            imgCode={imgUrl}
            updateUrl={(url) => setimgUrl(url)}
          />
          <div className="tips">{t("Create.DAOImageTip")}</div>
        </Lft>
        <Rht>
          <div>
            <div className="flexLine">
              <dl>
                <dd>
                  <InputItem
                    required
                    labelSize="16px"
                    title={t("Create.DAOName")}
                    placeholder={t("Create.FillName")}
                    maxLength={36}
                    value={name}
                    name="name"
                    onChange={(e) => {
                      setName(e.target.value);
                    }}
                  />
                </dd>
              </dl>
              <dl>
                <dd>
                  <InputItem
                    required
                    labelSize="16px"
                    title={t("Create.DAOHandle")}
                    placeholder={t("Create.DAOHandleTip")}
                    maxLength={36}
                    value={daohandle}
                    name="daohandle"
                    onChange={(e) => {
                      setDaohandle(e.target.value);
                    }}
                  />
                </dd>
              </dl>
            </div>
            <div className="flexLine">
              <dl>
                <dd>
                  <InputItem
                    labelSize="16px"
                    icon={<TwitterSvg />}
                    title={t("Create.twitter")}
                    value={twitter}
                    outPrefix="@"
                    name="twitter"
                    onChange={(e) => {
                      setTwitter(e.target.value);
                    }}
                  />
                </dd>
              </dl>
              <dl>
                <dd>
                  <InputItem
                    labelSize="16px"
                    icon={<TelegramSvg />}
                    title={t("Create.telegram")}
                    value={telegram}
                    outPrefix="http://t.me/"
                    name="telegram"
                    onChange={(e) => {
                      setTelegram(e.target.value);
                    }}
                  />
                </dd>
              </dl>
            </div>
            <dl>
              <dd>
                <InputItem
                  labelSize="16px"
                  icon={<DiscordSvg />}
                  title={t("Create.discord")}
                  placeholder={t("Create.discordTip")}
                  value={discord}
                  name="discord"
                  onChange={(e) => {
                    setDiscord(e.target.value);
                  }}
                />
              </dd>
            </dl>
            <dl>
              <dd>
                <InputItem
                  required
                  area
                  maxLength={1024}
                  labelSize="16px"
                  title={t("Create.Description")}
                  placeholder={t("Create.FillDAODescription")}
                  value={description}
                  name="description"
                  onChange={(e) => {
                    setdescription(e.target.value);
                  }}
                />
              </dd>
            </dl>
          </div>
          <ButtonGroup style={{ marginTop: "30px" }}>
            <Button onClick={closeModal}>{t("Cancel")}</Button>
            <Button primary onClick={submitModify}>
              {t("Submit")}
            </Button>
          </ButtonGroup>
        </Rht>
      </FirstLine>
    </General>
  );
}

const FirstLine = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 30px;
`;
const Lft = styled.div`
  margin-left: 35px;
  .label {
    font-size: 16px;
    margin-bottom: 12px;
  }
  .tips {
    margin-top: 10px;
  }
  img {
    width: 124px;
    height: 124px;
    border-radius: 14px;
  }
  .title {
    font-size: 16px;
    font-weight: 400;
    color: #10164b;
    line-height: 22px;
  }
  .tips {
    font-size: 14px;
    font-weight: 400;
    color: #a6a6b7;
    line-height: 19px;
    width: 124px;
  }
`;
const Rht = styled.div`
  width: 653px;
  margin-right: 50px;
  dl {
    width: 100%;
  }
  .flexLine {
    display: flex;
    align-items: center;
    justify-content: space-between;
    dl {
      width: 48%;
    }
  }

  /* dd {
    margin-bottom: 20px;
  } */
`;
