import { useEffect, useState } from "react";
import { ethers } from "ethers";
import { useWeb3React } from "@web3-react/core";
import { useVaultContract } from "./useContract";
import api from "../api";
import { getShortDisplay } from "../utils/publicJs";
import { useWeb3 } from "../api/contracts";
import { useCallback } from "react";

const getToken = async (address, web3Instance) => {
  try {
    const mockERC20 = await api.erc20.getContract(web3Instance, address);
    const symbol = await mockERC20.symbol();
    const deci = await mockERC20.decimals();
    return { symbol, deci, address };
  } catch (error) {
    console.error(`get token failed - ${address}`, error);
  }
};

const useVaultTokens = (vaultAddress, options = {}) => {
  const { provider: web3Instance } = useWeb3React();

  const vaultcontract = useVaultContract(vaultAddress);
  const [tokens, setTokens] = useState([]);

  useEffect(() => {
    web3Instance && vaultcontract && getTokens();
  }, [vaultcontract, web3Instance]);

  const getTokens = useCallback(async () => {
    let data = await api.vault.getTokenList(vaultcontract);
    if (!data || data.length === 0) {
      return;
    }
    let lst = [];

    for (const tokenAddress of data) {
      const token = await getToken(tokenAddress, web3Instance);
      if (!token) {
        continue;
      }

      let balance = await api.vault.getTokenBalance(
        vaultcontract,
        tokenAddress
      );
      balance = ethers.utils.formatUnits(balance, token.deci);
      lst.push({
        ...token,
        balance,
        balanceDisplay: getShortDisplay(balance),
      });
    }
    console.log("[useVaultTokens]", lst);
    setTokens(lst);
  }, [vaultcontract, web3Instance]);

  return tokens;
};

export default useVaultTokens;
