import React from "react";
import {
  Routes,
  Route,
  useLocation,
  useNavigate,
  useParams,
} from "react-router-dom";

import { SETTINGS_KEY } from "views/appAndsettings";
import ManagementNFT from "./walletConnect/ManagementNFT";
import Swap from "./swap/swap";
import Defi from "./defi/index";
import WCT from "./walletConnect/normal";
import useVenturePeriod from "hooks/useVenturePeriod";
import { useDaoContext } from "views/dao/provider";
import { useVentureContract } from "hooks/useContract";
import { VENTURE_MANAGER } from "utils/constant";
import { useEffect } from "react";

export default function Apps() {
  const navigate = useNavigate();
  const { fullhandle } = useParams();
  const {
    state: { componentAddressMap },
  } = useDaoContext();
  const ventureContract = useVentureContract(
    componentAddressMap.get(VENTURE_MANAGER)
  );
  const period = useVenturePeriod(ventureContract);

  useEffect(() => {
    if (period === undefined) {
      return;
    }
    if (period !== 2 && period !== 3) {
      navigate(`/dao/${fullhandle}/app_settings`);
    }
  }, [period]);
  return (
    <Routes>
      <Route path={SETTINGS_KEY.NFT} element={<ManagementNFT />} />
      <Route path={SETTINGS_KEY.SWAP} element={<Swap />} />
      {/* <Route path={SETTINGS_KEY.DEFI} element={<Defi />} /> */}
      <Route path={SETTINGS_KEY.WCT} element={<WCT />} />
      {/* TODO redirect to app_settings */}
    </Routes>
  );
}
