import { useEffect, memo, useState } from "react";
import { useWeb3React } from "@web3-react/core";
import { ethers } from "ethers";
import { useVaultContract } from "hooks/useContract";
import { useDaoContext } from "views/dao/provider";
import { VAULT_MANAGER } from "utils/constant";

import api from "api";
import { AddressBox } from "./styles";
import CopyStr from "components/copy";
import LinkImg from "img/shareGray.png";

const TransferNFTVote = memo((props) => {
  const { wid } = props;

  const { provider } = useWeb3React();
  const {
    state: { componentAddressMap },
  } = useDaoContext();
  const vaultContract = useVaultContract(
    componentAddressMap.get(VAULT_MANAGER)
  );

  const [toAddress, setToAddress] = useState();
  const [amount, setAmount] = useState(1);

  const [tokenAddress, setTokenAddress] = useState();
  const [tokenId, setTokenId] = useState();

  useEffect(async () => {
    if (wid === undefined || !vaultContract || !provider) {
      return;
    }
    const wdata = await vaultContract.nftWithdrawList(wid);
    setToAddress(wdata.proposer);
    setTokenAddress(wdata.token);
    setTokenId(wdata.tokenId.toNumber());
    const amt = wdata.amount.toNumber();
    setAmount(amt === 0 ? 1 : amt);
  }, [wid, vaultContract, provider]);
  return (
    <>
      <dl>
        <dt>Recipient</dt>
        <AddressBox>
          {toAddress} <CopyStr address={toAddress} gray={true} />
          <img src={LinkImg} alt="" />
        </AddressBox>
      </dl>
      <dl>
        <dt>NFT Address</dt>
        <AddressBox>
          {tokenAddress} <CopyStr address={tokenAddress} gray={true} />
          <img src={LinkImg} alt="" />
        </AddressBox>
      </dl>
      <dl>
        <dt>Token id</dt>
        <AddressBox>{tokenId}</AddressBox>
      </dl>
      <dl>
        <dt>Amount</dt>
        <dd className="flexLine">
          <span className="num font-bold">{amount}</span>
        </dd>
      </dl>
    </>
  );
});

export default TransferNFTVote;
