import { useState, useCallback, memo, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate, Link } from "react-router-dom";
import { Input } from "antd";
import styled from "styled-components";

import BannerBg from "img/newHome/banner.png";
import SearchImg from "img/newHome/search.svg";
import Banner1280 from "img/newHome/banner1280.png";

import MOON from "img/supported/moon.png";
import BSC from "img/supported/bsc.png";
import OKEX from "img/supported/okex.png";
import HERPER from "img/supported/hyper.png";
import HUOBI from "img/supported/huobi.png";
import CMS from "img/supported/cms.png";
import SIGNUM from "img/supported/signum.png";

import DaoItem from "components/common/daoItem";
import { useAppContext } from "components/provider/appProvider";
import { QueryDaos } from "api/graphql/dao";
import { useLazyQuery } from "@apollo/client";
import useGateway from "hooks/useGateway";
import { useWeb3React } from "@web3-react/core";
import { useNetworkData } from "hooks/useNetwork";

const LOGO = memo(({ src, width, right }) => {
  return <LOGOImg src={src} width={width} right={right} alt="" />;
});

export default function NewHome() {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [keyword,setKeyword] = useState('');

  const {
    state: { recommendDaos },
  } = useAppContext();

  const [list, setList] = useState([]);

  const gateway = useGateway();
  const { chainId } = useWeb3React();
  const networkData = useNetworkData(chainId);

  const handleKeyword = (e) =>{
    setKeyword(e.target.value)
  }
  // const go2Search = useCallback(
  //   ({ target: { value } }) => {
  //     if (value) {
  //       navigate(`/explore?search=${value}`);
  //     }
  //   },
  //   [navigate]
  // );
const go2Search = () => {
      if (keyword) {
        navigate(`/explore?search=${keyword}`);
      }
    };

  const [queryDaos] = useLazyQuery(QueryDaos, {
    onCompleted(data) {
      console.log("====data", data);
      setList(data.daoListByIds || []);
    },
    onError(error) {
      console.error(error);
    },
  });

  useEffect(() => {
    if (!chainId || !recommendDaos.length) {
      return;
    }
    queryDaos({
      variables: {
        chainId,
        daoIds: recommendDaos,
      },
      fetchPolicy: "network-only",
    });
  }, [chainId, recommendDaos]);

  return (
    <BgBox>
      <Box>
        <Banner>
          <TitleBox className="font-demiBold">{t("BannerText")}</TitleBox>
          <Slogan>{t("BannerSubText")}</Slogan>
          <SearchBox>
            <SearchInput
              bordered={false}
              placeholder={t("Header.Search")}
              onChange={handleKeyword}
              onPressEnter={go2Search}
            />
            <BtnBox onClick={go2Search}>
              <img src={SearchImg} alt="" />
            </BtnBox>
          </SearchBox>
          <CreateDaoButton to="create">{t("CreateDao")}</CreateDaoButton>
        </Banner>
        <UlBox>
          {list.map((dao, i) => (
            <DaoItem
              key={i}
              data={dao}
              gateway={gateway}
              networkSimple={networkData && networkData.simple}
            />
          ))}
        </UlBox>
        <SupportedBox>
          <SupportNav>
            <span>{t("Supported")}</span>
            <div className="line"></div>
          </SupportNav>
          <SupportedLogo>
            <img src={MOON} alt="" />
            <img src={BSC} alt="" />
            <img src={OKEX} alt="" />
            <img src={HERPER} alt="" />
            <img src={HUOBI} alt="" />
            <img src={CMS} alt="" />
            <img src={SIGNUM} alt="" />
          </SupportedLogo>
        </SupportedBox>
      </Box>
    </BgBox>
  );
}

