import { memo, useState, useEffect, useCallback } from "react";
import styled from "styled-components";
import DropSvg from "components/svg/common/select";

const Select = memo((props) => {
  const { list, onChange, selected, ...rest } = props;
  const [show, setShow] = useState(false);

  useEffect(() => {
    document.addEventListener("click", () => {
      setShow(false);
    });
  }, []);

  const showList = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    setShow(true);
  };

  const choose = useCallback(
    (index, e) => {
      e.stopPropagation();
      setShow(false);
      if (index !== selected) {
        onChange(index);
      }
    },
    [selected]
  );

  return (
    <DropBoxBg onClick={(e) => showList(e)} {...rest}>
      <span>{list[selected].label}</span>
      <DropSvg />
      {show && (
        <DropBox>
          {list.map((item, index) => (
            <li key={index} onClick={(e) => choose(index, e)}>
              {item.label}
            </li>
          ))}
        </DropBox>
      )}
    </DropBoxBg>
  );
});

export default Select;

const DropBoxBg = styled.div`
  width: 200px;
  height: 44px;
  background: #ffffff;
  border-radius: 14px;
  border: 1px solid #edeef3;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 11px 0 23px;
  position: relative;
  margin-top: 20px;
  img {
    width: 24px;
    height: 24px;
  }
`;

const DropBox = styled.ul`
  position: absolute;
  left: 0;
  top: 55px;
  z-index: 9999;
  width: 190px;
  padding: 10px 0;
  background: #ffffff;
  border-radius: 8px 8px 14px 14px;
  border: 1px solid #edeef3;
  li {
    padding: 6px 23px;
    cursor: pointer;
    &:hover {
      background: #fbfbfa;
    }
  }
`;
