import { ApolloClient, InMemoryCache, createHttpLink } from "@apollo/client";
import { ApolloProvider } from "@apollo/client";
import { setContext } from "@apollo/client/link/context";
import { useWeb3React } from "@web3-react/core";
import { useNetworkData } from "hooks/useNetwork";
import { useMemo } from "react";
import { LOCALSTORAGE } from "utils/constant";

const HttpProvider = ({ children }) => {
  const { chainId } = useWeb3React();
  const networkData = useNetworkData(chainId);

  const client = useMemo(() => {
    const httpLink = createHttpLink({
      uri: "https://api.subdao.network/v1/query"
    });
    const authLink = setContext((_, { headers }) => {
      return {
        headers: {
          ...headers,
          Authorization: localStorage.getItem(LOCALSTORAGE.USER_TOKEN) || "",
        },
      };
    });
    return new ApolloClient({
      link: authLink.concat(httpLink),
      cache: new InMemoryCache(),
    });
  }, [networkData]);
  return <ApolloProvider client={client}>{children}</ApolloProvider>;
};

export default HttpProvider;
