import { memo, useState, useMemo, useCallback } from "react";
import styled from "styled-components";
import LinkImg from "../../img/newHome/link.png";
import AddToken from "./addToken";
import { useDaoContext } from "../../views/dao/provider";
import { VAULT_MANAGER, DAO_ORG } from "../../utils/constant";
import useVaultTokens from "../../hooks/useVaultTokens";
import { TokenLogo } from "components/common/avatar";
import { useAppContext } from "components/provider/appProvider";
import EmptyBox from "components/common/empty";


const Tokens = memo(() => {
  const {
    state: { exploreScan,tokens:tokenList },
  } = useAppContext();

  const { state: daoState, dispatch } = useDaoContext();
  const {
    componentAddressMap,
    whoAmI: { isOwner },
  } = daoState;
  const tokens = useVaultTokens(componentAddressMap.get(VAULT_MANAGER));
  // const tokens = Array.from(
  //   { length: 40 },
  //   (item) =>
  //     (item = {
  //       address: "0x9ED55eBE818C3c973b562b3D8681883d4F27534D",
  //       balance: "10.0",
  //       balanceDisplay: "10.0",
  //       deci: 6,
  //       symbol: "USDT",
  //     })
  // );

  const [show, setShow] = useState(false);

  const handleAdd = async () => {
    setShow(true);
  };
  const handleClose = (updateFlag) => {
    setShow(false);
    if (updateFlag) {
      dispatch({ type: "UPDATE_FUND_OR_PERIOD", payload: "tokens" });
    }
  };

  const checkExist = useCallback(
    (address) => {
      return (tokens || []).find((t) => t.address === address);
    },
    [tokens]
  );

  const switchToken = useCallback((add) =>{
    if(!tokenList.length) return"";
    const img = tokenList.filter(item=>item.address.toLowerCase() === add.toLowerCase())
    return img[0]?.logo;
  },[tokenList])


  return (
    <Box>
      {show && <AddToken handleClose={handleClose} checkExist={checkExist} />}
      <TypeBox>
        {!tokens.length && <EmptyBox />}
        {tokens.map((token, index) => (
          <li key={index}>
            <div>
              <Title>{token.balanceDisplay}</Title>
            </div>
            <SymbolBox>
              <div className="lft">
                {/*<TokenLogo size="32px" />*/}
                <img src={switchToken(token.address)} alt="" className="logo"/>
                <div className="symbol">{token.symbol}</div>
              </div>
              <LinkIcon
                href={`${exploreScan}token/${token.address}`}
                target="_blank"
                rel="noreferrer"
              >
                <img src={LinkImg} alt="" />
              </LinkIcon>
            </SymbolBox>
          </li>
        ))}
      </TypeBox>
      {isOwner && (
        <BtnBox>
          <Button onClick={() => handleAdd()}>Add Token</Button>
        </BtnBox>
      )}
    </Box>
  );
});

export default Tokens;

const Box = styled.div`
  position: relative;
  height: 100%;
`;
const TypeBox = styled.ul`
  display: flex;
  /* justify-content: space-between; */
  align-items: flex-start;
  margin: 20px 0 80px;
  flex-wrap: wrap;
  height: calc(100% - 80px);
  overflow: auto;
  li {
    width: 23%;
    padding: 17px 20px;
    margin-bottom: 20px;
    background: #ffffff;
    box-shadow: 0px 0px 10px 0px rgba(16, 22, 75, 0.05);
    border-radius: 16px;
    border: 1px solid #f0f0f0;
    display: flex;
    flex-direction: column;
    margin-right: 18px;
    &:nth-child(4n) {
      margin-right: 0;
    }
  }
`;
const Title = styled.div`
  font-weight: 400;
  color: #10164b;
  margin-right: 15px;
  font-size: 24px;
  line-height: 29px;
`;
const SymbolBox = styled.div`
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .lft {
    display: flex;
    align-items: center;
    img {
      width: 32px;
      height: 32px;
      border-radius: 32px;
      margin-right: 10px;
    }
  }
  .symbol {
    font-size: 14px;
    font-weight: 400;
    color: #10164b;
    line-height: 29px;
  }
  
`;
const BtnBox = styled.div`
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 80px;
  background: #fff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  z-index: 999;
  border-top: 1px solid #f5f5f5;
`;
const Button = styled.div`
  width: 105px;
  height: 34px;
  background: #d52473;
  border-radius: 8px;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  line-height: 34px;
  cursor: pointer;
`;

const LinkIcon = styled.a`
  img {
    width: 24px;
  }
`;
