import { memo, useMemo, useState, useEffect } from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import { useNavigate, useParams } from "react-router-dom";
import useMatchRoute, { VoteRoutes } from "hooks/useMatchRoute";

import Tabs from "../layout/tabs";
import NewVote from "../modal/newVote";
import { useDaoContext } from "views/dao/provider";
import { useAppContext } from "components/provider/appProvider";
import { useVoteContract } from "hooks/useContract";
import { VOTE_MANAGER } from "utils/constant";

const TabLayout = memo((props) => {
  const { t } = useTranslation();
  const subRoute = useMatchRoute(VoteRoutes);
  const navigate = useNavigate();
  const { dispatch } = useAppContext();
  const {
    state: { componentAddressMap, fullHandle, whoAmI: {isOwner, isGP} },
  } = useDaoContext();

  const [currentNav, setCurrentNav] = useState();
  const [show, setShow] = useState(false);
  const [voteDisabled, setVoteDisabled] = useState(false);

  const voteContract = useVoteContract(componentAddressMap.get(VOTE_MANAGER));

  const tabs = useMemo(() => {
    return [t("Vote.Queue"), t("Vote.History")];
  }, []);

  const tabsRoute = useMemo(() => {
    return ["queue", "history"];
  }, []);

  useEffect(() => {
    const vheight = document.getElementById("contentBox").offsetHeight;
    const oHeight =
      document.body.clientHeight || document.documentElement.clientHeight;
    if  (oHeight > 900 && vheight >350) {
      document.getElementById("inner").style.height = `${vheight}px`;
    } else {
      document.getElementById("inner").style.height = `550px`;
    }
  }, []);

  useEffect(() => {
    setCurrentNav(subRoute === "history" ? 1 : 0);
  }, [subRoute]);

  const handleCurrent = (tabIndex) => {
    setCurrentNav(tabIndex);
    console.log("fullhandle: ", fullHandle);
    navigate(`/dao/${fullHandle}/vote/${tabsRoute[tabIndex]}`);
  };

  const handleNew = () => {
    if (voteDisabled) {
      dispatch({ type: 'MSGTYPE', payload: { msg: t('Msg.VoteDisabled'), closable: true } });
      return;
    }
    setShow(true);
  };
  const CloseModal = () => {
    setShow(false);
  };

  useEffect(async () => {
    if (!voteContract) {
      return;
    }
    try {
      const status = await voteContract.isOpenVote();
      setVoteDisabled(!status);
    } catch (error) {
      console.error("Ops, got new vote status failed", error);
    }
  }, [voteContract]);

  return (
    <InfoBox>
      {show && <NewVote CloseModal={CloseModal} />}
      <FirstLine>
        <Tabs
          navList={tabs}
          current={currentNav}
          handleCurrent={handleCurrent}
        />
        {
          (isGP || isOwner) && <BtnBox onClick={() => handleNew()}>{t("Vote.NewVote")}</BtnBox>
        }
      </FirstLine>

      <Content id="contentBox">
        <div id="inner">{props.children}</div>
      </Content>
    </InfoBox>
  );
});

export default TabLayout;

const InfoBox = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 40px 20px;
`;

const Content = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  flex: 1;
  height: 100%;
  margin-top: 30px;
  #inner {
    overflow-y: auto;
    height: 200px;
  }
`;
const FirstLine = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;
const BtnBox = styled.div`
  width: 100px;
  height: 34px;
  background: #d52473;
  border-radius: 8px;
  border: 1px solid #d52473;
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  line-height: 34px;
  text-align: center;
  cursor: pointer;
`;
