import React, { useEffect, useState, useCallback } from "react";
import styled from "styled-components";
import { useDaoContext } from "views/dao/provider";
import api from "api";
import { useDaoOrgContract, useOrgContract } from "hooks/useContract";
import useSigner from "hooks/useSigner";
import { DAO_ORG, ORG_MANAGER } from "utils/constant";
import CopyStr from "../copy";

import EditSvg from "components/svg/manager/edit";
import DoneSvg from "components/svg/manager/done";
import { useTranslation } from "react-i18next";
import { useAppContext } from "components/provider/appProvider";
import Avatar from "components/common/avatar";
import EmptyBox from "../common/empty";

export default function Managers() {
  const { t } = useTranslation();
  const { state: daoState } = useDaoContext();
  const { dispatch } = useAppContext();

  const {
    componentAddressMap,
    whoAmI: { isOwner },
  } = daoState;
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const orgContract = useOrgContract(componentAddressMap.get(ORG_MANAGER));
  const signer = useSigner();

  const [gplist, setGplist] = useState([]);

  useEffect(() => {
    const vheight = document.getElementById("contentBox").offsetHeight;
    const oHeight =
      document.body.clientHeight || document.documentElement.clientHeight;
    if  (oHeight > 900 && vheight >350) {
      document.getElementById("inner").style.height = `${vheight}px`;
    } else {
      document.getElementById("inner").style.height = `550px`;
    }
  }, []);

  useEffect(async () => {
    if (!orgContract) {
      return;
    }
    try {
      const { lst } = await api.org.getDaoMembersList(orgContract, 0, 1000);
      setGplist(lst);
    } catch (error) {
      console.error("getDaoMembersList failed", error);
    }
  }, [orgContract]);

  const showEdit = useCallback(
    (index) => {
      const lst = [];
      gplist.forEach((gp, i) => {
        lst.push({
          name: gp.name,
          address: gp.address,
          isEdit: i === index ? true : gp.isEdit,
          newName: i === index ? gp.name : gp.newName,
        });
      });
      setGplist(lst);
    },
    [gplist]
  );

  const cancelEdit = useCallback(
    (index, isSave) => {
      const lst = [];
      gplist.forEach((gp, i) => {
        lst.push({
          name: isSave && i === index ? gp.newName : gp.name,
          address: gp.address,
          isEdit: false,
        });
      });
      setGplist(lst);
    },
    [gplist]
  );

  const saveEdit = async (index) => {
    const gp = gplist[index];
    if (gp.name === gp.newName) {
      // no change
      cancelEdit(index);
      return;
    }
    try {
      dispatch({ type: "LOADINGTYPE", payload: `${t("Org.MsgEditName")}` });

      await api.org.editMemberName(daoOrg, signer, gp.address, gp.newName);

      cancelEdit(index, true);
    } catch (error) {
      console.error("EditMemberName Failed:", error);
      cancelEdit(index);
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: "Edit failed",
          closable: true,
        },
      });
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  const updateGpNewName = useCallback(
    (index, value) => {
      setGplist(
        gplist.map((gp, i) => {
          if (i === index) {
            return { ...gp, newName: value };
          } else {
            return { ...gp };
          }
        })
      );
    },
    [gplist]
  );

  return (
    <Content id="contentBox">
      <div id="inner">
        <UlBox>
          {!gplist.length && <EmptyBox />}
          { !!gplist.length&&gplist.map((gp, index) => (
            <li key={index}>
              <Avatar size="72px" address={gp.address} />
              <div>
                {isOwner &&
                  (gp.isEdit ? (
                    <EditLine>
                      <input
                        type="text"
                        className="font-medium"
                        value={gp.newName}
                        onChange={(e) => {
                          updateGpNewName(index, e.target.value);
                        }}
                      />
                      <DoneSvg
                        style={{ cursor: "Pointer" }}
                        onClick={() => {
                          saveEdit(index);
                        }}
                      />
                    </EditLine>
                  ) : (
                    <NameLine className="font-medium">
                      <span className="text">{gp.name}</span>
                      <EditButton
                        onClick={() => {
                          showEdit(index);
                        }}
                      />
                    </NameLine>
                  ))}
                <div className="address">
                  {gp.address} <CopyStr address={gp.address} />{" "}
                </div>
              </div>
            </li>
          ))}
        </UlBox>
      </div>
    </Content>
  );
}

const Content = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  flex: 1;
  height: 100%;
  margin-top: 30px;
  padding: 10px 20px 40px;
  #inner {
    overflow-y: auto;
    height: 200px;
  }
`;
const UlBox = styled.ul`
  margin-left: -2.3333%;
  &:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }
  li {
    width: 31%;
    float: left;
    background: #ffffff;
    box-shadow: 0px 0px 10px 0px rgba(16, 22, 75, 0.05);
    border-radius: 24px;
    border: 1px solid #f0f0f0;
    padding: 24px 22px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    word-break: break-all;
    margin-left: 2.3333333%;
    margin-bottom: 24px;

    & > div {
      margin-left: 14px;
    }

    .address {
      word-break: break-all;
    }
  }
`;

const NameLine = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4px;
  height: 36px;
  line-height: 36px;
  font-size: 18px;
  .text {
    color: #0e164e;
    width: calc(100% - 24px);
    height: 100%;
  }
  img {
    width: 15px;
    height: 15px;
    cursor: pointer;
  }
`;

const EditLine = styled(NameLine)`
  height: 36px;
  line-height: 36px;

  img {
    width: 24px;
    height: 24px;
  }

  input {
    width: calc(100% - 24px);
    height: 32px;
    background: #f7f7f7;
    border-radius: 4px;
    border: 0;
    padding: 0 5px;
    font-size: 18px;
    font-weight: 500;
    color: #0e164e;
  }
`;

const EditButton = styled(EditSvg)`
  cursor: pointer;
  transform: scale(0.7);
`;
