import { memo, useMemo, useState, useCallback, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useWeb3React } from "@web3-react/core";

import styled from "styled-components";

import Twitter from "components/svg/header/twitter";
import Medium from "components/svg/header/medium";
import Github from "components/svg/header/github";

import MoreSVG from "components/svg/header/more";
import DocSVG from "components/svg/header/doc";
import Button from "components/common/button";
import { useAppContext } from "components/provider/appProvider";
import useNativeCurrency from "hooks/useNativeCurrency";
import { LOCALSTORAGE } from "utils/constant";
import {Tooltip} from "antd";
import ExpiryImg from "../../img/voteNV/explains.png";

const MoreBox = memo(() => {
  const { t } = useTranslation();

  const { dispatch } = useAppContext();

  const { provider, account, chainId, connector } = useWeb3React();
  const native = useNativeCurrency(provider, account, chainId);

  const onLogout = useCallback(() => {
    connector.deactivate ? connector.deactivate() : connector.resetState();
    localStorage.removeItem(LOCALSTORAGE.WALLET);
    dispatch({ type: "CHANGE_WALLET", payload: undefined });
    window.location.reload();
  }, [connector]);

  const FormatNum = useCallback((size) => {
    console.log("==FormatNum===", size);
    const stringData = String(size)
    if (stringData.length > 6) {
      return stringData.slice(0, 6) + "..."
    } else {
      return stringData
    }
  }, []);

  const Contact = useMemo(() => {
    return (
      <ContactBox>
        <span>{t("Header.Contact")}</span>
        <a href="https://medium.com/@subdao" target="_blank" rel="noreferrer">
          <Medium />
        </a>
        <a href="https://github.com/SubDAO-Network" target="_blank" rel="noreferrer">
          <Github />
        </a>
        <a href="https://twitter.com/subdao_network" target="_blank" rel="noreferrer">
          <Twitter />
        </a>
      </ContactBox>
    );
  }, []);

  const Doc = useMemo(() => {
    return (
      <>
        <DocSVG className="doc" />
        <a href="https://wiki.subdao.network/" target="_blank" rel="noreferrer">
          Docs
        </a>
      </>
    );
  }, []);

  const loginBox = useMemo(() => {
    return (
      <>
        <FullLine>{Doc}</FullLine>
        {Contact}
      </>
    );
  }, []);

  const logoutBox = useMemo(() => {
    return (
      <>
        <HalfBox>
          <HalfLine>
            <img
              src={native.logo}
              className="logo"
              alt=""
              style={{ marginTop: "-0.2rem" }}
            />
              <Tooltip title={native.balance}>
                 <div className="balance">{FormatNum(native.balance)}</div>
              </Tooltip>

            {/*<span>{native.balance}</span>*/}
          </HalfLine>
          <HalfLine>{Doc}</HalfLine>
        </HalfBox>
        {Contact}
        <DisconnectBtn>
          <Button height={54} primary onClick={onLogout}>
            {t("Header.Disconnect")}
          </Button>
        </DisconnectBtn>
      </>
    );
  }, [native, onLogout]);

  return (
    <MoreBoxStyled>
      <MoreSVG color={"#FFFFFF"} style={{ cursor: "pointer" }} />
      <BoxContent className="boxContent">
        <DropBox>{account ? logoutBox : loginBox}</DropBox>
      </BoxContent>
    </MoreBoxStyled>
  );
});

export default MoreBox;

const MoreBoxStyled = styled.div`
  position: relative;
  height: 54px;
  &:hover {
    svg #color {
      fill: #efefef;
    }
    .boxContent {
      display: block;
    }
  }
`;
const BoxContent = styled.div`
  display: none;
  position: absolute;
  top: 54px;
  right: 0;
  padding-top: 17px;
`;

const DropBox = styled.div`
  background: #fff;
  width: 312px;
  box-shadow: 0px 0px 10px 0px rgba(16, 22, 75, 0.1);
  border-radius: 24px;
  box-sizing: border-box;
  padding: 20px;
`;

const BaseLine = styled.div`
  box-shadow: 0px 0px 10px 0px rgba(16, 22, 75, 0.1);
  border-radius: 14px;
  height: 62px;
  display: flex;
  align-items: center;
  &,
  a {
    font-size: 18px;
    color: #0e164e;
  }
  padding: 0 20px;
  .doc {
    margin-right: 14px;
  }
`;

const FullLine = styled(BaseLine)``;

const HalfLine = styled(BaseLine)`
  width: 50%;
  position: relative;
  &:first-child {
    margin-right: 20px;
  }
  .logo {
    margin-right: 8px;
    width: 24px;
  }
  &:hover {
    //span {
    //  display: block;
    //}
  }
  .balance {
    //position: absolute;
    //top: -10px;
    //left: 42px;
    //display: none;
  }
`;

const HalfBox = styled.div`
  display: flex;
`;

const ContactBox = styled(FullLine)`
  margin-top: 14px;
  span {
    margin-right: 15px;
  }
  a {
    margin-right: 12px;
    height: 24px;
  }
`;

const DisconnectBtn = styled.div`
  margin-top: 14px;
  &,
  button {
    width: 100%;
    font-size: 18px !important;
  }
`;
