import * as history from "history";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import SelectedImg from "../../../img/Selected.png";
import RectImg from "../../../img/Rectangle.png";
import SupplyModal from "./modals/supplyModal";
import { useEffect, useState, useMemo } from "react";
import api from "api";
import { useDaoOrgContract } from "hooks/useContract";
import { useDaoContext } from "views/dao/provider";
import { useAppContext } from "components/provider/appProvider";
import { VAULT_MANAGER, DAO_ORG } from "utils/constant";
import useVaultTokens from "hooks/useVaultTokens";
import useSigner from "hooks/useSigner";
import { getReserves } from "api/graphql/aave";
import { SECONDS_PER_YEAR } from "utils/constant";

import BigNumber from "bignumber.js";
import { RAY, rayPow, valueToZDBigNumber } from "@aave/math-utils";
import { useCallback } from "react";
import { getShortDisplay } from "utils/publicJs";
import { useTranslation } from "react-i18next";

export default function TxList(props) {
  const [showModal, setShowModal] = useState(false);

  const {
    state: { componentAddressMap },
  } = useDaoContext();

  const vaultTokens = useVaultTokens(componentAddressMap.get(VAULT_MANAGER));

  const [list, setList] = useState([]);
  const [openReserve, setOpenReserve] = useState({});

  const balanceMap = useMemo(() => {
    if (!vaultTokens.length) {
      return {};
    }
    const map = {};
    vaultTokens.forEach((token) => {
      map[token.address.toLowerCase()] = {
        balance: Number(token.balance),
        balanceDisplay: token.balanceDisplay,
      };
    });
    return map;
  }, [vaultTokens]);

  const closeModal = () => {
    setOpenReserve({});
    setShowModal(false);
  };

  const openSupply = useCallback((reserve) => {
    const address = reserve.underlyingAsset;
    if (!balanceMap[address]) {
      return
    }
    setOpenReserve(reserve);
    setShowModal(true);
  }, [balanceMap]);

  

  const handleReserves = useCallback((reserves) => {
    const lst = [];
    for (const r of reserves) {
      if (!r.borrowingEnabled || !r.pool.active) {
        continue;
      }

      const supplyAPY = rayPow(
        valueToZDBigNumber(r.liquidityRate)
          .dividedBy(SECONDS_PER_YEAR)
          .plus(RAY),
        SECONDS_PER_YEAR
      ).minus(RAY);
      const supplyAPYDisplay = BigNumber(supplyAPY.toString())
        .div(BigNumber(RAY.toString()))
        .toNumber()
        .toFixed(4);

      const variableBorrowAPY = rayPow(
        valueToZDBigNumber(r.variableBorrowRate)
          .dividedBy(SECONDS_PER_YEAR)
          .plus(RAY),
        SECONDS_PER_YEAR
      ).minus(RAY);
      const stableBorrowAPY = rayPow(
        valueToZDBigNumber(r.stableBorrowRate)
          .dividedBy(SECONDS_PER_YEAR)
          .plus(RAY),
        SECONDS_PER_YEAR
      ).minus(RAY);
      lst.push({
        ...r,
        logo: `https://app.aave.com/icons/tokens/${r.symbol.toLowerCase()}.svg`,
        supplyAPY,
        supplyAPYDisplay,
        variableBorrowAPY: variableBorrowAPY.toString(),
        stableBorrowAPY: stableBorrowAPY.toString(),
      });
    }
    setList(lst);
  }, []);

  useEffect(async () => {
    const resp = await getReserves();
    handleReserves(resp.reserves);
  }, []);


  return (
    <>
      {showModal && (
        <SupplyModal
          reserve={openReserve}
          closeModal={closeModal}
        />
      )}
      <TableBox>
        <table cellPadding="0" cellSpacing="0">
          <thead>
            <tr>
              <th>Asset</th>
              <th>Wallet balance</th>
              <th style={{ textAlign: "center" }}>APY</th>
              <th style={{ textAlign: "center" }}>Can be collateral</th>
              <th style={{ textAlign: "center" }}>Action</th>
            </tr>
          </thead>
          <tbody>
            {list.map((r, i) => (
              <tr key={i}>
                <td className="font-medium info">
                  <img className="logo" src={r.logo} alt="" />
                  {r.symbol}
                </td>
                <td>
                  <div className="font-medium">
                    {balanceMap[r.underlyingAsset]
                      ? balanceMap[r.underlyingAsset].balanceDisplay
                      : "0.0"}
                  </div>
                  {/* <div className="tips font-medium">$8,981.61</div> */}
                </td>
                <td className="font-medium">{r.supplyAPYDisplay * 100} %</td>
                <td style={{ textAlign: "center" }}>
                  <img src={RectImg} alt="" />
                  {/* <img src={SelectedImg} alt="" /> */}
                </td>
                <td style={{ textAlign: "center" }}>
                  <Button
                    onClick={() => openSupply(r)}
                  >
                    Supply
                  </Button>
                  <Button className="borderLine">
                    <a
                      target="_blank"
                      rel="noreferrer"
                      href={`https://app.aave.com/reserve-overview/?underlyingAsset=${r.underlyingAsset}`}
                    >
                      Details
                    </a>
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </TableBox>
    </>
  );
}
const TableBox = styled.div`
  position: relative;
  height: 100%;
  table {
    width: 100%;
    margin-bottom: 80px;
  }
  th {
    height: 65px;
    color: #b2b4c5;
    font-size: 16px;
    white-space: nowrap;
    text-align: left;
    &:first-child {
      padding-left: 20px;
    }
  }
  td {
    text-align: left;
    white-space: nowrap;
    cursor: pointer;
    height: 65px;
    font-size: 18px;
    font-weight: 500;
    color: #10164b;
    line-height: 25px;
    &:first-child {
      padding-left: 20px;
    }
    &:last-child {
    }
    &.info {
      text-align: left;
    }
    .logo {
      margin-right: 13px;
      width: 34px;
      height: 34px;
      border-radius: 34px;
    }
    .tips {
      font-size: 14px;
      font-weight: 500;
      color: #959bb4;
      line-height: 19px;
    }
  }
  tr:hover {
    td {
      background: #fbfbfa;
      &:first-child {
        border-top-left-radius: 14px;
        border-bottom-left-radius: 14px;
      }
      &:last-child {
        border-top-right-radius: 14px;
        border-bottom-right-radius: 14px;
      }
    }
  }
`;

const Button = styled.button`
  width: 83px;
  height: 24px;
  background: #d51172;
  border: 1px solid rgba(213, 17, 114, 0.5);
  line-height: 20px;
  color: #ffffff;
  margin-right: 20px;
  cursor: pointer;
  &.borderLine {
    background: #fff;
    color: #d51172;
    margin-right: 0;
  }
`;
