import React, { memo, useCallback, useEffect, useState, useRef } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import LogoUpload from "components/upload/logo";
import { InputItem, InputLabel } from "components/common/inputItem";
import VentureTemplate from "components/create/template";

import TwitterSvg from "components/svg/create/socials/twitter";
import DiscordSvg from "components/svg/create/socials/discord";
import TelegramIcon from "img/common/telegram.png";

import { CREATE_STATUS } from "../../utils/constant";
import { useWeb3React } from "@web3-react/core";
import { useAppContext } from "components/provider/appProvider";

import { CheckHandleUnique } from "api/graphql/dao";
import { useLazyQuery } from "@apollo/client";
import { useMemo } from "react";

const KEY = "basic_info";
export const LOCAL_KEY = `CreateDao_${KEY}`;

const BasicInfoStep = memo((props) => {
  const { updateNavState } = props;
  const { t } = useTranslation();
  const { chainId } = useWeb3React();

  const [name, setName] = useState("");
  const [twitter, setTwitter] = useState("");
  const [discord, setDiscord] = useState("");
  const [telegram, setTelegram] = useState("");
  const [description, setdescription] = useState("");
  const [daohandle, setDaohandle] = useState("");
  const [imgUrl, setimgUrl] = useState("");

  const [invalidHandle, setInvalidHandle] = useState();

  const logoRef = useRef();

  const [doCheckHandle, { checking }] = useLazyQuery(CheckHandleUnique, {
    onCompleted: (data) => {
      if (data && data.checkDaoHandleAvailable) {
        setInvalidHandle(undefined);
      } else {
        setInvalidHandle(t("Msg.HandleError"));
      }
    },
    onError: (error) => {
      console.error("====", error);
    },
  });

  const checkDaoHandle = useCallback(
    async (handle) => {
      if (!handle) {
        setInvalidHandle();
        return;
      }
      doCheckHandle({
        variables: {
          chainId,
          handle: handle,
        },
      });
    },
    [chainId]
  );

  const savePage = async () => {
    const data = {
      name,
      description,
      imgUrl,
      daohandle,
      twitter,
      discord,
      telegram,
    };
    sessionStorage.setItem(LOCAL_KEY, JSON.stringify(data));

    if (name && description && daohandle) {
      updateNavState(KEY, CREATE_STATUS.PASS);
    } else {
      updateNavState(KEY, CREATE_STATUS.FAIL);
    }
  };

  useEffect(() => {
    try {
      const logoImg = sessionStorage.getItem(`${LOCAL_KEY}_logo`);
      if (logoImg) {
        setimgUrl(logoImg);
      }
      const data = JSON.parse(sessionStorage.getItem(LOCAL_KEY) || "");
      if (data.name) {
        setName(data.name);
      }
      if (data.description) {
        setdescription(data.description);
      }
      if (data.daohandle) {
        setDaohandle(data.daohandle);
      }
      data.twitter && setTwitter(data.twitter);
      data.discord && setDiscord(data.discord);
      data.telegram && setTelegram(data.telegram);
    } catch (error) {
      console.warn("get local failed", error);
    }
  }, []);

  const updateLogo = useCallback((url) => {
    setimgUrl(url);
    sessionStorage.setItem(`${LOCAL_KEY}_logo`, url);
  }, []);

  const descError = useMemo(() => {
    if (description && description.length > 1024) {
      return true;
    }
  }, [description]);

  return (
    <>
      <VentureTemplate />
      <EditArea>
        <UploadItem>
          <InputLabel title={t("Create.DAOImage")} labelSize="16px" />
          <LogoUpload ref={logoRef} imgCode={imgUrl} updateUrl={updateLogo} />
          <LogoTip>{t("Create.DAOImageTip")}</LogoTip>
        </UploadItem>
        <EditRightArea>
          <FlexRow>
            <InputItem
              required
              labelSize="16px"
              title={t("Create.DAOName")}
              placeholder={t("Create.FillName")}
              maxLength={32}
              value={name}
              name="name"
              onBlur={savePage}
              onChange={(e) => {
                if ((e.target.value.length) <= 32) {
                  setName(e.target.value);
                }
              }}
            />
            <InputItem
              required
              labelSize="16px"
              title={t("Create.DAOHandle")}
              placeholder={t("Create.DAOHandleTip")}
              maxLength={32}
              value={daohandle}
              name="daohandle"
              errorMsg={invalidHandle}
              onBlur={() => {
                savePage();
                checkDaoHandle(daohandle);
              }}
              onChange={(e) => {
                if ((e.target.value.length) <= 32) {
                  setDaohandle(e.target.value);
                }
              }}
            />
          </FlexRow>
          <FlexRow>
            <InputItem
              labelSize="16px"
              icon={<TwitterSvg />}
              title={t("Create.twitter")}
              value={twitter}
              outPrefix="@"
              name="twitter"
              onBlur={savePage}
              onChange={(e) => {
                setTwitter(e.target.value);
              }}
            />
            <InputItem
              labelSize="16px"
              icon={<img src={TelegramIcon} alt="" style={{width: "24px", verticalAlign: "unset"}} />}
              title={t("Create.telegram")}
              value={telegram}
              outPrefix="http://t.me/"
              name="telegram"
              onBlur={savePage}
              onChange={(e) => {
                setTelegram(e.target.value);
              }}
            />
          </FlexRow>
          <FlexRow>
            <InputItem
              labelSize="16px"
              icon={<DiscordSvg />}
              title={t("Create.discord")}
              placeholder={t("Create.discordTip")}
              value={discord}
              name="discord"
              onBlur={savePage}
              onChange={(e) => {
                setDiscord(e.target.value);
              }}
            />
          </FlexRow>
          <FlexRow>
            <InputItem
              required
              isError={descError}
              area
              labelSize="16px"
              title={t("Create.Description")}
              placeholder={t("Create.FillDAODescription")}
              value={description}
              name="description"
              onBlur={savePage}
              onChange={(e) => {
                setdescription(e.target.value);
              }}
            />
            <LimitBox val={description && description.length} max={1024} />
          </FlexRow>
        </EditRightArea>
      </EditArea>
    </>
  );
});

export default BasicInfoStep;

const LimitBox = memo(({ val, max }) => {
  return (
    <LimitBoxStyled>
      {val || 0}/{max}
    </LimitBoxStyled>
  );
});

const LimitBoxStyled = styled.div`
  color: rgba(16, 22, 75, 0.1);
  line-height: 18px;
  font-size: 16px;
  position: absolute;
  right: 10px;
  bottom: 4px;
`;

const EditArea = styled.div`
  display: flex;
  margin-top: 35px;
`;

const EditRightArea = styled.div`
  flex: 1;
  margin-left: 57px;
`;

const FlexRow = styled.div`
  display: flex;
  align-items: end;
  margin-bottom: 42px;
  position: relative;
  & > div {
    flex: 1;
    &:nth-child(2n) {
      margin-left: 48px;
    }
  }
  &:last-child {
    margin-bottom: 0;
  }
`;
const UploadItem = styled.div``;

const LogoTip = styled.div`
  color: #a6a6b7;
  margin-top: 12px;
  width: 130px;
`;
