import React, { memo, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { Modal } from "antd";
import styled from "styled-components";

import FinishedSvg from "../../components/svg/create/selected";
import SuccessSvg from "../../components/svg/create/success";
import CopySvg from "../../components/svg/common/copy";
import LinkSvg from "../../components/svg/common/link";
import { LoadingBox } from "components/LoadingModal";
import { useAppContext } from "components/provider/appProvider";


const STEP_STATUS = {
  OFF: 0,
  ON: 1,
  FINISH: 2,
};

const StepItem = memo((props) => {
  const { label, step, currentStep } = props;

  const status = useMemo(() => {
    if (currentStep > step) {
      return STEP_STATUS.FINISH;
    }
    if (currentStep === step) {
      return STEP_STATUS.ON;
    }
    return STEP_STATUS.OFF;
  }, [step, currentStep]);

  return (
    <Step>
      {status === STEP_STATUS.FINISH ? (
        <FinishedSvg color="#00C172" />
      ) : (
        <StepIcon status={status}>{step}</StepIcon>
      )}
      <StepLabel status={status}>{label}</StepLabel>
    </Step>
  );
});

const CreatingModal = memo((props) => {
  const { t } = useTranslation();
  const { show, step, closeModal, transHash } = props;

  const {
    state: { exploreScan },
  } = useAppContext();

  const STEPS = useMemo(() => {
    return [...Array(3)];
  }, []);

  const showStepContent = useMemo(() => {
    const TEXT = (
      <>
        <MainText className="font-medium">
          {t(`Create.Step${step}MainText`)}
        </MainText>
        <SubText>{t(`Create.Step${step}Text`)}</SubText>
      </>
    );
    switch (step) {
      case 1:
      case 2:
        return (
          <>
            <LoadingBox />
            {TEXT}
          </>
        );
      case 3:
        return (
          <>
            <div className="icon">
              <SuccessSvg />
            </div>
            {TEXT}
            <SubTransaction>
              <SubTransText>{transHash}</SubTransText>
              <CopySvg />
              <a href={`${exploreScan}tx/${transHash}`} rel="noreferrer" target="_blank">
                <LinkSvg style={{ position: "relative", top: "3px"}} />
              </a>
            </SubTransaction>
          </>
        );
      default:
        return <></>;
    }
  }, [step]);

  const handleClose = () => {
    closeModal();
  };

  return (
    <ModalStyled
      visible={show}
      footer={null}
      width={708}
      closable={step === 3}
      maskClosable={false}
      onCancel={handleClose}
    >
      <StepBody>{showStepContent}</StepBody>
      <StepFooter>
        {STEPS.map((_, i) => (
          <StepItem
            key={i}
            label={t(`Create.Step${i + 1}Label`)}
            step={i + 1}
            currentStep={step}
          />
        ))}
      </StepFooter>
    </ModalStyled>
  );
});

export default CreatingModal;

const ModalStyled = styled(Modal)`
  .ant-modal-body {
    padding: 74px 0 0 0 !important;
  }
`;

const StepBody = styled.div`
  color: #10164b;
  text-align: center;
  height: 217px;
  .icon {
    margin-bottom: 25px;
  }
`;

const MainText = styled.div`
  font-size: 22px;
`;

const SubText = styled.div`
  line-height: 19px;
  margin-top: 6px;
  width: 333px;
  font-size: 14px;
  display: inline-block;
`;
const SubTransText = styled.span`
  font-size: 14px;
  margin-right: 8px;
`;

const SubTransaction = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    cursor: pointer;
  }
`;

const StepFooter = styled.div`
  width: 100%;
  height: 75px;
  background: #fafafa;
  border-radius: 0px 0px 18px 18px;
  display: flex;
`;

const Step = styled.div`
  display: flex;
  align-items: center;
  margin-left: 63px;
  &:first-child {
    margin-left: 76px;
  }
`;
const StepIcon = styled.span`
  width: 24px;
  height: 24px;
  background: ${(props) =>
    props.status === STEP_STATUS.ON ? "#00C172" : "#D8D8D8"};
  color: #ffffff;
  border-radius: 50%;
  text-align: center;
  line-height: 24px;
`;

const StepLabel = styled.span`
  margin-left: 6px;
  line-height: 24px;
  color: ${(props) =>
    props.status === STEP_STATUS.ON ? "#00C56A" : "#10164B"};
`;
