import SetImg from "../../../img/swap/Set.png";
import UniswapImg from "img/voteNV/uniswap.png";

import DropDownWhiteImg from "../../../img/swap/dropdownWhite.png";
import DropdownImg from "../../../img/swap/dropdown.png";
import SwipeImg from "../../../img/swap/Swipe.png";
import InfoImg from "../../../img/swap/info.png";
import OilImg from "../../../img/swap/oil.png";
import DownArrow from "../../../img/swap/downArrow.png";
import TopArrowImg from "../../../img/swap/topArrow.png";
import styled from "styled-components";
import { useCallback, useEffect, useState, useMemo } from "react";
import SwapConfirm from "./swapConfirm";
import SwapSetting from "./swapSetting";
import SwapLoading from "./swaploading";
import SwapLoadingProgress from "./swapLoadingProgress";

import ApplyModal from "./ApplyModal";
import SelectTokenModal from "./tokenModal";

import { useTranslation } from "react-i18next";
import SwapRoute from "./swapRoute";
import useRouter from "hooks/useRouter";
import { useDaoContext } from "views/dao/provider";
import { VAULT_MANAGER, DAO_ORG } from "utils/constant";
import { ethers } from "ethers";
import api from "api";
import { useDaoOrgContract } from "hooks/useContract";
import useSigner from "hooks/useSigner";
import useVaultTokens from "hooks/useVaultTokens";
import { useAppContext } from "components/provider/appProvider";

import { InputNumber } from "antd";
import { useWeb3React } from "@web3-react/core";
import { TokenLogo } from "components/common/avatar";
import { getShortDisplay } from "utils/publicJs";
import { useNetworkData } from "hooks/useNetwork";

export default function SwapIndex(props) {
  const { t } = useTranslation();

  const { chainId, provider } = useWeb3React();

  const {
    state: { componentAddressMap },
  } = useDaoContext();

  const vaultAddress = useMemo(() => {
    return componentAddressMap.get(VAULT_MANAGER);
  }, [componentAddressMap]);
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const {
    state: { tokens },
    dispatch,
  } = useAppContext();

  const [from, setFrom] = useState(true);
  const [to, setTo] = useState(false);
  const [showPrice, setShowPrice] = useState(false);
  const [showOutput, setShowOutput] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [showSetting, setShowSetting] = useState(false);
  const [loading, setLoading] = useState(false);

  const [showApply, setShowApply] = useState(false);
  const [showTokenType, setShowTokenType] = useState(0);

  const [fromToken, setFromToken] = useState();
  const [toToken, setToToken] = useState();

  const [fromAmount, setFromAmount] = useState();
  const [toAmount, setToAmount] = useState();
  const [lessToAmount, setLessToAmount] = useState();

  const [swapPrice, setSwapPrice] = useState();
  const [tokenPath, setTokenPath] = useState([]);

  const networkData = useNetworkData(chainId);

  const swapRouterAddress = useMemo(() => {
    if (networkData) {
      return networkData.swapRouter;
    }
  }, [networkData]);

  const { swapRoute, routeState } = useRouter(fromToken, toToken, fromAmount);

  const vaultTokens = useVaultTokens(componentAddressMap.get(VAULT_MANAGER));
  const displayTokens = useMemo(() => {
    return showTokenType === 1 ? vaultTokens : tokens;
  }, [showTokenType]);

  const handleOutput = () => {
    setShowOutput(!showOutput);
  };

  const CloseSetting = () => {
    setShowSetting(false);
  };
  const handleSetting = () => {
    setShowSetting(true);
  };

  const closeLoading = () => {
    setLoading(false);
  };
  const handleLoading = () => {
    setLoading(true);
  };

  // token modal
  const closeToken = useCallback(
    (token) => {
      console.log("token,", token, showTokenType);
      if (showTokenType === 1) {
        token.address && setFromToken(token);
      } else if (showTokenType === 2) {
        token.address && setToToken(token);
      }
      setShowTokenType(0);
    },
    [showTokenType]
  );

  useEffect(() => {
    if (swapRoute) {
      setTokenPath(swapRoute.path);
    } else {
      if (fromToken && toToken && swapPrice) {
        setTokenPath([fromToken, toToken]);
      }
    }
  }, [swapRoute, fromToken, toToken, swapPrice]);

  const swap = () => {
    setShowModal(true);
  };
  const confirmSwap = async () => {
    try {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
      await api.venture.buyToken(daoOrg, signer, {
        swapRouterAddress,
        paths: tokenPath.map((t) => t.address),
        fromAmount: ethers.utils.parseUnits(
          String(fromAmount),
          fromToken.deci || fromToken.decimals
        ),
        toAmount: ethers.utils.parseUnits(
          String(lessToAmount),
          toToken.deci || toToken.decimals
        ),
      });
      setShowModal(false);
    } catch (error) {
      console.log(error?.error?.message);
      dispatch({ type: "LOADINGTYPE", payload: null });
      dispatch({ type: 'MSGTYPE', payload: { msg: error?.error?.message } });
      setShowModal(false);
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  useEffect(() => {
    setToAmount();
    setFromAmount();
  }, [fromToken, toToken]);

  useEffect(() => {}, [fromToken, toToken, fromAmount]);

  const fromTokenInfo = useMemo(() => {
    if (!fromToken) {
      return;
    }
    return {
      symbol: fromToken.symbol,
      amount: fromAmount,
    };
  }, [fromAmount, fromToken]);

  const toTokenInfo = useMemo(() => {
    if (!toToken) {
      return;
    }
    return {
      symbol: toToken.symbol,
      amount: toAmount,
    };
  }, [toAmount, toToken]);

  useEffect(() => {
    if (fromAmount && swapPrice) {
      setLessToAmount(Number(swapPrice) * (1 - 0.5 / 100) * fromAmount);
      setToAmount(Number(swapPrice) * fromAmount);

    }
  }, [fromAmount, swapPrice]);

  useEffect(async () => {
    if (!fromToken || !toToken) {
      return;
    }
    try {
      const num = await api.venture.getAmountOutMin(provider, signer, {
        swapRouterAddress,
        path: [fromToken.address, toToken.address],
        amountIn: ethers.utils.parseUnits("1", fromToken.deci),
      });
      setSwapPrice(
        getShortDisplay(
          ethers.utils.formatUnits(num, toToken.deci || toToken.decimals)
        )
      );
    } catch (error) {
      console.error("getAmountOutMin", error);
    }
  }, [fromToken, toToken]);

  const swapData = useMemo(() => {
    return {
      fromToken,
      toToken,
      fromAmount,
      toAmount,
      swapPrice,
      swapRoute,
      lessToAmount
    };
  }, [fromToken, toToken, fromAmount, toAmount, swapRoute, swapPrice, lessToAmount]);

  const swapBtnDisabled = useMemo(() => {
    if (!fromToken || !toToken) {
      return true;
    }
    if (!fromAmount || !toAmount) {
      return true;
    }
    if (!tokenPath.length) {
      return;
    }
  }, [fromToken, toToken, fromAmount, toAmount, tokenPath]);

  if (showTokenType > 0) {
    return <SelectTokenModal tokens={displayTokens} closeModal={closeToken} />;
  }

  return (
    <div>
      {showApply && <ApplyModal />}
      {showModal && (
        <SwapConfirm
          closeModal={() => setShowModal(false)}
          data={swapData}
          handleConfirm={confirmSwap}
        />
      )}
      {loading && (
        <SwapLoadingProgress
          closeModal={closeLoading}
          fromToken={fromTokenInfo}
          toToken={toTokenInfo}
        />
      )}

      <Tips>{t("Swap.Intro")}</Tips>
      <BoxBtm>
        <BoxShadow>
          <div className="topClose">
            {showSetting && <SwapSetting CloseSetting={CloseSetting} />}

            <img src={SetImg} alt="" onClick={() => handleSetting()} />
          </div>
          <div className="topImg">
            {chainId === 56 ? (
              <SwapTitle className="font-bold"></SwapTitle>
            ) : (
              <img src={UniswapImg} alt="" />
            )}
          </div>
          <InputBoxBg>
            <div>
              <InputStyled
                placeholder="0.0"
                controls={false}
                precision={2}
                min={0}
                value={fromAmount}
                onChange={(v) => setFromAmount(Number(v))}
              />
            </div>
            <div>
              <SelectBox
                className={!fromToken ? "redBg" : ""}
                onClick={() => setShowTokenType(1)}
              >
                {fromToken ? (
                  <>
                    <span className="name font-bold">
                      <TokenLogo src={fromToken.logo} />
                      {fromToken.symbol}
                    </span>
                    <img
                      src={!fromToken ? DropDownWhiteImg : DropdownImg}
                      alt=""
                    />
                  </>
                ) : (
                  <>
                    <span>Select a token</span>
                    <img
                      src={!fromToken ? DropDownWhiteImg : DropdownImg}
                      alt=""
                    />
                  </>
                )}
              </SelectBox>
              {fromToken && (
                <RhtBalance>
                  <div>
                    {t("Balance")} : {fromToken.balanceDisplay}
                  </div>
                  <div
                    className="max"
                    onClick={() => {
                      setFromAmount(fromToken.balance);
                    }}
                  >
                    {t("Max")}
                  </div>
                </RhtBalance>
              )}
            </div>
          </InputBoxBg>
          <CenterBox>
            <img src={SwipeImg} alt="" />
          </CenterBox>
          <InputBoxBg>
            <div>
              <InputStyled
                placeholder="0.0"
                controls={false}
                precision={2}
                min={0}
                value={toAmount}
                onChange={(v) => setToAmount(Number(v))}
              />
            </div>
            <div>
              <SelectBox
                className={!toToken ? "redBg" : ""}
                onClick={() => setShowTokenType(2)}
              >
                {toToken ? (
                  <>
                    <span className="name font-bold">
                      <TokenLogo src={toToken.logo} />
                      {toToken.symbol}
                    </span>
                    <img
                      src={!toToken ? DropDownWhiteImg : DropdownImg}
                      alt=""
                    />
                  </>
                ) : (
                  <>
                    <span>Select a token</span>
                    <img
                      src={!toToken ? DropDownWhiteImg : DropdownImg}
                      alt=""
                    />
                  </>
                )}
              </SelectBox>
            </div>
          </InputBoxBg>
          <MidBox>
            {!!swapPrice && fromToken && toToken && (
              <>
                <FirstLine onClick={() => handleOutput()}>
                  <div className="lft">
                    <img src={InfoImg} alt="" />1{fromToken.symbol}={swapPrice}
                    {toToken.symbol}
                  </div>
                  <div className="rht">
                    {/* <img src={OilImg} alt="" /> */}
                    {/* <span>$1.77</span> */}
                    {!showOutput && <img src={DownArrow} alt="" />}
                    {showOutput && <img src={TopArrowImg} alt="" />}
                  </div>
                </FirstLine>
                {/*<FirstLine onClick={()=>handlePending()}>*/}
                {/*    <div className="lft"><SwapLoading /><span className="tips">Fetching best price…</span></div>*/}
                {/*    <div className="rht"><img src={DownArrow} alt=""/></div>*/}
                {/*</FirstLine>*/}
                <InsufficientBox>
                  {showOutput && (
                    <li>
                      <div className="dl font-bold">
                        <div>
                          <div>Expected Output</div>
                          <div>
                            {getShortDisplay(toAmount)} {toToken.symbol}
                          </div>
                        </div>
                        {/* <div>
                          <div>Price Impact</div>
                          <div>0.24%</div>
                        </div> */}
                      </div>
                      <div className="dl">
                        <div>
                          <div>Minimum received after slippage (0.50%)</div>
                          <div>
                            {getShortDisplay(lessToAmount)} {toToken.symbol}
                          </div>
                        </div>
                        {/* <div>
                          <div>NetworkFee</div>
                          <div>~$1.89</div>
                        </div> */}
                      </div>
                    </li>
                  )}

                  <SwapRoute tokenPath={tokenPath} tokens={tokens} />
                </InsufficientBox>
              </>
            )}
          </MidBox>

          <ButtonBox>
            <button
              className="font-medium"
              disabled={swapBtnDisabled}
              // onClick={() => handleModal()}
              onClick={swap}
            >
              {/*<span>Insufficient ETH balance</span>*/}
              <span>Swap</span>
              {/*<div>Allow the Uniswap Protocol to use your UNI*/}
              {/*    <Tooltip title="If no one votes or executes within the validity period, the proposal will automatically become invalid.">*/}
              {/*        <img src={TipsImg} alt=""/>*/}
              {/*    </Tooltip>*/}
              {/*</div>*/}
            </button>
          </ButtonBox>
        </BoxShadow>
      </BoxBtm>
    </div>
  );
}

