import React, { memo } from "react";
import styled from "styled-components";
import { Modal } from "antd";

import ShadowIcon from "img/loading/shadow.png";
import LogoIcon from "img/loading/logo.png";

import { useTranslation } from "react-i18next";
import { useAppContext } from "./provider/appProvider";

export const LoadingBox = memo(() => {
  return (
    <LoadingStyled className="loading">
      <div className="jumpIcon" />
    </LoadingStyled>
  );
});

const LoadingModal = (props) => {
  const { t } = useTranslation();
  const { showMsg, handleClose } = props;
  const {
    state: { loadingType },
  } = useAppContext();

  return (
    <Modal
      width={364}
      visible={showMsg}
      onCancel={handleClose}
      maskClosable={false}
      footer={null}
      centered={true}
    >
      <LoadingBox />
      <TitleBox>{loadingType}</TitleBox>
    </Modal>
  );
};

export default LoadingModal;

const LoadingStyled = styled.div`
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  .jumpIcon {
    height: 55px;
    position: relative;
    margin-left: -15px;
  }
  .jumpIcon::before {
    background: url(${ShadowIcon});
    background-size: 100%;
    background-repeat: no-repeat;
    content: "";
    width: 30px;
    height: 10px;
    position: absolute;
    top: 45px;
    left: 0;
    animation: shadow 0.5s linear infinite;
  }
  .jumpIcon::after {
    background: url(${LogoIcon});
    background-size: 100%;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 30px;
    height: 30px;
    animation: rotate 0.5s linear infinite;
  }
  @keyframes shadow {
    0%,
    100% {
      transform: scaleX(1);
    }
    50% {
      transform: scaleX(1.2);
    }
  }
  @keyframes rotate {
    0% {
      transform: translateY(0);
    }
    25% {
      transform: translateY(5px);
    }
    50% {
      transform: translateY(15px) scale(1.1, 0.9);
    }
    75% {
      transform: translateY(5px);
    }
    100% {
      transform: translateY(0);
    }
  }
`;

const TitleBox = styled.div`
  text-align: center;
  margin-top: 8px;
  font-size: 16px;
  color: #10164b;
`;
