import React, { memo, useMemo, useEffect, useState } from "react";
import { useCallback } from "react";
import styled from "styled-components";
import SideNavLayout from "./layout";
import BasicInfoStep, { LOCAL_KEY as BasicKey } from "./basicInfo";
import AddManager, { LOCAL_KEY as AddManagerKey } from "./addManager";
import Fundraising, { LOCAL_KEY as FundKey } from "./fundraising";
import Preivew from "./preview";

import { CheckHandleUnique } from "api/graphql/dao";
import { useLazyQuery } from "@apollo/client";
import { useWeb3React } from "@web3-react/core";
import { ethers } from "ethers";
import { checkBonus } from "utils/check";

const STEPS = [
  {
    name: "Create.Basicinformation",
    key: "basic_info",
  },
  {
    name: "Create.AddManager",
    key: "add_manager",
  },
  {
    name: "Create.Fundraising",
    key: "fundraising",
  },
  {
    name: "Create.Preview",
    key: "preview",
  },
];

const CreatePage = memo(() => {
  const [step, setStep] = useState(STEPS[0].key);
  const [navs, setNavs] = useState(STEPS);

  const { chainId } = useWeb3React();

  const onChangeStep = (key) => {
    console.log("[onChangeStep]", key);
    setStep(key);
  };
  const showStep = useCallback((key) => {
    switch (key) {
      case "basic_info":
        return <BasicInfoStep updateNavState={updateNavState} />;
      case "add_manager":
        return <AddManager updateNavState={updateNavState} />;
      case "fundraising":
        return <Fundraising updateNavState={updateNavState} />;
      case "preview":
        return <Preivew />;
      default:
        return <></>;
    }
  }, []);

  const updateNavState = useCallback(
    (key, status) => {
      setNavs(
        STEPS.map((n) => {
          return n.key === key
            ? {
                ...n,
                status,
              }
            : { ...n };
        })
      );
    },
    [navs]
  );

  const containerStyle = useMemo(() => {
    return step === "preview"
      ? {
          background: "transparent",
          boxShadow: "none",
          border: "none",
          padding: 0,
        }
      : { padding: "70px 97px" };
  }, [step]);

  const [doCheckHandle] = useLazyQuery(CheckHandleUnique);

  const checkBasic = async () => {
    try {
      const data = JSON.parse(sessionStorage.getItem(BasicKey) || "");
      if (data.name && data.description && data.daohandle) {
        // check handle
        const resp = await doCheckHandle({
          variables: {
            chainId,
            handle: data.daohandle,
          },
        });
        return resp.data.checkDaoHandleAvailable;
      }
      return false;
    } catch (error) {
      return false;
    }
  };

  const checkInvalidAddress = useCallback((addressList) => {
    for (const a of addressList) {
      if (!ethers.utils.isAddress(a.address)) return a.address;
    }
  }, []);

  const checkAddManager = useCallback(() => {
    try {
      const data = JSON.parse(sessionStorage.getItem(AddManagerKey) || "");
      if (
        !data.GpCarry ||
        !data.manageFee ||
        !data.addressList ||
        !data.minSupportNumber
      ) {
        return false;
      }
      if (
        data.minSupportNumber <= 0 ||
        data.minSupportNumber > data.addressList.length
      ) {
        return false;
      }
      const invalidAddress = checkInvalidAddress(data.addressList);
      if (invalidAddress) {
        return false;
      }
      if (!checkBonus(data.addressList)) {
        return false;
      }
      return true;
    } catch (error) {
      return false;
    }
  }, []);

  const checkFund = useCallback(() => {
    try {
      const data = JSON.parse(sessionStorage.getItem(FundKey) || "");
      if (
        !data.selectToken ||
        !data.selectToken.address ||
        !data.equityToken ||
        !data.equityToken.symbol ||
        !data.equityToken.name
      ) {
        return false;
      }
      if (
        data.softCap <= 0 ||
        data.hardCap <= 0 ||
        data.softCap > data.hardCap
      ) {
        return false;
      }
      // if (data.raiseTime - Date.now() < 600000) {
      //   return false;
      // }
      // check white list ?
      if (data.addressInput) {
        for (const a of data.addressInput.split("\n")) {
          if (!ethers.utils.isAddress(a.trim())) {
            return false;
          }
        }
      }
      return true;
    } catch (error) {
      console.warn("get local failed", error);
      return false;
    }
  }, []);

  useEffect(async () => {
    // check available
    const newNavs = [...navs];
    if (chainId) {
      newNavs[0].available = await checkBasic();
    }
    newNavs[1].available = checkAddManager();
    newNavs[2].available = checkFund();
    setNavs(newNavs);
  }, [step, chainId]);

  return (
    <CreatePageStyled className="container">
      <SideNavLayout
        step={step}
        navs={navs}
        onChangeNav={onChangeStep}
        containerStyle={containerStyle}
      >
        {showStep(step)}
      </SideNavLayout>
    </CreatePageStyled>
  );
});

export default CreatePage;

const CreatePageStyled = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px !important;
`;
