import { memo, useCallback, useState, useEffect } from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import { useWeb3React } from "@web3-react/core";
import { useLazyQuery } from "@apollo/client";

import General from "components/modal/general";
import { LoadingBox } from "components/LoadingModal";

import { SUPPORTED_WALLETS } from "./wallet";
import { getConnectorForWallet } from "./connector";
import { useAppContext } from "components/provider/appProvider";
import { LOCALSTORAGE } from "utils/constant";
import { signMessage } from "utils/sign";
import { LoginQuery } from "api/graphql/auth";
import { checkTokenValid } from "utils/auth";

const WALLET_VIEWS = {
  OPTIONS: "options",
  ACCOUNT: "account",
  PENDING: "pending",
};

const WalletModal = memo((props) => {
  const { t } = useTranslation();
  const { show, closeModal } = props;
  const { chainId, connector, account, provider } = useWeb3React();

  const { dispatch } = useAppContext();

  const [walletView, setWalletView] = useState(WALLET_VIEWS.options);
  const [pendingConnector, setPendingConnector] = useState();

  const [loginFlag, setLoginFlag] = useState(false)

  useEffect(() => {
    loginFlag && closeModal();
  }, [loginFlag])

  useEffect(async () => {
    console.log(chainId , account , walletView)
    if (pendingConnector && chainId && account && walletView === WALLET_VIEWS.PENDING) {
      if (!checkTokenValid()) {
        try {
          await handleLogin();
          setPendingConnector(undefined);
          setWalletView(WALLET_VIEWS.OPTIONS);
          // close modal
          // closeModal();
        } catch (error) {
          console.error("handleLogin failed", error);
          setWalletView(WALLET_VIEWS.OPTIONS);
        }
      } else {
        setLoginFlag(true);
        setPendingConnector(undefined);
        setWalletView(WALLET_VIEWS.OPTIONS);
        // close modal
        // closeModal();
      }
    }
  }, [pendingConnector, account, chainId]);

  const [doLogin, { loading }] = useLazyQuery(LoginQuery, {
    onCompleted(data) {
      const loginData = data.login;
      console.log("====loginData", loginData);
      localStorage.setItem(LOCALSTORAGE.USER_TOKEN, loginData.token);
      localStorage.setItem(
        LOCALSTORAGE.TOKEN_EXPIRE,
        String(Date.now() + loginData.tokenExp * 1000)
      );
      console.log("====TOKEN_KEY", localStorage.getItem(LOCALSTORAGE.USER_TOKEN));
      console.log(
        "====TOKEN_EXPIRE",
        localStorage.getItem(LOCALSTORAGE.TOKEN_EXPIRE)
      );
      setLoginFlag(true);
    }
  });

  const handleLogin = async () => {
    if (!account || !chainId) return;
    const now = Date.now();

    const signData = await provider.send("personal_sign", [
      signMessage(chainId, account, now),
      account,
    ]);

    const resp = await doLogin({
      variables: {
        sign: signData,
        account,
        timestamp: now,
        chainId
      },
    });
    console.log("~~~~~~~~~", resp);
  };

  const tryActivation = useCallback(async (wallet) => {
    const connector = getConnectorForWallet(wallet);
    try {
      setPendingConnector(connector);
      setWalletView(WALLET_VIEWS.PENDING);

      await connector.activate();

      dispatch({ type: "CHANGE_WALLET", payload: wallet });
      localStorage.setItem(LOCALSTORAGE.WALLET, wallet);
    } catch (error) {
      console.error(`web3-react connection error: ${error}`);
    }
  }, []);

  const getOptions = useCallback(() => {
    if (!(window.web3 || window.ethereum)) {
      return (
        <WalletModalOption
          id="install"
          header={t("Wallet.Install")}
          link={"https://metamask.io/"}
          icon={SUPPORTED_WALLETS.find((w) => w.name === "MetaMask").iconURL}
        />
      );
    }
    return Object.keys(SUPPORTED_WALLETS).map((key, index) => {
      const option = SUPPORTED_WALLETS[key];
      const optionProps = {
        id: `connect-${key}`,
        link: option.href,
        header: option.name,
        color: option.color,
        key,
        icon: option.iconURL,
        onClick() {
          tryActivation(option.wallet);
        },
      };
      return <WalletModalOption {...optionProps} />;
    });
  }, [connector]);

  const getModalContent = useCallback(() => {
    if (walletView === WALLET_VIEWS.PENDING) {
      return (
        <PendingView>
          <LoadingBox />
          <SubHeader>{t("Wallet.Connecting")}</SubHeader>
        </PendingView>
      );
    } else {
      return getOptions();
    }
  }, [walletView]);

  return (
    <General show={show} width={423} height={366} closeModal={closeModal}>
      <ModalTitle className="font-medium">{t("Wallet.Connect")}</ModalTitle>
      {getModalContent()}
    </General>
  );
});

export default WalletModal;

const WalletModalOption = memo((props) => {
  const { clickable, size, onClick, color, header, icon, active, id } = props;
  return (
    <OptionCard id={id} onClick={onClick} active={active}>
      <OptionCardLeft>
        <HeaderText color={color}>{header}</HeaderText>
      </OptionCardLeft>
      <IconWrapper size={size}>
        <img src={icon} alt={"Icon"} />
      </IconWrapper>
    </OptionCard>
  );
});

const ModalTitle = styled.div`
  line-height: 22px;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 27px;
  font-size: 16px;
`;

const OptionCard = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 14px;
  padding: 0 20px;

  height: 62px;
  line-height: 64px;
  background: #ffffff;
  box-shadow: 0px 0px 2px 0px rgba(16, 22, 75, 0.1);
  border-radius: 14px;
  border: 1px solid #f9f9f9;
  cursor: pointer;
  &:hover {
    border: 1px solid rgba(213, 17, 114, 0.4);
  }
`;

const OptionCardLeft = styled.div`
  justify-content: center;
  height: 100%;
`;

const HeaderText = styled.div`
  font-size: 18px;
  color: #0e164e;
`;

const SubHeader = styled.div`
  margin-top: 10px;
  text-align: center;
`;

const IconWrapper = styled.div`
  align-items: center;
  justify-content: center;
  span {
    height: ${({ size }) => (size ? size + "px" : "24px")};
    width: ${({ size }) => (size ? size + "px" : "24px")};
  }
  img {
    height: 40px;
  }
`;

const PendingView = styled.div``;
