import { useState, useEffect, useCallback } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

import AddManager from "./addManager";
import ManagerTable from "./managerTable";
import BackNav from "components/common/backNav";

import Button from "components/common/button";
import NewVote from "components/modal/newVote";

import { useLazyQuery } from "@apollo/client";
import { getSuperList, SubgraphApiQuery } from "api/graphql/subQuery";
import { useWeb3React } from "@web3-react/core";
import { useDaoContext } from "views/dao/provider";
import api from "api";
import moment from "moment";
import { ethers } from "ethers";
import PaginationBox from "components/common/pagination";

export default function SuperManager() {
  const { t } = useTranslation();

  const { chainId, provider } = useWeb3React();
  const {
    state: { daoId },
  } = useDaoContext();
  const [list, setList] = useState([]);
  const [showModal, setShowModal] = useState(false);

  const [page, setPage] = useState(0);
  const [pageSize, setPagesize] = useState(10);
  const [total, setToal] = useState(0);

  useEffect(() => {
    const vheight = document.getElementById("contentBox").offsetHeight;
    const oHeight =
      document.body.clientHeight || document.documentElement.clientHeight;
    if  (oHeight > 900 && vheight >350) {
      document.getElementById("inner").style.height = `${vheight}px`;
    } else {
      document.getElementById("inner").style.height = `550px`;
    }
  }, []);

  const CloseModal = useCallback(() => {
    setShowModal(false);
  }, []);

  const handleModal = useCallback(() => {
    setShowModal(true);
  }, []);

  const handleResult = async (tokenAddress) => {
    try {
      const contract = await api.erc20.getContract(provider, tokenAddress);
      const symbol = await contract.symbol();
      const deci = await contract.decimals();
      return {
        symbol,
        deci,
        tokenAddress,
      };
    } catch (error) {
      return {};
    }
  };

  const [queryList] = useLazyQuery(SubgraphApiQuery, {
    onCompleted: async (data) => {
      const res = JSON.parse(data.subgraphApi);
      console.log("====data", res);

      try {
        const lst = res.data.spendLimitRecords || [];
        const reqs = lst.map((l) => handleResult(l.tokenIn));
        const result = await Promise.all(reqs);
        const pureList = lst.map((r, i) => {
          return {
            address: r.address,
            expireAt: moment(Number(r.deadline) * 1000).format(
              "MMMM Do YYYY, h:mm A"
            ),
            all: ethers.utils.formatUnits(r.limit, result[i].deci || 18),
            spent: ethers.utils.formatUnits(r.spend, result[i].deci || 18),
            symbol: result[i].symbol,
          };
        });
        setList(pureList);
      } catch (error) {
        console.error(error);
      }
    },
    onError(error) {
      console.error("====error", error);
    },
  });

  useEffect(() => {
    if (!chainId || !daoId) {
      return;
    }
    queryList({
      variables: {
        query: getSuperList(daoId, page, pageSize),
        chainId,
      },
    });
  }, [chainId, daoId, page, pageSize]);

  const onChangePage = useCallback((v) => {
    setPage(v);
  }, []);

  const onShowSizeChange = useCallback((c, size) => {
    setPagesize(size);
  }, []);

  return (
    <Box>
      {showModal && (
        <NewVote
          selectedAction={true}
          selectedType={3}
          CloseModal={CloseModal}
        />
      )}
      <BackNav to="app_settings" />
      <ContentBox id="contentBox">
        <div id="inner">
          {list.length === 0 ? (
            <AddManager />
          ) : (
            <>
              <ManagerTable list={list} />
              <Pagination>
                <PaginationBox
                  showQuickJumper
                  pageSize={pageSize}
                  defaultCurrent={page}
                  total={total}
                  onChange={onChangePage}
                  onShowSizeChange={onShowSizeChange}
                />
              </Pagination>
            </>
          )}

          <BtnBox>
            <Button primary width="auto" onClick={handleModal}>
              {t("Super.New")}
            </Button>
          </BtnBox>
        </div>
      </ContentBox>
    </Box>
  );
}

const Box = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 40px 20px;
`;

const ContentBox = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
`;

const BtnBox = styled.div`
  height: 80px;
  background: #fff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
`;

const Pagination = styled.div`
  text-align: right;
  margin-top: 10px;
`;
