import React, { memo, useState, useEffect, useCallback, useMemo } from "react";
import { ReactSVG } from "react-svg";
import { Input } from "antd";
import CopyStr from "../copy";

import LogoSVG from "img/logoSubdao.svg";
import styled from "styled-components";
import { Modal, Button } from "antd";
import MsgModal from "../MsgModal";
import LoadingModal from "../LoadingModal";
import { useLocation, useNavigate } from "react-router-dom";
import publicJs from "utils/publicJs";

import { useTranslation } from "react-i18next";
import DropImg from "img/header/downdrop.png";
import SearchImg from "img/search.png";

import WalletModal from "../wallet/modal";
import { useWeb3React } from "@web3-react/core";
import NetworkMenu from "./networkMenu";
import MoreBox from "./moreBox";
import Avatar from "components/common/avatar";
import { useAppContext } from "components/provider/appProvider";
import {LOCALSTORAGE} from "../../utils/constant";

export default function Headertop() {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const { account, chainId } = useWeb3React();

  const { state, dispatch } = useAppContext();
  const { msgType, loadingType } = state;

  const [showMsg, setshowMsg] = useState(false);
  const [loading, setloading] = useState(false);

  const [showButton, setShowButton] = useState(false);

  const { pathname } = useLocation();

  useEffect(() => {
    let selectedStorage;
    if (account == null) {
      selectedStorage = sessionStorage.getItem("account");
    } else {
      selectedStorage = account;
    }
  }, [account]);
  useEffect(()=>{
    const initProvider = async() =>{
        window.ethereum.on('chainChanged', (chainId) => {
          window.location.reload()
        });
        window.ethereum.on('accountsChanged', function (arr) {
          sessionStorage.removeItem('account');
            dispatch({ type: "CHANGE_WALLET", payload: undefined });
            localStorage.removeItem(LOCALSTORAGE.WALLET);
            localStorage.removeItem(LOCALSTORAGE.USER_TOKEN);
            localStorage.removeItem(LOCALSTORAGE.TOKEN_EXPIRE);
          navigate("/");
          window.location.reload()
        });
    }

    initProvider();
  },[])

  const toFirst = () => {
    navigate("/");
  };

  //
  useEffect(() => {
    setshowMsg(msgType != null);
  }, [msgType]);
  //
  useEffect(() => {
    setloading(loadingType != null);
  }, [loadingType]);

  const toExplore = () => {
    navigate("/explore");
  };

  const [showWallet, setShowWallet] = useState(false);

  const showWalletContent = useMemo(() => {
    return showWallet ? (
      <WalletModal
        show={showWallet}
        closeModal={() => {
          setShowWallet(false);
        }}
      />
    ) : (
      <></>
    );
  }, [showWallet]);

  const go2Search = useCallback(
    ({ target: { value } }) => {
      if (!value) {
        return;
      }
      navigate(`/explore?search=${value}`);
    },
    [navigate]
  );

  return (
    <HeaderBg className={pathname === "/" ? "white" : "gray"}>
      {showWalletContent}
      <div className="row">
        <div className="lftTit">
          <div className="toFirst" onClick={() => toFirst()}>
            <ReactSVG className="img" src={LogoSVG} />
          </div>
        </div>
        <div className="rhtBtn">
          <div className="header-button">
            <Modal
              visible={showButton}
              onCancel={() => {
                setShowButton(false);
              }}
              footer={null}
            >
              <i className="fa fa-user-times homeTop" />
              <h4>{t("Header.connect")}</h4>
            </Modal>
            <Explore onClick={() => toExplore()}>Explore</Explore>
            {/*{!!account && (*/}
            {/*  <BoxBrder>*/}
            {/*    <SearchInput*/}
            {/*      bordered={false}*/}
            {/*      placeholder={t("Header.Search")}*/}
            {/*      onPressEnter={go2Search}*/}
            {/*      prefix={<img src={SearchImg} alt="" className="search" />}*/}
            {/*    />*/}
            {/*  </BoxBrder>*/}
            {/*)}*/}
            <BoxBrder>
              <NetworkMenu />
              <img src={DropImg} alt="" className="img" />
            </BoxBrder>

            {account ? (
              <BoxBrder>
                <Avatar address={account} />
                <div className="addressBrdr">
                  <span>{publicJs.AddresstoShow(account)}</span>
                  <CopyStr address={account} />
                </div>
              </BoxBrder>
            ) : (
              <Button className="signin" onClick={() => setShowWallet(true)}>
                {t("Wallet.ConnectWallet")}
              </Button>
            )}
            <MoreBox account={account} />
          </div>
        </div>
      </div>
      <MsgModal
        showMsg={showMsg}
        handleClose={() => {
          setshowMsg(false);
          dispatch({ type: "MSGTYPE", payload: null });
        }}
      />
      <LoadingModal
        showMsg={loading}
        handleClose={() => {
          setloading(false);
          dispatch({ type: "LOADINGTYPE", payload: null });
        }}
      />
    </HeaderBg>
  );
}

const SearchInput = styled(Input)`
  height: 54px;
  width: 196px;

  input {
    font-size: 18px;
  }
`;

