import React, { useCallback, useState, useEffect } from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";

import { useDaoContext } from "views/dao/provider";
import { useOrgContract, useDaoOrgContract } from "hooks/useContract";
import { useAppContext } from "components/provider/appProvider";
import useSigner from "hooks/useSigner";

import { ORG_MANAGER, DAO_ORG, VOTE_MANAGER } from "utils/constant";
import publicJs from "utils/publicJs";
import { checkBonus } from "utils/check";

import api from "../../../api";

import Button from "components/common/button";
import DropSvg from "components/svg/common/select";
import VoteLoadingModal, { VoteTxStatus } from "./loading";
import DateBox from "components/common/date";
import VentureVoteConfig from "./voteConfig";
import { getVoteIdByEvent } from "utils/utils";


export default function RemoveManager(props) {
  const { handleClose } = props;

  const { t } = useTranslation();
  const { state: daoState } = useDaoContext();
  const { componentAddressMap } = daoState;
  const { dispatch } = useAppContext();

  const orgContract = useOrgContract(componentAddressMap.get(ORG_MANAGER));
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const [gplist, setGplist] = useState([]);
  const [removeGpAddress, setRemoveGpAddress] = useState();
  const [showSelect, setShowSelect] = useState(false);
  const [endTime, setEndTime] = useState();

  const [status, setStatus] = useState(VoteTxStatus.Default);
  const [txHash, setTxHash] = useState();
  const [minSupportNumber, setMinSupportNumber] = useState();
  const [newVoteId, setVoteId] = useState()

  useEffect(() => {
    document.addEventListener("click", () => {
      setShowSelect(false);
    });
    return () => {
      document.removeEventListener("click", () => {
        setShowSelect(false);
      });
    };
  }, []);

  const getGpList = useCallback(async () => {
    const { lst } = await api.org.getDaoMembersList(orgContract, 0, 1000);
    console.log(lst);
    setGplist(lst);
  }, [orgContract]);

  useEffect(() => {
    if (orgContract) {
      getGpList();
    }
  }, [orgContract]);

  const handleChangeBonus = useCallback(
    (i, v) => {
      const newArray = [...gplist];
      newArray[i].bonus = Number(v);
      setGplist(newArray);
    },
    [gplist]
  );

  const choose = (v, e) => {
    e.stopPropagation();
    setShowSelect(false);
    setRemoveGpAddress(v);
  };
  const handleSelected = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    setShowSelect(true);
  };

  const checkValid = () => {
    if (!removeGpAddress) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid remove manager", closable: true },
      });
      return;
    }
    if (!checkBonus(gplist.filter((g) => g.address !== removeGpAddress))) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: t("Msg.BonusMatch"), closable: true },
      });
      return;
    }
    if (
      !minSupportNumber ||
      minSupportNumber <= 0 ||
      minSupportNumber > gplist.length + 1
    ) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid min support number", closable: true },
      });
      return;
    }

    return true;
  };

  const createVote = async () => {
    if (!checkValid()) {
      return;
    }
   
    let duration = Math.round((endTime - Date.now()) / 1000);
    if (duration < 86400) {
      duration = 86400;
    }

    let txHash;
    try {
      setStatus(VoteTxStatus.Loading);
      const lastGplist = gplist.filter((g) => g.address !== removeGpAddress);
      const res = await api.vote.newKickGPVote(daoOrg, signer, {
        gpAddress: removeGpAddress,
        vote_time: duration,
        minSurportRequire: minSupportNumber,
        gpList: lastGplist.map((g) => g.address),
        gpBonus: lastGplist.map((g) => g.bonus * 100),
      });
      txHash = res.hash;
      const r = await res.wait();
      const event = r.events.find(e => e.address === componentAddressMap.get(VOTE_MANAGER))
      const vid = getVoteIdByEvent(event);
      setVoteId(vid);

      setStatus(VoteTxStatus.Success);
    } catch (error) {
      setStatus(VoteTxStatus.Failed);
      setTxHash(txHash);
      console.error("newTransfrtPeriod failed", error);
    }
  };

  const closeLoading = useCallback(() => {
    setStatus(VoteTxStatus.Default);
    txHash && setTxHash();
  }, []);

  const closeAll = useCallback(() => {
    closeLoading();
    props.handleClose();
  }, [props.handleClose]);

  return (
    <AddBox>
      <VoteLoadingModal
        status={status}
        txHash={txHash}
        handleRetry={createVote}
        voteId={newVoteId}
        handleClose={closeLoading}
        handleCloseAll={closeAll}
      />
      <dl>
        <dt>{t("Vote.RemoveAddressOf")}</dt>
        <dd>
          <BorderBox onClick={(e) => handleSelected(e)}>
            <div className="selectBox">
              <span>{removeGpAddress}</span>
              <DropSvg />
            </div>
          </BorderBox>
          {showSelect && (
            <DropBox>
              {gplist.map((gp, index) => (
                <li key={index} onClick={(e) => choose(gp.address, e)}>
                  {gp.address}
                </li>
              ))}
            </DropBox>
          )}
        </dd>
      </dl>
      <dl>
        <dt>{t("Vote.ResetInterest")}</dt>
        <dd>
          <UlBox>
            {gplist.map((gp, index) =>
              removeGpAddress === gp.address ? (
                <React.Fragment key={index}></React.Fragment>
              ) : (
                <li key={index}>
                  <div className="lft">
                    <span>Address</span>
                    <div className="address">
                      {publicJs.AddresstoShow(gp.address)}
                    </div>
                  </div>
                  <div className="rht">
                    <span>Bonus</span>
                    <BonusInput>
                      <input
                        type="number"
                        value={gp.bonus}
                        onChange={(e) =>
                          handleChangeBonus(index, e.target.value)
                        }
                      />
                      <span>%</span>
                    </BonusInput>
                  </div>
                </li>
              )
            )}
          </UlBox>
        </dd>
      </dl>
      <VentureVoteConfig
        minSupportNumber={minSupportNumber}
        updateMinSupportNumber={setMinSupportNumber}
        gpLength={gplist.length - 1}
      />
      <DateBox
        label={t("Vote.Expiry")}
        targetTime={endTime}
        updateTargetTime={setEndTime}
      />
      <BtnGroup>
        <Button onClick={handleClose}>{t("Cancel")}</Button>
        <Button primary onClick={createVote}>
          {t("Submit")}
        </Button>
      </BtnGroup>
    </AddBox>
  );
}

const BonusInput = styled.div`
  flex: 1;
  margin-left: 30px;
  display: flex;
  align-items: center;
  input {
    text-align: right;
    flex: 1;
    margin-left: 0 !important;
    margin-right: 20px !important;
  }
`;

const AddBox = styled.div`
  dt {
    font-size: 14px;
    font-weight: 400;
    color: #a6a6b9;
    line-height: 19px;
    margin-bottom: 10px;
  }
  dd {
    position: relative;
  }
  dl {
    margin-bottom: 20px;
  }
`;

