import { memo, useState, useCallback, useEffect } from "react";
import styled from "styled-components";

import DropSvg from "components/svg/common/select";
import SelectSvg from "components/svg/shape/select";

const TokenSelect = memo((props) => {
  const { list, onChange, selected } = props;
  const [show, setShow] = useState(false);

  useEffect(() => {
    document.addEventListener("click", () => {
      setShow(false);
    });
  }, []);

  const showList = useCallback(
    (e) => {
      e.nativeEvent.stopImmediatePropagation();
      setShow(!show);
    },
    [show]
  );

  const choose = useCallback(
    (item, e) => {
      e.stopPropagation();
      setShow(false);
      if (item.address !== selected?.address) {
        onChange(item);
      }
    },
    [selected]
  );

  return (
    <ReceiveBox onClick={showList}>
      <div className="box">
        <img src={selected?.logo} alt="" className="lft" />
        <div>
          <div className="symbol">{selected?.symbol}</div>
        </div>
      </div>
      <DropSvg />
      {show && (
        <DropBox>
          {list.map((item, index) => (
            <li onClick={(e) => choose(item, e)} key={`rc_${index}`}>
              <div>
                <img src={item.logo} alt="" className="lft" />
                <div className="name">{item.symbol}</div>
                <div className="symbol font-medium">{item.symbol}</div>
              </div>
              {selected && selected.address === item.address && (
                <SelectSvg className="select" />
              )}
            </li>
          ))}
        </DropBox>
      )}
    </ReceiveBox>
  );
});

export default TokenSelect;

const ReceiveBox = styled.div`
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  position: relative;
  .box {
    display: flex;
    align-items: center;
  }
  .lft {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    margin-right: 10px;
  }
  .name {
    font-weight: 500;
    color: #0e164e;
    line-height: 24px;
    margin-right: 10px;
  }
  .symbol {
    font-weight: 500;
    line-height: 24px;
    font-weight: 500;
    color: #0e164e;
  }
`;
const DropBox = styled.ul`
  position: absolute;
  left: 0;
  top: 55px;
  z-index: 9999;
  padding-top: 12px;
  padding-bottom: 6px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  width: 100%;
  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    & > div {
      display: flex;
      align-items: center;
    }
    .select {
      margin-right: 13px;
    }
    cursor: pointer;
    &:hover {
      background: #fbfbfa;
    }
  }
`;
