import React, { useMemo, useEffect, useState, useCallback } from "react";

import { QueryDaoByHandle } from "api/graphql/dao";
import { useLazyQuery } from "@apollo/client";
import { NetworkList } from "network";

const useDaoDetailByHandle = (full_handle) => {
  const [id, setDaoId] = useState();
  const [dao, setDao] = useState();

  const { handle, chainId } = useMemo(() => {
    const arr = full_handle.split(":");
    const network = NetworkList.find((n) => n.simple === arr[0]);
    return { handle: arr[1], chainId: network && network.chainId };
  }, [full_handle]);

  const [queryDaoDetail, { loading }] = useLazyQuery(QueryDaoByHandle, {
    onCompleted: (data) => {
      console.log("====", data);
      try {
        setDao({
          ...data.dao,
          is_like: data.is_like
        })
        setDaoId(data.dao.dao_id)
      } catch (error) {
        console.error("parse daoByHandleAndChainId failed", error)
      }
    },
    onError: (error) => {
      console.error("====", error);
    },
  });

  useEffect(async () => {
    // rquest
    if (!chainId) {
      return;
    }
    queryDaoDetail({
      variables: {
        chainId,
        handle
      }
    });
  }, [handle, chainId]);

  return { id, handle, dao };
};

export default useDaoDetailByHandle;
