import { useEffect, useState, useMemo } from "react";
import General from "./general";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import DropSvg from "../svg/common/select";
import Transfer from "./vote/transfer";
import SuperManager from "./vote/super";
import Fee from "./vote/fee";
import AddManager from "./vote/add";
import Redemption from "./vote/redemption";
import RemoveManager from "./vote/remove";

export default function NewVote(props) {
  const { CloseModal, selectedAction, selectedType } = props;
  const { t } = useTranslation();

  const [voteTypes, setVoteTypes] = useState([
    t("Vote.AddManager"),
    t("Vote.RemoveManager"),
    t("Vote.Transfer"),
    t("Vote.SuperManager"),
    t("Vote.ManagementFeePool"),
    t("Vote.ChangePeriod"),
  ]);
  const [show, setShow] = useState(false);
  const [currentType, setCurrentType] = useState(0);
  const [boxHeight, setBoxHeight] = useState(undefined);

  useEffect(() => {
    document.addEventListener("click", () => {
      setShow(false);
    });
  }, []);

  useEffect(() => {
    if (!selectedType) return;
    if (selectedType === "addRemove") {
      setVoteTypes([t("Vote.AddManager"), t("Vote.RemoveManager")]);
      setCurrentType(0);
    } else {
      setCurrentType(selectedType);
    }
  }, [selectedType]);

  const handleSelected = (e) => {
    if (selectedAction) return;
    e.nativeEvent.stopImmediatePropagation();
    setShow(true);
  };
  const choose = (num, e) => {
    e.stopPropagation();
    setShow(false);
    setCurrentType(num);
  };
  useEffect(() => {
    switch (currentType) {
      case 4:
      case 0:
      case 1:
          setBoxHeight(550)
          break;
      case 5:
        setBoxHeight(450);
        break;
      default:
        setBoxHeight(undefined);
        break;
    }
  }, [currentType]);

  return (
    <General closeModal={CloseModal} width="566" height={boxHeight}>
      <UlBox>
        <dl>
          <dt>Vote Type</dt>
          <dd>
            <BorderBox onClick={(e) => handleSelected(e)}>
              <div className="selectBox">
                <span>{voteTypes[currentType]}</span>
                {!selectedAction && <DropSvg />}
              </div>
            </BorderBox>
            {show && (
              <DropBox>
                {voteTypes.map((item, index) => (
                  <li key={index} onClick={(e) => choose(index, e)}>
                    {item}
                  </li>
                ))}
              </DropBox>
            )}
          </dd>
        </dl>

        {currentType === 0 && <AddManager handleClose={CloseModal} />}
        {currentType === 1 && <RemoveManager handleClose={CloseModal} />}
        {currentType === 2 && <Transfer handleClose={CloseModal} />}
        {currentType === 3 && <SuperManager handleClose={CloseModal} />}
        {currentType === 4 && <Fee handleClose={CloseModal} />}
        {currentType === 5 && <Redemption handleClose={CloseModal} />}
      </UlBox>
    </General>
  );
}

const UlBox = styled.div`
  padding: 30px 20px 0;
  dt {
    font-size: 14px;
    font-weight: 400;
    color: #a6a6b9;
    line-height: 19px;
    margin-bottom: 10px;
  }
  dd {
    position: relative;
  }
  dl {
    margin-bottom: 20px;
  }
`;

const BorderBox = styled.div`
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  padding: 0 20px;
  min-height: 44px;
  display: flex;
  align-items: center;
  .inputTxt {
    line-height: 40px;
    width: 100%;
  }
  .selectBox {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
`;

const DropBox = styled.ul`
  position: absolute;
  left: 0;
  top: 55px;
  z-index: 9999;
  padding: 10px 0;
  background: #ffffff;
  border-radius: 8px 8px 14px 14px;
  border: 1px solid #edeef3;
  width: 100%;
  li {
    padding: 6px 23px;
    cursor: pointer;
    &:hover {
      background: #fbfbfa;
    }
  }
`;
