import React, { memo, useEffect, useCallback, useState, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { ethers } from "ethers";

import LogoSVG from "../../img/logoSubdao.svg";
import LinkImg from "../../img/newHome/link.png";
import Twitter from "../../img/newHome/twitter.png";
import Telegram from "../../img/newHome/telegram.png";
import Discord from "../../img/newHome/discord.png";
import moment from "moment";
import Button from "components/common/button";
import { TokenAvatar } from "components/common/avatar";

import CreatingModal from "./creatingModal";
import { useAppContext } from "components/provider/appProvider";
import { useWeb3React } from "@web3-react/core";
import { useDaoFactoryContract } from "hooks/useContract";
import useSigner from "hooks/useSigner";
import { useMutation } from "@apollo/client";
import { CreateDao } from "api/graphql/dao";
import { useNetworkData } from "hooks/useNetwork";
import goMerklizedSetup, { createAndUpload } from "utils/whitelist.js";
import useGateway from "hooks/useGateway";

const Managers = memo((props) => {
  const { addressList } = props;
  return (
    <ManagerStyled>
      <ManagerTitle>Managers</ManagerTitle>
      <ul className="font-medium">
        {addressList.map((gp, i) => (
          <GPItem key={i}>
            <div className="block name-block">{gp.name}</div>
            <div className="block address-block">{gp.address}</div>
            <div className="block bonus-block">{gp.bonus}</div>
            <div className="bonus-symbol">%</div>
          </GPItem>
        ))}
      </ul>
    </ManagerStyled>
  );
});

const Preivew = memo(() => {
  const { t } = useTranslation();
  const navigator = useNavigate();
  const { account, chainId } = useWeb3React();
  const { dispatch } = useAppContext();
  const maincontract = useDaoFactoryContract();
  const signer = useSigner();
  const networkData = useNetworkData(chainId);

  const [baseData, setBaseData] = useState({});

  const [GpCarry, setGpCarry] = useState(0);
  const [manageFee, setManageFee] = useState(0);
  const [addressList, setAddressList] = useState([]);

  const [selectToken, setSelectToken] = useState({});
  const [equityToken, setEquityToken] = useState({});
  const [softCap, setSoftCap] = useState(0);
  const [hardCap, setHardCap] = useState(0);
  const [raiseTime, setRaiseTime] = useState(0);
  const [minSupportNumber, setMinSupportNumber] = useState(0);
  const [addressInput, setAddressInput] = useState("");
  const [tree, setTree] = useState({ root: ethers.constants.HashZero, recordsCode: "" });

  const [newDaoId, setNewDaoId] = useState(null);

  const [loadingStep, setLoadingStep] = useState();
  const [transHash, setTransHash] = useState();
  const [imgCode, setImgCode] = useState();

  const gateway = useGateway();
  const logo = useMemo(() => {
    if (imgCode && gateway) {
      return gateway + imgCode
    } else {
      return LogoSVG
    }
  }, [imgCode, gateway])

  useEffect(() => {
    try {
      const img = sessionStorage.getItem("CreateDao_basic_info_logo")
      if (img) setImgCode(img);
      const basicInfo = JSON.parse(
        sessionStorage.getItem("CreateDao_basic_info") || ""
      );
      setBaseData(basicInfo);
    } catch (error) { }
    try {
      const data = JSON.parse(
        sessionStorage.getItem("CreateDao_add_manager") || ""
      );
      if (data.GpCarry) {
        setGpCarry(data.GpCarry);
      }
      if (data.manageFee) {
        setManageFee(data.manageFee);
      }
      if (data.addressList) {
        setAddressList(data.addressList);
      }
      if (data.minSupportNumber) {
        setMinSupportNumber(data.minSupportNumber);
      }
    } catch (error) { }
    try {
      const data = JSON.parse(
        sessionStorage.getItem("CreateDao_fundraising") || ""
      );
      if (data.selectToken) {
        setSelectToken(data.selectToken);
      }
      if (data.equityToken) {
        setEquityToken(data.equityToken);
      }
      if (data.softCap) {
        setSoftCap(data.softCap);
      }
      if (data.hardCap) {
        setHardCap(data.hardCap);
      }
      if (data.raiseTime) {
        setRaiseTime(data.raiseTime);
      }
      if (data.addressInput) {
        setAddressInput(data.addressInput);
      }
    } catch (error) { }
  }, []);

  const closeDate = useMemo(() => {
    return raiseTime
      ? moment(raiseTime).format("dddd, MMMM Do YYYY, h:mm A")
      : "";
  }, [raiseTime]);

  const voteConfigDisplay = useMemo(() => {
    return `${minSupportNumber}/${addressList.length}`;
  }, [addressList, minSupportNumber]);

  const SocailUrls = useCallback(() => {
    if (!baseData) {
      return <></>;
    }
    return (
      <>
        {[
          { key: "twitter", icon: Twitter },
          { key: "telegram", icon: Telegram },
          { key: "discord", icon: Discord },
        ].map((item) => {
          return baseData[item.key] ? (
            <a href={baseData[item.key]} key={item.key}>
              <img src={item.icon} alt="" />
            </a>
          ) : (
            <React.Fragment key={item.key} />
          );
        })}
      </>
    );
  }, [baseData]);

  const checkRequire = useCallback((data_array) => {
    return data_array.some((data) => !data);
  }, []);

  const checkBonus = useCallback((list) => {
    const bonus_arr = list.map((a) => ethers.BigNumber.from(a.bonus || 0));
    const v = bonus_arr.reduce(function (
      accumulator,
      currentValue,
      currentIndex,
      array
    ) {
      return accumulator.add(currentValue);
    });
    return v.toNumber() === 100;
  }, []);

  const checkInvalidAddress = useCallback((addressList) => {
    for (const a of addressList) {
      if (!ethers.utils.isAddress(a.address)) return a.address;
    }
  }, []);

  const [BundDao] = useMutation(CreateDao, {
    onCompleted: (data) => {
      console.log("====", data);
      if (data && data.createDao) {
        setLoadingStep(3);
      }
    },
    onError: (error) => {
      console.error("BundDao failed", error);
    },
  });

  const handleWhitelist = useCallback(async (addressInput, managers) => {
    const addressList = addressInput.split("\n");
    if (!addressList.length) {
      return { root: ethers.constants.HashZero, recordsCode: "" }
    }
    const addressWithManagers = {}
    addressList.forEach(a => { addressWithManagers[a] = true })

    // Only add manager to whitelist if they want enable whitelist, otherwise should not add them.
    if (Object.keys(addressWithManagers).length > 0) {
      managers.forEach(a => { addressWithManagers[a.address] = true })
    }

    try {
      // upload
      console.log("whitelist:", Object.keys(addressWithManagers))
      const { root, records } = goMerklizedSetup(Object.keys(addressWithManagers));
      const recordsCode = await createAndUpload(records);
      console.log("[createAndUpload] recordsCode: ", recordsCode);
      return { root, recordsCode };
    } catch (error) {
      console.error("handleWhiteList failed: ", error);
      const msg =
        (error.data && error.data.message) || "create whitelist failed";
      dispatch({ type: "MSGTYPE", payload: { msg: msg, closable: true } });
    }
  }, []);

  const handleSubmit = async () => {
    // check data
    if (
      checkRequire([
        baseData.description,
        baseData.name,
        baseData.daohandle,
        raiseTime,
        addressList.length,
        GpCarry,
        manageFee,
        minSupportNumber,
        softCap,
        hardCap,
        selectToken.address,
        equityToken.name,
        equityToken.symbol,
      ])
    ) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: t("Msg.FillTip"), closable: true },
      });
      return;
    }
    if (softCap > hardCap) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: t("Msg.CapInvalid"), closable: true },
      });
      return;
    }
    // check whitelist
    if (addressInput) {
      for (const a of addressInput.split("\n")) {
        if (!ethers.utils.isAddress(a.trim())) {
          return false;
        }
      }
    }

    if (raiseTime - Date.now() < 60) {
      // less than 10min
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: "invalid time",
          closable: true,
        },
      });
      return;
    }

    if (minSupportNumber <= 0 || minSupportNumber > addressList.length) {
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: t("Msg.VoteConfigError"),
          closable: true,
        },
      });
    }

    const invalidAddress = checkInvalidAddress(addressList);
    if (invalidAddress) {
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: t("Msg.GpAddressInvalid", { address: invalidAddress }),
          closable: true,
        },
      });
      return;
    }
    if (!checkBonus(addressList)) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: t("Msg.BonusMatch"), closable: true },
      });
      return;
    }
    if (addressInput) {
      const tree = await handleWhitelist(addressInput, addressList);
      console.log("tree: ", tree);
      if (!tree) {
        return;
      }
      setTree(tree);
    }


    // console.log(">>>>", {
    //   baseData,
    //   raiseTime,
    //   addressList,
    //   GpCarry,
    //   manageFee,
    //   softCap,
    //   hardCap,
    //   selectToken,
    //   equityToken,
    //   minSupportNumber,
    // });

    const initDao = async () => {
      setLoadingStep(1);
      try {
        console.log(
          `// minSupportNumber: ${minSupportNumber}, reject: ${addressList.length - minSupportNumber + 1
          }`
        );
        const res = await maincontract.connect(signer).instanceByTemplate(
          "VentureTemplate",
          ["VaultManager", "ManageFeeVaultManager", "VenturesStockManager"],
          [
            // VaultManager
            ethers.utils.defaultAbiCoder.encode(
              ["address", "uint256", "uint256"],
              [
                selectToken.address,
                minSupportNumber,
                addressList.length - minSupportNumber + 1,
              ]
            ),
            // ManageFeeVaultManager
            ethers.utils.defaultAbiCoder.encode(
              ["address"],
              [selectToken.address]
            ),
            // VenturesStockManager
            ethers.utils.defaultAbiCoder.encode(
              ["string", "string"],
              [equityToken.name, equityToken.symbol]
            ),
          ]
        );
        const r = await res.wait();
        const event = r.events.filter((e) => e.event === "InstanceDAO")[0];

        const newDaoId = event.args[0].toString();

        setNewDaoId(newDaoId);
        console.log("---> newDaoId: ", newDaoId);
      } catch (error) {
        console.error("init dao failed", error);
        dispatch({ type: "LOADINGTYPE", payload: null });
        setLoadingStep(null);
      }
    };
    initDao();
  };

  useEffect(async () => {
    if (!newDaoId) return;
    setLoadingStep(2);

    const getVenturesManagerParams = () => {
      const p1 = [
        Math.ceil(raiseTime / 1000),
        ethers.BigNumber.from(GpCarry * 100),
        ethers.BigNumber.from(manageFee * 100),
        ethers.utils.parseUnits(String(softCap), selectToken.deci),
        ethers.utils.parseUnits(String(hardCap), selectToken.deci),
        selectToken.address,
        account,
      ];
      const p2 = [
        addressList.map((a) => a.address),
        addressList.map((a) => ethers.BigNumber.from(a.bonus * 100)),
      ];
      const p3 = [tree.root || ethers.constants.HashZero, tree.recordsCode];
      return ethers.utils.defaultAbiCoder.encode(
        ["bytes", "bytes", "bytes"],
        [
          ethers.utils.defaultAbiCoder.encode(
            [
              "uint256",
              "uint256",
              "uint256",
              "uint256",
              "uint256",
              "address",
              "address",
            ],
            p1
          ),
          ethers.utils.defaultAbiCoder.encode(["address[]", "uint256[]"], p2),
          ethers.utils.defaultAbiCoder.encode(["bytes32", "string"], p3),
        ]
      );
    };

    const getOrgManagerParams = () => {
      return ethers.utils.defaultAbiCoder.encode(
        ["uint256", "string[]", "address[]"],
        [
          2,
          addressList.map((m) => m.name || ""),
          addressList.map((a) => a.address),
        ]
      );
    };
    const getVoteManagerParams = () => {
      return ethers.utils.defaultAbiCoder.encode(
        ["uint256"],
        [minSupportNumber]
      );
    };
    const getGrantMethodManagerParams = () => {
      return "0x";
    };
    const getVoteExecutionManagerParams = () => {
      return "0x";
    };
    const getDao = async () => {
      try {
        try {
          const res = await maincontract
            .connect(signer)
            .addComponents(
              newDaoId,
              [
                "VenturesManager",
                "OrgManager",
                "VoteManager",
                "GrantMethodManager",
                "VoteExecutionManager",
              ],
              [
                getVenturesManagerParams(),
                getOrgManagerParams(),
                getVoteManagerParams(),
                getGrantMethodManagerParams(),
                getVoteExecutionManagerParams(),
              ]
            );
          setTransHash(res.hash);
          await res.wait();

          BundDao({
            variables: {
              chainId: chainId,
              daoId: newDaoId,
              logo: imgCode,
              desc: baseData.description,
              handle: baseData.daohandle,
              name: baseData.name,
            },
          });
        } catch (error) {
          console.error(t("venture.manager"), error);
          dispatch({ type: "LOADINGTYPE", payload: null });
          setLoadingStep(null);
          dispatch({
            type: "MSGTYPE",
            payload: { msg: error?.error?.message, closable: true },
          });
        } finally {
          dispatch({ type: "LOADINGTYPE", payload: null });
        }
      } catch (error) {
        console.error("instanceMap failed", error);
        setLoadingStep(null);
        dispatch({ type: "LOADINGTYPE", payload: null });
      }
    };

    getDao();
  }, [newDaoId]);

  useEffect(() => {
    if (loadingStep !== 3) return;
    sessionStorage.removeItem("CreateDao_basic_info");
    sessionStorage.removeItem("CreateDao_basic_info_logo");
    sessionStorage.removeItem("CreateDao_add_manager");
    sessionStorage.removeItem("CreateDao_fundraising");
  }, [loadingStep]);

  const closeModal = useCallback(() => {
    setLoadingStep(false);
    // jump2dao
    navigator(`/dao/${networkData.simple}:${baseData.daohandle}/homepage/info`);
  }, [baseData, networkData]);

  return (
    <>
      <CreatingModal
        show={!!loadingStep}
        closeModal={closeModal}
        step={loadingStep}
        transHash={transHash}
      />
      <TopBox>
        <img src={logo} alt="" className="logo" />
        <Rht>
          <div>
            <TitleLine>
              <TitleBox className="font-bold">{baseData.name}</TitleBox>
              <RatioBox className="font-bold">{voteConfigDisplay}</RatioBox>
              <ImgUl>
                <SocailUrls />
              </ImgUl>
            </TitleLine>

            <Description>
              {baseData.description && baseData.description.slice(0, 300)}
              <span className="more">
                <strong>...</strong>
                more
              </span>
            </Description>
          </div>
          <RhtBox>
            <PriceBox className="font-black">
              0 <span className="font-regular">USDT</span>
            </PriceBox>
          </RhtBox>
        </Rht>
      </TopBox>
      <BottomBox>
        <Period>
          <span className="title">{t("Create.RaiseTime")}</span>
          <span className="date font-bold">{closeDate}</span>
        </Period>
        <Fund>
          <FirstLine>
            <div className="lft">
              <Title>{t("Create.RaisedFunds")}</Title>
              <div className="font-bold progressNum font-black">0</div>
              <Symbol>
                <img src={selectToken.logo} alt="" />
                <span>{selectToken.symbol}</span>
              </Symbol>
            </div>
            <div className="rht">
              <Title className="progressTitle">{t("Create.CurProgress")}</Title>
              <div className="font-bold progressNum font-black">0%</div>
            </div>
          </FirstLine>
          <PercentBox width={0}>
            <div className="percent" />
          </PercentBox>
          <LastLine>
            <div>
              <Title>{t("Create.softcap")}</Title>
              <div className="num font-bold">{softCap}</div>
              <span className="symbol">{selectToken.symbol}</span>
            </div>
            <div>
              <Title>{t("Create.hardcap")}</Title>
              <div className="num font-bold">{hardCap}</div>
              <span className="symbol">{selectToken.symbol}</span>
            </div>
          </LastLine>
        </Fund>
        <TypeBox>
          <li>
            <div>
              <Title>{t("Create.FundType")}</Title>
            </div>
            <SymbolBox>
              <div className="lft">
                <img src={selectToken.logo} alt="" />
                <div className="symbol">{selectToken.symbol}</div>
              </div>
            </SymbolBox>
          </li>
          <li>
            <div className="top">
              <Title>{t("Create.TokenName")}</Title>
            </div>
            <SymbolBox>
              <div>
                <div className="lft">
                  <TokenAvatar size="44px" symbol={equityToken.symbol} />
                  <div className="symbol">{equityToken.symbol}</div>
                </div>
              </div>
            </SymbolBox>
          </li>
          <li>
            <div className="top">
              <Title>{t("Create.TotalCarried")}</Title>
            </div>
            <LineBox>
              <div className="first">
                <PercentNum>{GpCarry || 0}%</PercentNum>
              </div>
              <PercentBox width={GpCarry || 0}>
                <div className="progress" />
              </PercentBox>
            </LineBox>
          </li>
          <li>
            <div className="top">
              <Title>{t("Create.TotalManageFee")}</Title>
            </div>
            <LineBox>
              <div className="first">
                <PercentNum>{manageFee || 0}%</PercentNum>
              </div>
              <PercentBox width={manageFee || 0}>
                <div className="progress" />
              </PercentBox>
            </LineBox>
          </li>
        </TypeBox>
        <Managers addressList={addressList} />
        <Voteconfig>
          <span>Any votes require the confirmation of</span>
          <span className="value font-bold">{minSupportNumber}</span>
          <span>out of {addressList.length} manager(s)</span>
        </Voteconfig>
        <SubmitBtn>
          <Button primary onClick={handleSubmit}>
            {t("Submit")}
          </Button>
        </SubmitBtn>
      </BottomBox>
    </>
  );
});

export default Preivew;

