import React, { useState, useCallback, useEffect, useMemo } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { ethers } from "ethers";

import { useDaoContext } from "views/dao/provider";
import { useManageFeeContract, useDaoOrgContract } from "hooks/useContract";
import useManageFee from "hooks/useManageFee";
import useSigner from "hooks/useSigner";

import { VENTURE_MANAGEFEE_MANAGER, DAO_ORG, VOTE_MANAGER } from "utils/constant";
import TipsImg from "img/newHome/tips.png";
import VoteLoadingModal, { VoteTxStatus } from "./loading";
import api from "../../../api";
import CheckInput from "components/common/checkInput";
import DateBox from "components/common/date";
import { useAppContext } from "components/provider/appProvider";
import { getVoteIdByEvent } from "utils/utils";

export default function Fee(props) {
  const { handleClose } = props;
  const { t } = useTranslation();
  const { dispatch } = useAppContext();
  const { state: daoState } = useDaoContext();
  const { componentAddressMap } = daoState;

  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const manageFee = useManageFee(
    componentAddressMap.get(VENTURE_MANAGEFEE_MANAGER),
    { useBalance: true }
  );
  const signer = useSigner();

  const [status, setStatus] = useState(VoteTxStatus.Default);
  const [txHash, setTxHash] = useState();

  const [transferAddress, setTransferAddress] = useState();
  const [addressInvalidMsg, setAddressInvalidMsg] = useState();

  const [tokenAmount, setTokenAmount] = useState();
  const [endTime, setEndTime] = useState();
  const [newVoteId, setVoteId] = useState()

  const maxValue = useMemo(() => {
    if (manageFee) {
      return {
        value: manageFee.balance,
        display: manageFee.balanceDisplay,
        symbol: manageFee.symbol,
      };
    }
    return {
      value: 0,
      display: 0,
    };
  }, [manageFee]);

  const onChangeAddress = useCallback((e) => {
    const v = e.target.value;
    setTransferAddress(v);
    if (!ethers.utils.isAddress(v)) {
      setAddressInvalidMsg(t("Msg.InvalidAddress"));
    } else {
      setAddressInvalidMsg(null);
    }
  }, []);

  const handleTokenAmount = async (e) => {
    setTokenAmount(Number(e.target.value));
  };

  const setMax = useCallback(() => {
    setTokenAmount(Number(manageFee.balance));
  }, [manageFee]);

  const createVote = async () => {
    if (!transferAddress || !ethers.utils.isAddress(transferAddress)) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid address", closable: true },
      });
      return;
    }
    if (tokenAmount > Number(manageFee.balance) || tokenAmount <= 0) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid amount", closable: true },
      });
    }
    let duration = Math.round((endTime - Date.now()) / 1000);
    if (duration < 86400) {
      duration = 86400;
    }
    let txHash;
    try {
      setStatus(VoteTxStatus.Loading);
      const res = await api.vote.newTransferManageFeeVote(daoOrg, signer, {
        toAddress: transferAddress,
        tokenAddress: manageFee.address,
        tokenAmount: ethers.utils.parseUnits(
          String(tokenAmount),
          manageFee.deci
        ),
        vote_time: duration,
      });
      txHash = res.hash;

      const r = await res.wait();
      const event = r.events.find(e => e.address === componentAddressMap.get(VOTE_MANAGER))
      const vid = getVoteIdByEvent(event);
      setVoteId(vid);

      setStatus(VoteTxStatus.Success);
    } catch (error) {
      setStatus(VoteTxStatus.Failed);
      setTxHash(txHash);
      console.error("newTransfrtPeriod failed", error);
    }
  };

  const closeLoading = useCallback(() => {
    setStatus(VoteTxStatus.Default);
    txHash && setTxHash();
  }, []);

  const closeAll = useCallback(() => {
    closeLoading();
    props.handleClose();
  }, [props.handleClose]);

  return (
    <FeeBox>
      <VoteLoadingModal
        status={status}
        txHash={txHash}
        handleRetry={createVote}
        voteId={newVoteId}
        handleClose={closeLoading}
        handleCloseAll={closeAll}
      />
      <Tips>
        <img src={TipsImg} alt="" />
        <div className="tips">{t("Vote.ManageFeeTip")}</div>
      </Tips>
      <dl>
        <dt>{t("Vote.RecipientAddress")}</dt>
        <dd>
          <CheckInput
            autoFocus
            value={transferAddress}
            onChange={onChangeAddress}
            errorMsg={addressInvalidMsg}
          />
        </dd>
      </dl>
      <dl>
        <dt>{t("Vote.Amount")}</dt>
        <dd>
          <AmountBox>
            <input
              type="number"
              value={tokenAmount}
              onChange={handleTokenAmount}
              autoComplete="off"
            />
            <span className="balance">Balance : {maxValue.display}</span>
            <Max onClick={setMax}>{t("Max")}</Max>
            <span className="symbol">{maxValue.symbol}</span>
          </AmountBox>
        </dd>
      </dl>
      <DateBox label={t("Vote.Expiry")} targetTime={endTime} updateTargetTime={setEndTime} />
      <BtnGroup>
        <button onClick={handleClose}>{t("Cancel")}</button>
        <button onClick={createVote}>{t("Submit")}</button>
      </BtnGroup>
    </FeeBox>
  );
}

const Tips = styled.div`
  display: flex;
  align-items: flex-start;
  background: #f7f7f7;
  border-radius: 14px;
  padding: 11px;
  margin-bottom: 20px;
  .tips {
    font-size: 12px;
    font-weight: 400;
    color: #a6a6b9;
    line-height: 16px;
    margin-left: 5px;
  }
`;
const FeeBox = styled.div`
  dt {
    font-size: 14px;
    font-weight: 400;
    color: #a6a6b9;
    line-height: 19px;
    margin-bottom: 10px;
  }
  dd {
    position: relative;
  }
  dl {
    margin-bottom: 20px;
  }
`;

const BtnGroup = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  button {
    cursor: pointer;
    width: 140px;
    height: 50px;
    border-radius: 14px;
    border: 1px solid #d52473;
    &:first-child {
      background: #ffffff;
      color: #d52473;
      margin-right: 30px;
    }
    &:last-child {
      background: #d52473;
      color: #ffffff;
    }
  }
`;

const AmountBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  padding: 0 20px;
  min-height: 44px;
  input {
    flex-grow: 1;
    border: 0;
  }
  .symbol {
    font-size: 14px;
    font-weight: 400;
    color: #0e164e;
    line-height: 19px;
  }
  .balance {
    font-size: 12px;
    font-weight: 400;
    color: #94949a;
    line-height: 19px;
  }
`;

const Max = styled.div`
  text-align: center;
  width: 48px;
  background: #fae9f1;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 400;
  color: #d52473;
  line-height: 16px;
  margin: 0 20px;
  cursor: pointer;
`;
