import { useEffect, memo, useState } from "react";
import { ethers } from "ethers";
import moment from "moment";
import styled from "styled-components";
import { useGrantManagerContract } from "hooks/useContract";
import { useDaoContext } from "views/dao/provider";
import { GRANT_MANAGER } from "utils/constant";
import api from "api";
import CopyStr from "components/copy";
import LinkImg from "img/shareGray.png";
import { useWeb3React } from "@web3-react/core";
import useFundLogo from "hooks/useFundLogo";
import publicJs, { getShortDisplay } from "utils/publicJs";
import { useMemo } from "react";
import Avatar from "components/common/avatar";

import UniswapLogo from "img/voteNV/uniswap.png";
import AaveLogo from "img/voteNV/aave.png";

const SuperDapp = {
  GP_Buy: UniswapLogo,
  GP_Loan: AaveLogo,
};

const SuperVote = memo((props) => {
  const { vid, wid, orgMap, start_time } = props;
  const { provider } = useWeb3React();
  const {
    state: { componentAddressMap },
  } = useDaoContext();
  const fundLogo = useFundLogo();

  const grantContract = useGrantManagerContract(
    componentAddressMap.get(GRANT_MANAGER)
  );

  const [toAddress, setToAddress] = useState();

  const [amount, setAmount] = useState(0);
  const [symbol, setSymbol] = useState("");

  const [expire, setExpire] = useState();
  const [method, setMethod] = useState();

  const addressName = useMemo(() => {
    return orgMap && toAddress && orgMap[toAddress];
  }, [toAddress, orgMap]);

  const dapp = useMemo(() => {
    return SuperDapp[method];
  }, [method]);

  useEffect(async () => {
    try {
      const wdata = await grantContract.registerVotes(vid);
      setToAddress(wdata.gp);
      setExpire(
        moment((wdata.expire.toNumber() + start_time)*1000).format("MMMM Do YYYY, h:mm A")
      );
      setMethod(wdata.method);
      if (wdata.extra) {
        const result = ethers.utils.defaultAbiCoder.decode(
          ["address", "address", "uint256"],
          wdata.extra
        );

        try {
          const erc20 = await api.erc20.getContract(provider, result[0]);
          const deci = await erc20.decimals();
          setAmount(getShortDisplay(ethers.utils.formatUnits(result[2], deci)));
          setSymbol(await erc20.symbol());
        } catch (error) {
          console.error(error);
        }
      }
    } catch (error) {
      console.error("get wdata failed", error);
    }
  }, [wid, provider]);
  return (
    <>
      <dl>
        <dt>Address</dt>
        <dd className="bgBox">
          <Avatar address={toAddress} size="30px" />
          <Account className="account">
            {addressName && <div className="name">{addressName}</div>}
            <div className="address">
              {publicJs.AddresstoShow(toAddress)}{" "}
              <CopyStr address={toAddress} gray={true} />
            </div>
          </Account>
        </dd>
      </dl>
      <dl>
        <dt>Available dapp</dt>
        <dd className="flexLine">{dapp && <Dapp src={dapp} alt="" />}</dd>
      </dl>
      <dl>
        <dt>Expiry date</dt>
        <dd className="flexLine">{expire}</dd>
      </dl>
      <dl>
        <dt>Spending limit</dt>
        <dd className="flexLine">
          <span className="num font-bold">{amount}</span>
          <img src="" alt="" className="symbolBox" />
          <span className="symbol">{symbol}</span>
        </dd>
      </dl>
    </>
  );
});

export default SuperVote;

const Account = styled.div`
  margin-left: 10px;
`;
const Dapp = styled.img`
  height: 24px;
`;
