import React, { useEffect, useState, useMemo } from "react";
import styled from "styled-components";
import { useLocation, useNavigate } from "react-router-dom";

import TxDetail from "./TxDetail";
import BackNav from "components/common/backNav";
import { SETTINGS_KEY } from "views/appAndsettings";
import ConnectInput from "./connectInput";

import Tabs from "../../layout/tabs";
import TxQueue from "./WCT/txQueue";
import TxHistory from "./WCT/txHistory";

export default function SettingList() {
  const navigate = useNavigate();

  const location = useLocation();
  const query = new URLSearchParams(location.search);
  const txId = query.get("tx");

  const [show, setShow] = useState(false);
  const [current, setCurrent] = useState(0);

  useEffect(() => {
    setShow(false);
    const vheight = document.getElementById("contentBox").offsetHeight;
    const oHeight =
      document.body.clientHeight || document.documentElement.clientHeight;
    console.log("====oHeight===", oHeight, vheight);
    if  (oHeight > 900 && vheight >350) {
      document.getElementById("inner").style.height = `${vheight}px`;
    } else {
      document.getElementById("inner").style.height = `550px`;
    }
    setTimeout(() => {
      setShow(true);
    }, 100);
  }, []);

  const backTo = useMemo(() => {
    return txId ? `app/${SETTINGS_KEY.WCT}` : "app_settings";
  }, [txId]);

  const nav = useMemo(() => {
    return ["Queue", "History"];
  }, []);

  const handleCurrent = (num) => {
    setCurrent(num);
  };

  return (
    <Box>
      <BackNav to={backTo} state={txId ? "" : nav[current]} />
      <ContentBox id="contentBox">
        <div id="inner">
          {show &&
            (txId ? (
              <TxDetail id={txId} />
            ) : (
              <TableContent>
                <ConnectInput />
                <FirstLine>
                  <Tabs
                    navList={nav}
                    current={current}
                    handleCurrent={handleCurrent}
                  />
                </FirstLine>
                {current === 0 && <TxQueue />}
                {current === 1 && <TxHistory />}
              </TableContent>
            ))}
        </div>
      </ContentBox>
    </Box>
  );
}

const Box = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0 20px;
  padding: 40px 20px;
`;

const ContentBox = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  #inner {
    height: 100%;
    overflow-y: auto;
  }
`;
const Collection = styled.div`
  display: flex;
  align-items: center;
  img {
    margin-right: 4px;
  }
  .title {
    font-size: 14px;
    font-weight: 400;
    color: #101352;
    line-height: 19px;
    margin-right: 20px;
  }
  .tips {
    font-size: 14px;
    font-weight: 400;
    color: #a6a6b7;
    line-height: 19px;
  }
`;

const ShadowBox = styled.div`
  margin: 15px 0 26px;
  width: 360px;
  height: 44px;
  background: #ffffff;
  display: flex;
  padding: 0 13px;
  align-items: stretch;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;

  & > div {
    display: flex;
    align-items: center;
  }
  img {
    margin-right: 12px;
  }
  .tipsBox {
    font-size: 14px;
    font-weight: 400;
    color: #a6a6b7;
    line-height: 19px;
    padding-left: 14px;
    border-left: 1px solid #f5f5f7;
  }
`;

const TableContent = styled.div`
  height: 100%;
`;

const FirstLine = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 6px solid rgba(245, 245, 247, 0.57);
  padding-top: 24px;
  margin-bottom: 24px;
`;
