import React, { useEffect, useState, useCallback } from "react";
import { Button, Input, Modal } from "antd";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { ethers } from "ethers";

import { useDaoContext } from "../../views/dao/provider";
import api from "../../api";
import { useERC20Contract } from "../../hooks/useContract";
import { useWeb3React } from "@web3-react/core";
import { TokenAvatar } from "components/common/avatar";
import { useDaoOrgContract } from "../../hooks/useContract";
import { DAO_ORG } from "../../utils/constant";
import useSigner from "hooks/useSigner";
import { useAppContext } from "components/provider/appProvider";
import useFundLogo from "hooks/useFundLogo";

export default function Withdraw(props) {
  const { show, close } = props;
  const { t } = useTranslation();

  const { provider, account } = useWeb3React();
  const { dispatch } = useAppContext();
  const { state: daoState } = useDaoContext();
  const { componentAddressMap, fundToken, stockToken } = daoState;
  const [amount, setAmount] = useState();
  const [amountFormat, setAmountFormat] = useState();
  const [fundAmount, setFundAmount] = useState();
  const [balance, setBalance] = useState({});

  const ERC20contract = useERC20Contract(stockToken && stockToken.address);

  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const fundlogo = useFundLogo();

  const handleClose = () => {
    close();
  };

  const confirm = async () => {
    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    // console.log(
    //   String(amount),
    //   ethers.utils.parseUnits(String(amount), fundToken.deci)
    // );
    try {
      await api.venture.withdraw(
        daoOrg,
        signer,
        // ethers.utils.parseUnits(String(amount), fundToken.deci)
          amount
      );

    } catch (error) {
      console.error(error);
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: "withdraw failed",
          closable: true,
        },
      });
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  const handleChangeAmount = useCallback((e) => {
    // setAmount(e.target.value);
    if(!e.target.value){
      setAmount('0');
      setAmountFormat('')
    }
    let val;
    if(Number(e.target.value) > Number(balance.balanceDisplay)){
      val = balance.balanceDisplay;
    }else{
      val = e.target.value;
    }
    if(!fundToken) return;
    const formatAmount = ethers.utils.parseUnits(val, fundToken.deci)
    setAmount(formatAmount.toString());
    setAmountFormat(val)
  }, [fundToken]);

  const estimate = async () => {
    if(amount ==='' || amount ==='0'||!amount ) {
      setFundAmount(0);
      return;
    }
    try {
      const templateConfigAddress = await daoOrg.templateConfig();
      const res = await api.venture.getClaimEarningEstimate(provider, signer, {
        account,
        template: templateConfigAddress,
        amount:amount?.toString(),
      });

      // // usdt
      // console.log("====> ", ethers.utils.formatUnits(res[0], fundToken.deci));
      // // 需要 stock token
      // console.log("====> ", ethers.utils.formatUnits(res[1], fundToken.deci));
      setFundAmount(Number(ethers.utils.formatUnits(res[0]?.toString(), fundToken?.deci)));
    } catch (error) {
      console.error("getClaimBonusEstimate failed", error);
    }
  };
  useEffect(()=>{
    if(account==null)return;
    estimate();
  },[account,amount])

  useEffect(async () => {
    if (!show ) {
      return;
    }
    if (!daoOrg || !provider || !account || !fundToken) {
      return;
    }
    estimate();
  }, [show, daoOrg, account, provider,fundToken]);

  useEffect(async () => {
    if (!ERC20contract || !account) {
      return;
    }
    const deci = await ERC20contract.decimals();
    const balance = await ERC20contract.balanceOf(account);
    const balanceDisplay = ethers.utils.formatUnits(balance, deci);
    setBalance({ balance, balanceDisplay });
  }, [ERC20contract, account]);

  const handleMax = (bal) =>{
    setAmount(bal.balance);
    setAmountFormat(bal.balanceDisplay);
  }

  return (
    <ModalBox
      visible={show}
      onCancel={handleClose}
      footer={null}
      destroyOnClose={true}
      maskClosable={false}
      width={364}
    >
      <div className="title font-medium">
        <span>{t("Venture.Withdraw")}</span>
      </div>
      <ModalContent className="font-regular">
        <InputBox>
          <div>
            <InputLabel>{t("Venture.Redeem")}</InputLabel>
            <InputStyled
              autoFocus
              placeholder={t("Create.Enter")}
              value={amountFormat}
              autoComplete="off"
              type="number"
              onChange={handleChangeAmount}
            />
          </div>
          <div className="right">
            <TokenBox className="token">
              {stockToken && <TokenAvatar symbol={stockToken.symbol} />}
              <span>{stockToken && stockToken.symbol}</span>
            </TokenBox>
            <InputLabel>
              <span>Balance: </span>
              <span>{balance.balanceDisplay}</span>
              <span className="max" onClick={()=>handleMax(balance)}>{t("Max")}</span>
            </InputLabel>

          </div>
        </InputBox>
        <InputBox>
          <div>
            <InputLabel>{t("Venture.Receiving")}</InputLabel>
            <div className="amount">{fundAmount}</div>
          </div>
          <TokenBox>
            <img src={fundlogo} alt="" />
            <span>{fundToken && fundToken.symbol}</span>
          </TokenBox>
        </InputBox>
      </ModalContent>

      <Footer>
        <Button
          disabled={!amount || amount < 0 || amount > balance}
          type="primary"
          onClick={confirm}
          style={{ width: "100%" }}
        >
          {t("Venture.Withdraw")}
        </Button>
      </Footer>
    </ModalBox>
  );
}

const ModalBox = styled(Modal)``;

const ModalContent = styled.div`
  margin-top: 28px;
`;

const Footer = styled.div`
  margin-top: 31px;
  button {
    border-radius: 8px !important;
  }
`;

const InputBox = styled.div`
  width: 100%;
  height: 76px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #f9f9f9;
  border-radius: 12px;
  padding: 13px 20px;
  &:first-child {
    margin-bottom: 24px;
  }
  div.amount {
    height: 27px;
    line-height: 27px;
    font-size: 18px;
    color: #10164b;
  }
  .right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
`;

const InputLabel = styled.div`
  font-weight: 400;
  color: #a6a6b7;
  line-height: 19px;
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  .max {
    color: #0e164e;
    line-height: 19px;
    font-size: 14px;
    cursor: pointer;
    margin-left: 10px;
  }
`;

const InputStyled = styled(Input)`
  &.ant-input {
    background: transparent !important;
    height: 27px !important;
    padding: 0;
    font-size: 18px;
    border-radius: 0 !important;
    border: none;
    &:focus {
      box-shadow: none;
    }
  }
`;

const TokenBox = styled.div`
  display: flex;
  align-items: center;
  &.token {
    margin-bottom: 8px;
  }
  img {
    width: 24px;
    height: 24px;
    margin-right: 6px;
  }
  span {
    line-height: 24px;
    font-weight: 400;
    color: #0e164e;
  }
`;
