import React, { useState } from "react";
import cpoy from "../img/copy.png";
import CopyGray from "../img/copyGray.png";
import CopyIcon from "../img/common/copy.png";
import styled from "styled-components";
import { Tooltip } from "antd";

const IMG = styled.img`
  width: 24px !important;
  height: 24px !important;
  margin-left: 10px !important;
  cursor: pointer;
`;

export default function CopyStr(props) {
  const { address, gray } = props;
  const [show, setShow] = useState(false);

  const copyAddress = () => {
    setShow(!show);
    const cInput = document.createElement("input");
    cInput.setAttribute("id", "copyLayer");
    cInput.value = address;
    document.body.appendChild(cInput);
    cInput.select();
    document.execCommand("Copy");

    const thisNode = document.getElementById("copyLayer");
    thisNode.parentNode.removeChild(thisNode);
    setTimeout(() => {
      setShow(false);
    }, 1000);
  };

  return (
    <Tooltip title="Copied" visible={show} placement="top" zIndex="999">
      <Tooltip
        title={address}
        trigger={["hover", "click"]}
        placement="top"
        zIndex="999"
      >
        {gray ? (
          <IMG
            src={CopyGray}
            alt=""
            onClick={() => copyAddress()}
            className={props.className}
          />
        ) : (
          <IMG src={CopyIcon} alt="" onClick={() => copyAddress()} className={props.className} />
        )}
      </Tooltip>
    </Tooltip>
  );
}
