import { memo, useEffect } from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import { InputNumberItem } from "components/common/inputItem";

const VoteConfigBox = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  .vote-tip {
    margin-right: 10px;
  }
  .vote-end {
    margin-left: 10px;
  }
`;

const VentureVoteConfig = memo((props) => {
  const { t } = useTranslation();
  const { minSupportNumber, updateMinSupportNumber, gpLength } = props;

  return (
    <VoteConfigBox className="line">
      <span className="vote-tip">{t("Vote.VoteConfigTip")}</span>
      <InputNumberItem
        width="50px"
        className="min-support"
        name="minSupportNumber"
        min={1}
        controls={false}
        value={minSupportNumber}
        onChange={(v) => updateMinSupportNumber(Math.floor(v))}
        placeholder={t("Create.Enter")}
      />
      <span className="vote-end">
        {t("Vote.ConfigTextBackend", { num: gpLength })}
      </span>
    </VoteConfigBox>
  );
});
export default VentureVoteConfig;
