import React, {
  memo,
  useEffect,
  useRef,
  useState,
  useMemo,
  useCallback,
} from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import { InputItem, InputNumberItem } from "../../components/common/inputItem";
import VentureVoteConfig from "./ventureVoteConfigFixed";
import DeleteSvg from "../../components/svg/create/delete";
import { useWeb3React } from "@web3-react/core";

const KEY = "add_manager";
export const LOCAL_KEY = `CreateDao_${KEY}`;

const AddManager = memo((props) => {
  const { t } = useTranslation();
  const { account } = useWeb3React();

  const [init, setInit] = useState(true);

  const [configInvalid, setConfigInvalid] = useState(false);
  const [minSupportNumber, setMinSupportNumber] = useState();
  const [GpCarry, setGpCarry] = useState();
  const [manageFee, setManageFee] = useState();
  const [addressList, setAddressList] = useState([]);

  const addAddress = () => {
    const newArray = [...addressList];
    newArray.push({
      address: "",
      bonus: "",
      name: ""
    });
    setAddressList(newArray);
  };

  const handleSetGpInfo = (name, value, index) => {
    let newArray = [...addressList];
    newArray[index][name] = value;
    setAddressList(newArray);
  };

  const deleteAddress = (e, index) => {
    let newArray = [...addressList];
    newArray.splice(index, 1);
    setAddressList(newArray);
  };

  // const getValidGpLength = useMemo(() => {
  // let i = 0;
  // addressList.forEach((a) => a.address && i++);
  // return i;
  //   return addressList.length;
  // }, [addressList]);

  const savePage = () => {
    const data = {
      addressList,
      GpCarry,
      manageFee,
      minSupportNumber,
    };
    sessionStorage.setItem(LOCAL_KEY, JSON.stringify(data));
  };

  useEffect(() => {
    if (!init) {
      savePage();
    }
  }, [minSupportNumber]);


  useEffect(() => {
    if (!init) {
      if (!addressList.length) {
        setAddressList([
          {
            address: account,
            bonus: 100,
            name: ""
          },
        ]);
      } else {
        savePage();
      }
    }
  }, [init, addressList]);

  useEffect(() => {
    try {
      const data = JSON.parse(sessionStorage.getItem(LOCAL_KEY) || "");
      if (data.GpCarry) {
        setGpCarry(data.GpCarry);
      }
      if (data.manageFee) {
        setManageFee(data.manageFee);
      }
      if (data.addressList) {
        setAddressList(data.addressList);
      }
      if (data.minSupportNumber) {
        setMinSupportNumber(data.minSupportNumber);
      }
    } catch (error) {
      console.warn("get local failed", error);
    } finally {
      setInit(false);
    }
  }, []);

  return (
    <>
      <Intro>
        {t("Create.AddManagerIntro")}
        <span className="font-bold">{t("Create.CarriedInterest")}</span>
        {t("Create.AddManagerIntro2")}
        <span className="font-bold">{t("Create.ManagementFee")}</span>
        {t("Create.AddManagerIntro2")}
      </Intro>
      <EditArea>
        <FlexRow>
          <Block>
            <InputNumberItem
              width="241px"
              overlayClass="block"
              title={t("Create.GP")}
              onBlur={savePage}
              outSuffix="%"
              onChange={setGpCarry}
              value={GpCarry}
              controls={false}
              precision={2}
              max={100}
              toolTip={t("Hover.Create.CarriedInterest")}
              placeholder={t("Create.CarriedInterestPlaceholder")}
            />
          </Block>
          <Block>
            <InputNumberItem
              width="241px"
              overlayClass="block"
              title={t("Create.Manage")}
              onBlur={savePage}
              onChange={setManageFee}
              value={manageFee}
              outSuffix="%"
              controls={false}
              precision={2}
              max={100}
              toolTip={t("Hover.Create.ManagementFee")}
              placeholder={t("Create.ManagementFeePlaceholder")}
            />
          </Block>
        </FlexRow>
        <FlexRow>
          <FlexRowItemLeft>
            <GPAddressItem style={{ marginBottom: "12px" }}>
              <div className="name-block">{t("Create.ManagerName")}</div>
              <div className="address-block">{t("Create.Address")}</div>
              <div className="bonus-block">{t("Create.Bonus")}</div>
            </GPAddressItem>
            {addressList.map((item, idx) => {
              return (
                <GPAddressItem key={idx}>
                  <div className="name-block">
                    <InputItem
                      placeholder={t("Create.Enter")}
                      value={addressList[idx].name}
                      autoComplete="off"
                      onChange={(e) =>
                        handleSetGpInfo("name", e.target.value, idx)
                      }
                      onBlur={savePage}
                    />
                  </div>
                  <div className="address-block">
                    <InputItem
                      placeholder={t("Create.Enter")}
                      value={addressList[idx].address}
                      autoComplete="off"
                      onChange={(e) =>
                        handleSetGpInfo("address", e.target.value, idx)
                      }
                      onBlur={savePage}
                      disabled={idx === 0}
                    />
                  </div>
                  <div className="bonus-block">
                    <InputNumberItem
                      placeholder={t("Create.Bonus")}
                      value={addressList[idx].bonus}
                      controls={false}
                      precision={2}
                      max={100}
                      outSuffix="%"
                      onChange={(v) => handleSetGpInfo("bonus", v, idx)}
                      onBlur={savePage}
                      toolTipStyle={{top: "-10px"}}
                      toolTip={t("Hover.Create.Bonus")}
                    />
                    {!!idx && (
                      <DeleteSvg
                        className="remove"
                        onClick={(e) => deleteAddress(e, idx)}
                      />
                    )}
                  </div>
                </GPAddressItem>
              );
            })}
            <GPAddressItem>
              <div className="name-block"></div>
              <div className="address-block">
                <ButtonAdd>
                  <button onClick={addAddress}>{t("Create.AddMore")}</button>
                </ButtonAdd>
              </div>
              <div className="bonus-block"></div>
            </GPAddressItem>
          </FlexRowItemLeft>
        </FlexRow>
        <FlexRow>
          <VentureVoteConfig
            inputWidth="82px"
            setInvalid={setConfigInvalid}
            minSupportNumber={minSupportNumber}
            updateMinSupportNumber={setMinSupportNumber}
            gpLength={addressList.length}
          />
        </FlexRow>
      </EditArea>
    </>
  );
});

export default AddManager;

const Intro = styled.div`
  width: 100%;
  color: #606793;
  line-height: 19px;
  margin-bottom: 40px;
`;

const EditArea = styled.div``;
const FlexRow = styled.div`
  display: flex;
  margin-bottom: 30px;
`;

const Block = styled.div`
  &:first-child {
    margin-right: 95px;
  }
`;

const FlexRowItemLeft = styled.div`
  flex: 2;
  .block {
    flex: 1;
    &:last-child {
      margin-left: 50px;
    }
  }
  &.flex {
    display: flex;
  }
`;

const FlexRowItemRight = styled.div`
  flex: 1;
  margin-left: 50px;
`;

const GPAddressItem = styled.div`
  margin-bottom: 15px;
  display: flex;
  .name-block {
    width: 122px;
    margin-right: 18px;
  }
  .bonus-block {
    width: 126px;
    margin-left: 22px;
    position: relative;
  }
  .address-block {
    width: 413px;
  }
  .remove {
    position: absolute;
    right: -50px;
    top: 10px;
    cursor: pointer;
    width: 24px;
  }
`;

const ButtonAdd = styled.div`
  text-align: center;
  button {
    width: 129px;
    line-height: 35px;
    background: #ffe6f2;
    border: none;
    cursor: pointer;
  }
`;

const Suffix = styled.span`
  margin-left: 15px;
  color: #0e164e;
`;
