import { memo, useMemo, useEffect, useState, useCallback, useRef } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { ethers } from "ethers";
import { useDaoContext } from "views/dao/provider";
import { useDaoOrgContract } from "hooks/useContract";
import useSigner from "hooks/useSigner";
import DropSvg from "components/svg/common/select";
import Button, { ButtonGroup } from "components/common/button";
import VoteLoadingModal, { VoteTxStatus } from "./loading";
import { DAO_ORG, VOTE_MANAGER, VAULT_MANAGER } from "utils/constant";
import api from "api";
import CheckInput from "components/common/checkInput";
import DateBox from "components/common/date";

import { ReceiveBox, DropBox } from "./transfer/style";
import { useAppContext } from "components/provider/appProvider";
import TransferToken from "./transfer/transferToken";
import TransferNFT from "./transfer/transferNft";
import { getVoteIdByEvent } from "utils/utils";
import CopyStr from "components/copy";

export default function Transfer(props) {
  const { t } = useTranslation();

  const { dispatch } = useAppContext();
  const { state: daoState } = useDaoContext();
  const { componentAddressMap } = daoState;
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const [assetType] = useState([{ label: "Token" }, { label: "NFT" }]);
  const [currentType, setCurrentType] = useState(0);

  const [status, setStatus] = useState(VoteTxStatus.Default);
  const [txHash, setTxHash] = useState();

  const [toAddress, setToAddress] = useState();
  const [addressInvalidMsg, setAddressInvalidMsg] = useState();
  const [endTime, setEndTime] = useState();
  const [newVoteId, setVoteId] = useState();

  const transferTokenRef = useRef();
  const transferNFTRef = useRef();

  const onChangeToAddress = useCallback((e) => {
    const v = e.target.value;
    setToAddress(v);
    if (!ethers.utils.isAddress(v)) {
      setAddressInvalidMsg(t("Msg.InvalidAddress"));
    } else {
      setAddressInvalidMsg(null);
    }
  }, []);

  const changeType = useCallback((index) => {
    setCurrentType(index);
  }, []);

  const typeContent = useMemo(() => {
    if (currentType === 0) {
      return <TransferToken ref={transferTokenRef} />;
    } else if (currentType === 1) {
      return <TransferNFT ref={transferNFTRef} />;
    }
  }, [currentType]);

  const checkValid = () => {
    if (!toAddress || !ethers.utils.isAddress(toAddress)) {
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "invalid recipient", closable: true },
      });
      return;
    }
    if (currentType === 0 && !transferTokenRef.current.checkValid()) {
      return;
    }
    if (currentType === 1 && !transferNFTRef.current.checkValid()) {
      return;
    }
    return true;
  };

  const createVote = async () => {
    if (!checkValid()) {
      return;
    }
    let duration = Math.round((endTime - Date.now()) / 1000);
    if (duration < 86400) {
      duration = 86400;
    }
    let txHash;
    try {
      setStatus(VoteTxStatus.Loading);
      let res;
      if (currentType === 0) {
        res = await api.vote.newWithdrawVote(daoOrg, signer, {
          toAddress,
          ...transferTokenRef.current.data,
          vote_time: duration,
        });
      } else if (currentType === 1) {
        res = await api.vote.newTransferNFTVote(daoOrg, signer, {
          toAddress,
          ...transferNFTRef.current.data,
          vote_time: duration,
        });
      }
      txHash = res.hash;
      const r = await res.wait();
      const event = r.events.find(
        (e) => e.address === componentAddressMap.get(VOTE_MANAGER)
      );
      const vid = getVoteIdByEvent(event);
      setVoteId(vid);
      setStatus(VoteTxStatus.Success);
    } catch (error) {
      setStatus(VoteTxStatus.Failed);
      setTxHash(txHash);
      console.error("newTransfrt vote failed", error);
    }
  };

  const closeLoading = useCallback(() => {
    setStatus(VoteTxStatus.Default);
    txHash && setTxHash();
  }, []);

  const closeAll = useCallback(() => {
    closeLoading();
    props.handleClose();
  }, [props.handleClose]);

  const vaultAddress = useMemo(() => {
    return componentAddressMap.get(VAULT_MANAGER);
  }, [componentAddressMap]);

  return (
    <TransferBox>
      <VoteLoadingModal
        status={status}
        txHash={txHash}
        handleRetry={createVote}
        voteId={newVoteId}
        handleClose={closeLoading}
        handleCloseAll={closeAll}
      />
      <dl>
        <dt>Sending from</dt>
        <dd>
          <SendBox>
            <div>
              <div className="title font-medium">Vault</div>
              <div className="address">
                {vaultAddress} <CopyStr address={vaultAddress} />
              </div>
            </div>
          </SendBox>
        </dd>
      </dl>
      <dl>
        <dt>{t("Vote.AssetType")}</dt>
        <dd>
          <TypeSelect
            list={assetType}
            onChange={changeType}
            selected={currentType}
          />
        </dd>
      </dl>
      <dl>
        <dt>{t("Vote.Recipient")}</dt>
        <dd>
          <CheckInput
            autoFocus
            value={toAddress}
            onChange={onChangeToAddress}
            errorMsg={addressInvalidMsg}
          />
        </dd>
      </dl>
      {typeContent}
      <DateBox
        label={t("Vote.Expiry")}
        targetTime={endTime}
        updateTargetTime={setEndTime}
      />

      <ButtonGroup>
        <Button onClick={props.handleClose}>{t("Cancel")}</Button>
        <Button primary onClick={createVote}>
          {t("Submit")}
        </Button>
      </ButtonGroup>
    </TransferBox>
  );
}

const TypeSelect = memo((props) => {
  const { list, onChange, selected } = props;
  const [show, setShow] = useState(false);

  useEffect(() => {
    document.addEventListener("click", () => {
      setShow(false);
    });
  }, []);

  const showList = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    setShow(true);
  };

  const choose = useCallback(
    (index, e) => {
      e.stopPropagation();
      setShow(false);
      if (index !== selected) {
        onChange(index);
      }
    },
    [selected]
  );

  return (
    <ReceiveBox onClick={(e) => showList(e)}>
      <span>{list[selected].label}</span>
      <DropSvg />
      {show && (
        <DropBox>
          {list.map((item, index) => (
            <li key={index} onClick={(e) => choose(index, e)}>
              {item.label}
            </li>
          ))}
        </DropBox>
      )}
    </ReceiveBox>
  );
});

const TransferBox = styled.div`
  dt {
    font-size: 14px;
    font-weight: 400;
    color: #a6a6b9;
    line-height: 19px;
    margin-bottom: 10px;
  }
  dd {
    position: relative;
  }
  dl {
    margin-bottom: 20px;
  }
`;

const SendBox = styled.div`
  background: #f7f7f7;
  border-radius: 8px;
  display: flex;
  padding: 20px 10px 17px 20px;
  .lft {
    width: 40px;
    height: 40px;
    border-radius: 40px;
    margin-right: 10px;
  }
  .title {
    font-size: 18px;
    font-weight: 500;
    color: #10164b;
    line-height: 24px;
  }
  .address {
    font-size: 14px;
    font-weight: 500;
    color: #10164b;
    line-height: 24px;
  }
`;
