import React, { memo, useCallback, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";

import { PERIOD_NO } from "utils/constant";
import LikeSvg from "components/svg/dao/like";
// import LogoSVG from "img/logoSubdao.svg";
import LogoSVG from "img/defaultLogo.svg";
import UsdtSVG from "img/token/usdt.svg";

const DaoItem = memo((props) => {
  const navigator = useNavigate();
  const { data, networkSimple, gateway } = props;
  const {
    handle,
    name,
    logo,
    period,
    tav,
    et_holders,
    is_like,
    display_handle,
  } = data;
  const { t } = useTranslation();

  const statusBox = useMemo(() => {
    switch (period) {
      case PERIOD_NO.RAISE:
        return (
          <DepositStatus className="font-bold">
            {t("Period.InDeposit")}
          </DepositStatus>
        );
      case PERIOD_NO.MANAGEMENT:
        return (
          <ManagementStatus className="font-bold">
            {t("Period.InManagement")}
          </ManagementStatus>
        );
      case PERIOD_NO.INRETURN :
        return (
          <ReturnStatus className="font-bold">
            {t("Period.InReturn")}
          </ReturnStatus>
        );
      case PERIOD_NO.REDEMPTION:
        return (
          <RedemptionStatus className="font-bold">
            {t("Period.InRedemption")}
          </RedemptionStatus>
        );
      default:
        return <></>;
    }
  }, [period]);

  const jumpDetail = useCallback(() => {
    navigator(`/dao/${networkSimple}:${display_handle}/homepage/info`);
  }, [networkSimple, display_handle]);

  return (
    <DaoStyled onClick={jumpDetail}>
      <div>
        <img src={logo ? (gateway + logo) : LogoSVG} alt="" className="avatar" />
        {is_like && <Like />}
      </div>
      <DaoName className="font-medium">{name}</DaoName>
      {statusBox}
      <Num className="font-bold">
        <img src={UsdtSVG} alt="" />
        <span>{tav}</span>
      </Num>
      <HolderBox>
        <Num className="font-bold">{et_holders}</Num>
        {t("Explore.Holders")}
      </HolderBox>
    </DaoStyled>
  );
});

export default DaoItem;

const DaoStyled = styled.li`
  float: left;
  width: 17%;
  margin: 40px 3.7% 0 0;
  height: 289px;
  background: #f7f7f7;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;
  &:nth-child(5n) {
    margin-right: 0;
  }
  .avatar {
    width: 97px;
    height: 97px;
    border-radius: 50%;
  }
 
  @media (max-width: 1440px) {
    height: auto;
    padding: 20px 0;
    .avatar {
      width: 60px;
      height: 60px;
    }
  }
`;
const Like = styled(LikeSvg)`
  position: absolute;
  right: 17px;
  top: 19px;
`;

const DaoName = styled.div`
  font-size: 20px;
  font-weight: 500;
  color: #10164b;
  line-height: 27px;
  padding: 15px 0;
  min-height: 57px;
  white-space: nowrap;
`;
const Status = styled.div`
  font-size: 14px;
  font-weight: bold;
  color: #ffffff;
  line-height: 32px;
  width: 100%;
  text-align: center;
  height: 32px;
  margin-bottom: 11px;
`;

const DepositStatus = styled(Status)`
  background: #02a177;
`;
const ManagementStatus = styled(Status)`
  background: #1d267f;
`;
const RedemptionStatus = styled(Status)`
  background: #d51172;
`;
const ReturnStatus = styled(Status)`
  background: #e6e8f0;
  color: #686a73;
`;

const Num = styled.span`
  font-size: 16px;
  font-weight: bold;
  color: #10164b;
  line-height: 16px;
  display: flex;
  align-items: center;
  img {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-right: 8px;
  }
`;
const HolderBox = styled.div`
  display: flex;
  align-items: center;
  margin-top: 6px;
  font-size: 12px;
  font-weight: 400;
  color: #10164b;
  line-height: 16px;
  span {
    margin-right: 6px;
  }
`;
