import { useState, useEffect, useCallback } from "react";
import { useTranslation } from "react-i18next";
import { InputNumber, Input, Row, Col } from "antd";
import styled from "styled-components";
import EditImg from "../../img/portfolio/Edit.png";
import Del from "../../img/portfolio/Delete.png";
import WrongImg from "../../img/voteNV/wrong.png";
import ConfirmImg from "../../img/voteNV/Confirm.png";

import { useDaoContext } from "../../views/dao/provider";
import { useVentureContract } from "../../hooks/useContract";
import { VENTURE_MANAGER } from "../../utils/constant";
import ConfirmModal from "../modal/confirmModal";
import EmptyBox from "components/common/empty";

import { useMutation, useLazyQuery } from "@apollo/client";
import {
  CreateProject,
  QueryProjects,
  UpdateProject,
  DeleteProject,
} from "api/graphql/dao";
import { useWeb3React } from "@web3-react/core";
import { signProjectMessage, signDeleteProjectMessage } from "utils/sign";
import SelectToken from "./selectToken";
import UploadLogo from "components/upload/miniLogo";
import { useAppContext } from "components/provider/appProvider";

export default function Investment(props) {
  const { t } = useTranslation();
  const { chainId, provider, account } = useWeb3React();
  const { dispatch } = useAppContext();
  const { state: daoState } = useDaoContext();
  const {
    componentAddressMap,
    whoAmI: { isOwner },
  } = daoState;

  const ventureContract = useVentureContract(
    componentAddressMap.get(VENTURE_MANAGER)
  );

  const [confirmVisible, setConfirmVisible] = useState(false);
  const [deleteData, setDeleteData] = useState({});
  const [projectList, setProjectList] = useState([]);

  const getProjects = () => {
    console.warn("~~~getProjects");
    queryProjects({
      variables: {
        venture: ventureContract.address,
        chainId,
        page: 1,
        size: 10000,
      },
    });
  };

  const [queryProjects] = useLazyQuery(QueryProjects, {
    onCompleted: async (data) => {
      console.log("====", data);
      try {
        setProjectList(
          data.projects.data.map((l) => ({
            id: l.id,
            name: l.name,
            logo: l.logo,
            round: l.round,
            amount: l.amount,
            token: l.valuationToken,
            valuationAmount: l.valuationAmount,
          }))
        );
      } catch (error) {
        console.error("parse queryProjects failed", error);
      }
    },
    onError: (error) => {
      console.error("====", error);
    },
  });

  const [createProject] = useMutation(CreateProject, {
    onCompleted: async (data) => {
      console.log("====", data);
      getProjects();
      dispatch({ type: "LOADINGTYPE", payload: null });
    },
    onError: (error) => {
      console.error("====", error);
      dispatch({ type: "LOADINGTYPE", payload: null });
      dispatch({
        type: "MSGTYPE",
        payload: { msg: `${error}`, closable: true },
      });
    },
  });

  const [updateProject] = useMutation(UpdateProject, {
    onCompleted: async (data) => {
      console.log("====", data);
      getProjects();
      dispatch({ type: "LOADINGTYPE", payload: null });
    },
    onError: (error) => {
      console.error("====", error);
      dispatch({ type: "LOADINGTYPE", payload: null });
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "update failed", closable: true },
      });
    },
  });

  const [deleteProject] = useMutation(DeleteProject, {
    onCompleted: async (data) => {
      console.log("====", data);
      getProjects();
      setDeleteData({})
      dispatch({ type: "LOADINGTYPE", payload: null });
    },
    onError: (error) => {
      console.error("====", error);
      dispatch({ type: "LOADINGTYPE", payload: null });
      dispatch({
        type: "MSGTYPE",
        payload: { msg: "update failed", closable: true },
      });
    },
  });

  useEffect(async () => {
    if (ventureContract) {
      getProjects();
    }
  }, [ventureContract, chainId]);

  const change2edit = useCallback(
    (index) => {
      const editData = { ...projectList[index] };
      projectList[index].editData = editData;
      setProjectList([...projectList]);
    },
    [projectList]
  );

  const handleCancel = useCallback(
    (index) => {
      const data = projectList[index];
      console.log(index, data);
      const isNew = data.editData && data.editData.isNew;
      if (isNew) {
        setProjectList(projectList.slice(0, -1));
        return;
      }
      const list = [];
      for (const p of projectList) {
        if (p.id === data.id) {
          delete p.editData;
        }
        list.push({ ...p });
      }
      setProjectList(list);
    },
    [projectList]
  );

  const handleSave = useCallback(
    async (index) => {
      console.log("projectList[index]: ", projectList[index]);
      const editData = projectList[index].editData;
      console.log("editData", editData);
      if (
        !editData.logo ||
        !editData.name ||
        !editData.round ||
        !editData.amount ||
        !editData.valuationAmount ||
        !editData.token
      ) {
        dispatch({
          type: "MSGTYPE",
          payload: { msg: "require input all", closable: true },
        });
        return;
      }
      if (editData.amount <= 0 || editData.valuationAmount <= 0) {
        dispatch({
          type: "MSGTYPE",
          payload: { msg: "invalid amount", closable: true },
        });
        return;
      }
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
      // sign
      const now = Date.now();
      const signData = await provider.send("personal_sign", [
        signProjectMessage(
          {
            logo: editData.logo,
            name: editData.name,
            round: editData.round,
            amount: editData.amount.toFixed(6),
            valuationAmount: editData.valuationAmount.toFixed(6),
            token: editData.token,
          },
          now
        ),
        account,
      ]);

      if (editData.isNew) {
        // add new
        createProject({
          variables: {
            logo: editData.logo,
            name: editData.name,
            round: editData.round,
            amount: editData.amount,
            valuationAmount: editData.valuationAmount,
            valuationToken: editData.token,
            sign: signData,
            timestamp: now,
            chainId,
            venture: componentAddressMap.get(VENTURE_MANAGER),
          },
        });
        // delete editData.isNew;
      } else {
        // save
        updateProject({
          variables: {
            id: projectList[index].id,
            logo: editData.logo || "test",
            name: editData.name,
            round: editData.round,
            amount: editData.amount,
            valuationAmount: editData.valuationAmount,
            valuationToken: editData.token,
            sign: signData,
            timestamp: now,
            chainId,
            venture: componentAddressMap.get(VENTURE_MANAGER),
          },
        });
      }
      // projectList[index] = { ...projectList[index].editData };
      // setProjectList([...projectList]);
    },
    [projectList, provider, account, chainId, componentAddressMap]
  );

  const handleEdit = useCallback(
    (index, key, value) => {
      const editData = projectList[index].editData;
      projectList[index].editData = { ...editData, [key]: value };
      setProjectList([...projectList]);
    },
    [projectList]
  );

  const addNew = useCallback(() => {
    if (projectList.length) {
      const lastOne = projectList[projectList.length - 1];
      if (lastOne && lastOne.editData && lastOne.editData.isNew) {
        return;
      }
    }
    setProjectList([
      ...projectList,
      {
        editData: {
          isNew: true,
        },
      },
    ]);
  }, [projectList]);

  const showConfirmModal = useCallback((data) => {
    setConfirmVisible(true);
    setDeleteData(data);
  }, []);

  const closeConfirmModal = useCallback(() => {
    setConfirmVisible(false);
    setDeleteData({});
  }, []);

  const hanleConfirmDelete = useCallback(
    async () => {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
      // sign
      try {
        const now = Date.now();
        const signData = await provider.send("personal_sign", [
          signDeleteProjectMessage(
            {
              id: deleteData.id,
              chainId,
            },
            now
          ),
          account,
        ]);
        deleteProject({
          variables: {
            id: deleteData.id,
            sign: signData,
            timestamp: now,
            chainId,
            venture: componentAddressMap.get(VENTURE_MANAGER),
          },
        });
      } catch (error) {
        dispatch({ type: "LOADINGTYPE", payload: null });
      }

      closeConfirmModal();
    },
    [deleteData, chainId]
  );

  const changeToken = (index, address) => {
    const data = projectList[index];
    const newItem = {
      ...data,
      editData: {
        ...data.editData,
        token: address,
      },
    };
    const newList = [];
    for (let i = 0; i < projectList.length; i++) {
      newList.push(i === index ? newItem : { ...projectList[i] });
    }
    setProjectList(newList);
  };

  const onChangeLogo = (index, url) => {
    const data = projectList[index];
    const newItem = {
      ...data,
      editData: {
        ...data.editData,
        logo: url,
      },
    };
    const newList = [];
    for (let i = 0; i < projectList.length; i++) {
      newList.push(i === index ? newItem : { ...projectList[i] });
    }
    console.log(url)
    console.log(newList)
    setProjectList(newList);
  };

  return (
    <TableBox>
      <ConfirmModal
        data={deleteData}
        show={confirmVisible}
        handleClose={closeConfirmModal}
        message={t("Investment.DeleteMsg")}
        handleConfirm={hanleConfirmDelete}
      />
      <HeaderRow>
        <ColStyled span={2}>{t("Investment.Logo")}</ColStyled>
        <ColStyled span={5}>{t("Investment.ProjectName")}</ColStyled>
        <ColStyled span={3}>{t("Investment.Round")}</ColStyled>
        <ColStyled span={6}>{t("Investment.Valuation")}</ColStyled>
        <ColStyled span={4}>{t("Investment.Amount")}</ColStyled>
        <ColStyled span={4}></ColStyled>
      </HeaderRow>
      {projectList.map((item, index) =>
        item.editData ? (
          <BodyRow key={index}>
            <ColStyled span={2}>
              <UploadLogo
                imgCode={item.editData.logo}
                updateUrl={(url) => onChangeLogo(index, url)}
              />
            </ColStyled>
            <ColStyled span={5}>
              <EditInput
                value={item.editData.name}
                onChange={(e) => {
                  handleEdit(index, "name", e.target.value);
                }}
              />
            </ColStyled>
            <ColStyled span={3}>
              <EditInput
                value={item.editData.round}
                onChange={(e) => {
                  handleEdit(index, "round", e.target.value);
                }}
              />
            </ColStyled>
            <ColStyled span={6}>
              <div className="col-amount">
                <span className="amount">
                  <EditNumberInput
                    value={item.editData.valuationAmount}
                    min={0}
                    controls={false}
                    precision={6}
                    onChange={(v) => {
                      handleEdit(index, "valuationAmount", v);
                    }}
                  />
                </span>
                <SelectToken
                  isEdit={true}
                  token={item.editData.token}
                  onChange={(address) => changeToken(index, address)}
                />
              </div>
            </ColStyled>
            <ColStyled span={4}>
              <EditNumberInput
                value={item.editData.amount}
                min={0}
                controls={false}
                precision={6}
                onChange={(v) => {
                  handleEdit(index, "amount", v);
                }}
              />
            </ColStyled>
            <ColStyled span={4}>
              <Operation>
                <EditBg>
                  <img
                      className="confirmImg2"
                    src={ConfirmImg}
                    alt=""
                    onClick={() => {
                      handleSave(index);
                    }}
                  />
                  <div className="edit">{t("Save")}</div>
                </EditBg>
                <EditBg>
                  <img
                      className="confirmImg"
                    src={WrongImg}
                    alt=""
                    onClick={() => handleCancel(index)}
                  />
                  <div className="edit">{t("Cancel")}</div>
                </EditBg>
              </Operation>
            </ColStyled>
          </BodyRow>
        ) : (
          <BodyRow key={index}>
            <ColStyled span={2}>
              <UploadLogo disabled={true} imgCode={item.logo} />
            </ColStyled>
            <ColStyled span={5}>{item.name}</ColStyled>
            <ColStyled span={3}>{item.round}</ColStyled>
            <ColStyled span={6}>
              <div className="col-amount">
                <span className="amount">{item.valuationAmount}</span>
                <SelectToken token={item.token} />
              </div>
            </ColStyled>
            <ColStyled span={4}>{item.amount}</ColStyled>
            <ColStyled span={4}>
              <Operation>
                <EditBg>
                  <img
                    className="opImg"
                    src={EditImg}
                    alt=""
                    onClick={() => {
                      change2edit(index);
                    }}
                  />
                  <div className="edit">{t("Edit")}</div>
                </EditBg>
                <EditBg>
                  <img
                    className="opImg"
                    src={Del}
                    alt=""
                    onClick={() => {
                      showConfirmModal(item);
                    }}
                  />
                  <div className="edit">{t("Delete")}</div>
                </EditBg>
              </Operation>
            </ColStyled>
          </BodyRow>
        )
      )}
      {!projectList.length && <EmptyBox />}
      {isOwner && (
        <BtnBox>
          <Button onClick={addNew}>{t("Add")}</Button>
        </BtnBox>
      )}
    </TableBox>
  );
}

const EditInput = styled(Input)`
  /* width: 150px; */
  background: #ffffff;
  border-radius: 8px;
  border: 1px solid #efeff1;
`;
const EditNumberInput = styled(InputNumber)`
  /* width: 100px; */
  background: #ffffff;
  border-radius: 8px;
  border: 1px solid #efeff1;
`;

const TableBox = styled.div`
  position: relative;
  height: 100%;
  img {
    vertical-align: baseline;
  }
`;

const RowStyled = styled(Row)`
  height: 65px;
  line-height: 65px;
  padding-left: 45px;
  border-radius: 14px;
`;

const HeaderRow = styled(RowStyled)`
  background: #f5f5f7;
  font-size: 16px;
  font-weight: 400;
  color: #b2b4c5;
`;

const BodyRow = styled(RowStyled)`
  &:hover {
    background: #fbfbfa;
  }
`;

const ColStyled = styled(Col)`
  height: 65px;
  padding-right: 5px;
  .col-amount {
    display: flex;
    .amount {
      margin-right: 4px;
    }
  }

  .logo {
    width: 34px;
    height: 34px;
    margin-top: 15px;
  }
  .symbol {
    width: 22px;
    height: 22px;
  }
`;

const TokenBox = styled.div`
  display: flex;
  align-items: center;
  .token {
    margin: 0 3px;
  }
  .arrow {
    height: 6px;
    margin-left: 3px;
  }
`;

const Operation = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const EditBg = styled.div`
  position: relative;
  width: 24px;
  cursor: pointer;
  &:first-child {
    margin-right: 12px;
  }
  .opImg {
    margin-top: 20px;
  }
  .confirmImg{
    width: 29px!important;
    padding-top: 20px;
  }
  .confirmImg2{
    width: 20px!important;
  }
  .edit {
    position: absolute;
    left: -12px;
    top: -10px;
    background: #fff;
    box-shadow: 0 3px 4px rgba(16, 22, 75, 0.1);
    z-index: 99;
    font-size: 12px;
    font-weight: 400;
    color: #6f7395;
    line-height: 16px;
    padding: 3px 14px;
    border-radius: 5px;
    display: none;

    &:before {
      position: absolute;
      bottom: -6px;
      left: 50%;
      content: "";
      margin-left: -7px;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-top: 7px solid #fff;
    }
  }

  &:hover {
    .edit {
      display: block;
    }
  }
`;

const BtnBox = styled.div`
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 80px;
  background: #fff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
`;
const Button = styled.div`
  width: 105px;
  height: 34px;
  background: #d52473;
  border-radius: 8px;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  line-height: 34px;
  cursor: pointer;
`;
