import { memo } from "react";
import styled from "styled-components";
import EmptyIcon from "../../img/common/empty.png";

const EmptyBox = memo((props) => {
  const { msg } = props;
  return (
    <Empty>
      <img src={EmptyIcon} alt="" />
      {msg && <Message>{msg}</Message>}
    </Empty>
  );
});
export default EmptyBox;

const Empty = styled.div`
  min-height: 300px;
  width: 100%;
  text-align: center;
  img {
    width: 200px;
    margin-top: 100px;
  }
`;

const Message = styled.div`
  font-size: 16px;
  color: #10164b;
  line-height: 22px;
  margin-top: 13px;
`;
