import React, { memo, useState, useMemo, useCallback, useEffect } from "react";
import styled from "styled-components";
import EditDAO from "components/modal/EditDAO";
import LogoSVG from "img/logoSubdao.svg";
import { useDaoContext } from "./provider";
import { useWeb3React } from "@web3-react/core";
import { useTranslation } from "react-i18next";
import { useMutation } from "@apollo/client";
import { useVoteContract } from "hooks/useContract";
import {
  VOTE_MANAGER,
  VAULT_MANAGER,
  PERIOD_NO,
  LIKE_STATUS,
  VENTURE_STOCK_MANAGER,
  ORG_MANAGER,
} from "utils/constant";
import { LikeDao } from "api/graphql/dao";
import publicJs from "utils/publicJs";
import CopyStr from "components/copy";
import CopyButton from "components/common/copy";
import NewVote from "components/modal/newVote";

import LinkImg from "img/newHome/link.png";
import LinkSvg from "components/svg/dao/link";
import ShareSvg from "components/svg/dao/share";
import EditSvg from "components/svg/dao/edit";
import Twitter from "components/svg/dao/twitter";
import Telegram from "components/svg/dao/telegram";
import Discord from "components/svg/dao/discord";

import LikeIcon from "components/svg/dao/like";
import UnlikeIcon from "components/svg/dao/unlike";
import useGateway from "hooks/useGateway";
import useOrgMembers from "hooks/useOrgMembers";
import { useAppContext } from "components/provider/appProvider";

const DaoHeader = memo((props) => {
  const { t } = useTranslation();

  const {
    state: { exploreScan },
  } = useAppContext();
  const {
    state: { daoId, baseData, componentAddressMap, whoAmI: {isOwner, isGP} },
    dispatch,
  } = useDaoContext();
  const { account, chainId } = useWeb3React();

  const voteContract = useVoteContract(componentAddressMap.get(VOTE_MANAGER));
  const orgMembers = useOrgMembers(componentAddressMap.get(ORG_MANAGER));

  const [showModal, setShowModal] = useState(false);
  const [showVote, setShowVote] = useState(false);
  const [like, setLike] = useState(false);
  const [minRequire, setMinRequire] = useState();
  const [show,setShow] = useState(false)

  const gateway = useGateway();

  const closeModal = useCallback(() => {
    setShowModal(false);
  }, []);

  const handleEdit = useCallback(() => {
    setShowModal(true);
  }, []);

  const vaultContractAddress = useMemo(() => {
    return componentAddressMap.get(VAULT_MANAGER);
  }, [componentAddressMap]);
  const stockTokenAddress = useMemo(() => {
    return componentAddressMap.get(VENTURE_STOCK_MANAGER);
  }, [componentAddressMap]);

  const [likeDao, { loading }] = useMutation(LikeDao, {
    onCompleted: (data) => {
      console.log("====", data);
      if (data && data.likeDao) {
        dispatch({
          type: "SET_BASE_DATA",
          payload: {
            ...baseData,
            is_like: baseData.is_like ? LIKE_STATUS.NOT_LIKE : LIKE_STATUS.LIKE,
          },
        });
      }
    },
    onError: (error) => {
      console.error("BundDao failed", error);
    },
  });

  const clickLike = useCallback(() => {
    if (!baseData) {
      return;
    }
    likeDao({
      variables: {
        daoId: daoId,
        chainId: chainId,
        isLike: baseData.is_like ? LIKE_STATUS.NOT_LIKE : LIKE_STATUS.LIKE,
      },
    });
  }, [daoId, baseData, chainId]);

  useEffect(async () => {
    if (!voteContract) {
      return;
    }
    try {
      const minRequire = (await voteContract.defaultMinRequire()).toNumber();
      setMinRequire(minRequire);
    } catch (error) {
      console.error("get vote config failed: ", error);
    }
  }, [voteContract]);

  const voteConfig = useMemo(() => {
    if (!minRequire || !Object.keys(orgMembers).length) {
      return;
    }
    return `${minRequire}/${Object.keys(orgMembers).length}`;
  }, [minRequire,orgMembers]);

  const PeriodBar = useMemo(() => {
    if (!baseData) {
      return;
    }
    switch (baseData.period) {
      case PERIOD_NO.RAISE:
        return <TagBox className="font-bold">{t("Period.InDeposit")}</TagBox>;
      case PERIOD_NO.INRETURN:
        return (
          <TagBox className="font-bold return">{t("Period.InReturn")}</TagBox>
        );
      case PERIOD_NO.MANAGEMENT:
        return (
          <TagBox className="font-bold management">
            {t("Period.InManagement")}
          </TagBox>
        );
      case PERIOD_NO.REDEMPTION:
        return (
          <TagBox className="font-bold redemption">
            {t("Period.InRedemption")}
          </TagBox>
        );
      default:
        return <></>;
    }
  }, [baseData]);

  const socailUrls = useMemo(() => {
    if (!baseData) {
      return <></>;
    }
    return (
      <>
        {[
          {
            key: "twitter_url",
            icon: <Twitter className="icon socail" />,
            prefix: "https://twitter.com/",
          },
          {
            key: "telegram_url",
            icon: <Telegram className="icon socail" />,
            prefix: "http://t.me/",
          },
          {
            key: "discord_url",
            icon: <Discord className="icon socail" />,
            prefix: "",
          },
        ].map((item) => {
          return baseData[item.key] ? (
            <a href={`${item.prefix}${baseData[item.key]}`} key={item.key}>
              {item.icon}
            </a>
          ) : (
            <React.Fragment key={item.key} />
          );
        })}
      </>
    );
  }, [baseData]);

  useEffect(() => {
    if (baseData) {
      setLike(baseData.is_like);
    }
  }, [baseData]);

  const showTransfer = useCallback(() => {
    setShowVote(true);
  }, []);

  const shareLink = useMemo(() => {
    return `${window.location.protocol}//${window.location.host}/#/dao/${props.fullhandle}/homepage/info`;
  }, [props.fullhandle]);

  const closeVote = useCallback(() => {
    setShowVote(false);
  }, []);

  useEffect(()=>{
    document.addEventListener("click",()=>{
      setShow(false);
    })
  },[])
  const handleMore = (e) =>{
    e.nativeEvent.stopImmediatePropagation();
    setShow(!show);
  }

  const transferVisible = useMemo(() => {
    return (isGP || isOwner) && (baseData.period === PERIOD_NO.MANAGEMENT || baseData.period === PERIOD_NO.REDEMPTION);
  }, [isGP, isOwner, baseData]);

  return (
    <TopBox>
      {showVote && (
        <NewVote
          selectedAction={true}
          selectedType={2}
          CloseModal={closeVote}
        />
      )}
      {showModal && <EditDAO data={baseData} closeModal={closeModal} />}
      {baseData.logo ? (
        <Logo src={gateway + baseData.logo} alt="" />
      ) : (
        <NoneLogo src={LogoSVG} alt="" />
      )}

      <Rht>
        <div>
          <TitleLine>
            <TitleBox className="font-bold">{baseData.name}</TitleBox>
            {PeriodBar}
            <RatioBox className="font-bold">{voteConfig}</RatioBox>
            <ImgUl>
              {like ? (
                <LikeIcon className="icon" onClick={clickLike} />
              ) : (
                <UnlikeIcon className="icon" onClick={clickLike} />
              )}
              <CopyButton value={shareLink} style={{ marginLeft: "20px" }}>
                <ShareSvg />
              </CopyButton>
              {
                isOwner && <EditSvg className="icon" onClick={() => handleEdit()} />
              }
              {socailUrls}
            </ImgUl>
          </TitleLine>

          <Description>
            {
              show && <DescAll>{baseData.desc}</DescAll>
            }

            {baseData.desc?.slice(0,300)}
            {
              baseData?.desc?.length>300 &&<span className="more" onClick={handleMore} >
              <strong>...</strong>
                {t("Home.More")}
            </span>
          }

          </Description>
          <UlBox>
            <li>
              <div className="font-demiBold">{t("Home.Address")}</div>
              <span>{publicJs.AddresstoShow(vaultContractAddress)}</span>
              <CopyStr address={vaultContractAddress} />
              <a
                href={`${exploreScan}address/${vaultContractAddress}`}
                rel="noreferrer"
                target="_blank"
              >
                <img
                  src={LinkImg}
                  style={{ width: "24px", marginLeft: "10px" }}
                  alt=""
                />
              </a>
            </li>
            <li>
              <div className="font-demiBold">{t("Home.Holders")}</div>
              <span>{baseData.et_holders}</span>
              <a
                href={`${exploreScan}token/${stockTokenAddress}#balances`}
                rel="noreferrer"
                target="_blank"
              >
                <img
                  src={LinkImg}
                  style={{ width: "24px", marginLeft: "10px" }}
                  alt=""
                />
              </a>
              {/* <LinkSvg /> */}
            </li>
          </UlBox>
        </div>
        <RhtBox>
          <PriceBox className="font-black">
            {baseData.tav} <span className="font-regular">USDT</span>
          </PriceBox>
          {
            transferVisible && <ButtonTag onClick={showTransfer}>{t("Home.Transfer")}</ButtonTag>
          }
        </RhtBox>
      </Rht>
    </TopBox>
  );
});

export default DaoHeader;

