import ConnectContract from './connectContract';
import Accounts from "./Account";
import publicJs from "../utils/publicJs";

import {ethers} from 'ethers'
import abi from '../abi/web3Abi/VaultManager.json'
import NFTabi from "../abi/web3Abi/MockERC721.json";


const InitVault = async (state, dispatch, address,cb) =>  {
    let vaultcontract;
    const {apiState, api} = state;

    if (apiState !== 'READY' ) return;

    try {
        vaultcontract = await ConnectContract(api, 'vault', address);
        dispatch({ type: 'SET_VAULT', payload: vaultcontract });
        if(vaultcontract){
            cb(true)
        }
    } catch (e) {
        console.error(e);
    }
    return vaultcontract;

}

const value = 0;
const gasLimit = -1;

const getBalanceOf = async (vaultcontract,erc20) => {

    const AccountId = await Accounts.accountAddress();
    if (vaultcontract === null || !vaultcontract || !vaultcontract.query || !AccountId) return;

    let data = await vaultcontract.query.getBalanceOf(AccountId, {value, gasLimit},erc20);
    data = publicJs.formatResult(data);
    return data;

};

const getContract = async (web3Instance, vaultAddress) => {
    return await new ethers.Contract(vaultAddress, abi, web3Instance);
}

const getNFTContract = async (web3Instance, vaultAddress) => {
    return await new ethers.Contract(vaultAddress, NFTabi, web3Instance);
}

const getManageFeeContract = async (web3Instance, address) => {
    return await new ethers.Contract(address, abi, web3Instance);
}

const getTokenList = async (vaultContract) => {
    return await vaultContract.getTokenList()
};

const getTokenBalance = async (vaultContract, tokenAddress) => {
    return await vaultContract.getBalanceOf(tokenAddress)
}

const getBalanceList = async (vaultcontract) => {

    if (vaultcontract === null || !vaultcontract || !vaultcontract.query) return;
    let data = await vaultcontract.query.getBalance('', {value, gasLimit},0,10000);
    data = publicJs.formatResult(data);


    return data;
};

const getTransferHistory = async (vaultcontract) => {

    if (vaultcontract === null || !vaultcontract || !vaultcontract.query) return;


    let data = await vaultcontract.query.getTransferHistory('', {value, gasLimit},0,10000);

    data = publicJs.formatResult(data);

    return data;
};

const withdraw = async (vaultcontract,obj,cb) => {
    const AccountId = await Accounts.accountAddress();
    const injector = await Accounts.accountInjector();

    const { address, amount, selected} = obj;

    if (vaultcontract === null || !vaultcontract || !vaultcontract.query || !AccountId) return;
    let data;

    await vaultcontract.tx.withdraw({value, gasLimit}, selected,address, amount)
        .signAndSend(AccountId, { signer: injector.signer }, (result) => {
        if (result.status.isFinalized) {
            // console.log("====result.status.isInBlock",result.status.isInBlock || result.status.isFinalized);
            // data = (result.status.isInBlock || result.status.isFinalized);
            // return
            cb(true)
        }
    });
    return data;
}

const checkAuthority = async (vaultcontract) => {

    const AccountId = await Accounts.accountAddress();
    if (vaultcontract === null || !vaultcontract || !vaultcontract.query || !AccountId) return;

    let data = await vaultcontract.query.checkAuthority(AccountId, {value, gasLimit},AccountId);
    data = publicJs.formatResult(data);

    return data;
};


const transfer = async (daoOrg, signer, obj) => {
    const { tokenAddress, account,  amount, deposit} = obj
    const templateAction = ethers.utils.defaultAbiCoder.encode(
        ['address', 'bytes'],
        [
            window.mainAddress.actionVaultDepositToken,
            ethers.utils.defaultAbiCoder.encode(
                ['address', 'address', 'uint256', 'bool'],
                [
                    tokenAddress, 
                    account,
                    amount,
                    deposit // true is deposit; false is withdraw
                ]
            )
        ]
    );

    const res =  await daoOrg.connect(signer).action(templateAction);
    await res.wait()
}

const transfer2 = async (erc20_rw, to_address, amount, deci) => {
    const res = await erc20_rw.transfer(to_address, ethers.utils.parseUnits(String(amount), deci))
    await res.wait()
}

