import { gql } from "@apollo/client";


// 01-1-1
export const LoginQuery = gql`
  query MyQuery($sign: String!, $account: String!, $timestamp: Int!, $chainId: Int!) {
    login(sign: $sign, input: { chain_id: $chainId, wallet: $account, timestamp: $timestamp }) {
      token
      tokenExp
    }
  }
`;