import { useMemo, useCallback } from "react";
import styled from "styled-components";
import { useWeb3React } from "@web3-react/core";
import { useTranslation } from "react-i18next";

import { useAppContext } from "components/provider/appProvider";
import { SUPPORTED_WALLETS } from "components/wallet/wallet";
import Button from "components/common/button";
import api from "api";
import useSigner from "hooks/useSigner";
import SendModal from "./sendModal";
import { useState } from "react";
import useGateway from "hooks/useGateway";
import LogoSVG from "img/defaultLogo.svg";
import {useNavigate} from "react-router-dom";
import {useNetworkData} from "../../hooks/useNetwork";

const TableList = (props) => {
  const { list, updatelist } = props;
  const { t } = useTranslation();
  const { provider,chainId } = useWeb3React();
  const {
    state: { selectedWallet },
    dispatch,
  } = useAppContext();
  const navigator = useNavigate();
  const gateway = useGateway();
  const networkData = useNetworkData(chainId);

  const wallet = useMemo(() => {
    const currentWallet = Object.values(SUPPORTED_WALLETS).find(
      (w) => w.wallet === selectedWallet
    );
    return currentWallet ? (
      <WalletIcon src={currentWallet.iconURL} alt="" />
    ) : (
      <></>
    );
  }, [selectedWallet]);
  const [sendToken, setSendToken] = useState()

  const addToken = useCallback(
    async (token) => {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.AddingToken") });
      let wasAdd;
      try {
        wasAdd = await provider.send("wallet_watchAsset", {
          type: "ERC20",
          options: {
            address: token.address,
            symbol: token.symbol,
            decimals: 18,
            image: `https://avatars.dicebear.com/api/jdenticon/${token.symbol}.svg`,
          },
        });
      } catch (error) {
        console.error("addToken failed", error);
      } finally {
        dispatch({ type: "LOADINGTYPE", payload: null });
        if (!wasAdd) {
          dispatch({
            type: "MSGTYPE",
            payload: { msg: t("Msg.AddTokenFailed"), closable: true },
          });
        }
      }
    },
    [provider]
  );

  const transferToken = useCallback(async (token) => {
    setSendToken(token)
  }, []);

  const closeTransfer = useCallback((updateFlag) => {
    if (updateFlag) {
      updatelist && updatelist(sendToken)
    }
    setSendToken(undefined)

  }, [])


  const jumpDetail =(item) => {
    console.log(item)
    navigator(`/dao/${networkData?.simple}:${item.dao_display_handle}/homepage/info`);
  };


  return (
    <TableBox>
      <SendModal
        show={!!sendToken}
        token={sendToken}
        closeModal={closeTransfer}
      />
      <table cellPadding="0" cellSpacing="0">
        <thead>
          <tr>
            <th>DAO</th>
            <th>{t("Explore.EquityToken")}</th>
            <th>{t("Explore.Amount")}</th>
            <th>&nbsp;</th>
            <th>{t("Explore.Add2wallet")}</th>
          </tr>
        </thead>
        <tbody>
          {list.map((item, index) => (
            <tr key={index}>
              <td  onClick={()=>jumpDetail(item)}>
                <img
                  className="logo"
                  src={(gateway && item.dao_logo) ? gateway+item.dao_logo : LogoSVG}
                  alt=""
                />
               {item.dao_name}
              </td>
              <td>{item.symbol}</td>
              <td>{item.balanceDisplay}</td>
              <td>
                <Button
                  width={90}
                  height={36}
                  style={{ fontSize: "16px" }}
                  primary
                  onClick={() => {
                    transferToken(item);
                  }}
                >
                  {t("Send")}
                </Button>
              </td>
              <td>
                <span onClick={() => addToken(item)}>{wallet}</span>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </TableBox>
  );
};

export default TableList;

const TableBox = styled.div`
  margin-top: 30px;
  height: calc(100% - 80px);
  overflow-y: auto;
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none; /* Chrome Safari */
  }
  table {
    width: 100%;
  }
  th {
    background: #f5f5f7;
    height: 54px;
    color: #b2b4c5;
    font-size: 16px;
    &:first-child {
      border-top-left-radius: 14px;
      border-bottom-left-radius: 14px;
      text-align: left;
      padding-left: 50px;
    }
    &:last-child {
      border-top-right-radius: 14px;
      border-bottom-right-radius: 14px;
    }
  }
  td {
    text-align: center;
    white-space: nowrap;
    padding: 8px 0;

    font-size: 16px;
    font-weight: 400;
    color: #10164b;
    line-height: 22px;
    vertical-align: middle;
    &:first-child {
      text-align: left;
      padding-left: 50px;
    }
  }
  tr:hover {
    td {
      background: #fbfbfa;
      &:first-child {
        border-top-left-radius: 14px;
        border-bottom-left-radius: 14px;
      }
      &:last-child {
        border-top-right-radius: 14px;
        border-bottom-right-radius: 14px;
      }
    }
  }
  .symbol {
    width: 28px;
    height: 28px;
    margin-right: 8px;
  }
  .finished {
    width: 20px;
    margin-right: 10px;
  }
  .logo {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    margin-right: 17px;
  }
`;

const WalletIcon = styled.img`
  width: 34px;
  height: 34px;
  cursor: pointer;
`;
