import { useState, useEffect, memo, useMemo, useCallback } from "react";
import { useWeb3React } from "@web3-react/core";
import styled from "styled-components";
import { ethers } from "ethers";
import LinkImg from "../../img/newHome/link.png";
import SelectedImg from "../../img/Selected.png";
import CopyStr from "../copy";
import publicJs from "../../utils/publicJs";
import { useDaoContext } from "../../views/dao/provider";
import api from "../../api";
import { VAULT_MANAGER } from "../../utils/constant";

import { useLazyQuery } from "@apollo/client";
import { SubgraphApiQuery, getVaultHistory } from "api/graphql/subQuery";

import PaginationBox from "components/common/pagination";
import EmptyBox from "components/common/empty";
import { useAppContext } from "components/provider/appProvider";
import moment from "moment";

const VaultTypeItem = memo((props) => {
  const isIn = props.type === 2;
  return <TagBox className={isIn && "in"}>{isIn ? "IN" : "OUT"}</TagBox>;
});

const TransferHistory = memo(() => {
  const { provider: web3Instance, chainId } = useWeb3React();
  const {
    state: { exploreScan,tokens },
  } = useAppContext();
  const { state: daoState } = useDaoContext();
  const { componentAddressMap } = daoState;

  const [page, setPage] = useState(0);
  const [pageSize, setPageSize] = useState(10);
  const [total, setTotal] = useState(0);
  const [historylist, sethistorylist] = useState([]);

  const vaultAddress = useMemo(() => {
    return componentAddressMap.get(VAULT_MANAGER);
  }, [componentAddressMap]);

  const [queryHistory, { loading }] = useLazyQuery(SubgraphApiQuery, {
    onCompleted: async (data) => {
      const res = JSON.parse(data.subgraphApi);
      console.log("====", res);
      try {
        const lst = [];
        for (const v of res.data.vaults) {
          if (!v.toAddress) v.toAddress = v.vaultContractAddress;
          const tokenContract = await api.erc20.getContract(
            web3Instance,
            v.tokenAddress
          );
          v.tokenName = await tokenContract.symbol();
          v.amount = ethers.utils.formatUnits(
            v.amount,
            await tokenContract.decimals()
          );
          if (!v.fromAddress) v.fromAddress = v.vaultContractAddress;
          lst.push(v);
        }
        sethistorylist(lst);
        setTotal(res.data.totalCount && Number(res.data.totalCount.count));
      } catch (error) {
        console.error("parse daoByHandleAndChainId failed", error);
      }
    },
    onError: (error) => {
      console.error("====", error);
    },
  });

  useEffect(() => {
    if (!vaultAddress) return;
    queryHistory({
      variables: {
        query: getVaultHistory(
          vaultAddress.toLocaleLowerCase(),
          page,
          pageSize
        ),
        chainId,
      },
    });
  }, [chainId, vaultAddress, page, pageSize]);

  const onChangePage = useCallback((v) => {
    setPage(v);
  }, []);

  const onShowSizeChange = useCallback((c, size) => {
    setPageSize(size);
  }, []);

  const switchToken = useCallback((add) =>{
    if(!tokens.length) return"";
    const img = tokens.filter(item=>item.address.toLowerCase() === add.toLowerCase())
    return img[0]?.logo;
  },[tokens])

  if (!historylist.length) {
    return <EmptyBox />;
  }

  return (
    <>
      <TableBox>
        <table cellPadding="0" cellSpacing="0">
          <thead>
            <tr>
              <th>No.</th>
              <th>Token</th>
              <th>From</th>
              <th>To</th>
              <th>State</th>
              <th>Time</th>
            </tr>
          </thead>
          <tbody>
            {historylist.map((item, i) => (
              <tr key={i}>
                <td>{i + 1}</td>
                <TD>
                  <div>
                    <img src={switchToken(item.tokenAddress)} alt="" className="symbol" />
                    {item.amount}
                    <span>{item.tokenName}</span>
                  </div>
                </TD>
                <TD>
                  <div>
                    <span>{publicJs.AddresstoShow(item.fromAddress)} </span>
                    <CopyStr address={item.fromAddress} />
                  </div>
                </TD>
                <TD>
                  <div>
                    <span>{publicJs.AddresstoShow(item.toAddress)} </span>
                    <CopyStr address={item.toAddress} />
                    <VaultTypeItem type={item.vaultType} />
                    <a
                      href={`${exploreScan}tx/${item.txHash}`}
                      rel="noreferrer"
                      target="_blank"
                    >
                      <img style={{width: "24px"}} src={LinkImg} alt="" />
                    </a>
                  </div>
                </TD>
                <td>
                  <img src={SelectedImg} alt="" className="finished" />
                  Success
                </td>
                <td>{moment(Number(item.blockTimestamp)*1000).format(
                  "MMMM Do YYYY, h:mm A"
                  )}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </TableBox>
      <Pagniation>
        <PaginationBox
          showQuickJumper
          pageSize={pageSize}
          defaultCurrent={page}
          total={total}
          onChange={onChangePage}
          onShowSizeChange={onShowSizeChange}
        />
      </Pagniation>
    </>
  );
});

export default TransferHistory;

const TableBox = styled.div`
  height: calc(100% - 50px);
  overflow: auto;
  table {
    width: 100%;
  }
  th {
    background: #f5f5f7;
    height: 54px;
    color: #b2b4c5;
    font-size: 16px;
    &:first-child {
      border-top-left-radius: 14px;
      border-bottom-left-radius: 14px;
      padding-left: 20px;
    }
    &:last-child {
      border-top-right-radius: 14px;
      border-bottom-right-radius: 14px;
    }
  }
  td {
    text-align: center;
    height: 54px;
    white-space: nowrap;

    font-size: 14px;
    font-weight: 400;
    color: #0e164e;
    &:first-child {
      padding-left: 20px;
    }
  }
  tr:hover {
    td {
      background: #fbfbfa;
      &:first-child {
        border-top-left-radius: 14px;
        border-bottom-left-radius: 14px;
      }
      &:last-child {
        border-top-right-radius: 14px;
        border-bottom-right-radius: 14px;
      }
    }
  }
  .symbol {
    width: 28px;
    height: 28px;
    margin-right: 8px;
    border-radius: 28px;
  }
  .finished {
    width: 14px;
    margin-right: 6px;
  }
`;

const TagBox = styled.span`
  display: inline-block;
  width: 38px;
  height: 22px;
  background: #f8ebce;
  border-radius: 6px;
  margin-left: 40px;
  font-size: 12px;
  font-weight: 400;
  color: #a68946;
  line-height: 22px;
  margin-right: 6px;
  &.in {
    background: #cef8d7;
    color: #46a66a;
  }
`;

const TD = styled.td`
  > div {
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;

const Pagniation = styled.div`
  text-align: right;
  position: absolute;
  right: 0;
  bottom: 10px;
`;
