import { useCallback, useState } from "react";
import styled from "styled-components";
import SwapIndex from "./swapIndex";
import SwapLoading from "./swaploading";
import Status from "./Status";
import BackNav from "components/common/backNav";
import { useTranslation } from "react-i18next";

import SwapProvider from "components/provider/swapProvider";
import SwapPending from "./SwapPending";

export default function Swap() {
  const [showIndex, setShowIndex] = useState(true);
  const [showStatus, setShowStatus] = useState(false);

  const [pendingStatus, setPendingStatus] = useState(false);

  const closePending = useCallback(() => {
    setPendingStatus(false);
  }, []);
  const showPending = useCallback(() => {
    setPendingStatus(true);
  }, []);

  return (
    <SwapProvider>
      <Box>
        {pendingStatus && <SwapPending closeModal={closePending} />}
        <BackNav to="app_settings">
          {/* <BtnRht> */}
            {/* <span onClick={showPending}>1 Pending</span>{" "} */}
            {/* <SwapLoading
            width="14"
            height="14"
            backColor="#D52473"
            foreColor="#fff"
          /> */}
          {/* </BtnRht> */}
        </BackNav>
        <ContentBox id="contentBox">
          <div id="inner">
            {showIndex && <SwapIndex />}
            {showStatus && <Status />}
          </div>
        </ContentBox>
      </Box>
    </SwapProvider>
  );
}

const Box = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 40px 20px;
  background: linear-gradient(180deg, #f3f0fd 0%, #f9f9fc 100%);
  border-radius: 24px;
`;

const ContentBox = styled.div`
  flex-grow: 1;
  #inner {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 550px;
  }
`;
const BtnRht = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  width: 120px;
  height: 34px;
  background: #d52473;
  border-radius: 8px;
  span {
    margin-right: 8px;
  }
`;
