import { gql } from "@apollo/client";

export const QueryVoteQueue = gql`
  query MyQuery($chainId: Int!, $daoId: Int!, $page: Int!, $size: Int!) {
    voteQueueList(
      chainID: $chainId
      daoID: $daoId
      page: { page: $page, size: $size, sort_field: "vote_id", order: "desc" }
    ) {
      data {
        action
        agree_count
        creator
        start_time
        vote_time
        create_at
        dao_id
        metadata
        oppose_count
        vote_contract_address
        vote_id
      }
      page
      size
      total
    }
  }
`;

export const QueryVoteHistory = gql`
  query MyQuery($chainId: Int!, $daoId: Int!, $page: Int!, $size: Int!) {
    voteHistoryList(
      chainID: $chainId
      daoID: $daoId
      page: { page: $page, size: $size }
    ) {
      data {
        agree_count
        start_time
        vote_time
        metadata
        oppose_count
        vote_id
        status
      }
      page
      size
      total
    }
  }
`;

export const QueryVoteById = gql`
  query MyQuery($chainId: Int!, $daoId: Int!, $voteId: Int!) {
    voters: castVoteList(chainID: $chainId, daoID: $daoId, voteID: $voteId) {
      address
      support
    }
    info: vote(chainID: $chainId, daoID: $daoId, voteID: $voteId) {
      agree_count
      create_at
      creator
      metadata
      status
      tx_hash
      vote_id
      oppose_count
      start_time
      vote_time
      executor
    }
    action: voteAction(chainID: $chainId, daoID: $daoId, voteID: $voteId)
  }
`;
