import { useEffect, memo, useState } from "react";
import { useWeb3React } from "@web3-react/core";
import { ethers } from "ethers";
import { useVaultContract } from "hooks/useContract";
import { useDaoContext } from "views/dao/provider";
import { VAULT_MANAGER } from "utils/constant";

import api from "api";
import { AddressBox } from "./styles";
import CopyStr from "components/copy";
import LinkImg from "img/shareGray.png";

const TransferTokenVote = memo((props) => {
  const { wid } = props;

  const { provider } = useWeb3React();
  const {
    state: { componentAddressMap },
  } = useDaoContext();
  const vaultContract = useVaultContract(
    componentAddressMap.get(VAULT_MANAGER)
  );

  const [toAddress, setToAddress] = useState();
  const [tokenSymbol, setTokenSymbol] = useState("");
  const [toValue, setTovalule] = useState("");

  useEffect(async () => {
    if (wid === undefined || !vaultContract || !provider) {
      return;
    }
    const wdata = await api.vault.getTransferDetail(vaultContract, wid);
    setToAddress(wdata.proposer);
    const erc20 = await api.erc20.getContract(provider, wdata.token);

    setTokenSymbol(await erc20.symbol());
    setTovalule(
      ethers.utils.formatUnits(wdata.amount.toString(), await erc20.decimals())
    );
  }, [wid, vaultContract, provider]);
  return (
    <>
      <dl>
        <dt>Recipient</dt>
        <AddressBox>
          {toAddress} <CopyStr address={toAddress} gray={true} />
          <img src={LinkImg} alt="" />
        </AddressBox>
      </dl>
      <dl>
        <dt>Amount</dt>
        <dd className="flexLine">
          <span className="num font-bold">{toValue}</span>
          <img src="" alt="" className="symbolBox" />
          <span className="symbol">{tokenSymbol}</span>
        </dd>
      </dl>
    </>
  );
});

export default TransferTokenVote;
