import React, { memo, useMemo, useState, useCallback, useEffect } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

import { useLazyQuery } from "@apollo/client";
import { SearchDaos } from "api/graphql/explore";
import { useWeb3React } from "@web3-react/core";

import PaginationBox from "components/common/pagination";
import DaoItem from "components/common/daoItem";
import { useNetworkData } from "hooks/useNetwork";
import EmptyBox from "components/common/empty";

const SearchTab = memo((props) => {
  const { keyword ,gateway} = props;
  const { t } = useTranslation();
  const { chainId } = useWeb3React();
  const networkData = useNetworkData(chainId);

  const [page, setPage] = useState(1);
  const [pageSize, setPagesize] = useState(10);
  const [total, setToal] = useState(0);

  const [list, setList] = useState([]);

  const [goSearch] = useLazyQuery(SearchDaos, {
    onCompleted(data) {
      console.log("===", data);
      const search = data.searchDao;
      setToal(search.total);
      setList(search.data);
    },
  });

  useEffect(() => {
    return () => {
      setList([]);
      setPage(1);
    };
  }, []);

  useEffect(async () => {
    if (!chainId) {
      return;
    }
    try {
      goSearch({
        variables: {
          page: page,
          size: pageSize,
          chainId: chainId,
          keyword,
        },
      });
    } catch (error) {
      console.error(error);
    }
  }, [chainId, page, pageSize, keyword]);

  const onChangePage = useCallback((v) => {
    setPage(v);
  }, []);

  const onShowSizeChange = useCallback((c, size) => {
    setPagesize(size);
  }, []);

  return (
    <>
      <SearchLabel>{t("Explore.SearchLabel", { val: keyword })}</SearchLabel>
      <ListBox>
        <UlBox>
          {list.map((dao, i) => (
            <DaoItem
              key={i}
              gateway={gateway}
              data={dao}
              networkSimple={networkData && networkData.simple}
            />
          ))}
        </UlBox>
      </ListBox>
      {list.length ? (
          <PageBox>
            <PaginationBox
                showQuickJumper
                pageSize={pageSize}
                defaultCurrent={page}
                total={total}
                onChange={onChangePage}
                onShowSizeChange={onShowSizeChange}
            />
          </PageBox>

      ) : (
        <EmptyBox />
      )}
    </>
  );
});
export default SearchTab;

const ListBox = styled.div``;

const UlBox = styled.ul`
  &:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }
`;

const SearchLabel = styled.div`
  height: 25px;
  font-size: 18px;
  line-height: 25px;
`;

const PageBox = styled('div')`
  padding:40px 0;  
`
