import styled from "styled-components";
import { memo, useMemo } from "react";
import CopyStr from "../copy";
import { useDaoContext } from "../../views/dao/provider";
import {
  VOTE_MANAGER,
  VAULT_MANAGER,
  ORG_MANAGER,
  VENTURE_MANAGER,
  VENTURE_STOCK_MANAGER,
} from "../../utils/constant";

const Contracts = memo(() => {
  const { state: daoState } = useDaoContext();
  const { componentAddressMap } = daoState;

  const contractsList = useMemo(() => {
    return [
      { name: "Vault", key: VAULT_MANAGER },
      { name: "Vote", key: VOTE_MANAGER },
      { name: "Org", key: ORG_MANAGER },
      { name: "Venture", key: VENTURE_MANAGER },
      { name: "Token", key: VENTURE_STOCK_MANAGER },
    ];
  }, []);

  return (
    <UlBox>
      {contractsList.map((item, index) => (
        <dl key={index}>
          <dt>
            <div className={`dotBg dot${index + 1}`} />
            <span>{item.name}</span>
          </dt>
          <dd>
            <span>{componentAddressMap.get(item.key)}</span>
            <CopyStr address={componentAddressMap.get(item.key)} />
            {/*<img src={CopyImg} alt=""/>*/}
          </dd>
        </dl>
      ))}
    </UlBox>
  );
});

export default Contracts;

const UlBox = styled.div`
  padding: 0 0 0 40px;
  dl,
  dt,
  dd {
    display: flex;
    align-items: center;
  }
  dl {
    padding-left: 22px;
    margin-left: -32px;
    height: 45px;
    max-width: 667px;
    &:hover {
      background: #fbfbfa;
      border-radius: 14px;
    }
  }
  dt {
    width: 120px;
  }
  dd {
    padding-top: 7px;
  }

  .dotBg {
    width: 12px;
    height: 12px;
    border-radius: 12px;
    margin-right: 12px;
  }
  .dot1 {
    background: #d1ebd7;
  }
  .dot2 {
    background: #f8f8da;
  }
  .dot3 {
    background: #ecdada;
  }
  .dot4 {
    background: #d5d8ed;
  }
  .dot5 {
    background: #f5e7cd;
  }
`;
