import React, {
  memo,
  useMemo,
  useState,
  useRef,
  useEffect,
  useCallback,
} from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { useDaoContext } from "../../views/dao/provider";
import useVenturePeriod from "../../hooks/useVenturePeriod";
import { useDaoOrgContract, useVentureContract } from "../../hooks/useContract";
import { DAO_ORG, VENTURE_MANAGER } from "../../utils/constant";
import DepositModal from "../modal/deposit";
import Withdraw from "../modal/withdraw";
import WithdrawBonus from "../modal/withdrawBonus";
import ConfirmBox from "components/modal/confirmModal";
import useSigner from "hooks/useSigner";
import { useAppContext } from "components/provider/appProvider";
import { PERIOD_NO } from "utils/constant";
import api from "api";

const VenturePeriodPanel = memo(() => {
  const {
    state: { componentAddressMap, fundOrPeriod },
  } = useDaoContext();

  const ventureContract = useVentureContract(
    componentAddressMap.get(VENTURE_MANAGER)
  );
  const period = useVenturePeriod(ventureContract, fundOrPeriod);

  const PeriodComponent = useMemo(() => {
    switch (period) {
      case PERIOD_NO.RAISE:
        return <RaisingPeriod />;
      case PERIOD_NO.INRETURN:
        return <InReturnPeriod />;
      case PERIOD_NO.MANAGEMENT:
        return <ManagementPeriod />;
      case PERIOD_NO.REDEMPTION:
        return <RedemptionPeriod />;
      default:
        return <Period />;
    }
  }, [period]);

  return PeriodComponent;
  // return <RedemptionPeriod />
});

export default VenturePeriodPanel;

// period = 1
const RaisingPeriod = memo(() => {
  const { t } = useTranslation();

  const { state: daoState, dispatch: dispatchEvent } = useDaoContext();
  const {
    componentAddressMap,
    whoAmI: { isOwner, isGP },
    stockToken,
    fundInfo,
  } = daoState;
  const { dispatch } = useAppContext();

  const ventureContract = useVentureContract(
    componentAddressMap.get(VENTURE_MANAGER)
  );
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const [targetTime, setTargetTime] = useState(0);
  const [leftTime, setLeftTime] = useState(0);
  const TargetRef = useRef(leftTime);

  const [showDeposit, setShowDeposit] = useState(false);

  useEffect(() => {
    TargetRef.current = targetTime;
  });

  const reachTarget = useMemo(() => {
    console.log("[reachTarget]", fundInfo);

    if (!fundInfo) return false;

    return Number(fundInfo.raised) >= Number(fundInfo.goal) ? true : false;
  }, [fundInfo]);

  const generateCard = useCallback((num) => {
    if (!num && isNaN(num)) {
      return <></>;
    }
    const numstr = `${num < 10 ? "0" : ""}${num}`.split("");
    return numstr.map((s, i) => (
      <NumBox key={i} className="font-black">
        {s}
      </NumBox>
    ));
  }, []);

  useEffect(() => {
    const timerFunc = () => {
      const currentTime = parseInt(Date.now() / 1000);
      const delta = TargetRef.current - currentTime;
      // TODO check
      //   if (delta === 0) {
      //     props.update && props.update();
      //   }
      if (delta < 0) return;
      setLeftTime(delta);
    };
    timerFunc();
    const timer = setInterval(timerFunc, 1000);
    return () => clearInterval(timer);
  }, []);

  useEffect(async () => {
    if (ventureContract) {
      const stopTime = await ventureContract.RaiseSubTime();
      setTargetTime(stopTime.toNumber());
    }
  }, [ventureContract]);

  const LeftTimeDisplay = useMemo(() => {
    const days = parseInt(leftTime / 86400);
    let left = leftTime % 86400;
    const hours = parseInt(left / 3600);
    left = left % 3600;
    const minutes = parseInt(left / 60);
    const seconds = left % 60;

    return {
      days: generateCard(days),
      hours: generateCard(hours),
      minutes: generateCard(minutes),
      seconds: generateCard(seconds),
    };
  }, [leftTime]);

  const handleFreeze = useCallback(async () => {
    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    try {
      await api.venture.freeze(daoOrg, signer);
      dispatchEvent({ type: "UPDATE_FUND_OR_PERIOD", payload: "period" });
    } catch (error) {
      console.error("confirm failed", error);
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  }, []);

  const handleClose = useCallback(async () => {
    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    try {
      await api.venture.closeVenture(daoOrg, signer);
      dispatchEvent({ type: "UPDATE_FUND_OR_PERIOD", payload: "period" });
    } catch (error) {
      console.error("confirm failed", error);
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  }, []);

  if (!targetTime) {
    return <Period />;
  }

  // reach target
  if (leftTime <= 0 && isOwner) {
    return reachTarget ? (
      <Period>
        <span>{t("Period.FreezingPeriod")}</span>
        <div className="rht">
          <Deposit onClick={handleFreeze}>{t("Period.Start")}</Deposit>
        </div>
      </Period>
    ) : (
      <Period>
        <span>{t("Period.InReturnPeriod")}</span>
        <div className="rht">
          <Deposit onClick={handleClose}>{t("Period.Close")}</Deposit>
        </div>
      </Period>
    );
  }

  return (
    <RemainBox>
      <div className="lft">
        {LeftTimeDisplay.days}
        <span className="day">{t("Period.Days")}</span>
        {LeftTimeDisplay.hours}
        <span>:</span>
        {LeftTimeDisplay.minutes}
        <span>:</span>
        {LeftTimeDisplay.seconds}
        <span className="leftBox">{t("Period.Left")}</span>
      </div>
      {leftTime > 0 ? (
        <>
          <DepositModal
            show={showDeposit}
            stockToken={stockToken || {}}
            closeDeposit={() => {
              setShowDeposit(false);
            }}
          />
          <Deposit
            onClick={() => {
              setShowDeposit(true);
            }}
          >
            {t("Period.Deposit")}
          </Deposit>
        </>
      ) : (
        <></>
      )}
    </RemainBox>
  );
});

// period = 2
const InReturnPeriod = memo(() => {
  const { t } = useTranslation();
  const { state: daoState, dispatch: dispatchEvent } = useDaoContext();
  const { componentAddressMap } = daoState;
  const { dispatch } = useAppContext();

  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const [showConfirm, setShowConfirm] = useState(false);
  const [showConfirmTip, setShowConfirmTip] = useState("");

  const showConfimBox = useCallback(() => {
    setShowConfirm(true);
    setShowConfirmTip(t("handle withdraw"));
  }, []);

  const handleConfirm = async () => {
    setShowConfirm(false);
    try {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
      await api.venture.refund(daoOrg, signer);
      dispatchEvent({ type: "UPDATE_FUND_OR_PERIOD", payload: "fund" });
    } catch (error) {
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: "withdraw failed",
          closable: true,
        },
      });

      console.error("confirm failed", error);
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  return (
    <Period>
      <ConfirmBox
        show={showConfirm}
        message={showConfirmTip}
        handleConfirm={handleConfirm}
        handleClose={() => setShowConfirm(false)}
      />
      <span>{t("Period.InReturnPeriod")}</span>

      <div className="rht">
        <Deposit onClick={showConfimBox}>{t("Period.Withdraw")}</Deposit>
      </div>
    </Period>
  );
});

// period = 3
const ManagementPeriod = memo(() => {
  const { t } = useTranslation();
  return (
    <Period>
      <span>{t("Period.FreezingPeriod")}</span>
    </Period>
  );
});

// period = 4
const RedemptionPeriod = memo(() => {
  const { t } = useTranslation();
  const [showBonus, setShowBonus] = useState(false);
  const [showWithdraw, setShowWithdraw] = useState(false);
  return (
    <Period>
      <Withdraw
        show={showWithdraw}
        close={() => {
          setShowWithdraw(false);
        }}
      />
      <WithdrawBonus
        show={showBonus}
        close={() => {
          setShowBonus(false);
        }}
      />
      <span>{t("Period.RedemptionPeriod")}</span>
      <div className="rht">
        <Deposit
          onClick={() => {
            setShowWithdraw(true);
          }}
        >
          {t("Period.Withdraw")}
        </Deposit>
        <Deposit
          onClick={() => {
            setShowBonus(true);
          }}
          className="bonus"
        >
          {t("Period.Bonus")}
        </Deposit>
      </div>
    </Period>
  );
});

