import { gql } from "@apollo/client";

/******** QUERY ********/
export const QueryAllDaos = gql`
  query MyQuery($page: Int!, $size: Int!, $chainId: Int!, $sort: String) {
    allDaoList(
      page: { page: $page, size: $size, order: "desc", sort_field: $sort }
      req: { chain_id: $chainId }
    ) {
      page
      size
      total
      data {
        dao_id
        desc
        display_handle
        et_holders
        is_like
        tav
        period
        name
        logo
      }
    }
  }
`;

