import { useEffect, useState, useMemo, useCallback } from "react";
import styled from "styled-components";
import CloseImg from "../../img/portfolio/close.png";
import SearchImg from "../../img/portfolio/search.png";
import { useAppContext } from "components/provider/appProvider";
import { useTranslation } from "react-i18next";
import useSigner from "hooks/useSigner";
import { useDaoOrgContract } from "hooks/useContract";
import { useDaoContext } from "views/dao/provider";
import { DAO_ORG } from "utils/constant";
import api from "api";
import { ethers } from "ethers";
import { TokenLogo } from "components/common/avatar";
import { useWeb3React } from "@web3-react/core";

export default function AddToken(props) {
  const { handleClose, checkExist } = props;
  const { t } = useTranslation();

  const { provider } = useWeb3React();
  const {
    state: { tokens },
    dispatch,
  } = useAppContext();
  const {
    state: { componentAddressMap },
  } = useDaoContext();
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const [keyword, setKeyword] = useState("");
  const [displayList, setDisplayList] = useState([]);
  const recommend = useMemo(() => {
    return tokens.filter((t) => t.symbol === "USDT" || t.symbol === "WETH");
  }, [tokens]);

  const handleSearchToken = useCallback(async (address) => {
    try {
      const contract = await api.erc20.getContract(provider, address);
      const deci = await contract.decimals();
      setDisplayList([
        {
          name: await contract.name(),
          symbol: await contract.symbol(),
          deci,
          address,
        },
      ]);
    } catch (error) {
      console.error("handleSearchToken failed", error)
      setDisplayList([]);
    }
  }, []);

  useEffect(() => {
    const key = keyword.trim().toLowerCase();
    if (key) {
      const filterList = tokens.filter(
        (t) =>
          t.name.toLowerCase().indexOf(key) > -1 ||
          t.symbol.toLowerCase().indexOf(key) > -1
      );
      if (!filterList.length && ethers.utils.isAddress(key)) {
        handleSearchToken(key);
      } else {
        setDisplayList(filterList);
      }
    } else {
      setDisplayList([...tokens]);
    }
  }, [keyword]);

  const addToken = async (address) => {
    if (checkExist(address)) {
      return;
    }
    try {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
      await api.vault.addToken(daoOrg, signer, address);
      handleClose(true);
    } catch (error) {
      console.error("addVaultToken failed", error);
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: `add failed: ${
            (error && error.data && error.data.message) || "unknown"
          }`,
          closable: true,
        },
      });
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };
  return (
    <AddBox>
      <Bg>
        <Title>
          <span className="font-medium">Add token</span>
          <CloseBox onClick={handleClose}>
            <img src={CloseImg} alt="" />
          </CloseBox>
        </Title>
        <SearchBox>
          <img src={SearchImg} alt="" />
          <input
            type="text"
            placeholder="Search name or paste address"
            value={keyword}
            onChange={(e) => setKeyword(e.target.value)}
          />
        </SearchBox>
        <BtnGroup>
          {recommend.map((item, index) => (
            <Btn key={index} onClick={() => addToken(item.address)}>
              <TokenLogo src={item.logo} />
              {item.symbol}
            </Btn>
          ))}
        </BtnGroup>
        <ScrollBox>
          <UlBox>
            {displayList.map((item, index) => (
              <li key={`list_${index}`} onClick={() => addToken(item.address)}>
                <TokenLogo src={item.logo} />
                <div>
                  <div className="name">{item.symbol}</div>
                  <div className="tips">{item.name}</div>
                </div>
              </li>
            ))}
          </UlBox>
        </ScrollBox>
      </Bg>
    </AddBox>
  );
}

const AddBox = styled.div`
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(247, 247, 247, 0.9);
  left: 0;
  top: 0;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const Bg = styled.div`
  width: 423px;
  height: 475px;
  background: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(16, 22, 75, 0.1);
  border-radius: 18px;
  padding: 30px 0;
`;
const Title = styled.div`
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  color: #10164b;
  line-height: 22px;
  position: relative;
  padding: 0 30px;
`;
const CloseBox = styled.div`
  position: absolute;
  right: 10px;
  top: -10px;
  cursor: pointer;
`;
const SearchBox = styled.div`
  height: 44px;
  background: #ffffff;
  border-radius: 22px;
  border: 1px solid #eeecee;
  display: flex;
  align-items: center;
  padding: 0 16px;
  margin: 20px 30px 0;
  img {
    margin-right: 7px;
  }
  input {
    border: 0;
    flex-grow: 1;
  }
`;

const BtnGroup = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin: 16px 0;
  padding: 0 30px 16px;
  border-bottom: 1px solid #eeecee;
`;
const Btn = styled.div`
  text-transform: uppercase;
  width: 100px;
  height: 34px;
  background: #f9faff;
  border-radius: 10px;
  border: 1px solid #eeecee;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 10px 10px 0;
  cursor: pointer;
  &.active {
    background: #d52473;
    color: #ffffff;
  }
  img {
    width: 24px;
    height: 24px;
    border-radius: 14px;
    margin-right: 12px;
  }
`;
const UlBox = styled.div`
  li {
    display: flex;
    align-items: center;
    padding: 13px 30px;
    cursor: pointer;
    &:hover {
      background: #fbfbfa;
    }
    img {
      width: 34px;
      height: 34px;
      border-radius: 34px;
      margin-right: 8px;
    }
    .name {
      font-size: 14px;
      font-weight: 400;
      color: #0e164e;
      line-height: 16px;
    }
    .tips {
      font-size: 12px;
      font-weight: 400;
      color: #afafc0;
      line-height: 14px;
    }
  }
`;
const ScrollBox = styled.div`
  overflow-y: auto;
  height: 220px;
`;
