import React, { useEffect, useState, useMemo } from "react";
import { Button, Input, Modal } from "antd";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { ethers } from "ethers";

import { useDaoContext } from "../../views/dao/provider";
import api from "../../api";
import {
  useDaoOrgContract,
  useERC20Contract,
  useVentureContract,
} from "../../hooks/useContract";
import { DAO_ORG, VAULT_MANAGER, VENTURE_MANAGER } from "../../utils/constant";

import { useWeb3React } from "@web3-react/core";
import useSigner from "hooks/useSigner";

import SwapSVG from "../svg/shape/swap";
import CopySvg from "../svg/common/small/copy";
import LinkSvg from "../svg/common/small/link";
import SuccessSvg from "../svg/common/success";
import FailedSvg from "../svg/common/failed";
import ShadowIcon from "../../img/loading/shadow.png";
import LogoIcon from "../../img/loading/logo.png";
import { TokenAvatar } from "components/common/avatar";
import { useAppContext } from "components/provider/appProvider";
import CopyButton from "components/common/copy";
import useFundLogo from "hooks/useFundLogo";
import { useNavigate } from "react-router-dom";
import { useNetworkData } from "hooks/useNetwork";
import useGateway from "hooks/useGateway";
import { getJsonFileByFetch } from "api/apiHTTP";

const STATUS = {
  Default: null,
  Depositing: "Depositing",
  Success: "Success",
  Failed: "Failed",
};

export default function Deposit(props) {
  const { show, closeDeposit } = props;

  const navigate = useNavigate();
  const { account, provider: web3Instance } = useWeb3React();
  const {
    state: { componentAddressMap, fundToken, stockToken },
    dispatch,
  } = useDaoContext();

  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const ventureContract = useVentureContract(
    componentAddressMap.get(VENTURE_MANAGER)
  );
  const signer = useSigner();

  const gateway = useGateway();

  const [fundAmount, setFundAmount] = useState();
  const [tokenAmount, setTokenAmount] = useState();

  const [status, setStatus] = useState(STATUS.Default);
  const [TxHash, setTxHash] = useState();
  const [failReason, setFailReason] = useState('');

  const ERC20contract = useERC20Contract(fundToken && fundToken.address);
  const fundlogo = useFundLogo();

  const { t } = useTranslation();

  const fundDisplay = useMemo(() => {
    if (!fundToken) {
      return {};
    }

    return {
      symbol: fundToken.symbol,
      amount: fundAmount,
    };
  }, [fundToken, fundAmount]);

  const tokenDisplay = useMemo(() => {
    if (!stockToken) {
      return {};
    }
    return {
      symbol: stockToken.symbol,
      amount: tokenAmount,
    };
  }, [stockToken, tokenAmount]);

  useEffect(() => {
    if (!show) {
      setStatus(STATUS.Default);
      setFundAmount(undefined);
      setTokenAmount(undefined);
    }
  }, [show]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    // [NOTE] 1:1
    setFundAmount(Number(value));
    setTokenAmount(Number(value));
  };
  const getProof = async (code) => {
    try {
      const url = gateway + code;
      const records = await getJsonFileByFetch(url);
      const recodeData = records.find((r) => r.address === account);
      return recodeData ? recodeData.proof : [];
    } catch (error) {
      console.error(error);
      return [];
    }
  };

  const confirm = async () => {
    setStatus(STATUS.Depositing);
    let proof = [];
    const code = await ventureContract.whiteListDataURI();
    console.log("code: ", code)
    if (code) proof = await getProof(code);

    const { deci } = fundToken;

    try {
      // check balance
      const balance = await ERC20contract.balanceOf(account);
      const fundAmountBN = ethers.utils.parseUnits(String(fundAmount), deci);
      if (balance.lt(fundAmountBN)) {
        setFailReason("Insufficient balance");
        setStatus(STATUS.Failed);
        return;
      }
    } catch (error) {
      setFailReason(error?.error?.message || 'unknown');
      setStatus(STATUS.Failed);
      return;
    }

    try {
      const allowanceAmount = await ERC20contract.connect(signer).allowance(
        account,
        componentAddressMap.get(VAULT_MANAGER)
      );
      const allowance = Number(ethers.utils.formatUnits(allowanceAmount, deci));
      if (!allowance || allowance < fundAmount) {
        const res = await ERC20contract.connect(signer).approve(
          componentAddressMap.get(VAULT_MANAGER),
          ethers.utils.parseUnits(String(fundAmount), deci),
          { gasLimit: 50000 }
        );

        await res.wait();
      }
    } catch (error) {
      setFailReason(error?.error?.message || 'unknown')
      setStatus(STATUS.Failed);
      return;
    }

    try {
      const res = await api.venture.raise(
        daoOrg,
        signer,
        ethers.utils.parseUnits(String(fundAmount), deci),
        proof
      );
      console.log(res);
      setTxHash(res.hash);
      await res.wait();
      setStatus(STATUS.Success);
      // update home info
      dispatch({ type: "UPDATE_FUND_OR_PERIOD", payload: "fund" });
    } catch (error) {
      console.log(error.error.message);
      setFailReason(error?.error?.message || 'unknown')
      setStatus(STATUS.Failed);
    }
  };
  const checkToken = () => {
    navigate("/explore?tab=tokens");
  };

  const showStatus = useMemo(() => {
    switch (status) {
      case STATUS.Depositing:
        return <Depositing fund={fundDisplay} token={tokenDisplay} />;
      case STATUS.Success:
        return <DepositSuccess fund={fundDisplay} token={tokenDisplay} />;
      case STATUS.Failed:
        return <DepositFailed failReason={failReason}/>;
      default:
        return <></>;
    }
  }, [status, fundDisplay, tokenDisplay,failReason]);

  return (
    <ModalBox
      visible={show}
      onCancel={closeDeposit}
      footer={null}
      destroyOnClose={true}
      maskClosable={false}
      width={364}
    >
      <div className="title font-medium">
        <span>{t("Period.DepositModal")}</span>
      </div>
      <DepositContent className="font-regular">
        <InputBox>
          <div>
            <InputLabel>{t("Period.DepositAmount")}</InputLabel>
            <InputStyled
              placeholder={t("Create.Enter")}
              name="fundAmount"
              value={fundAmount}
              autoComplete="off"
              type="number"
              autoFocus
              onChange={(e) => handleInputChange(e)}
            />
          </div>
          <TokenBox>
            <img src={fundlogo} alt="" />
            <span>{fundToken && fundToken.symbol}</span>
          </TokenBox>
        </InputBox>
        <IconBox>
          <SwapSVG />
        </IconBox>
        <InputBox>
          <div>
            <InputLabel>{t("Period.ReceivingToken")}</InputLabel>
            <InputStyled
              placeholder={t("Create.Enter")}
              name="tokenAmount"
              value={tokenAmount}
              autoComplete="off"
              type="number"
              onChange={(e) => handleInputChange(e)}
            />
          </div>
          <TokenBox>
            <TokenAvatar symbol={stockToken && stockToken.symbol} />
            <span>{stockToken && stockToken.symbol}</span>
          </TokenBox>
        </InputBox>
      </DepositContent>

      <DepositStatus>
        {showStatus}
        {TxHash && <TransictionHash TxHash={TxHash} />}
      </DepositStatus>
      {status === STATUS.Default && (
        <Footer>
          <Button
            disabled={!fundAmount || fundAmount < 0}
            type="primary"
            onClick={confirm}
            style={{ width: "100%" }}
          >
            {t("Submit")}
          </Button>
        </Footer>
      )}
      {status === STATUS.Success && (
        <Footer>
          <Button type="primary" onClick={checkToken} style={{ width: "100%" }}>
            Check my tokens
          </Button>
        </Footer>
      )}
    </ModalBox>
  );
}

const TransictionHash = (props) => {
  const { TxHash } = props;

  const { chainId } = useWeb3React();
  const networkData = useNetworkData(chainId);

  const go2Scan = () => {
    window.open(
      networkData && networkData.scan && networkData.scan.host + TxHash
    );
  };
  return (
    <TransictionHashStyled>
      <div className="divide"></div>
      <div className="txTitle">Your transaction:</div>
      <div className="txContent">
        <span>{TxHash}</span>
        <CopyButton value={TxHash}>
          {" "}
          <CopySvg />
        </CopyButton>
        <LinkSvg onClick={go2Scan} />
      </div>
    </TransictionHashStyled>
  );
};

const DepositFailed = (props) => {
  console.log(props)
  return (
    <DepositStatusStyled className="fail">
      <FailedSvg />
      <StatusTitle className="font-medium">Deposit failed {props.failReason}</StatusTitle>
    </DepositStatusStyled>
  );
};

const DepositSuccess = (props) => {
  const { t } = useTranslation();
  const { fund, token } = props;
  return (
    <DepositStatusStyled className="success">
      <SuccessSvg />
      <StatusTitle className="font-medium">
        {t("Period.DepositedTitle", {
          amount: fund.amount,
          symbol: fund.symbol,
        })}
      </StatusTitle>
      <p>
        {t("Period.Received", { amount: token.amount, symbol: token.symbol })}{" "}
      </p>
      <p>
        DAO token can be circulated and transferred. It is the only proof of
        redemption. During redemption period, the redeemed tokens will be
        burned, and the total supply of equity tokens will be reduced
        accordingly.
      </p>
    </DepositStatusStyled>
  );
};

const Depositing = (props) => {
  const { t } = useTranslation();
  const { fund } = props;
  return (
    <DepositStatusStyled className="depositing">
      <Loading />
      <StatusTitle className="font-medium">
        {t("Period.DepositingTitle", {
          amount: fund.amount,
          symbol: fund.symbol,
        })}
      </StatusTitle>
      <p>
        The process should take a few minutes. It depends on network congestion
        and the gas fees you set.
      </p>
    </DepositStatusStyled>
  );
};

const Loading = () => {
  return (
    <LoadingStyled className="loagding">
      <div className="logo" />
    </LoadingStyled>
  );
};
