import React, { memo, useCallback } from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import { Button, Modal } from "antd";

const RejectModal = memo((props) => {
  const { t } = useTranslation();
  const { show, handleClose, handleConfirm } = props;

  const onConfirm = useCallback(() => {
    handleConfirm(false);
  }, [handleConfirm]);

  return (
    <Modal
      visible={show}
      onCancel={handleClose}
      footer={null}
      destroyOnClose={true}
      width={364}
      centered
      closable={false}
    >
      <div className="title">
        <span>{t("walletConnect.RejectTitle")}</span>
      </div>
      <ModalBoxBody>{t("walletConnect.RejectApprove")}</ModalBoxBody>
      <ModalBoxFooter>
        <Button type="primary" ghost onClick={handleClose}>
          {t("walletConnect.CancelBtn")}
        </Button>
        <Button type="primary" onClick={onConfirm}>{t("walletConnect.RejectBtn")}</Button>
      </ModalBoxFooter>
    </Modal>
  );
});

export default RejectModal;

const ModalBoxBody = styled.div`
  margin: 36px auto;
  line-height: 16px;
  color: #10164b;
`;
const ModalBoxFooter = styled.div`
  display: flex;
  justify-content: space-between;
  button {
    width: 157px;
    height: 44px !important;
    border-radius: 8px !important;
  }
`;
