import React, { useEffect, useMemo } from "react";
import styled from "styled-components";
import Tav from "./tav";
import LinkImg from "../../img/newHome/link.png";
import TipSVG from "components/svg/common/small/tip";
import { useDaoContext } from "../../views/dao/provider";
import { getShortDisplay } from "../../utils/publicJs";
import useManageFee from "hooks/useManageFee";
import { VENTURE_MANAGEFEE_MANAGER } from "../../utils/constant";

import VenturePeriodPanel from "./period";
import { useTranslation } from "react-i18next";
import { TokenAvatar } from "components/common/avatar";
import useFundLogo from "hooks/useFundLogo";

import { useLazyQuery } from "@apollo/client";
import { SubgraphApiQuery, testSubGraphql } from "api/graphql/subQuery";
import { Tooltip } from "antd";
import { useAppContext } from "components/provider/appProvider";

export default function Info() {
  const { t } = useTranslation();

  const {
    state: { exploreScan },
  } = useAppContext();
  const { state: daoState } = useDaoContext();
  const {
    componentAddressMap,
    fundInfo,
    fundToken,
    stockToken,
    carriedInterest,
    manageFee,
  } = daoState;

  const token = useManageFee(
    componentAddressMap.get(VENTURE_MANAGEFEE_MANAGER),
    { useBalance: true }
  );
  const fundlogo = useFundLogo();

  const raised = useMemo(() => {
    return fundInfo ? getShortDisplay(fundInfo.raised) : "";
  }, [fundInfo]);

  const goal = useMemo(() => {
    return fundInfo ? getShortDisplay(fundInfo.goal) : "";
  }, [fundInfo]);

  const hard = useMemo(() => {
    return fundInfo ? getShortDisplay(fundInfo.hard) : "";
  }, [fundInfo]);

  const progress = useMemo(() => {
    if (!fundInfo || !fundInfo.raised) return 0;
    return (Number(fundInfo.raised) * 100) / Number(fundInfo.goal);
  }, [fundInfo]);

  const fundTokenInfo = useMemo(() => {
    return fundToken
      ? {
          ...fundToken,
          icon: "",
        }
      : {};
  }, [fundToken]);

  const manageFeeInfo = useMemo(() => {
    return manageFee
      ? {
          display: (manageFee / 100).toFixed(2),
          progress: manageFee / 100,
        }
      : { display: 0, progress: 0 };
  }, [manageFee]);

  const carriedInterestInfo = useMemo(() => {
    return carriedInterest
      ? {
          display: (carriedInterest / 100).toFixed(2),
          progress: carriedInterest / 100,
        }
      : { display: 0, progress: 0 };
  }, [carriedInterest]);

  return (
    <div>
      <VenturePeriodPanel />
      <Tav />
      <Fund>
        <FirstLine>
          <div className="lft">
            <Title>{t("Info.Raised")}</Title>
            <div className="numBig font-black">{raised}</div>
            <Symbol>
              <img src={fundlogo} alt="" />
              <span>{fundTokenInfo.symbol}</span>
            </Symbol>
          </div>
          <div className="rht">
            <Title>{t("Info.CurrentProgress")}</Title>
            <div className="numRht font-black">{progress}%</div>
          </div>
        </FirstLine>
        <PercentBox width={progress > 100 ? 100 : progress}>
          <div className="percent" />
        </PercentBox>
        <LastLine>
          <div>
            <div>{t("Info.FundingTarget")}</div>
            <div className="num">{goal}</div>
          </div>
          <div>
            <div>{t("Info.UpperLimit")}</div>
            <div className="num">{hard}</div>
          </div>
        </LastLine>
      </Fund>
      <TypeBox>
        <li>
          <div>
            <Title>{t("Info.FundType")}</Title>
          </div>
          <SymbolBox>
            <div className="lft">
              <img src={fundlogo} alt="" />
              <div className="symbol">{fundTokenInfo.symbol}</div>
            </div>
            <a
              href={`${exploreScan}token/${fundTokenInfo.address}`}
              rel="noreferrer"
              target="_blank"
            >
              <LinkIcon src={LinkImg} alt="" />
            </a>
          </SymbolBox>
        </li>
        <li>
          <div className="top">
            <Title>{t("Info.EquityToken")}</Title>
            <Tooltip title={t("Hover.EquipToken")}>
              <TipSVG />
            </Tooltip>
          </div>
          <SymbolBox>
            <div>
              <div className="lft">
                <TokenAvatar symbol={stockToken?.symbol} />
                <div className="symbol">{stockToken?.symbol}</div>
              </div>
            </div>
            <div>
              {/*<ViewBox>View on etherscan.io</ViewBox>*/}
              <div>
                {" "}
                <a
                  href={`${exploreScan}token/${stockToken?.address}`}
                  rel="noreferrer"
                  target="_blank"
                >
                  <LinkIcon src={LinkImg} alt="" />
                </a>
              </div>
            </div>
          </SymbolBox>
        </li>
        <li>
          <div className="top">
            <Title>{t("Info.ManageFee")}</Title>
          </div>
          <LineBox>
            <div className="first">
              <PercentNum>{token?.balanceDisplay}<span>{token?.symbol}</span></PercentNum>
              <PercentNum>{manageFeeInfo.display}%</PercentNum>
            </div>
            <PercentBox width={manageFeeInfo.progress}>
              <div className="progress" />
            </PercentBox>
          </LineBox>
        </li>
        <li>
          <div className="top">
            <Title>{t("Info.CarriedInterest")}</Title>
            <Tooltip title={t("Hover.CarriedInterest")}>
              <TipSVG />
            </Tooltip>
          </div>
          <LineBox>
            <div className="first">
              <PercentNum>{carriedInterestInfo.display}%</PercentNum>
            </div>
            <PercentBox width={carriedInterestInfo.progress}>
              <div className="progress" />
            </PercentBox>
          </LineBox>
        </li>
      </TypeBox>
    </div>
  );
}

