import Tabs from "../layout/tabs";
import React, { useCallback, useEffect, useState, useMemo } from "react";
import styled from "styled-components";
import Info from "./Info";
import Contracts from "./contract";
import TransferHistory from "./transfer";
import { useParams, useNavigate, Routes, Route, Navigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import useMatchRoute, { HomePageRoutes } from "hooks/useMatchRoute";

export default function HomeDefault() {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const subRoute = useMatchRoute(HomePageRoutes);

  const { fullhandle } = useParams();
  const [current, setCurrent] = useState(0);

  const nav = useMemo(() => {
    return [t("Home.Info"), t("Home.Contracts"), t("Home.History")];
  }, []);
  const navRoutes = useMemo(() => {
    return ["info", "contracts", "transfer"];
  }, []);

  useEffect(() => {
    if (!subRoute) {
      setCurrent(0);
      return;
    }
    for (let i = 0; i < navRoutes.length; i++) {
      if (navRoutes[i] === subRoute) {
        setCurrent(i);
        break;
      }
    }
  }, [subRoute]);

  useEffect(() => {
    const vheight = document.getElementById("contentBox").offsetHeight;
    const oHeight = document.body.clientHeight || document.documentElement.clientHeight;
    if (oHeight > 900 && vheight >350) {
      document.getElementById("inner").style.height = `${vheight}px`;
    } else {
      document.getElementById("inner").style.height = `550px`;
    }
  }, []);

  const handleCurrent = (num) => {
    setCurrent(num);
    navigate(`/dao/${fullhandle}/homepage/${navRoutes[num]}`);
  };

  return (
    <InfoBox>
      <Tabs navList={nav} current={current} handleCurrent={handleCurrent} />
      <Content id="contentBox">
        <div id="inner">
          <Routes>
            <Route path="info" element={<Info />} />
            <Route path="contracts" element={<Contracts />} />
            <Route path="transfer" element={<TransferHistory />} />
            <Route path="*" element={<Navigate to={`/dao/${fullhandle}/homepage/info`} />} />
          </Routes>
        </div>
      </Content>
    </InfoBox>
  );
}

const InfoBox = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 40px 20px;
`;

const Content = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  flex: 1;
  height: 100%;
  margin-top: 30px;
  #inner {
    overflow-y: auto;
    height: 200px;
    position: relative;
  }
`;
