import Tabs from "../layout/tabs";
import React, { useCallback, useEffect, useState } from "react";
import styled from "styled-components";
import Tokens from "./tokens";
import NFT from "./nft";
import Investment from "./investment";
import {
  useParams,
  useNavigate,
  Navigate,
  Routes,
  Route,
} from "react-router-dom";
import useMatchRoute, { PortfolioRoutes } from "hooks/useMatchRoute";
import { useMemo } from "react";
import { useTranslation } from "react-i18next";

export default function Portfolio() {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const subRoute = useMatchRoute(PortfolioRoutes);

  const { fullhandle } = useParams();
  const [current, setCurrent] = useState(0);

  const nav = useMemo(() => {
    return [t("Portfolio.Tokens"), t("Portfolio.NFT"), t("Portfolio.Offchain")];
  }, []);

  const navRoutes = useMemo(() => {
    return ["tokens", "nft", "investment"];
  }, []);

  useEffect(() => {
    const vheight = document.getElementById("contentBox").offsetHeight;
    const oHeight =
      document.body.clientHeight || document.documentElement.clientHeight;
    if  (oHeight > 900 && vheight >350) {
      document.getElementById("inner").style.height = `${vheight}px`;
    } else {
      document.getElementById("inner").style.height = `550px`;
    }
  }, []);

  useEffect(() => {
    if (!subRoute) {
      setCurrent(0);
      return;
    }
    const idx = navRoutes.findIndex((n) => n === subRoute);
    setCurrent(idx > -1 ? idx : 0);
  }, [subRoute]);

  const handleCurrent = (num) => {
    setCurrent(num);
    navigate(`/dao/${fullhandle}/portfolio/${navRoutes[num]}`);
  };

  return (
    <InfoBox>
      <Tabs navList={nav} current={current} handleCurrent={handleCurrent} />
      <Content id="contentBox">
        <div id="inner">
          <Routes>
            <Route path="tokens" element={<Tokens />} />
            <Route path="nft" element={<NFT />} />
            <Route path="investment" element={<Investment />} />
            <Route path="*" element={<Navigate to="./tokens" />} />
          </Routes>
        </div>
      </Content>
    </InfoBox>
  );
}

const InfoBox = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 40px 20px;
`;

const Content = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  flex: 1;
  height: 100%;
  margin-top: 30px;
  #inner {
    /* overflow-y: auto; */
    height: 200px;
  }
`;
