// 02-2-1
export const CreateDao = gql`
  mutation MyMutation(
    $daoId: Int!
    $chainId: Int!
    $logo: String
    $desc: String!
    $handle: String!
    $name: String!
  ) {
    createDao(
      req: {
        dao_id: $daoId
        chain_id: $chainId
        logo: $logo
        name: $name
        desc: $desc
        handle: $handle
      }
    )
  }
`;

