import { useGrantManagerContract, useDaoOrgContract } from "hooks/useContract";
import { useDaoContext } from "views/dao/provider";
import { ethers } from "ethers";
import { useState } from "react";
import { getShortDisplay } from "utils/publicJs";
import { InputNumber } from "antd";
import { useAppContext } from "components/provider/appProvider";
import { useTranslation } from "react-i18next";
import { DAO_ORG } from "utils/constant";
import useSigner from "hooks/useSigner";
import Done from "./Done";
import FailedModal from "./Failed";
import api from "api";
import { useEffect } from "react";
import { useMemo } from "react";
import { useNetworkData } from "hooks/useNetwork";

const STATUS = {
  Default: 0,
  Success: 1,
  Failed: 2,
};

export default function WithdrawModal(props) {
  const { closeModal, reserve } = props;
  const { t } = useTranslation();

  const { account } = useWeb3React();
  const {
    dispatch,
    state: { exploreScan },
  } = useAppContext();
  const {
    state: { componentAddressMap },
  } = useDaoContext();
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const [balance, setBalance] = useState(0);
  const [balanceDisplay, setBalanceDisplay] = useState(0);
  const [amount, setAmount] = useState();

  const [status, setStatus] = useState(STATUS.Default);
  const [txHash, setTxHash] = useState();

  const txLink = useMemo(() => {
    if (exploreScan && txHash) {
      return `${exploreScan}tx/${txHash}`;
    }
    return "";
  }, [txHash, exploreScan]);


  const onClickSuppy = async () => {
    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    try {
      const res = await api.aave.aaveWithdraw(daoOrg, signer, {
        amount: ethers.utils.parseUnits(String(amount), reserve.reserve.decimals),
        tokenAddress: reserve.reserve.underlyingAsset,
      });
      console.log(res)
      setTxHash(res.hash);
      await res.wait();
      setStatus(STATUS.Success);
    } catch (error) {
      console.error("aaveWithdraw failed", error);
      setStatus(STATUS.Failed);
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  const closeDone = () => {
    setStatus(STATUS.Default);
    closeModal();
  };

  const closeFailed = () => {
    setStatus(STATUS.Default);
    setTxHash();
    closeModal();
  };

  useEffect(() => {
    if (reserve) {
      setBalance(Number(reserve.balance))
    }
  }, [reserve]);
  return (
    <>
      
      <General width="440" height="490" closeModal={closeModal}>
        <TitleBox className="font-medium">Withdraw {reserve.reserve && reserve.reserve.symbol}</TitleBox>
        <InputBoxBg>
          <div className="input">
            <InputStyled
              autoFocus
              className="font-medium"
              value={amount}
              min={0}
              controls={false}
              onChange={(v) => setAmount(v)}
            />
            {/* <BalanceBg className="font-medium">1054.97</BalanceBg> */}
            {/* <PriceBox>
            $1.052.70 <span>(0.155%)</span>
          </PriceBox> */}
          </div>
          <div>
            <SelectBox>
              <span className="name font-bold">
                <img src={reserve.logo} alt="" />
                {reserve.reserve && reserve.reserve.symbol}
              </span>
            </SelectBox>
            <RhtBalance>
              <div>Balance : {getShortDisplay(reserve.balance)}</div>
              <div className="max" onClick={() => setAmount(balance)}>
                Max
              </div>
            </RhtBalance>
          </div>
        </InputBoxBg>
        <TransactionBox>
          <div className="title">Transaction overview</div>
          <BorderBox>
            <dl>
              <dt className="font-medium">Supply APY</dt>
              <dd className="first font-medium">
                {reserve.supplyAPYDisplay * 100} %
              </dd>
            </dl>
            <dl>
              <dt className="font-medium">Collateralization</dt>
              <dd className="green font-medium">Enabled</dd>
            </dl>
            <dl>
              <dt className="font-medium">Health factor</dt>
              <dd>
                <div className="green font-medium">
                  7.49 <img src={NextImg} alt="" /> 7.50
                </div>
                <div className="tips">Liquidation at &lt; 1.0</div>
              </dd>
            </dl>
          </BorderBox>
        </TransactionBox>
        {/* <LastLine>
        <img src={GasImg} alt="" /> $1.77
      </LastLine> */}
        <BtnGroup>
          <button
            disabled={amount > balance}
            onClick={onClickSuppy}
          >
            Withdraw {reserve.reserve && reserve.reserve.symbol}
          </button>
        </BtnGroup>
      </General>
      {status === STATUS.Success && (
        <Done
          amount={amount}
          symbol={reserve.reserve && reserve.reserve.symbol}
          handleClose={closeDone}
          txLink={txLink}
        />
      )}
      {status === STATUS.Failed && (
        <FailedModal
          title="Withdraw failed"
          content="Withdraw failed, please restart supply"
          handleClose={closeFailed} />
      )}
    </>
  );
}

