import { useMemo, useState } from "react";
import styled from "styled-components";
import NoNum from "../../../img/noDaos.png";
import Tabs from "../../layout/tabs";
import TxList from "./assets2supply";
import MySupplies from "./MySupplies";
import AssetsToBorrow from "./AssetsToBorrow";
import MyBorrow from "./MyBorrow";

export default function TxPanel() {
  const [current, setCurrent] = useState(0);

  const nav = useMemo(() => {
    return ["Assets to supply", "My supplies","Assets to borrow","My borrows"];
  }, []);

  const handleCurrent = (num) => {
    setCurrent(num);
  };

  return (
    <>
      <FirstLine>
        <Tabs navList={nav} current={current} handleCurrent={handleCurrent} />
      </FirstLine>
      <Content id="contentBox">
        <div id="inner">
            {/*<NoNumBox>*/}
            {/*    <div>*/}
            {/*        <img src={NoNum} alt=""/>*/}
            {/*        <div>No borrows</div>*/}
            {/*    </div>*/}

            {/*</NoNumBox>*/}


            {
                current === 0 && <TxList />
            }
            {
                current === 1 && <MySupplies  />
            }
            {
                current === 2 && <AssetsToBorrow  />
            }
            {
                current === 3 && <MyBorrow  />
            }


        </div>
      </Content>
    </>
  );
}

const FirstLine = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 6px solid rgba(245, 245, 247, 0.57);
  padding-top: 24px;
`;

const Content = styled.div`
  flex: 1;
  height: 100%;
  margin-top: 30px;
`;

const NoNumBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 300px;
  text-align: center;
    img{
      width: 200px;
      margin-bottom: 13px;
    }
`
