import styled from "styled-components";
import { useState, useCallback } from "react";
import { useTranslation } from "react-i18next";
import { useDaoContext } from "views/dao/provider";
import { useDaoOrgContract } from "hooks/useContract";
import useSigner from "hooks/useSigner";
import api from "api";
import Button, { ButtonGroup } from "components/common/button";
import VoteLoadingModal, { VoteTxStatus } from "./loading";
import TipsImg from "img/newHome/tips.png";
import { DAO_ORG, VOTE_MANAGER } from "utils/constant";
import DateBox from "components/common/date";
import { getVoteIdByEvent } from "utils/utils";

export default function Redemption(props) {
  const { t } = useTranslation();
  const { state: daoState } = useDaoContext();
  const { componentAddressMap } = daoState;
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const [status, setStatus] = useState(VoteTxStatus.Default);
  const [txHash, setTxHash] = useState();
  const [endTime, setEndTime] = useState();
  const [newVoteId, setVoteId] = useState();

  const createVote = async () => {
    let duration = Math.round((endTime - Date.now()) / 1000);
    if (duration < 86400) {
      duration = 86400;
    }
    let txHash;
    try {
      setStatus(VoteTxStatus.Loading);
      const res = await api.vote.newTransfrtPeriod(daoOrg, signer, {
        vote_time: duration,
      });
      txHash = res.hash;
      const r = await res.wait();
      const event = r.events.find(
        (e) => e.address === componentAddressMap.get(VOTE_MANAGER)
      );
      const vid = getVoteIdByEvent(event);
      setVoteId(vid);
      setStatus(VoteTxStatus.Success);
    } catch (error) {
      setStatus(VoteTxStatus.Failed);
      setTxHash(txHash);
      console.error("newTransfrtPeriod failed", error);
    }
  };

  const closeLoading = useCallback(() => {
    setStatus(VoteTxStatus.Default);
    txHash && setTxHash();
  }, []);

  const closeAll = useCallback(() => {
    closeLoading();
    props.handleClose();
  }, [props.handleClose]);

  return (
    <div>
      <VoteLoadingModal
        status={status}
        txHash={txHash}
        handleRetry={createVote}
        voteId={newVoteId}
        handleClose={closeLoading}
        handleCloseAll={closeAll}
      />
      <DateBox
        label={t("Vote.Expiry")}
        targetTime={endTime}
        updateTargetTime={setEndTime}
      />
      <Tips>
        <img src={TipsImg} alt="" />
        <div className="tips">
          When Ventures DAO enters the redemption period, all functions of
          Management are still available, but users can perform redemption
          operations.
        </div>
      </Tips>
      <ButtonGroup>
        <Button>{t("Cancel")}</Button>
        <Button primary onClick={createVote}>
          {t("Submit")}
        </Button>
      </ButtonGroup>
    </div>
  );
}

const Tips = styled.div`
  display: flex;
  align-items: flex-start;
  background: #f7f7f7;
  border-radius: 14px;
  padding: 11px;
  margin-bottom: 40px;
  .tips {
    font-size: 12px;
    font-weight: 400;
    color: #a6a6b9;
    line-height: 16px;
    margin-left: 5px;
  }
`;

const BtnGroup = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 40px 0 20px;
  button {
    width: 140px;
    height: 50px;
    border-radius: 14px;
    border: 1px solid #d52473;
    &:first-child {
      background: #ffffff;
      color: #d52473;
      margin-right: 30px;
    }
    &:last-child {
      background: #d52473;
      color: #ffffff;
    }
  }
`;
