import React, { memo, useState } from "react";
import { Input, InputNumber, DatePicker } from "antd";
import styled from "styled-components";
import StarSvg from "../svg/create/star";
import InvalidSvg from "../svg/common/invalid";

const { TextArea } = Input;

export const InputLabel = memo((props) => {
  const { title, required, icon, labelSize, ...rest } = props;

  return (
    <InputLabelStyled {...rest} labelSize={labelSize}>
      {icon && <span className="label-icon">{icon}</span>}
      {required && <StarSvg className="star-icon" />}
      <span className="label-text">{title}</span>
    </InputLabelStyled>
  );
});

export const InputItem = memo((props) => {
  const {
    title,
    required,
    icon,
    area,
    overlayClass,
    width,
    outPrefix,
    outSuffix,
    suffix,
    labelSize,
    errorMsg,
    isError,
    ...rest
  } = props;
  return (
    <InputItemStyled className={overlayClass} width={width}>
      {title && (
        <InputLabel
          title={title}
          required={required}
          icon={icon}
          labelSize={labelSize}
        />
      )}
      {area ? (
        <TextAreaStyled className={isError ? "errorBound" : ""} {...rest} />
      ) : (
        <InputWrapper>
          {outPrefix && <OutPrefix>{outPrefix}</OutPrefix>}
          <InputStyled {...rest} />
          {outSuffix && <OutSuffix>{outSuffix}</OutSuffix>}
          {suffix && <Suffix>{suffix}</Suffix>}
        </InputWrapper>
      )}
      {errorMsg && (
        <Error>
          <InvalidSvg />
          <span>{errorMsg}</span>
        </Error>
      )}
    </InputItemStyled>
  );
});

export const InputNumberItem = memo((props) => {
  const {
    title,
    required,
    icon,
    area,
    overlayClass,
    width,
    labelSize,
    outPrefix,
    outSuffix,
    suffix,
    errorMsg,
    avaliableMsg,
    toolTip,
    toolTipStyle,
    onBlur,
    isError,
    ...rest
  } = props;
  const [showTooltip, setShowTooltip] = useState(false);
  return (
    <InputItemStyled className={overlayClass} width={width}>
      {title && (
        <InputLabel
          title={title}
          required={required}
          icon={icon}
          labelSize={labelSize}
        />
      )}
      {/* <InputWrapper className="errorBound"> */}
      <InputWrapper>
        {outPrefix && <OutPrefix>{outPrefix}</OutPrefix>}
        <InputNumberStyled
          className={isError ? "errorBound" : ""}
          {...rest}
          onFocus={() => setShowTooltip(true)}
          onBlur={(e) => {
            setShowTooltip(false);
            onBlur && onBlur(e);
          }}
        />
        {outSuffix && <OutSuffix>{outSuffix}</OutSuffix>}
        {suffix && <Suffix>{suffix}</Suffix>}
      </InputWrapper>
      {toolTip && showTooltip && (
        <Tooltip style={toolTipStyle}>{toolTip}</Tooltip>
      )}
      {errorMsg && (
        <Error>
          <InvalidSvg />
          <span>{errorMsg}</span>
        </Error>
      )}
    </InputItemStyled>
  );
});

export const DateInput = memo((props) => {
  return <DatePickerStyled {...props} />;
});

