export const VOTE_TYPE = {
  TRANSFER_TOKEN: "transfer",
  TRANSFER_NFT: "transferNFT",
  TRANSFER_MANAGE_FEE: "transferManageFee",
  TRANSFER_PERIOD: "transferperiod",
  ADD_MANAGER: "acceptgp",
  REMOVE_MANAGER: "impeach",
  APPROVE_MANAGER: "grantmethod",
};

export const VOTE_INFO = {
  [VOTE_TYPE.TRANSFER_NFT]: {
    name: "Vote.Transfer",
    shortName: "Vote.Transfer",
    icon: TransferImg,
  },
  [VOTE_TYPE.TRANSFER_TOKEN]: {
    name: "Vote.Transfer",
    shortName: "Vote.Transfer",
    icon: TransferImg,
  },
  [VOTE_TYPE.TRANSFER_PERIOD]: {
    name: "Vote.ChangePeriod",
    shortName: "Vote.Redemption",
    icon: RedemptionImg,
  },
  [VOTE_TYPE.ADD_MANAGER]: {
    name: "Vote.AddManager",
    shortName: "Vote.AddManager",
    icon: ManagerImg,
  },
  [VOTE_TYPE.REMOVE_MANAGER]: {
    name: "Vote.RemoveManager",
    shortName: "Vote.RemoveManager",
    icon: ManagerImg,
  },
  [VOTE_TYPE.APPROVE_MANAGER]: {
    name: "Vote.SuperManager",
    shortName: "Vote.SuperManager",
    icon: SuperImg,
  },
  [VOTE_TYPE.TRANSFER_MANAGE_FEE]: {
    name: "Vote.ManagementFeePool",
    shortName: "Vote.ManagementFee",
    icon: FeeImg,
  },
};

export const formatVoteType = (metadata) => {
  if (!metadata) {
    return;
  }
  if (metadata.startsWith("transfer#")) {
    return VOTE_TYPE.TRANSFER_TOKEN;
  }
  if (metadata.startsWith("transferNFT#")) {
    return VOTE_TYPE.TRANSFER_NFT;
  }
  if (metadata.startsWith("grantmethod#")) {
    return VOTE_TYPE.APPROVE_MANAGER;
  }
  if (metadata.startsWith("impeach#")) {
    return VOTE_TYPE.REMOVE_MANAGER;
  }
  if (metadata.startsWith("acceptgp#")) {
    return VOTE_TYPE.ADD_MANAGER;
  }
  if (metadata.startsWith("transferManageFee#")) {
    return VOTE_TYPE.TRANSFER_MANAGE_FEE;
  }
  if (metadata.startsWith("transferperiod#")) {
    return VOTE_TYPE.TRANSFER_PERIOD;
  }
};

export default function VoteDetail() {
  const { t } = useTranslation();

  const { id } = useParams();
  const { state: lastNav } = useLocation();
  const { chainId } = useWeb3React();

  const {
    state: {
      componentAddressMap,
      fullHandle,
      daoId,
      whoAmI: { isOwner, isGP },
    },
  } = useDaoContext();
  const {
    state: { exploreScan },
    dispatch,
  } = useAppContext();

  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();
  const orgMembersMap = useOrgMembers(componentAddressMap.get(ORG_MANAGER));

  const [type, setType] = useState(VOTE_TYPE.ADD_MANAGER);
  const [voteData, setVoteData] = useState({});
  const [wid, setWid] = useState();
  const [optionlist, setoptionlist] = useState([]);
  const [voters, setVotes] = useState([]);
  const [voteId, setVoteId] = useState();
  const [action, setAction] = useState();
  const [canExe, setCanExe] = useState(false);

  useEffect(() => {
    setVoteId(Number(id));
  }, [id]);

  const voteContract = useVoteContract(componentAddressMap.get(VOTE_MANAGER));
  useEffect(async () => {
    if (!voteContract || voteId === undefined) {
      return;
    }
    const canExecute = await voteContract.canExecute(voteId);
    setCanExe(canExecute);
  }, [voteId, voteContract]);

  const [queryVoteById] = useLazyQuery(QueryVoteById, {
    onCompleted(data) {
      console.log("====", data);
      setVotes(data.voters);
      const vdata = data.info || {};
      setType(formatVoteType(vdata.metadata));
      setVoteData({
        ...vdata,
        startAt: moment(vdata.start_time * 1000).format(
          "MMMM Do YYYY, h:mm A"
        ),
        executor:
          ethers.constants.AddressZero !== vdata.executor ? vdata.executor : "",
        endTime: vdata.start_time + vdata.vote_time,
        left:
          vdata.start_time + vdata.vote_time - Math.round(Date.now() / 1000),
      });
      setAction(data.action);
    },
    onError(error) {
      console.error(error);
    },
  });

  useEffect(() => {
    setoptionlist([
      { value: voteData.agree_count || 0, name: "Agree" },
      { value: voteData.oppose_count || 0, name: "Disagree" },
    ]);
  }, [voteData]);

  const VoteDetailByType = useMemo(() => {
    switch (type) {
      case VOTE_TYPE.TRANSFER_TOKEN:
        return <TransferTokenVote wid={wid} />;
      case VOTE_TYPE.TRANSFER_NFT:
        return <TransferNFTVote wid={wid} />;
      case VOTE_TYPE.ADD_MANAGER:
        return <AddManagerVote wid={voteId} orgMembersMap={orgMembersMap} />;
      case VOTE_TYPE.REMOVE_MANAGER:
        return <RemoveManagerVote wid={voteId} orgMembersMap={orgMembersMap} />;
      case VOTE_TYPE.TRANSFER_MANAGE_FEE:
        return <TransferManageFeeVote wid={wid} />;
      case VOTE_TYPE.APPROVE_MANAGER:
        return <ApproveVote wid={wid} vid={voteId} start_time={voteData.start_time} />;
      default:
        return <></>;
    }
  }, [type, wid, orgMembersMap, voteData]);

  const voteStatusIcon = useMemo(() => {
    switch (voteData.status) {
      case 1:
        return AgreeImg;
      case 2:
        return RejectedImg;
      default:
        return OngoingImg;
    }
  }, [voteData]);

  useEffect(() => {
    console.error("voteData=======",voteData)
    if (!!voteData.metadata) {
      setWid(parseInt(voteData.metadata.split("#")[1]));
    }
  }, [voteData]);

  const voteInfoByType = useMemo(() => {
    return VOTE_INFO[type] || {};
  }, [type]);

  const doChoose = async (flag) => {
    if (!isOwner && !isGP) {
      return;
    }
    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    try {
      await api.vote.doVote(daoOrg, signer, voteId, flag);
    } catch (error) {
      console.error(error);
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: "Vote failed",
          closable: true,
        },
      });
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  useEffect(() => {
    if (!chainId || !daoId) {
      return;
    }
    queryVoteById({
      variables: {
        chainId,
        daoId,
        voteId,
      },
      fetchPolicy: 'network-only',
    });
  }, [chainId, daoId, voteId]);

  // useEffect(() => {
  //   setVotes([
  //     {
  //       address: "0xa2f87DC5ec6F659dC7A13c8f12663D251E72B698",
  //       support: true,
  //     },
  //     {
  //       address: "0xa2f87DC5ec6F659dC7A13c8f12663D251E72B698",
  //       support: false,
  //     },
  //   ]);
  //   setVoteData({
  //     agree_count: 1,
  //     create_at: "2012/5/6",
  //     creator: "0xa2f87DC5ec6F659dC7A13c8f12663D251E72B698",
  //     metadata: "transfer#0",
  //     status: "",
  //     tx_hash: "0xa2f87DC5ec6F659dC7A13c8f12663D251E72B698",
  //     vote_type: VOTE_TYPE.TRANSFER_PERIOD,
  //     oppose_count: 1,
  //   });
  // }, []);

  const FormatLeftTime = useCallback(({ leftTime }) => {
    const days = parseInt(leftTime / 86400);
    let left = leftTime % 86400;
    const hours = parseInt(left / 3600);
    left = left % 3600;
    const minutes = parseInt(left / 60);
    if (days > 0) {
      return (
        <>
          <span className="font-demiBold">{days}</span> d{" "}
          <span className="font-demiBold">{hours}</span> h{" "}
          <span className="font-demiBold">{minutes}</span> min left
        </>
      );
      // return `${days} d ${minutes} min left`;
    }
    if (hours > 0) {
      return (
        <>
          <span className="font-demiBold">{hours}</span> h{" "}
          <span className="font-demiBold">{minutes}</span> min left
        </>
      );
    }
    if (minutes > 0) {
      return (
        <>
          <span className="font-demiBold">{minutes}</span> min left
        </>
      );
      // return `${minutes} min left`;
    }
    return <span>0 min left</span>;
  }, []);

  const ActionButtons = memo(({ action, lefttime, isExecute, canExecute }) => {
    if (lefttime > 0 && !action) {
      return (
        <BtnGroup>
          <div onClick={() => doChoose(true)}>Agree</div>
          <div onClick={() => doChoose(false)} className="disagree">
            Disagree
          </div>
        </BtnGroup>
      );
    } else if (lefttime <= 0 && !isExecute && canExecute) {
      return (
        <BtnGroup>
          <div onClick={() => doExcute(true)}>Execute</div>
        </BtnGroup>
      );
    }
    return <></>;
  });

  const doExcute = async () => {
    if (!isOwner && !isGP) {
      return;
    }
    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    try {
      if (type === VOTE_TYPE.ADD_MANAGER) {
        await api.vote.executeAddGPVote(daoOrg, signer, voteId);
      } else if (type === VOTE_TYPE.REMOVE_MANAGER) {
        await api.vote.executeKickGPVote(daoOrg, signer, voteId);
      } else if (type === VOTE_TYPE.TRANSFER_NFT) {
        await api.vote.executeTransferNFTVote(daoOrg, signer, wid);
      } else if (type === VOTE_TYPE.TRANSFER_TOKEN) {
        await api.vote.executeWithdrawVote(daoOrg, signer, wid);
      } else if (type === VOTE_TYPE.TRANSFER_PERIOD) {
        await api.vote.executeTransferPeriodVote(daoOrg, signer, voteId);
      } else if (type === VOTE_TYPE.TRANSFER_MANAGE_FEE) {
        await api.vote.executeTransferManageFeeVote(daoOrg, signer, wid);
      } else if (type === VOTE_TYPE.APPROVE_MANAGER) {
        await api.vote.executeApproveGPVote(daoOrg, signer, voteId);
      }
    } catch (error) {
      console.error(error);
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: `Excute failed: ${
            (error.data && error.data.message) || "unknown"
          }`,
          closable: true,
        },
      });
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  return (
    <Box>
      <BackNav to={`vote/${lastNav || "queue"}`} />
      <ContentBox>
        <FirstLine>
          <LftBox>
            <img src={voteStatusIcon} alt="" />
          </LftBox>
          <RhtLine>
            <dl>
              <dt>Type</dt>
              <dd className="font-demiBold">{t(voteInfoByType.name)}</dd>
            </dl>
            <dl>
              <dt>Created time</dt>
              <dd className="font-demiBold">{voteData.startAt}</dd>
            </dl>
            <dl>
              <dt>Agree/Disagree</dt>
              <dd className="font-demiBold">
                {voteData.agree_count} : {voteData.oppose_count}
              </dd>
            </dl>
            <dl>
              <dt>Expiry</dt>
              <dd>
                <FormatLeftTime leftTime={voteData.left} />
              </dd>
            </dl>
          </RhtLine>
        </FirstLine>
        <MainBox>
          <MidBox>
            {VoteDetailByType}
            <dl>
              <dt>TxHash</dt>
              <AddressBox>
                {publicJs.AddresstoShow(voteData.tx_hash)}{" "}
                <CopyStr address={voteData.tx_hash} gray={true} />
                <a
                  href={`${exploreScan}tx/${voteData.tx_hash}`}
                  rel="noreferrer"
                  target="_blank"
                >
                  <img src={LinkImg} alt="" />
                </a>
              </AddressBox>
            </dl>
            {!voteData.executor && (
              <dl>
                <dt>Executed</dt>
                <dd>n/a</dd>
                {/* {voteData.status === 1 || voteData.status === 2 ? (
                    <dd>
                      <AddressBox>
                        {publicJs.AddresstoShow(voteData.executor)}{" "}
                        <CopyStr address={voteData.executor} gray={true} />
                        <img src={LinkImg} alt="" />
                      </AddressBox>
                    </dd>
                  ) : (
                    <dd>n/a</dd>
                  )} */}
              </dl>
            )}
          </MidBox>
          <MidBox>
            <dl>
              <dt>Creator</dt>
              <dd>
                <ItemBox>
                  <Avatar address={voteData.creator} size="24px" />
                  <div>
                    <div className="name">
                      {orgMembersMap[voteData.creator]}
                    </div>
                    <div className="address">
                      {publicJs.AddresstoShow(voteData.creator)}{" "}
                      <CopyStr address={voteData.creator} gray={true} />
                    </div>
                  </div>
                </ItemBox>
              </dd>
            </dl>
            <dl>
              <dt>
                Votes ({voteData.agree_count}:{voteData.oppose_count})
              </dt>
              <VotersBox>
                {voters.map((item, i) => (
                  <ItemLine key={i}>
                    <img
                      style={{ width: "20px" }}
                      src={item.support ? ConfirmImg : WrongImg}
                      alt=""
                      className="agreeImg"
                    />
                    <ItemBox>
                      <Avatar address={item.address} size="24px" />
                      <div>
                        <div>{orgMembersMap[item.address]}</div>
                        <div>
                          {publicJs.AddresstoShow(item.address)}{" "}
                          <CopyStr gray={true} address={item.address} />{" "}
                          {/* <TagBox>Hide</TagBox> */}
                        </div>
                      </div>
                    </ItemBox>
                  </ItemLine>
                ))}
              </VotersBox>
            </dl>
            {voteData.executor && (
              <dl>
                <dt>Executed</dt>
                <dd>
                  <ItemBox>
                    <Avatar address={voteData.executor} size="24px" />
                    <div>
                      <div>{orgMembersMap[voteData.executor]}</div>
                      <div>
                        {publicJs.AddresstoShow(voteData.executor)}{" "}
                        <CopyStr gray={true} address={voteData.executor} />
                      </div>
                    </div>
                  </ItemBox>
                </dd>
              </dl>
            )}
          </MidBox>
          <div>
            {optionlist && (
              <VoteEcharts optionlist={optionlist} status={voteData.status} />
            )}
          </div>
        </MainBox>
        {/* <div onClick={doExcute}>Execute</div> */}
        <ActionButtons
          lefttime={voteData.left}
          action={action}
          isExecute={voteData.executor}
          canExecute={(isOwner || isGP) && canExe}
        />
      </ContentBox>
    </Box>
  );
}

