import styled from "styled-components";

const TabsBox = styled.ul`
    display: flex;
  padding: 0 25px;
  li{
    font-size: 16px;
    font-weight: 400;
    color: #10164B;
    line-height: 19px;
    margin-right: 40px;
    cursor: pointer;
  }
  .active{
    font-size: 18px;
    font-weight: 500;
    color: #D51172;
    border-bottom: 3px solid #D51172;
    padding-bottom: 10px;
  }
`

export default function Tabs(props){
    const {navList,current,handleCurrent} = props;

    const handleSelected = (num) =>{
        handleCurrent(num)
    }

    return <TabsBox>
        {
            navList.map((item,index)=>(<li key={`nav_${index}`} className={current === index ?"active":""} onClick={()=>handleSelected(index)}>
                {item}
            </li>))
        }
    </TabsBox>
}