import React, { useCallback, useState, useEffect, useMemo } from "react";
import { useNavigate, Routes, Route, useParams, Navigate } from "react-router-dom";
import { useWeb3React } from "@web3-react/core";
import styled from "styled-components";
import { ethers } from "ethers";

import HomeDefault from "components/home";
import Portfolio from "components/portfolio/portfolio";

import HomeImg from "img/icon/svg/home.svg";
import HomeHover from "img/icon/svg/homeHover.svg";
import PortfolioImg from "img/icon/svg/portfolio.svg";
import PortfolioHover from "img/icon/svg/portfolioHover.svg";
import VoteImg from "img/icon/svg/vote.svg";
import VoteHover from "img/icon/svg/voteHover.svg";
import ManagersImg from "img/icon/svg/managers.svg";
import ManagersHover from "img/icon/svg/managersHover.svg";
import ManagementImg from "img/icon/svg/management.svg";
import ManagementHover from "img/icon/svg/managementHover.svg";

import { useDaoContext } from "views/dao/provider";
// import useDaoIdByHandle from "hooks/useDaoIdByHandle";
import useDaoDetailByHandle from "hooks/useDaoDetailByHandle";
import api from "api/index";
import {
  VENTURE_MANAGER,
  VENTURE_MANAGEFEE_MANAGER,
  VENTURE_STOCK_MANAGER,
  ORG_MANAGER,
  DAO_ORG,
} from "utils/constant";
import VoteNV from "components/newVersionVote";
import Managers from "components/manager/managers";
import Settings from "components/settings";
import Apps from "components/apps";
import AppAndSettings from "views/appAndsettings";
import SideNavLayout from "components/layout";
import {
  useDaoFactoryContract,
  useOrgContract,
  useVentureContract,
  useERC20Contract,
} from "hooks/useContract";
import useManageFee from "hooks/useManageFee";
import DaoHeader from "./header";

const STEPS = [
  {
    name: "Left.Home",
    key: "homepage",
    icon: HomeImg,
    iconHover: HomeHover,
    newDot: false,
  },
  {
    name: "Left.Portfolio",
    key: "portfolio",
    icon: PortfolioImg,
    iconHover: PortfolioHover,
    newDot: false,
  },
  {
    name: "Left.Vote",
    key: "vote",
    icon: VoteImg,
    iconHover: VoteHover,
    newDot: false,
  },
  {
    name: "Left.Managers",
    key: "managers",
    icon: ManagersImg,
    iconHover: ManagersHover,
    newDot: false,
  },
  {
    name: "Left.Management",
    key: "app_settings",
    icon: ManagementImg,
    iconHover: ManagementHover,
    newDot: false,
  },
];

export default function Frame(props) {
  const navigate = useNavigate();
  const { fullhandle } = useParams();

  const { subRoute } = props;
  const { id, handle, dao } = useDaoDetailByHandle(fullhandle);

  const {
    state: { componentAddressMap, fundOrPeriod, fundInfo, fundToken },
    dispatch,
  } = useDaoContext();
  const { account, provider: web3Instance } = useWeb3React();
  const maincontract = useDaoFactoryContract();

  const orgContract = useOrgContract(componentAddressMap.get(ORG_MANAGER));
  const ventureContract = useVentureContract(
    componentAddressMap.get(VENTURE_MANAGER)
  );
  const stockContract = useERC20Contract(
    componentAddressMap.get(VENTURE_STOCK_MANAGER)
  );
  const manageFee = useManageFee(
    componentAddressMap.get(VENTURE_MANAGEFEE_MANAGER)
  );

  const [step, setStep] = useState("homepage");

  useEffect(() => {
    setStep(subRoute);
  }, [subRoute]);

  const onChangeStep = (key) => {
    console.log("[onChangeStep]", key);
    setStep(key);
    navigate(`/dao/${fullhandle}/${key}`);
  };

  useEffect(() => {
    if (!id || !dao) {
      return;
    }
    dispatch({
      type: "SET_BASE_DATA",
      payload: {
        id: id,
        handle: dao.handle,
        name: dao.name,
        desc: dao.desc,
        logo: dao.logo,
        et_holders: dao.et_holders,
        period: dao.period,
        tav: dao.tav,
        is_like: dao.is_like,
        twitter_url: dao.twitter_url,
        discord_url: dao.discord_url,
        telegram_url: dao.telegram_url,
      },
    });
    dispatch({
      type: "SET_DAO_ID",
      payload: id,
    });
    dispatch({
      type: "SET_FULL_HANDLE",
      payload: fullhandle,
    });
  }, [id, dao, fullhandle]);

  const initDaoBase = useCallback(
    async (_id) => {
      try {
        return maincontract.instanceMap(_id);
      } catch (error) {
        console.error("[instanceMap] failed", error);
      }
    },
    [maincontract]
  );

  const initDaoContract = useCallback(
    async (_daoOrganization) => {
      const componentMap = new Map();

      let components = [];
      let templateConfig;
      try {
        componentMap.set(DAO_ORG, _daoOrganization);
        const daoOrg = await api.daoOrg.getContract(
          web3Instance,
          _daoOrganization
        );
        const templateConfigAddress = await daoOrg.templateConfig();
        templateConfig = await api.template.getContract(
          web3Instance,
          templateConfigAddress
        );
        components = await templateConfig.listComponents(0, 10);
      } catch (error) {
        console.error("listComponents failed", error);
        return;
      }
      const reqs = components.map(address => templateConfig.componentAddrToLabel(address))
      const res = await Promise.all(reqs)
      res.forEach((label, i) => {
        componentMap.set(label, components[i])
      })
      // for (const address of components) {
      //   const label = await templateConfig.componentAddrToLabel(address);
      //   componentMap.set(label, address);
      //   console.log(`-> ${label} - ${address}`);
      // }
      dispatch({ type: "SET_COMPONENTS_ADDRESS", payload: componentMap });
    },
    [web3Instance]
  );

  useEffect(async () => {
    if (stockContract) {
      const symbol = await stockContract.symbol();
      const deci = await stockContract.decimals();
      dispatch({
        type: "SET_STOCK_TOKEN",
        payload: {
          symbol,
          deci,
          address: stockContract.address,
        },
      });
    }
  }, [stockContract]);

  useEffect(async () => {
    if (!ventureContract || !web3Instance) {
      return;
    }
    try {
      const raised = await ventureContract.getRaisedAmount();
      const goal = await ventureContract.RaiseMinSoftCap();
      const hard = await ventureContract.RaiseMaxHardCap();
      const address = await ventureContract.TokenType();

      const fundContract = await api.erc20.getContract(web3Instance, address);
      const symbol = await fundContract.symbol();
      const deci = await fundContract.decimals();
      const fundInfo = {
        raised: ethers.utils.formatUnits(raised, deci),
        goal: ethers.utils.formatUnits(goal, deci),
        hard: ethers.utils.formatUnits(hard, deci),
      };
      const fundToken = {
        symbol,
        address,
        deci,
      };
      dispatch({ type: "SET_FUND_INFO", payload: fundInfo });
      dispatch({ type: "SET_FUND_TOKEN", payload: fundToken });
    } catch (error) {
      console.error("getfund failed", error);
    }
  }, [ventureContract, web3Instance]);

  useEffect(async () => {
    if (fundOrPeriod === "fund") {
      try {
        const raised = await ventureContract.getRaisedAmount();
        dispatch({
          type: "SET_FUND_INFO",
          payload: {
            ...fundInfo,
            raised: ethers.utils.formatUnits(raised, fundToken.deci),
          },
        });
        dispatch({ type: "UPDATE_FUND_OR_PERIOD", payload: null });
      } catch (error) {}
    }
  }, [fundOrPeriod]);

  useEffect(async () => {
    if (!ventureContract) {
      return;
    }
    try {
      const bonus = await ventureContract.totalGPBonusPercent();
      dispatch({ type: "SET_CARRIED_INTEREST", payload: bonus.toNumber() });
    } catch (error) {
      console.error("get totalGPBonusPercent failed", error);
    }

    try {
      const fee = await ventureContract.ManageFeePercent();
      dispatch({ type: "SET_MANAGE_FEE", payload: fee.toNumber() });
    } catch (error) {
      console.error("get ManageFeePercent failed", error);
    }
  }, [ventureContract]);

  useEffect(() => {
    if (manageFee) {
      dispatch({ type: "SET_MANAGE_FEE_TOKEN", payload: manageFee });
    }
  }, [manageFee]);

  useEffect(async () => {
    if (!orgContract) {
      return;
    }
    try {
      await api.org.whoAmI(orgContract, account).then((data) => {
        if (!data) return;
        dispatch({
          type: "WhO_AM_I",
          payload: {
            isOwner: data[0],
            isGP: data[2],
          },
        });
      });
    } catch (error) {
      console.error("get whoAmI failed", error);
    }
  }, [orgContract]);

  useEffect(async () => {
    if (!id || !maincontract) {
      return;
    }
    // base data
    const data = await initDaoBase(id);
    // base contract
    if (data) {
      initDaoContract(data.daoOrganization);
    }
  }, [id, maincontract]);

  return (
    <Box>
      <DaoHeader fullhandle={fullhandle} />
      <SideNavLayout navs={STEPS} onChangeNav={onChangeStep} step={step}>
        <Routes>
          <Route path="homepage/*" element={<HomeDefault />} />
          <Route path="portfolio/*" element={<Portfolio />} />
          <Route path="vote/*" element={<VoteNV />} />
          <Route path="managers" element={<Managers />} />
          {/* app & settings */}
          <Route path="app_settings" element={<AppAndSettings />} />
          <Route path="app/*" element={<Apps />} />
          <Route path="settings/*" element={<Settings />} />
        </Routes>
      </SideNavLayout>
    </Box>
  );
}

const Box = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
`;
