import React, { useEffect, useState, useCallback, useMemo } from "react";
import { Button, Input, Modal, Tooltip } from "antd";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { ethers } from "ethers";

import { useDaoContext } from "../../views/dao/provider";
import api from "../../api";
import { useDaoOrgContract } from "../../hooks/useContract";
import { DAO_ORG } from "../../utils/constant";
import useSigner from "hooks/useSigner";

import Tip from "../../components/svg/tip";
import { useWeb3React } from "@web3-react/core";
import { useAppContext } from "components/provider/appProvider";
import useFundLogo from "hooks/useFundLogo";

export default function WithdrawBonus(props) {
  const { show, close } = props;

  const { t } = useTranslation();
  const { provider, account } = useWeb3React();
  const { dispatch } = useAppContext();
  const { state: daoState } = useDaoContext();
  const { componentAddressMap, fundToken } = daoState;

  const [amount, setAmount] = useState();
  const [maxAmount, setMaxAmount] = useState(0);

  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const fundlogo = useFundLogo()

  const handleClose = () => {
    close();
  };

  const confirm = async () => {
    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    console.log(String(amount), ethers.utils.parseUnits(String(amount), fundToken.deci))
    try {
      await api.venture.withdrawBonus(
        daoOrg,
        signer,
        ethers.utils.parseUnits(String(amount), fundToken.deci)
      );
    } catch (error) {
      console.error(error);
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: "withdraw failed",
          closable: true,
        },
      });
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  const handleChangeAmount = useCallback((e) => {
    setAmount(e.target.value);
  }, []);

  useEffect(async () => {
    if (!show) {
      return
    }
    if (!daoOrg || !provider || !account || !fundToken) {
      return;
    }
    try {
      const templateConfigAddress = await daoOrg.templateConfig();
      const res = await api.venture.getClaimBonusEstimate(provider, signer, {
        account,
        template: templateConfigAddress,
        amountClaim: 0,
      });
      setMaxAmount(ethers.utils.formatUnits(res, fundToken.deci));
    } catch (error) {
      console.error("getClaimBonusEstimate failed", error);
    }
  }, [show, daoOrg, account, provider,fundToken]);

  return (
    <ModalBox
      visible={show}
      onCancel={handleClose}
      footer={null}
      destroyOnClose={true}
      maskClosable={false}
      width={364}
    >
      <div className="title font-medium">
        <span>{t("Venture.Bonus")}</span>
      </div>
      <ModalContent className="font-regular">
        <DisplayBox>
          <InputLabel>
            <span>Bonus unlocked</span>
            <Tooltip
              overlayClassName="tipTool"
              placement="topLeft"
              title={t("Venture.BonusTip")}
              color="#FFFFFF"
              arrowPointAtCenter
            >
              <Tip className="tip" />
            </Tooltip>
          </InputLabel>
          <TokenBox className="top">
            <span className="amount">{maxAmount}</span>
            <img src={fundlogo} alt="" />
            <span>{fundToken && fundToken.symbol}</span>
          </TokenBox>
        </DisplayBox>
        <InputBox>
          <div>
            <InputLabel>{t("Venture.Amount")}</InputLabel>
            <InputStyled
              autoFocus
              placeholder={t("Create.Enter")}
              value={amount}
              autoComplete="off"
              type="number"
              onChange={handleChangeAmount}
            />
          </div>
          <div className="right">
            <TokenBox>
              <img src={fundlogo} alt="" />
              <span>{fundToken && fundToken.symbol}</span>
            </TokenBox>
            <span
              className="max font-medium"
              onClick={() => {
                setAmount(maxAmount);
              }}
            >
              {t("Max")}
            </span>
          </div>
        </InputBox>
      </ModalContent>

      <Footer>
        <Button
          disabled={!amount || amount < 0 || amount > maxAmount}
          type="primary"
          onClick={confirm}
          style={{ width: "100%" }}
        >
          {t("Venture.Withdraw")}
        </Button>
      </Footer>
    </ModalBox>
  );
}

const ModalBox = styled(Modal)``;

const ModalContent = styled.div`
  margin-top: 28px;
`;

const Footer = styled.div`
  margin-top: 31px;
  button {
    border-radius: 8px !important;
  }
`;

const InputBox = styled.div`
  width: 100%;
  height: 76px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #f9f9f9;
  border-radius: 12px;
  padding: 13px 20px;
  .right {
    text-align: right;
    .max {
      color: #0e164e;
      line-height: 19px;
      font-size: 14px;
      margin-top: 3px;
      cursor: pointer;
    }
  }
`;

const DisplayBox = styled(InputBox)`
  background: transparent;
  display: block;
  height: auto;
  margin-bottom: 24px;
`;

const InputLabel = styled.div`
  font-weight: 400;
  color: #a6a6b7;
  line-height: 19px;
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  .tip {
    margin-left: 6px;
  }
`;

const InputStyled = styled(Input)`
  &.ant-input {
    background: transparent !important;
    height: 27px !important;
    padding: 0;
    font-size: 18px;
    border-radius: 0 !important;
    border: none;
    &:focus {
      box-shadow: none;
    }
  }
`;

const TokenBox = styled.div`
  display: flex;
  align-items: center;
  &.top {
    margin-top: 14px;
  }
  img {
    width: 24px;
    height: 24px;
    margin-right: 6px;
    margin-left: 10px;
  }
  span {
    line-height: 24px;
    font-weight: 400;
    color: #0e164e;
  }
  span.amount {
    color: #10164b;
    font-size: 18px;
  }
`;
