import React, { memo } from "react";
import styled from "styled-components";
import InvalidSvg from "../svg/common/invalid";

const CheckInput = memo((props) => {
  const { errorMsg, ...rest } = props;
  return (
    <BorderBox>
      <Input type="text" {...rest} className={errorMsg ? "error" : ""} />
      {errorMsg && (
        <Error>
          <InvalidSvg />
          <span>{errorMsg}</span>
        </Error>
      )}
    </BorderBox>
  );
});

export default CheckInput;

const BorderBox = styled.div`
  position: relative;
`;

const Input = styled.input`
  background: #ffffff;
  box-shadow: 0px 0px 4px 0px rgba(16, 22, 75, 0.1);
  border-radius: 8px;
  padding: 0 20px;
  line-height: 44px;
  width: 100%;
  border: 0;
  &.error {
    border: 1px solid #ff6868;
  }
`;

const Error = styled.div`
  font-size: 12px;
  color: #e00000;
  margin-top: 13px;
  margin-left: 3px;
  display: flex;
  align-items: center;
  line-height: 14px;
  & > span {
    margin-left: 6px;
  }
`;
