import { ethers } from "ethers";
import abi from "../abi/web3Abi/DaoFactory";
import getNetworkData from "../network"

let loadMain = false;

const ConnectNetwork = async (web3Instance)=>{
    const network = await web3Instance.getNetwork()
    const chainid = network.chainId

    const networkData = getNetworkData(chainid)
    window.mainAddress = networkData.mainAddress
    window.serverApi = networkData.serverApi
    window.graphApi = networkData.graphApi

    return chainid
}

export default async function mainConnect(state, dispatch) {
    const {maincontract, maincontractState, web3Instance, web3InstanceState} = state;
    if (web3InstanceState !== 'READY') return
    if (maincontract && maincontractState !== 'LOAD_MAINCONTRACT') return
    const asyncLoadMain = async () => {
        try {
            const chainid = await ConnectNetwork(web3Instance)
            dispatch({type: 'SET_CHAINID', payload: chainid});

            const { DaoFactory } = window.mainAddress;

            let contract = new ethers.Contract(DaoFactory, abi, web3Instance);
            dispatch({type: 'SET_MAINCONTRACT', payload: contract});
            loadMain = false
        } catch (e) {
            console.error(e);
            dispatch({type: 'MAINCONTRACT_ERROR'});
        }
    };
    if (!loadMain) {
        loadMain = true
        asyncLoadMain();
    }
}
