import { ethers } from "ethers";

export const checkBonus = (list) => {
    const bonus_arr = list.map((a) => ethers.BigNumber.from(a.bonus || 0));
    const v = bonus_arr.reduce(function (
      accumulator,
      currentValue,
      currentIndex,
      array
    ) {
      return accumulator.add(currentValue);
    });
    return v.toNumber() === 100;
  };