import { memo, useCallback, useEffect } from "react";
import { Menu, Dropdown } from "antd";
import { useWeb3React } from "@web3-react/core";
import styled from "styled-components";

import { NetworkList } from "../../network";

import { useAppContext } from "components/provider/appProvider";
import { walletConnect } from "components/wallet/connector";
import { useTranslation } from "react-i18next";
import { useNetworkData } from "hooks/useNetwork";
import { getJsonFileByFetch } from "api/apiHTTP";
import { useLazyQuery } from "@apollo/client";
import { LoginQuery } from "api/graphql/auth";
import { signMessage } from "utils/sign";
import { LOCALSTORAGE } from "utils/constant";
import { clearStorage } from "utils/auth";

const NetworkMenu = memo((props) => {
  const { t } = useTranslation();
  const { connector, chainId, account, provider } = useWeb3React();
  const networkData = useNetworkData(chainId);
  const {
    state: { selectedWallet },
    dispatch,
  } = useAppContext();

  const getConstant = async (network) => {
    const simpleName = network.simple;
    try {
      const data = await getJsonFileByFetch(
        `${network.constantHost}/${simpleName}.json`
      );
      dispatch({
        type: "SET_FUNDLIST",
        payload: (data.fundList || []).map((fund) => ({
          ...fund,
          logo: `${network.constantHost}${fund.logo}`,
        })),
      });
      dispatch({
        type: "SET_RECOMMEND",
        payload: data.bestpractices || [],
      });
    } catch (error) {
      console.error("get fundList failed", error);
    }

    try {
      const data = await getJsonFileByFetch(
        // `${network.constantHost}/tokens/1.json`
        `${network.constantHost}/tokens/${network.chainId}.json`
      );
      dispatch({
        type: "SET_ALL_TOKENS",
        payload: (data || []).map((token) => ({
          ...token,
          logo: token.logoURI,
          // logo: token.logo,
        })),
      });
    } catch (error) {
      console.error("get all tokens failed", error);
    }
  };

  useEffect(() => {
    if (networkData && chainId && networkData.chainId !== chainId) {
      dispatch({
        type: "MSGTYPE",
        payload: { closable: true, msg: t("Msg.WrongChainMatch") },
      });
    }
    if (networkData && chainId) {
      // set global contract address
      window.mainAddress = networkData.mainAddress;
      getConstant(networkData);
    }
  }, [networkData, chainId]);

  useEffect(() => {
    if (networkData && networkData.scan) {
      dispatch({ type: "SET_EXPLORE_SCAN", payload: networkData.scan.host });
    }
  }, [networkData]);

  const [doLogin, { loading }] = useLazyQuery(LoginQuery, {
    onCompleted(data) {
      const loginData = data.login;
      console.log("====loginData", loginData);
      localStorage.setItem(LOCALSTORAGE.USER_TOKEN, loginData.token);
      localStorage.setItem(
        LOCALSTORAGE.TOKEN_EXPIRE,
        String(Date.now() + loginData.tokenExp * 1000)
      );
      console.log(
        "====TOKEN_KEY",
        localStorage.getItem(LOCALSTORAGE.USER_TOKEN)
      );
      console.log(
        "====TOKEN_EXPIRE",
        localStorage.getItem(LOCALSTORAGE.TOKEN_EXPIRE)
      );
      dispatch({ type: "LOADINGTYPE", payload: null });
    },
    onError(error) {
      dispatch({ type: "LOADINGTYPE", payload: null });
      connector.deactivate();
    },
  });
  const handleLogin = async () => {
    dispatch({ type: "LOADINGTYPE", payload: "Signing" });
    clearStorage();
    let signData;
    const now = Date.now();
    try {
      signData = await provider.send("personal_sign", [
        signMessage(chainId, account, now),
        account,
      ]);
    } catch (error) {
      dispatch({ type: "LOADINGTYPE", payload: null });
      console.error("Sign account failed", error);
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: "Sign account failed",
          closable: true,
        },
      });
      // disconent
      connector.deactivate();
    }
    if (signData) {
      doLogin({
        variables: {
          sign: signData,
          account,
          timestamp: now,
          chainId,
        },
      });
    }
  };

  const switchChain = useCallback(
    async (chainInfo) => {
      try {
        if (connector === walletConnect) {
          await connector.activate(chainInfo.chainId);
        } else {
          await connector.activate({
            chainId: chainInfo.chainId,
            chainName: chainInfo.chainName,
            nativeCurrency: chainInfo.nativeCurrency,
            logo: chainInfo.logo,
          });
        }
        if (account) {
          handleLogin();
        }
      } catch (error) {
        console.error(error);
        dispatch({
          type: "MSGTYPE",
          payload: {
            msg: `${t("Msg.SwitchChainError", {
              chainName: chainInfo.chainName,
            })}`,
            closable: true,
          },
        });
      }
    },
    [connector, selectedWallet, account]
  );

  const MenuList = () => {
    return (
      <MyMenu className="myMenu">
        {NetworkList.map((item, i) => (
          <Menu.Item key={i} className="menuT">
            <div
              className={
                networkData && networkData.name === item.chainName
                  ? "menu-content selected"
                  : "menu-content"
              }
              onClick={() => {
                switchChain(item);
              }}
            >
              {/*{*/}
              {/*    networkData && networkData.name === item.chainName ? <img src={NetworkOn} alt="" /> : <img src={NetworkOff} alt="" />*/}
              {/*}   */}
              {<img src={item.logo} alt="" />}
              {item.chainName}
            </div>
          </Menu.Item>
        ))}
      </MyMenu>
    );
  };

  return (
    <Dropdown overlay={MenuList} trigger={["hover"]}>
      <Network>
        {<img src={(networkData && networkData.logo) || NetworkList[0].logo} alt="" className="logo"/>}
        {(networkData && networkData.name) || NetworkList[0].chainName}
      </Network>
    </Dropdown>
  );
});

export default NetworkMenu;

const MyMenu = styled(Menu)`
  margin-top: 20px;
  width: 246px;
  background: #ffffff;
  padding: 25px 10px;
  color: #0e164e;
  border-radius: 24px !important;
  .ant-dropdown-menu-item:hover,
  .ant-dropdown-menu-submenu-title {
    background: #f9f9f9;
    border-radius: 8px;
  }
  .menu-content {
    padding: 0 10px;
    height: 40px;
    font-size: 18px;
    font-weight: 300;
    line-height: 40px;
    img {
      width: 20px;
      height: 20px;
      margin-right: 10px;
      position: relative;
      top: -2px;
    }
  }
`;
const Network = styled.div`
  cursor: pointer;
  -moz-user-select: none;
  user-select: none;
  margin-top: 3px;
  display: flex;
  align-items: center;
  .logo{
    width: 25px;
    height: 25px;
    margin-right: 10px;
    border-radius: 25px;
  }
  img.icon {
    margin-left: 10px !important;
    margin-top: -5px !important;
  }
`;
