import axios from 'axios';
import { ethers } from 'ethers';

const POST = (data)=>{
    return axios({
        method: 'POST',
        url: window.serverApi,
        headers: {
            'Content-Type': 'application/json'
        },
        data: data
    })
}

const POST_GRAPH = data=>{
    return axios({
        method: 'POST',
        url: window.graphApi,
        headers: {
            'Content-Type': 'application/json'
        },
        data: data
    })
}

