import React from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

import LogoIcon from "img/walletConnect/logo.png";

const WalletConnectLogo = (props) => {
  const { t } = useTranslation();

  const { small } = props;
  return (
    <WalletConnectLogoStyled>
      <LogoStyled src={LogoIcon} alt="" />
      {small ? (
        <SmallTitleStyled>{t("walletConnect.title")}</SmallTitleStyled>
      ) : (
        <TitleStyled>{t("walletConnect.title")}</TitleStyled>
      )}
    </WalletConnectLogoStyled>
  );
};

export default WalletConnectLogo;

const WalletConnectLogoStyled = styled.div`
  display: flex;
  align-items: center;
  color: #10164b;
`;

const LogoStyled = styled.img`
  width: 24px;
  height: 24px;
  margin-right: 12px;
`;

const SmallTitleStyled = styled.span`
  font-size: 14px;
  color: #101352;
`;

const TitleStyled = styled.span`
  font-size: 18px;
`;
