import React, { memo } from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import Button from "../../components/common/button";

const ImgBox = styled.div`
  margin: auto 30px;
  img {
    width: 32px;
  }
  @media (max-width: 1440px) {
    margin: auto 10px auto 24px;
    img {
      width: 24px;
    }
  }
`;

const SideNavLayout = memo((props) => {
  const { t } = useTranslation();
  const { navs, onChangeNav, containerStyle, step } = props;
  const [curNav, setCurNav] = useState();
  const [ list ] = useState(["basic_info","add_manager","fundraising","preview"])

  useEffect(() => {
    if (navs.length <= 0) return;
    setCurNav(step);
  }, [step, navs]);

  const handleNext = () =>{
      const cur = list.findIndex(item=>item===step);
      const curStr = list[ cur + 1];
      onChangeNav(curStr)
  }

  return (
    <SideNavLayoutStyled className="container">
      <SideNav>
        <NavUl>
          {navs.map((n, i) => (
            <NavLi
              key={i}
              onClick={() => {
                if (n.key === curNav) {
                  return;
                }
                setCurNav(n.key);
                onChangeNav && onChangeNav(n.key);
              }}
              className={n.key === curNav ? "selected" : ""}
            >
              <span className={`dot ${n.available && "available"} `} />
              {t(n.name)}
            </NavLi>
          ))}
        </NavUl>
      </SideNav>
      <LayoutContainer style={containerStyle}>
          <div>{props.children}</div>
          {
              curNav !=="preview" &&<NextBox>
                  <Button primary onClick={()=>handleNext()}>
                    {t("next")}
                  </Button>
              </NextBox>
          }

      </LayoutContainer>
    </SideNavLayoutStyled>
  );
});

export default SideNavLayout;

const SideNavLayoutStyled = styled.div`
  display: flex;
  align-items: stretch;
  flex-grow: 1;
  height: 100%;
  @media (max-width: 1440px) {
    align-items: flex-start;
  }
`;

const SideNav = styled.div`
  width: 286px;
  background: #ffffff;
  box-shadow: 0px 0px 16px 0px rgba(16, 22, 75, 0.06);
  border-radius: 24px;
  margin-right: 16px;
  padding: 26px 25px;

  @media (max-width: 1366px) {
    width: 182px;
    padding: 30px 10px;
  }
  @media (min-width: 1367px) and (max-width: 1440px) {
    width: 212px;
    padding: 30px 10px;
  }
  @media (max-width: 1440px) {
    height: 500px;
  }
`;

const LayoutContainer = styled.div`
  flex: 1;
  background: #ffffff;
  box-shadow: 0px 0px 16px 0px rgba(16, 22, 75, 0.06);
  border-radius: 24px;
  border: 1px solid #f0f0f2;
  //padding: 40px 20px;
`;

const NavUl = styled.ul``;

const NavLi = styled.li`
  display: flex;
  align-items: center;
  border-radius: 14px;
  line-height: 70px;
  font-size: 18px;
  cursor: pointer;
  position: relative;
  .newDot {
    width: 9px;
    height: 9px;
    background: #ff0000;
    border-radius: 9px;
    position: absolute;
    right: 5px;
    top: 15px;
  }

  .dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #ccc;
    display: block;
    margin: 0 30px;
    &.available {
      background: #02A177;
      border: none;
    }
  }
  &.selected {
    background: #d51172;
    color: #ffffff;
    .dot {
      background: transparent;
      border: 2px solid #ffffff;
    }
  }
  @media (max-width: 1440px) {
    font-size: 14px;
    line-height: 50px;
  }
`;

const NextBox = styled('div')`
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-top: 40px;
`
