import { memo, useMemo, useEffect, useState, useCallback } from "react";
import styled from "styled-components";
import { useDaoContext } from "views/dao/provider";
import SearchImg from "../../../img/portfolio/search.png";
import BackSvg from "components/svg/common/back";

import { VAULT_MANAGER } from "utils/constant";
import useVaultTokens from "hooks/useVaultTokens";
import { useTranslation } from "react-i18next";
import { ethers } from "ethers";
import api from "api";
import { useWeb3React } from "@web3-react/core";
import LogoSVG from "../../../img/defaultLogo.svg";

const SelectTokenModal = memo((props) => {
  const { closeModal, tokens } = props;
  const { t } = useTranslation();

  const [searchVal, setSearchVal] = useState("");
  const [showList, setShowList] = useState([]);

  const { provider } = useWeb3React();

  const recommend = useMemo(() => {
    return tokens.filter((t) => t.symbol === "USDT" || t.symbol === "WETH");
  }, [tokens]);

  const handleSearchToken = useCallback(async (address) => {
    try {
      const contract = await api.erc20.getContract(provider, address);
      const deci = await contract.decimals();
      setShowList([
        {
          name: await contract.name(),
          symbol: await contract.symbol(),
          deci,
          address,
        },
      ]);
    } catch (error) {
      console.error("handleSearchToken failed", error);
      setShowList([]);
    }
  }, []);

  useEffect(() => {
    if (searchVal) {
      const v = searchVal.toLowerCase();
      const filterList = tokens.filter((t) => {
        return (
          t.address.toLowerCase() === v ||
          (t.name || "").toLowerCase().includes(v) ||
          (t.symbol || "").toLowerCase().includes(v)
        );
      });
      if (!filterList.length && ethers.utils.isAddress(searchVal)) {
        handleSearchToken(searchVal);
      } else {
        setShowList(filterList);
      }
      setShowList(filterList);
    } else {
      setShowList(tokens.slice(0, 50));
    }
  }, [tokens, searchVal]);

  const updateSearchVal = useCallback(({ target: { value } }) => {
    setSearchVal(value);
  }, []);

  return (
    <AddBox>
      <Bg>
        <Title>
          <CloseBox onClick={closeModal}>
            <BackSvg />
          </CloseBox>
          <span className="font-medium">Select a token</span>
        </Title>
        <SearchBox>
          <img src={SearchImg} alt="" />
          <input
            type="text"
            placeholder={t("Explore.TokenPlaceholder")}
            onChange={updateSearchVal}
          />
        </SearchBox>
        <BtnGroup>
          {recommend.map((item, index) => (
            <Btn key={index} onClick={() => closeModal(item)}>
              {/*<img src={item.logo} alt="" />*/}
              <img src={item.logo?item.logo:LogoSVG} alt="" />
              {item.symbol}
            </Btn>
          ))}
        </BtnGroup>
        <ScrollBox>
          <UlBox>
            {showList.map((item, index) => (
              <li key={`list_${index}`} onClick={() => closeModal(item)}>
                <img src={item.logo?item.logo:LogoSVG} alt="" />
                <div>
                  <div className="name">{item.symbol}</div>
                  <div className="tips">{item.name}</div>
                </div>
              </li>
            ))}
          </UlBox>
        </ScrollBox>
      </Bg>
    </AddBox>
  );
});

export default SelectTokenModal;

