import { useCallback, useState, memo } from "react";
import { Tooltip } from "antd";
import * as history from "history";
import { useParams, useNavigate } from "react-router-dom";
import styled from "styled-components";

import ConfirmModal from "../modals/confirm";
import RejectModal from "../modals/reject";

import ExpiryImg from "img/voteNV/explains.png";
import TransferImg from "img/voteNV/transfer.png";
import GemLogo from "img/walletConnect/gem_logo.svg";
import moment from "moment";
import { useLazyQuery } from "@apollo/client";
import {
  SubgraphApiQuery,
  getWCTHistory,
  getConfirmedStatus,
  getWCTlist,
} from "api/graphql/subQuery";
import { useEffect } from "react";
import { useWeb3React } from "@web3-react/core";
import api from "api";

import EmptyBox from "components/common/empty";
import PaginationBox from "components/common/pagination";
import Loading from "components/loadingNEW";
import useSigner from "hooks/useSigner";
import { useDaoContext } from "views/dao/provider";
import { DAO_ORG } from "utils/constant";
import { useDaoOrgContract } from "hooks/useContract";
import { useAppContext } from "components/provider/appProvider";
import { useTranslation } from "react-i18next";

export default function TxList(props) {
  const navigate = useNavigate();
  const { fullhandle } = useParams();
  const { t } = useTranslation();

  const { chainId, account } = useWeb3React();
  const signer = useSigner();
  const { dispatch } = useAppContext();
  const {
    state: {
      daoId,
      componentAddressMap,
      whoAmI: { isOwner },
    },
  } = useDaoContext();

  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));

  const [confirmVisible, setConfirmVisible] = useState(false);
  const [rejectVisible, setRejectVisible] = useState(false);
  const [list, setList] = useState([{}]);
  const [currentId, setCurrentId] = useState();

  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [total, setTotal] = useState(0);

  const [txStatusMap, setTxStatusMap] = useState({});

  const toGo = (id) => {
    navigate(`/dao/${fullhandle}/app/wallet_connect?tx=${id}`);
  };

  const handleConfirm = useCallback(
    async (flag) => {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
      try {
        await api.vault.confirmTransaction(daoOrg, signer, {
          id: currentId,
          state: flag,
        });
      } catch (error) {
        console.error(error);
        dispatch({
          type: "MSGTYPE",
          payload: { msg: "excuted failed", closable: true },
        });
      } finally {
        dispatch({ type: "LOADINGTYPE", payload: null });
        confirmVisible && setConfirmVisible(false);
        rejectVisible && setRejectVisible(false);
        setCurrentId(undefined);
      }
    },
    [daoOrg, signer, confirmVisible, rejectVisible, currentId]
  );

  const showConfirm = useCallback((e, id) => {
    e.stopPropagation();
    setConfirmVisible(true);
    setCurrentId(id);
  }, []);

  const showReject = useCallback((e, id) => {
    e.stopPropagation();
    setRejectVisible(true);
    setCurrentId(id);
  }, []);

  const closeConfirm = useCallback(() => {
    setConfirmVisible(false);
  }, []);

  const closeReject = useCallback(() => {
    setRejectVisible(false);
  }, []);

  const formatStartTime = useCallback((time) => {
    return moment(time * 1000).format("MMMM Do YYYY, h:mm A");
  }, []);

  const [getQueue, { loading }] = useLazyQuery(SubgraphApiQuery, {
    onCompleted(resp) {
      try {
        const data = JSON.parse(resp.subgraphApi);
        const lst = (data.data.transactionRecords || []).map(t=>({...t, createdAt: formatStartTime(Number(t.createAt))
        }));
        setList(lst);
        getStatus({
          variables: {
            chainId,
            query: getConfirmedStatus(daoId, lst.map((l) => l.txId), account),
          },
        });
        const tc = data.data.totalCount;
        if (tc) {
          setTotal(tc.count);
        }
      } catch (error) {}
    },
    onError(error) {
      console.error("getQueue failed", error);
    },
  });

  const [getStatus] = useLazyQuery(SubgraphApiQuery, {
    onCompleted(resp) {
      console.log("====", resp);
      try {
        const data = JSON.parse(resp.subgraphApi);
        const statusList = data.data.transactionConfirmRecords || [];
        const statusMap = {};
        statusList.forEach((s) => {
          statusMap[s.txID] = s.state;
        });
        setTxStatusMap(statusMap);
      } catch (error) {}
    },
    onError(error) {
      console.error("getQueue failed", error);
    },
  });

  useEffect(() => {
    if (!chainId || !daoId) {
      return;
    }
    getQueue({
      variables: {
        query: getWCTlist(daoId, page, pageSize, false),
        chainId,
      },
      fetchPolicy: 'network-only',
    });
  }, [daoId, chainId, page, pageSize]);

  const onChangePage = useCallback((v) => {
    setPage(v);
  }, []);

  const onShowSizeChange = useCallback((c, size) => {
    setPageSize(size);
  }, []);

  const UserStateAction = memo(({ tx, state }) => {
    if (state === undefined) {
      return (
        <>
          <AgreeButton
            onClick={(e) => {
              showConfirm(e, tx.txID);
            }}
          >
            Agree
          </AgreeButton>{" "}
          <DisagreeButton onClick={(e) => showReject(e, tx.txID)}>
            Disagree
          </DisagreeButton>
        </>
      );
    }
    if (state) {
      return <StatusButton>Confirmed</StatusButton>;
    } else {
      return <StatusButton>Rejected</StatusButton>;
    }
  });

  return (
    <TableBox>
      <ConfirmModal
        show={confirmVisible}
        handleClose={closeConfirm}
        handleConfirm={handleConfirm}
      />
      <RejectModal
        show={rejectVisible}
        handleClose={closeReject}
        handleConfirm={handleConfirm}
      />
      <table cellPadding="0" cellSpacing="0">
        <thead>
          <tr>
            <th>No.</th>
            <th>Source</th>
            <th>            </th>
            <th>Created time</th>
            <th className="Left">
              Confirm/Reject
              <Tooltip title="The ratio of approval to disapproval.">
                <img src={ExpiryImg} alt="" />
              </Tooltip>
            </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {list.map((tx) => (
            <tr key={tx.txID} onClick={() => toGo(tx.txID)}>
              <td className="font-medium">{tx.txID}</td>
              <td className="font-medium Left">
                {tx.source === "Gem" ? <img src={GemLogo} alt="" /> : <></>}
                <span>{tx.source}</span>
              </td>
              <td className="font-medium">{tx.interaction}</td>
              <td className="font-medium">{tx.createdAt}</td>
              <td className="font-medium Left">
                <span className="percent">
                  {tx.agree_count} : {tx.oppose_count}
                </span>{" "}
                <UserStateAction tx={tx} state={txStatusMap[tx.txID]} />
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      {!loading && !list.length && <EmptyBox />}
      {loading && <Loading style={{ position: "absolute", top: "30%" }} />}
      <div style={{ textAlign: "right" }}>
        <PaginationBox
          showQuickJumper
          pageSize={pageSize}
          defaultCurrent={page}
          total={total}
          onChange={onChangePage}
          onShowSizeChange={onShowSizeChange}
        />
      </div>
    </TableBox>
  );
}

