const ButtonStyled = styled.span`
  display: inline-block;
  text-align: center;
  width: 83px;
  height: 24px;
  color: #ffffff;
  line-height: 24px;
  font-weight: 400;
  border-radius: 17px;
  font-size: 14px;
  cursor: pointer;
`;

const AgreeButton = styled(ButtonStyled)`
  background: #02a177;
`;

const DisagreeButton = styled(ButtonStyled)`
  background: #d51172;
`;

const StatusButton = styled(ButtonStyled)`
  background: #bfbfc1;
`;
