import { useState, useEffect, useMemo, useCallback } from "react";
import { Button } from "antd";
import styled from "styled-components";

import CopyStr from "../../copy";
import PublicJS from "../../../utils/publicJs";
import OngoingImg from "img/voteNV/Ongoing.png";
import RejectedImg from "../../../img/voteNV/Group.png";
import ConfirmedImg from "../../../img/voteNV/agree.png";
import LinkImg from "../../../img/shareGray.png";
import ConfirmImg from "../../../img/voteNV/Confirm.png";
import WrongImg from "../../../img/voteNV/wrong.png";
import moment from "moment";
import { useDaoContext } from "views/dao/provider";
import { useAppContext } from "components/provider/appProvider";
import { useDaoOrgContract } from "hooks/useContract";
import { DAO_ORG, ORG_MANAGER } from "utils/constant";
import useOrgMembers from "hooks/useOrgMembers";
import useSigner from "hooks/useSigner";

import { useLazyQuery } from "@apollo/client";
import { SubgraphApiQuery, getTxDetail } from "api/graphql/subQuery";
import { useWeb3React } from "@web3-react/core";
import Avatar from "components/common/avatar";
import { ethers } from "ethers";
import CopyButton from "components/common/copy";

import ConfirmModal from "./modals/confirm";
import RejectModal from "./modals/reject";
import api from "api";
import { useTranslation } from "react-i18next";

export const UserTxSTATUS = {
  Default: 0,
  Confirmed: 1,
  Rejected: 2,
};

export const TxSTATUS = {
  Default: 0,
  Confirmed: 1,
  Rejected: 2,
};

export default function VoteDetail(props) {
  const { id: txId } = props;
  const { t } = useTranslation();

  const { chainId } = useWeb3React();
  const {
    state: {
      componentAddressMap,
      fullHandle,
      daoId,
      whoAmI: { isOwner, isGP },
    },
  } = useDaoContext();
  const { dispatch } = useAppContext();

  const signer = useSigner();
  const orgMembersMap = useOrgMembers(componentAddressMap.get(ORG_MANAGER));
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));

  const [txData, setTxData] = useState({});

  const [voters, setVotes] = useState([]);

  const [confirmVisible, setConfirmVisible] = useState(false);
  const [rejectVisible, setRejectVisible] = useState(false);

  const formatStartTime = useCallback((time) => {
    return moment(time * 1000).format("MMMM Do YYYY, h:mm A");
  }, []);

  const [getDetail] = useLazyQuery(SubgraphApiQuery, {
    onCompleted(resp) {
      try {
        const data = JSON.parse(resp.subgraphApi).data;
        console.log("=====> ", data);
        setVotes(data.voters);
        if (data.info) {
          const tx = {
            ...data.info,
            createAt: formatStartTime(Number(data.info.createAt)),
          };
          setTxData(tx);
        }
      } catch (error) {
        console.error(error);
      }
    },
    onError(error) {
      console.error(error);
    },
  });

  useEffect(() => {
    if (!chainId || !daoId || txId === undefined) {
      return;
    }
    getDetail({
      variables: {
        chainId,
        query: getTxDetail(daoId, txId),
      },
    });
  }, [chainId, daoId, txId]);

  const dataBytes = useMemo(() => {
    if (txData.data) {
      try {
        return ethers.utils.arrayify(txData.data).length + "bytes";
      } catch (error) {
        return txData.data;
      }
    }
    return "";
  }, [txData]);

  const voteStatusIcon = useMemo(() => {
    switch (txData.status) {
      case 1:
        return ConfirmedImg;
      case 2:
        return RejectedImg;
      default:
        return OngoingImg;
    }
  }, [txData]);

  const showConfirm = useCallback(() => {
    setConfirmVisible(true);
  }, []);

  const showReject = useCallback(() => {
    setRejectVisible(true);
  }, []);

  const closeConfirm = useCallback(() => {
    setConfirmVisible(false);
  }, []);

  const closeReject = useCallback(() => {
    setRejectVisible(false);
  }, []);

  const handleConfirm = useCallback(
    async (flag) => {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
      try {
        await api.vault.confirmTransaction(daoOrg, signer, {
          id: txId,
          state: flag,
        });
      } catch (error) {
        console.error(error);
        dispatch({
          type: "MSGTYPE",
          payload: { msg: "excuted failed", closable: true },
        });
      } finally {
        dispatch({ type: "LOADINGTYPE", payload: null });
        confirmVisible && setConfirmVisible(false);
        rejectVisible && setRejectVisible(false);
      }
    },
    [daoOrg, signer, confirmVisible, rejectVisible, txId]
  );

  return (
    <ContentBox>
      <ConfirmModal
        show={confirmVisible}
        handleClose={closeConfirm}
        handleConfirm={handleConfirm}
      />
      <RejectModal
        show={rejectVisible}
        handleClose={closeReject}
        handleConfirm={handleConfirm}
      />
      <FirstLine>
        <LftBox>
          <img style={{ width: "54px" }} src={voteStatusIcon} alt="" />
        </LftBox>
        <RhtLine>
          <dl>
            <dt>Source</dt>
            <dd className="font-demiBold">{txData.source}</dd>
          </dl>
          <dl>
            <dt>Created time</dt>
            <dd className="font-demiBold">{txData.createAt}</dd>
          </dl>
          <dl>
            <dt>Confirm/Reject</dt>
            <dd className="font-demiBold">{`${txData.agree_count} : ${txData.oppose_count}`}</dd>
          </dl>
        </RhtLine>
      </FirstLine>
      <MainBox>
        <LeftBox>
          <dl>
            <dt>TxHash</dt>
            <AddressBox>
              {PublicJS.AddresstoShow(txData.txHash)}{" "}
              <CopyStr address={txData.txHash} gray={true} />
              <img src={LinkImg} alt="" />
            </AddressBox>
          </dl>
          <dl>
            <dt>Data（hex encoded）</dt>
            <dd>
              {dataBytes} <CopyButton value={txData.data} />
            </dd>
          </dl>
          {ethers.constants.AddressZero === txData.executor && (
            <dl className="flexLine">
              <dt>Executed</dt>
              <dd className="timeRht">n/a</dd>
            </dl>
          )}
        </LeftBox>
        <RightBox>
          <dl>
            <dt>Creator</dt>
            <dd>
              <ItemBox>
                <Avatar address={txData.creator} />
                <div>
                  <div className="name">{orgMembersMap[txData.creator]}</div>
                  <div className="address">
                    {PublicJS.AddresstoShow(txData.creator) + " "}
                    <CopyStr address={txData.creator} gray={true} />
                  </div>
                </div>
              </ItemBox>
            </dd>
          </dl>
          <dl>
            <dt>Votes ({`${txData.agree_count}:${txData.oppose_count}`})</dt>
            <dd>
              {voters.map((voter) => (
                <ItemLine key={voter.account}>
                  <img
                    style={{ width: "20px" }}
                    src={voter.state ? ConfirmImg : WrongImg}
                    alt=""
                    className="agreeImg"
                  />
                  <ItemBox>
                    <Avatar size="24px" address={voter.sender} />
                    <div>
                      <div>{orgMembersMap[voter.name]}123</div>
                      <div>
                        {PublicJS.AddresstoShow(voter.sender) + " "}
                        <CopyStr gray={true} address={voter.sender} />{" "}
                        {/* <TagBox>Hide</TagBox> */}
                      </div>
                    </div>
                  </ItemBox>
                </ItemLine>
              ))}
            </dd>
          </dl>
          <dl>
            {ethers.constants.AddressZero !== txData.executor && (
              <>
                <dt>Executed</dt>
                <dd>
                  <ItemBox>
                    <Avatar address={txData.executor} />
                    <div>
                      <div>{orgMembersMap[txData.executor]}</div>
                      <div>
                        {PublicJS.AddresstoShow(txData.executor)}{" "}
                        <CopyStr gray={true} address={txData.executor} />
                      </div>
                    </div>
                  </ItemBox>
                </dd>
              </>
            )}
          </dl>
        </RightBox>
      </MainBox>
      {txData.status === 0 && txData.state === UserTxSTATUS.Default && (
        <BtnGroup>
          <Button onClick={showConfirm}>Agree</Button>
          <Button className="disagree" onClick={showReject}>
            Disagree
          </Button>
        </BtnGroup>
      )}
    </ContentBox>
  );
}

