import { useEffect, useState, useCallback } from "react";
import { ethers } from "ethers";
import { useWeb3React } from "@web3-react/core";
import { useVaultContract } from "./useContract";
import api from "../api";
import { useDaoContext } from "views/dao/provider";
import { getShortDisplay } from "../utils/publicJs";
import { DaoNFTs } from "api/graphql/dao";
import { useLazyQuery } from "@apollo/client";

const useVaultNFTs = () => {
  const { chainId } = useWeb3React();

  const {
    state: { daoId },
  } = useDaoContext();

  const [list, setList] = useState([]);

  const [getList] = useLazyQuery(DaoNFTs, {
    onCompleted(data) {
      console.log("===data", data);
      setList(data.daoNfts || []);
    },
    onError(error) {
      console.error("query DaoNFTs failed", error);
    },
  });

  useEffect(() => {
    if (!chainId || !daoId) {
      return;
    }
    getList({
      variables: {
        chainId,
        daoId,
      },
    });
  }, [daoId, chainId]);

  return list;
};

export default useVaultNFTs;
