import styled from "styled-components";
import ExpiryImg from "../../img/voteNV/explains.png";
import { useCallback, useState, useEffect, memo } from "react";
import { Tooltip } from "antd";
import { useParams, useNavigate } from "react-router-dom";

import { useLazyQuery } from "@apollo/client";
import { QueryVoteQueue } from "api/graphql/vote";
import { useWeb3React } from "@web3-react/core";
import PaginationBox from "components/common/pagination";
import EmptyBox from "components/common/empty";
import { useDaoContext } from "views/dao/provider";
import useSigner from "hooks/useSigner";
import { useAppContext } from "components/provider/appProvider";
import { useTranslation } from "react-i18next";
import api from "api";
import { useDaoOrgContract, useVoteContract } from "hooks/useContract";
import { DAO_ORG, VOTE_MANAGER } from "utils/constant";
import { VOTE_INFO, VOTE_TYPE, formatVoteType } from "./detail";
import { ethers } from "ethers";
import moment from "moment";

const UserVoteStatus = {
  Default: 0,
  Agree: 1,
  Disagree: 2,
};

export default function Queue() {
  const navigate = useNavigate();
  const { t } = useTranslation();

  const { chainId } = useWeb3React();
  const {
    state: {
      daoId,
      fullHandle,
      componentAddressMap,
      whoAmI: { isOwner, isGP },
    },
  } = useDaoContext();
  const { dispatch } = useAppContext();

  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();

  const [page, setPage] = useState(1);
  const [pageSize, setPagesize] = useState(10);
  const [total, setToal] = useState(0);

  const [list, setList] = useState([]);
  const toGo = (id) => {
    navigate(`/dao/${fullHandle}/vote/${id}`, { state: "queue" });
  };

  const formatStartTime = useCallback((time) => {
    return moment(time * 1000).format("MMMM Do YYYY, h:mm A");
  }, []);
  const FormatLeftTime = useCallback(({ leftTime }) => {
    const days = parseInt(leftTime / 86400);
    let left = leftTime % 86400;
    const hours = parseInt(left / 3600);
    left = left % 3600;
    const minutes = parseInt(left / 60);
    const seconds = parseInt(left)
    if (days > 0) {
      return (
        <>
          <Num className="font-black">{days}</Num> d{" "}
          <Num className="font-black">{hours}</Num> h{" "}
          <Num className="font-black">{minutes}</Num> min left
        </>
      );
      // return `${days} d ${minutes} min left`;
    }
    if (hours > 0) {
      return (
        <>
          <Num className="font-black">{hours}</Num> h{" "}
          <Num className="font-black">{minutes}</Num> min left
        </>
      );
    }
    if (minutes > 0) {
      return (
        <>
          <Num className="font-black">{minutes}</Num> min left
        </>
      );
      // return `${minutes} min left`;
    }
    if(minutes <=0 && seconds>0){
      return <span>1 min left</span>;
    }
    return <span>0 min left</span>;
  }, []);

  const doChoose = async (e, vid, selected) => {
    e.stopPropagation();

    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    try {
      await api.vote.doVote(daoOrg, signer, vid, selected);
    } catch (error) {
      console.error(error);
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: `Vote failed: ${
            (error.data && error.data.message) || "unknown"
          }`,
          closable: true,
        },
      });
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  const [queryVotes] = useLazyQuery(QueryVoteQueue, {
    onCompleted(data) {
      console.log("===data", data);
      setList(
        data.voteQueueList.data.map((v) => ({
          ...v,
          startAt: formatStartTime(v.start_time),
          vote_type: formatVoteType(v.metadata),
          endTime: v.start_time + v.vote_time,
          wid: parseInt(v.metadata.split("#")[1]),
          left: v.start_time + v.vote_time - Math.round(Date.now() / 1000),
        }))
      );
      setToal(data.voteQueueList.total);
    },
    onError(error) {
      console.error("===error", error);
    },
  });

  const onChangePage = useCallback((v) => {
    setPage(v);
  }, []);

  const onShowSizeChange = useCallback((c, size) => {
    setPagesize(size);
  }, []);

  useEffect(() => {
    if (!daoId || !chainId) {
      return;
    }
    queryVotes({
      variables: {
        daoId,
        chainId,
        page,
        size: pageSize,
      },
      fetchPolicy: 'network-only',
    });
  }, [daoId, chainId, page, pageSize]);

  const voteContract = useVoteContract(componentAddressMap.get(VOTE_MANAGER));
  useEffect(async () => {
    if (voteContract) {
      const len = await api.vote.getVotesLength(voteContract);
      console.warn("=====len", len);
    }
  }, [voteContract]);

  const doExcute = async (e, vote) => {
    e.stopPropagation();
    if (!isOwner && !isGP) {
      return;
    }

    dispatch({ type: "LOADINGTYPE", payload: t("Msg.Loading") });
    try {
      const tp = vote.vote_type;

      if (tp === VOTE_TYPE.ADD_MANAGER) {
        await api.vote.executeAddGPVote(daoOrg, signer, vote.vote_id);
      } else if (tp === VOTE_TYPE.REMOVE_MANAGER) {
        await api.vote.executeKickGPVote(daoOrg, signer, vote.vote_id);
      } else if (tp === VOTE_TYPE.TRANSFER_NFT) {
        await api.vote.executeTransferNFTVote(daoOrg, signer, vote.wid);
      } else if (tp === VOTE_TYPE.TRANSFER_TOKEN) {
        await api.vote.executeWithdrawVote(daoOrg, signer, vote.wid);
      } else if (tp === VOTE_TYPE.TRANSFER_PERIOD) {
        await api.vote.executeTransferPeriodVote(daoOrg, signer, vote.vote_id);
      } else if (tp === VOTE_TYPE.TRANSFER_MANAGE_FEE) {
        await api.vote.executeTransferManageFeeVote(
          daoOrg,
          signer,
          vote.wid
        );
      } else if (tp === VOTE_TYPE.APPROVE_MANAGER) {
        await api.vote.executeApproveGPVote(daoOrg, signer, vote.vote_id);
      }
    } catch (error) {
      dispatch({
        type: "MSGTYPE",
        payload: {
          msg: `Execute failed: ${
            (error?.error?.message) || "unknown"
          }`,
          closable: true,
        },
      });
    } finally {
      dispatch({ type: "LOADINGTYPE", payload: null });
    }
  };

  const Action = memo(({ vote }) => {
    const action = vote.action;
    // check expire time
    const left = vote.endTime - Math.ceil(Date.now() / 1000);
    if (left > 0 && (isOwner  || isGP)) {

      if (action === UserVoteStatus.Default ) {
        return (
          <>
            <span
              className="agree"
              onClick={(e) => doChoose(e, vote.vote_id, true)}
            >
              Agree
            </span>{" "}
            <span
              className="disagree"
              onClick={(e) => doChoose(e, vote.vote_id, false)}
            >
              Disagree
            </span>
          </>
        );
      } else if (action === UserVoteStatus.Agree) {
        return <span className="agreed">Agreed</span>;
      } else if (action === UserVoteStatus.Disagree) {
        return <span className="agreed">Disagree</span>;
      }
    } else {
      return !isOwner && !isGP ? (
        <></>
      ) : (
        <span className="agree" onClick={(e) => doExcute(e, vote)}>
          Execute
        </span>
      );
    }
  });

  // useEffect(() => {
  //   setList([
  //     {
  //       vote_id: 4,
  //       create_at: "May 24th 2022, 11:59 PM",
  //       startAt: formatStartTime(Math.round(Date.now() / 1000)),
  //       left: 10 * 60 * 60,
  //       startTime: Math.round(Date.now() / 1000),
  //       voteTime: 10 * 60 * 60,
  //       endTime: Math.round(Date.now() / 1000) + 10 * 60 * 60,
  //       agree_count: 1,
  //       oppose_count: 1,
  //       action: UserVoteStatus.Default,
  //       vote_type: VOTE_TYPE.APPROVE_MANAGER,
  //     },
  //     {
  //       vote_id: 5,
  //       create_at: "May 24th 2022, 11:59 PM",
  //       startAt: formatStartTime(Math.round(Date.now() / 1000)),
  //       left: 10 * 60 * 60,
  //       startTime: Math.round(Date.now() / 1000),
  //       voteTime: 10 * 60 * 60,
  //       endTime: Math.round(Date.now() / 1000) + 10 * 60 * 60,
  //       agree_count: 1,
  //       oppose_count: 1,
  //       action: UserVoteStatus.Agree,
  //       vote_type: VOTE_TYPE.TRANSFER_MANAGE_FEE,
  //     },
  //     {
  //       vote_id: 0,
  //       create_at: "May 24th 2022, 11:59 PM",
  //       startAt: formatStartTime(Math.round(Date.now() / 1000)),
  //       left: 10 * 60 * 60,
  //       startTime: Math.round(Date.now() / 1000),
  //       voteTime: 10 * 60 * 60,
  //       endTime: Math.round(Date.now() / 1000) + 10 * 60 * 60,
  //       agree_count: 1,
  //       oppose_count: 1,
  //       action: UserVoteStatus.Default,
  //       vote_type: VOTE_TYPE.APPROVE_MANAGER,
  //     },
  //     {
  //       vote_id: 3,
  //       create_at: "May 24th 2022, 11:59 PM",
  //       startAt: formatStartTime(Math.round(Date.now() / 1000)),
  //       left: 10 * 60 * 60,
  //       startTime: Math.round(Date.now() / 1000),
  //       voteTime: 10 * 60 * 60,
  //       endTime: Math.round(Date.now() / 1000)-10000,
  //       agree_count: 1,
  //       oppose_count: 1,
  //       action: UserVoteStatus.Default,
  //       vote_type: VOTE_TYPE.APPROVE_MANAGER,
  //     },
  //   ]);
  // }, []);

  return (
    <>
      <TableBox>
        <table cellPadding="0" cellSpacing="0">
          <thead>
            <tr>
              <th>No.</th>
              <th>Type</th>
              <th>Created time</th>
              <th className="status">Agree/Disagree</th>
              <th>
                Expiry
                <Tooltip title="If no one votes or executes within the validity period, the proposal will automatically become invalid.">
                  <img src={ExpiryImg} alt="" />
                </Tooltip>
              </th>
            </tr>
          </thead>
          <tbody>
            {list.map((vote) => (
              <tr key={vote.vote_id} onClick={() => toGo(vote.vote_id)}>
                <td className="font-medium">{vote.vote_id}</td>
                <td className="font-medium Left">
                  <img src={VOTE_INFO[vote.vote_type].icon} alt="" />
                  <span>{t(VOTE_INFO[vote.vote_type].shortName)}</span>
                </td>
                <td className="font-medium">{vote.startAt}</td>
                <td className="font-medium Left">
                  <span className="percent">
                    {vote.agree_count} : {vote.oppose_count}
                  </span>{" "}

                  <Action vote={vote} />
                </td>
                <td className="font-medium">
                  <FormatLeftTime leftTime={vote.left} />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        {!list.length && <EmptyBox />}
      </TableBox>
      <>
        {!!list.length && (
          <Pagination>
            <PaginationBox
              showQuickJumper
              pageSize={pageSize}
              defaultCurrent={page}
              total={total}
              onChange={onChangePage}
              onShowSizeChange={onShowSizeChange}
            />
          </Pagination>
        )}
      </>
    </>
  );
}

const TableBox = styled.div`
  position: relative;
  height: calc(100% - 60px);
  overflow: auto;
  table {
    width: 100%;
  }
  th {
    background: #f5f5f7;
    height: 54px;
    color: #b2b4c5;
    font-size: 16px;
    &:first-child {
      border-top-left-radius: 14px;
      border-bottom-left-radius: 14px;
      padding-left: 20px;
    }
    &:last-child {
      border-top-right-radius: 14px;
      border-bottom-right-radius: 14px;
      padding-right: 80px;
    }
    &.status {
      text-align: left;
      margin-left: 70px;
    }
  }
  td {
    text-align: center;
    white-space: nowrap;
    cursor: pointer;
    height: 65px;
    font-size: 16px;
    font-weight: 400;
    color: #0e164e;
    &:first-child {
      padding-left: 20px;
    }
    &:last-child {
      padding-right: 80px;
    }
    img {
      display: inline-block;
      width: 24px;
      margin: 0 9px 3px 0;
    }
  }
  tr:hover {
    td {
      background: #fbfbfa;
      &:first-child {
        border-top-left-radius: 14px;
        border-bottom-left-radius: 14px;
      }
      &:last-child {
        border-top-right-radius: 14px;
        border-bottom-right-radius: 14px;
      }
    }
  }
  .Left {
    text-align: left;
    padding-left: 20px;
    padding-right: 20px;
  }
  .agree,
  .disagree,
  .agreed {
    display: inline-block;
    text-align: center;
    width: 83px;
    height: 24px;
    color: #ffffff;
    line-height: 24px;
    font-weight: 400;
    border-radius: 17px;
    font-size: 14px;
    cursor: pointer;
  }
  .agree {
    background: #02a177;
  }
  .disagree {
    background: #d51172;
  }
  .agreed {
    background: #bfbfc1;
  }
  .percent {
    width: 80px;
    text-align: center;
    display: inline-block;
  }
  .symbol {
    width: 46px;
    height: 46px;
    border-radius: 46px;
    margin: 8px 0;
  }
  .symbolLft {
    width: 22px;
    height: 22px;
    border-radius: 22px;
    margin: 0 3px;
  }
  .edit {
    margin-right: 12px;
  }
`;
const Num = styled.span`
  letter-spacing: 5px;
`;

const Pagination = styled.div`
  text-align: right;
  margin-top: 10px;
`;
