import { useEffect, useCallback, useState } from "react";
import { useWeb3React } from "@web3-react/core";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

import { TokenAvatar } from "components/common/avatar";
import { useAppContext } from "components/provider/appProvider";
import CloseImg from "../../img/closeImg.png";
import SearchImg from "../../img/portfolio/search.png";

import { useLazyQuery, useMutation } from "@apollo/client";
import { QueryTokens, AddMyToken } from "api/graphql/explore";

export default function AddModal(props) {
  const { closeModal } = props;
  const { t } = useTranslation();
  const { chainId, provider } = useWeb3React();
  const { dispatch } = useAppContext();

  const [searchVal, setSearchVal] = useState();
  const [tokenList, setTokenList] = useState([]);
  const [showList, setShowList] = useState([]);

  const [queryTokens] = useLazyQuery(QueryTokens, {
    onCompleted(data) {
      console.log("===data", data);
      setTokenList(data.tokenList.data || []);
    },
    onError(error) {
    },
  });

  const [saveAddToken] = useMutation(AddMyToken, {
    onCompleted(data) {
      console.log("===data", data);
      dispatch({ type: "LOADINGTYPE", payload: null });
      closeModal();
    },
    onError(error) {
      dispatch({ type: "LOADINGTYPE", payload: null });
    },
  });

  const addToken = useCallback(
    async (token) => {
      dispatch({ type: "LOADINGTYPE", payload: t("Msg.AddingToken") });
      saveAddToken({
        variables: {
          chainId,
          daoId: token.dao_id,
          symbol: token.symbol,
          address: token.address,
          name: token.name
        },
      });
    },
    [chainId, provider, closeModal]
  );

  const updateSearchVal = useCallback(({ target: { value } }) => {
    setSearchVal(value);
  }, []);

  useEffect(() => {
    // TODO get token list
  }, []);

  useEffect(() => {
    if (searchVal) {
      const v = searchVal.toLowerCase();
      const list = tokenList.filter((t) => {
        return (
          t.address.toLowerCase() === v ||
          t.name.toLowerCase().includes(v) ||
          t.symbol.toLowerCase().includes(v)
        );
      });
      setShowList(list);
    } else {
      setShowList(tokenList);
    }
  }, [tokenList, searchVal]);

  useEffect(() => {
    if (!chainId) {
      return;
    }
    queryTokens({
      variables: {
        chainId,
        page: 1,
        size: 100000,
      },
    });
  }, [chainId]);

  return (
    <AddBox>
      <Bg>
        <Title>
          <span className="font-medium">{t("Explore.AddToken")}</span>
          <CloseBox onClick={closeModal}>
            <img src={CloseImg} alt="" />
          </CloseBox>
        </Title>
        <SearchBox>
          <img src={SearchImg} alt="" />
          <input
            type="text"
            placeholder={t("Explore.TokenPlaceholder")}
            onChange={updateSearchVal}
          />
        </SearchBox>
        <ScrollBox>
          <UlBox>
            {showList.map((item, index) => (
              <li key={`list_${index}`} onClick={() => addToken(item)}>
                <TokenAvatar
                  className="avatar"
                  size="34px"
                  symbol={item.symbol}
                />
                <div>
                  <div className="symbol">{item.symbol}</div>
                  <div className="name">{item.name}</div>
                </div>
              </li>
            ))}
          </UlBox>
        </ScrollBox>
      </Bg>
    </AddBox>
  );
}

const AddBox = styled.div`
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(247, 247, 247, 0.9);
  left: 0;
  top: 0;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const Bg = styled.div`
  width: 423px;
  height: 475px;
  background: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(16, 22, 75, 0.1);
  border-radius: 18px;
  padding: 30px 0;
`;
const Title = styled.div`
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  color: #10164b;
  line-height: 22px;
  position: relative;
  padding: 0 30px;
`;
const CloseBox = styled.div`
  position: absolute;
  right: 10px;
  top: -10px;
  cursor: pointer;
`;
const SearchBox = styled.div`
  height: 44px;
  background: #ffffff;
  border-radius: 22px;
  border: 1px solid #eeecee;
  display: flex;
  align-items: center;
  padding: 0 16px;
  margin: 20px 30px;
  img {
    margin-right: 7px;
  }
  input {
    border: 0;
    flex-grow: 1;
  }
`;

const UlBox = styled.div`
  li {
    display: flex;
    align-items: center;
    padding: 10px 30px;
    cursor: pointer;
    &:hover {
      background: #fbfbfa;
    }
    .avatar {
      border-radius: 50%;
      margin-right: 8px;
    }
    .symbol {
      font-size: 14px;
      font-weight: 400;
      color: #0e164e;
      line-height: 16px;
    }
    .name {
      font-size: 12px;
      font-weight: 400;
      color: #afafc0;
      line-height: 14px;
      margin-top: 5px;
    }
  }
`;
const ScrollBox = styled.div`
  overflow-y: auto;
  height: 220px;
`;
